<!-- 2017/3/22-提醒 -->
<template>
  <section class="remind" id="remind" style="padding: 20px 40px 0 40px;">
    <SubNav :ListSubNav="ListSubNav" @subNav="tabSwitch"></SubNav>
    <keep-alive>
      <component :is='currentView'
                 :payMeList="PayEveryList"
                 :payPageCount="payPageCount"
                 :payPageSize="payPageSize"
                 @payPageChange="payPageChange"
                 :receiveList="reveiveEveryList"
                 :receivePageCount="receivePageCount"
                 :receivePageSize="receivePageSize"
                 @receivePageChange="receivePageChange"
                 :commonList="commonEveryList"
                 :commonPageCount="commonPageCount"
                 :commonPageSize="commonPageSize"
                 @commonPageChange="commonPageChange"
                 :replyList="replyEveryList"
                 :replyPageCount="replyPageCount"
                 :replyPageSize="replyPageSize"
                 @replyPageChange="replyPageChange"
      ></component>
    </keep-alive>
  </section>
</template>

<script>

  import SubNav from 'components/Message/SubNav' // 关注组件

  import Payme from './Remind/Payme' // 关注组件
  import Received from './Remind/Received' // 收到赞的组件
  import Comment from './Remind/Comment' // 评论组件
  import Reply from './Remind/Reply' // 评论组件
  import {mapGetters} from "vuex"
  export default {
    name:"remind",
    data: () => {
      return {
        currentView:Payme,
        ListSubNav:[
          {name:"关注",bol:true,tab:"Payme",num:0},
          {name:"赞",bol:false,tab:"Received",num:0},
          {name:"评论",bol:false,tab:"Comment",num:0},
          {name:"回复",bol:false,tab:"Reply",num:0},
        ],
        PaymeList:[],
        PayEveryList:[],
        payPageSize:15,  // 关注 每页的个数
        payPageCount:0,  //关注 总个数
        receiveList:[],
        reveiveEveryList:[],
        receivePageSize:15,  //  赞每页的个数
        receivePageCount:0,  // 赞总页数
        commonList:[],
        commonEveryList:[],
        commonPageSize:10,   // 评论每页的个数
        commonPageCount:0, // 评论总个数
        replyList:[],
        replyEveryList:[],
        replyPageSize:8,  // 回复每页的个数
        replyPageCount:0// 回复总个数
      }
    },
    components:{
      SubNav,
      Payme,
      Received,
      Comment,
      Reply
    },
    computed: mapGetters(['remindNum']),
    watch: {
      remindNum(val){
        this.ListSubNav[2].num = val;
      },
    },
    mounted(){
      this.getRemindList();
    },
    methods:{
      tabSwitch(index) {
        for(var i = 0; i < this.ListSubNav.length; i++){
          this.ListSubNav[i].bol = false;
          if(index == i){
            this.ListSubNav[i].bol = true;
            this.currentView = this.ListSubNav[i].tab;
          }
        }
        if(index == 2){  // 此地方接口写未读的评论
          this.getToken().then(token=>{
            this.$http.get('comment/markread', {  // 关注
              params: {
                access_token: token,
                pid:0,
                uid:7
              }
            }).then(res => {
              if (res.data.stats === '2000') {
                this.getRemindList();// 如果成功，回调，获取列表的接口再请求一次
              }
            }).catch(err => {
              console.log(err);
            });
          })
        }
      },
      payPageChange(val){
        this.pagination(val,this.PaymeList,"PayEveryList",this.payPageSize);
      },
      receivePageChange(val){
        this.pagination(val,this.receiveList,"reveiveEveryList",this.receivePageSize);
      },
      commonPageChange(val){
        this.pagination(val,this.commonList,"commonEveryList",this.commonPageSize);
      },
      replyPageChange(val){
        this.pagination(val,this.replyList,"replyEveryList",this.replyPageSize);
      },
      getRemindList(){
        let _this = this;
        this.getToken().then(token=> {
          this.$http.get('message/showmessage', {  // 关注
            params: {
              access_token: token,
              type:1,
              uid:20
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this.PaymeList = res.data.data.list;
              _this.payPageChange(1);
              _this.payPageCount = res.data.data.list.length;
            }
          }).catch(err => {
            console.log(err);
          });
          this.$http.get('message/showmessage', {  // 赞
            params: {
              access_token: token,
              type:2,
              uid:34
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this.receiveList = res.data.data.list;
              _this.receivePageChange(1);
              _this.receivePageCount = res.data.data.list.length;
            }
          }).catch(err => {
            console.log(err);
          });
          this.$http.get('comment/showmycomment', {  // 评论
            params: {
              access_token: token,
              pid:0,
              uid:7
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this.commonList = res.data.data.list;
              _this.commonPageChange(1);
              _this.commonPageCount = res.data.data.list.length;
              let a = 0; // 评论未读条数
              res.data.data.list.forEach((item,index)=>{
                  if(res.data.data.list[index].readstats === "1"){
                    a++;
                  }else {
                    a = 0
                  }
              })
              this.$store.dispatch('onSubmitComment',a);
            }
          }).catch(err => {
            console.log(err);
          });
          this.$http.get('comment/showmycomment', {  // 回复
            params: {
              access_token: token,
              pid:1,
              uid:7
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this.replyList = res.data.data.list;
              _this.replyPageChange(1);
              _this.replyPageCount = res.data.data.list.length;
            }
          }).catch(err => {
            console.log(err);
          });
        })
      }
    }
  }
</script>





