<!-- 2017/3/22- yyy -个人主页 收藏-->
<template>
  <section>
    <section class="workicon">
      <personalNav :ListComNav="ListComNav" @homeCom="tabSwitch" class="workicon__nav"></personalNav>
      <keep-alive>
        <component :is='currentView'
                   :loadOver="loadOver"
                   @loadMore="loadMore"
                   :items="lists"
                   :loadMoreShow="loadMoreShow"
                   :ComShop="ShopEveryList"
                   :shopPageCount="shopPageCount"
                   :shopPageSize="shopPageSize"
                   @shopPageChange="shopPageChange"
                   :ComSell="SellEveryList"
                   :sellPageCount="sellPageCount"
                   :sellPageSize="sellPageSize"
                   @sellPageChange="sellPageChange"
                   :ComCommercial="CommercialEveryList"
                   :commerPageCount="commerPageCount"
                   :commerPageSize="commerPageSize"
                   @commerPageChange="commerPageChange"
                   :ComManu="manuEveryList"
                   :manuPageCount="manuPageCount"
                   :manuPageSize="manuPageSize"
                   @manuPageChange="manuPageChange"
                   :ComCollect="CollectEveryList"
                   :collectPageCount="collectPageCount"
                   :collectPageSize="collectPageSize"
                   @collectPageChange="collectPageChange"
                   :FabuActivityList="fabuEveryList"
                   :fabuPageCount="fabuPageCount"
                   :fabuPageSize="fabuPageSize"
                   @fabuPageChange="fabuPageChange"
        ></component>
      </keep-alive>
      <div class="workicon__propmt" v-if="workiconPropmt">暂无数据</div>
    </section>
  </section>
</template>


<script>

  import personalNav from 'components/Personal/ComNav';
  import ComPaint from 'components/Common/Flow';  // 个人画作公共部分
  import ComShop from 'components/Common/ComShop';  // 个人画铺公共部分
  import ComSell from 'components/Common/ComSell';  // 个人待出售设计公共部分
  import ComCommercial from 'components/Personal/ComCommercial';  // 个人待出售设计公共部分
  import ComManu from 'components/Search/ComManuscripts';  // 约稿组件
  import ComCollect from 'components/Search/ComCollect' // 征稿组件
  import ComActivity from 'components/Personal/MeFabuActivity';  // 活动
  import Nosearch from 'components/Common/Nosearch' // 没有搜索内容的时候，展示的内容

  export default {
    data: () => {
      return {
        ListComNav:[
          {name:"画作",bol:true,tab:"ComPaint"},
          {name:"画铺",bol:false,tab:"ComShop"},
          {name:"待出售设计",bol:false,tab:"ComSell"},
          {name:"已商用设计",bol:false,tab:"ComCommercial"},
          {name:"约稿",bol:false,tab:"ComManu"},
          {name:"征稿",bol:false,tab:"ComCollect"},
          {name:"活动",bol:false,tab:"ComActivity"},
        ],
        currentView:ComPaint,
        workiconPropmt:false,
        loadOver:"点击加载更多...",
        loadMoreShow:false,
        lists:[],  // 动态获取数据
        paintPageSize:30, // 每页展示多少个画作
        paintPageIndex:1,
        paintLastCount:0,
        ShopEveryList:[],
        shopPageSize:30, // 个人主页-->收藏-->画铺每页显示几条
        shopPageCount:0, // 个人主页-->收藏-->画铺总页数
        shopPageIndex:1,
        shopLastCount:0,
        SellEveryList:[],
        sellPageSize:30,  // 个人主页-->收藏-->待出售设计每页个数
        sellPageCount:0, //个人主页-->收藏-->待出售设计总个数
        sellPageIndex:1,
        sellLastCount:0,
        CommercialEveryList:[],
        commerPageSize:30,  // 个人主页-->作品-->已商用每页个数
        commerPageCount:0, // 个人主页-->作品-->已商用总个数
        commerPageIndex:1,
        commerLastCount:0,
        manuEveryList:[],
        manuPageCount:0, //  收藏-->约稿总页数
        manuPageSize:20,  // 收藏-->约稿总每页的个数
        manuPageIndex:1,
        manuLastCount:0,
        CollectEveryList:[],
        collectPageCount:0, // 收藏-->征稿总页数
        collectPageSize:20,  // 收藏-->征稿每页的个数
        collectPageIndex:1,
        collectLastCount:0,
        fabuEveryList:[],
        fabuPageCount:0,  // 收藏-->活动总个数
        fabuPageSize:10,  // 收藏-->活动每页个数
        fabuPageIndex:1,
        fabuLastCount:0,
      }
    },
    components:{
      personalNav,
      ComPaint,
      ComShop,
      ComSell,
      ComCommercial,
      ComManu,
      ComCollect,
      ComActivity
    },
    mounted(){
      this.getCollectList()
    },
    methods:{
      tabSwitch(index){
        for(let i = 0; i < this.ListComNav.length; i++){
          this.ListComNav[i].bol = false;
          if(index === i){
            this.ListComNav[i].bol = true;
            this.currentView = this.ListComNav[i].tab;
          }
        }
        switch (index){
          case 0:
            this.workiconPropmt = false;
            break;
          case 1:
            (this.shopLastCount === 0)? (this.workiconPropmt = true) : (this.workiconPropmt = false);
            break;
          case 2:
            (this.sellLastCount === 0)? (this.workiconPropmt = true) : (this.workiconPropmt = false);
            break;
          case 3:
            (this.commerLastCount === 0)? (this.workiconPropmt = true) : (this.workiconPropmt = false);
            break;
          case 4:
            (this.manuLastCount === 0)? (this.workiconPropmt = true) : (this.workiconPropmt = false);
            break;
          case 5:
            (this.collectLastCount === 0)? (this.workiconPropmt = true) : (this.workiconPropmt = false);
            break;
          case 6:
            (this.fabuLastCount === 0)? (this.workiconPropmt = true) : (this.workiconPropmt = false);
            break;
        }
      },
      shopPageChange(val){
        this.shopPageIndex = val;
        this.getCollectList();
      },
      sellPageChange(val){
        this.sellPageIndex = val;
        this.getCollectList();
      },
      commerPageChange(val){
        this.commerPageIndex = val;
        this.getCollectList();
      },
      manuPageChange(val){
        this.manuPageIndex = val;
        this.getCollectList();
      },
      collectPageChange(val){
        this.collectPageIndex = val;
        this.getCollectList();
      },
      fabuPageChange(val){
        this.fabuPageIndex = val;
        this.getCollectList();
      },
      loadMore(){ // 加载更多图片
        this.paintPageIndex++;
        this.getCollectList();
      },
      getCollectList(){
        let _this = this;
        this.getToken().then(token => {
          this.$http.get('huazuo/findAllhuazuo', { // 我收藏的画作
            params: {
              access_token: token,
              order:"createtime",
              collectuid:8,    // 收藏用户的id 就是个人用户uid
              page: _this.paintPageIndex,  // 一次展示多少个
              show: _this.paintPageSize, // 第几页
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              this.lists = res.data.data.list;
              this.workiconPropmt = false;
              this.loadMoreShow = true;
              this.paintLastCount = res.data.data.list.length;
              if(res.data.data.list.length === 0){
                this.loadOver = "全部加载完毕或暂无数据";
              }
            }
          }).catch(err => {
            console.log(err);
          });

          this.$http.get('huapu/findAllhuapu', { // 我收藏的画铺
            params: {
              access_token: token,
              uid:2966,
              filter:'follow',  // 用的时候在添加这个字段，现在还没有数据
              page: _this.shopPageIndex,
              show: _this.shopPageSize   // 一页展示多少个
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this.ShopEveryList = res.data.data.list;
              _this.shopPageCount = parseInt(res.data.data.total);
              _this.shopLastCount = res.data.data.list.length;
            }
          }).catch(err => {
            console.log(err);
          });

          this.$http.get('sheji/findallsheji', { // 我收藏-->待出售设计
            params: {
              access_token: token,
              busintype:1,   // 1 未商用
              collectuid:6,  // 收藏用户的id  uid
              page: _this.sellPageIndex,
              show: _this.sellPageSize
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this.SellEveryList = res.data.data.list;
              _this.sellPageCount = parseInt(res.data.data.total);
              _this.sellLastCount = res.data.data.list.length
            }
          }).catch(err => {
            console.log(err);
          });

          this.$http.get('sheji/findallsheji', { // 我收藏-->已商用设计
            params: {
              access_token: token,
              busintype:2,  // 已商用
              collectuid:5, // 收藏用户的id uid
              page: _this.commerPageIndex,
              show: _this.commerPageSize
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this.CommercialEveryList = res.data.data.list;
              _this.commerPageCount = parseInt(res.data.data.total);
              _this.commerLastCount = res.data.data.list.length
            }
          }).catch(err => {
            console.log(err);
          });

          this.$http.get('yuegao/findallyuegao', { // 我收藏-->约稿
            params: {
              access_token: token,
              collectuid:6,
              page: _this.manuPageIndex,
              show: _this.manuPageSize
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this.manuEveryList = res.data.data.list;
              _this.manuPageCount = parseInt(res.data.data.total);
              _this.manuLastCount = res.data.data.list.length
            }
          }).catch(err => {
            console.log(err);
          });

          this.$http.get('zhenggao/findallzhenggao', { // 我收藏-->征稿
            params: {
              access_token: token,
              collectuid:8,
              order:'time',
              page: _this.collectPageIndex,
              show: _this.collectPageSize
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this.CollectEveryList = res.data.data.list;
              _this.collectPageCount = parseInt(res.data.data.total);
              _this.collectLastCount = res.data.data.list.length
            }
          }).catch(err => {
            console.log(err);
          });

          this.$http.get('huodong/findallhuodong', { // 我收藏-->活动
            params: {
              access_token: token,
              uid:5,
              condition:"collect",
              page: _this.fabuPageIndex,
              show: _this.fabuPageSize
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this.fabuEveryList = res.data.data.list;
              _this.fabuPageCount = parseInt(res.data.data.total);
              _this.fabuLastCount = res.data.data.list.length
            }
          }).catch(err => {
            console.log(err);
          });
        })
      }
    }
  }

</script>

<style lang="scss" scoped>

  @import "../../common/css/reset.css";
  @import "../../common/css/define";

  .workicon__nav {
    margin: 40px 0 20px 0;
  }
  .workicon__propmt {
    width: 1180px;
    text-align: center;
    margin: 30px auto;
    font-size: 16px;
    font-style: italic;
    color: $paleBlue;
    cursor: pointer;
  }

</style>



