<!--约稿画师组件-->
<template>
  <div class="pact-painter">
    <PactNav
      :linkNav="'javascript:;'"
      :menus="lists"
      class="l-margin"
      @change="change"></PactNav>
    <PainterView
      :value="margin"
      :painter="items"
      :items="lists2">
      <Pagination
        :pageCount="pageCount"
        :pageSize="pageSize"
        @pageChange="pageChange"
        slot="page">
      </Pagination>
    </PainterView>
  </div>
</template>

<!--数据交互-->
<script>
  import PactNav from 'components/Pact/PactNav.vue'
  import PainterView from 'components/Index/Painter'
  import Pagination from 'components/Common/Pagination'

  export default {
    components: {
      PactNav,
      PainterView,
      Pagination
    },
    data: () => ({
      lists: {
        menu: [
          {title: '最近更新', isActive: true, orderBy: ''},
          {title: '按点赞数', isActive: false, orderBy: 'over'},
          {title: '官方推荐', isActive: false, orderBy: 'recommend'},
          {title: '我关注的', isActive: false, orderBy: 'follow'}
        ], btn: "申请推荐"
      },
      items: [], //画师列表
      margin: 20,//painter__pic 行间样式
      lists2: [1, 3, 4],
      params: {order: '', filter: '', uid: ''},
      pageSize: 30, // 每页显示的几条
      pageCount: 0,  // 总条数
      pageIndex: 1,  //当前页数
    }),
    mounted(){
      this.getData();
    },
    methods: {
      change(index){ //菜单切换
        this.lists.menu.forEach(item => {
          item.isActive = false;
        });
        this.lists.menu[index].isActive = true;
        this.params.order = this.lists.menu[index].orderBy;
        this.getData();
      },

      getData(){  //获取画师数据
        this.getToken().then(token => {
          this.$http.get('painter/jiegaopainterlist', {
            params: {
              access_token: token,
              order: this.params.order,
              filter: '',
              uid: '',
              page: this.pageIndex
            }
          }).then(res => {
            this.pageCount = parseInt(res.data.data.total);
            this.items = res.data.data.list;
          }).catch(err => {
            console.log(err);
          })
        })
      },

      //分页调用
      pageChange(val){
        this.pageIndex = val;
        this.getData()
      }
    }

  }
</script>

<!--组件样式-->
<style scoped lang="scss">
  @import "../../common/css/define";

  .pact-painter {
    margin-bottom: 20px;
    .l-margin {
      margin: 40px auto 20px;
    }
    .painter {
      background-color: #fff;
      max-width: 1160px;
      padding: 10px 10px 40px 10px;
      @include wrapShow;

      .block {
        margin-top: 40px;
      }
    }
  }
</style>



