<!-- 2017/3/22- 约稿通知-->
<template>
  <section class="manuscripts" id="manuscripts" style="padding: 20px 40px 0 40px;">
    <SubNav :ListSubNav="ListSubNav" @subNav="tabSwitch"></SubNav>
    <keep-alive>
      <component :is='currentView'
                 :yingzhengTongzhiList="yingzhengEveryList"
                 :yingzhengPageSize="yingzhengPageSize"
                 :yingzhengPageCount="yingzhengPageCount"
                 @yingzhengPageChange="yingzhengPageChange"
                 :refuseTongzhiList="refuseEveryList"
                 :refusePageCount="refusePageCount"
                 :refusePageSize="refusePageSize"
                 @refusePageChange="refusePageChange"
      ></component>
    </keep-alive>
  </section>
</template>

<script>

  import SubNav from 'components/Message/SubNav' // 关注组件
  import Notapp from './Manuscripts/Notapp' // 应征通知组件
  import Declined from './Manuscripts/Declined' // 选用/婉拒组件
  import {mapGetters} from "vuex"
  export default {
    name:"manuscripts",
    components: {
      SubNav,
      Notapp,
      Declined
    },
    data: () => {
      return {
        currentView:"Notapp",
        ListSubNav:[
          {name:"应征通知",bol:true,tab:"Notapp",num:0},
          {name:"选用/婉拒通知",bol:false,tab:"Declined",num:0}
        ],
        yingzhengEveryList:[],  // 应征通知每页列表
        yingzhengPageSize:10,  // 应征通知共多少页
        yingzhengPageCount:0, // 征通知总页数
        yingzhengPageIndex:1,
        refuseEveryList:[],  // 选用/婉拒通知每页列表
        refusePageSize:10, // 选用/婉拒通知一页多少数据
        refusePageCount:0,  // 选用/婉拒通知总页数
        refusePageIndex:1
      }
    },
    mounted(){
      this.getManuList();
      this.readMessage();
    },
    computed: mapGetters(['manuscriptsYingzhengNum','manuscriptsRefuseNum']),
    watch: {
      manuscriptsYingzhengNum(val){
        this.ListSubNav[0].num = val;
      },
      manuscriptsRefuseNum(val){
        this.ListSubNav[1].num = val;
      }
    },
    methods : {
      readMessage(){
        let subnav = document.getElementsByClassName("subnav")[0].getElementsByTagName("ul")[0].getElementsByTagName("li")[0];
        if(subnav){
          let _this = this;
          this.getToken().then(token=> { // 统计系统信息已读
            this.$http.get('message/markreadnotice', {
              params: {
                access_token: token,
                type:1,
                uid:20
              }
            }).then(res => {
              if (res.data.stats === '2000') {
                this.getManuList()
              }
            }).catch(err => {
              console.log(err);
            });
          })
        }
      },
      tabSwitch(index) {
        for(let i = 0; i < this.ListSubNav.length; i++){
          this.ListSubNav[i].bol = false;
          if(index == i){
            this.ListSubNav[i].bol = true;
            this.currentView = this.ListSubNav[i].tab;
          }
        }
        if(index === 1){
          this.getToken().then(token=> { // 统计系统信息已读
            this.$http.get('yuegao/markread', {
              params: {
                access_token: token,
                uid:1766
              }
            }).then(res => {
              if (res.data.stats === '2000') {
                this.getManuList();
              }
            }).catch(err => {
              console.log(err);
            });
          })
        }
      },
      yingzhengPageChange(val){
        this.yingzhengPageIndex = val;
        this.getManuList();
      },
      refusePageChange(val){
        this.refusePageIndex = val;
        this.getManuList();
      },
      getManuList(){
        let _this = this;
        this.getToken().then(token=> {
          this.$http.get('message/shownotice', { // 约稿-->应征通知
            params: {
              access_token: token,
              uid:20,
              type:1,
              page: _this.yingzhengPageIndex,
              show: _this.yingzhengPageSize
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this.yingzhengEveryList = res.data.data.list;
              _this.yingzhengPageCount = parseInt(res.data.data.total);
              let a = 0;  // 约稿通知-->应征通知未读条数
              res.data.data.list.forEach((item,index)=>{
                if(res.data.data.list[index].readstats === '1'){
                  a++
                }else {
                  a=0
                }
              });
              this.$store.dispatch('onSubmitYuegaoYingzheng',a);
            }
          }).catch(err => {
            console.log(err);
          });
          this.$http.get('yuegao/getyuegaonotice', { // 选用/婉拒
            params: {
              access_token: token,
              uid:1766,
              page: _this.refusePageIndex,
              show: _this.refusePageSize
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this.refuseEveryList = res.data.data.list;
              _this.refusePageCount = parseInt(res.data.data.total);
              let a = 0;  // 约稿通知-->选用拒绝通知未读条数
              res.data.data.list.forEach((item,index)=>{
                if(res.data.data.list[index].readstats === '1'){
                  a++
                }else {
                  a = 0
                }
              });
              this.$store.dispatch('onSubmitYuegaoRefuse',a);
            }
          }).catch(err => {
            console.log(err);
          });
        })
      }
    }
  }
</script>




