<!-- 2017/3/22- yyy -个人主页 作品-画作-公共部分 -->
<template>
  <section class="subnav">
    <ul>
      <li v-for="item,index in ListSubNav" @click="tabSwitch(index,item.tab)" class="subnav__list" :class="{active:item.bol}">{{item.name}}<i v-if="item.num>0?true:false">({{item.num}})</i></li>
    </ul>
  </section>
</template>

<script>
  export default {
    props:["ListSubNav"],
    methods:{
      tabSwitch(index,type){
        this.$emit('subNav',index,type);
      }
    }
  }
</script>

<style lang="scss" scoped>

  @import "../../common/css/reset.css";
  @import "../../common/css/define";

  .subnav {
    margin-bottom: 40px;
  }
  .subnav__list {
    height: 30px;
    line-height: 30px;
    display: inline-block;
    border-radius: 4px;
    font-size: 14px;
    color: $black;
    padding: 0 30px;
    margin-right: 20px;
    cursor: pointer;
    letter-spacing: 0.1em;
    i {
      color: $pink;
    }
    &:last-child {
      margin-right: 0;
    }
  }
  .active {
    @include linear;
    color: $white;
    i {
      color: $white;
    }
  }

</style>



