<!--约稿导航-->
<template>
  <ul class="pact-nav">
    <li v-for="(menu,index) in menus.menu" @click="change(index)" :class="{'is--active':menu.isActive}">{{menu.title}}
    </li>
    <router-link
      tag="li"
      :to="linkNav"
      class="pact-nav__btn">{{menus.btn}}
    </router-link>
  </ul>
</template>

<!--数据交互-->
<script>
  export default {
    props: ['menus','linkNav'],
    methods: {
      change(index){
        this.$emit('change', index)
      }
    }
  }
</script>

<!--组件样式-->
<style lang="scss" scoped>
  @import "../../common/css/define";

  .pact-nav {
    padding: 0 20px;
    max-width: 1140px;
    margin: 0 auto;
    background-color: #fff;
    @include wrapShow;

    > li {
      display: inline-block;
      height: 88px;
      line-height: 88px;
      margin-right: 40px;
      font-size: 14px;
      color: #666;
      cursor: pointer;
      &.is--active {
        color: $pink;
        font-weight: bold;
      }
    }
    .pact-nav__btn {
      width: 120px;
      height: 50px;
      line-height: 50px;
      color: #fff;
      font-size: 16px;
      text-align: center;
      border-radius: 4px;
      float: right;
      margin: 20px 0 0 0;
      @include boxShow;
      @include linear;
    }
  }
</style>



