<template>
  <section class="bg">
    <section class="message clearfix">
      <MesNav class="message-nav" :MesNav="menu" @iconTabTitle="iconTabTitle"></MesNav>
      <section class="message-con">
        <keep-alive>
          <component :is='currentView' @returnDiaLog="returnDiaLog" @taggedRead="taggedRead"></component>
        </keep-alive>
      </section>
    </section>
  </section>
</template>

<script>
  import MesNav from 'components/Message/MesNav' // 导航
  import Private from 'components/Message/Private' //私信汇总
  import Remind from 'components/Message/Remind' //提醒组件
  import Manuscripts from 'components/Message/Manuscripts' //约稿通知组件
  import Collectmanu from 'components/Message/Collectmanu' //投稿通知组件
  import Activity from 'components/Message/Activity' //活动组件
  import System from 'components/Message/System' //投稿通知组件
  import {mapGetters} from "vuex"
  export default {
    name: 'app',
    data: () => {
      return {
        menu: [
          {name: "私信", bol: true, tab: "Private", hash: "private",bolNum:false, num01: 0,num02:0,storeBol:true,num:0},
          {name: "提醒", bol: false, tab: "Remind", hash: "remind",bolNum:false, num01: 0,num02:0,storeBol:true,num:0},
          {name: "约稿通知", bol: false, tab: "Manuscripts", hash: "yuegao",bolNum:false, num01: 0,num02:0,storeBol:true,num:0},
          {name: "征稿通知", bol: false, tab: "Collectmanu", hash: "zhenggao",bolNum:false, num01: 0,num02:0,storeBol:true,num:0},
          {name: "活动通知", bol: false, tab: "Activity", hash: "activity",bolNum:false, num01: 0,num02:0,storeBol:true,num:0},
          {name: "系统通知", bol: false, tab: "System", hash: "system",bolNum:false, num01: 0,num02:0,storeBol:true,num:0},
        ],
        currentView: Private,
        privateAllNum:0,
        remindAllNum:0,
        manuscriptsAllNum:0,
        collectmanuAllNum:0,
        activityAllNum:0,
        systemAllNum:0
      }
    },
    components: {
      Private,
      MesNav,
      Remind,
      Manuscripts,
      Collectmanu,
      Activity,
      System
    },
    mounted(){
      this.urlTabTitle();
      this.readLocalStorage();
//      console.log(this.$route.fullPath);
    },
    watch:{
      $route(){
//        console.log(this.$route.fullPath);
      }
    },
    methods: {
      readLocalStorage(){
        this.menu[0].num = parseInt(localStorage.privateAllNum);
        this.menu[1].num = parseInt(localStorage.remindAllNum);
        this.menu[2].num = parseInt(localStorage.manuscriptsYingzheng) + parseInt(localStorage.manuscriptsAllResfue);
        this.menu[3].num=  parseInt(localStorage.collectmanuAllTougao) + parseInt(localStorage.collectmanuAllResfue);
        this.menu[4].num = parseInt(localStorage.activityAllJoin) + parseInt(localStorage.activityAllWin);
        this.menu[5].num = parseInt(localStorage.systemAllNum);
      },
      iconTabTitle: function (index) {
        for (var i = 0; i < this.menu.length; i++) {
          this.menu[i].bol = false;
          if (index === i) {
            this.menu[i].bol = true;
            this.currentView = this.menu[i].tab;
            window.location.hash = this.menu[i].hash;
          }
        };
        switch (index){
          case 0:
            this.switchBol(0);
            break;
          case 1:
            this.switchBol(1);
            break;
          case 2:
            this.switchBol(2);
            break
          case 3:
            this.switchBol(3);
            break;
          case 4:
            this.switchBol(4);
            break;
          case 5:
            this.switchBol(5);
            break;
        }
      },
      urlContent(index){
        for (var i = 0; i < this.menu.length; i++) {
          this.menu[i].bol = false;
          this.currentView = this.menu[i].tab;
        }
        this.menu[index].bol = true;
        this.currentView = this.menu[index].tab;
        let a = /privateType/g; // 通过type来判断是否从私信来的
        if(!a.test(document.location.href)){
          window.location.hash = this.menu[index].hash
        }
      },
      urlTabTitle(){  // 接收来自  组件header-->消息里面的参数，判断跳转到哪个对应的
        let url = document.location.href;
        let a = /private/g,b=/remind/g,c=/yuegao/g,d=/zhenggao/g,e=/activity/g,f=/system/g;
        if(a.test(url)){
          this.urlContent(0);this.switchBol(0);
        }else if (b.test(url)){
          this.urlContent(1);this.switchBol(1);
        }else if (c.test(url)){
          this.urlContent(2);this.switchBol(2);
        }else if (e.test(url)){
          this.urlContent(3);this.switchBol(3);
        }else if (e.test(url)){
          this.urlContent(4);this.switchBol(4);
        }else if (f.test(url)){
          this.urlContent(5);this.switchBol(5);
        }
      },
      switchBol(index){
        this.menu[index].bolNum = true;
        this.menu[index].storeBol = false;
      }
    },
    computed: mapGetters(['privateNum','remindNum','manuscriptsYingzhengNum','manuscriptsRefuseNum','collectmanuTougaoNum','collectmanuRefuseNum','activityJoinNum','activityPrizeNum','systemNum']),
    watch: {
      privateNum(val){
        this.menu[0].num01 = val;
      },
      remindNum(val){
        this.menu[1].num01 = val;
      },
      manuscriptsYingzhengNum(val){
        this.menu[2].num01 = val;
      },
      manuscriptsRefuseNum(val){
        this.menu[2].num02 = val;
      },
      collectmanuTougaoNum(val){
        this.menu[3].num01 = val;
      },
      collectmanuRefuseNum(val){
        this.menu[3].num02 = val;
      },
      activityJoinNum(val){
        this.menu[4].num01 = val;
      },
      activityPrizeNum(val){
        this.menu[4].num02 = val;
      },
      systemNum(val){
        this.menu[5].num01 = val;
      },
    },
  }

</script>


<style scoped lang="scss">
  @import "../../common/css/reset.css";
  @import "../../common/css/define";

  .bg {
    width: 100%;
    background-color: #fafafa;
    padding-top: 40px;
  }

  .message {
    width: 1180px;
    background-color: $white;
    margin: 0 auto;
    border: 1px solid $lightgrey;
    border-radius: 4px;
    @include wrapShow;
    .message-con {
      width: 1000px;
      min-height: 1000px;
      box-sizing: border-box;
      float: left;
    }
  }

</style>



