<!--发布约稿组件-->
<template>
  <div class="pact-send">
    <h6 class="pact-send__title">发布约稿</h6>
    <section class="pact-send__content">
      <dl>
        <dd class="pact-send__name">
          <h6>&#12288;&#12288;标题</h6>
          <input type="text"
                 v-model="newData.title"/>
        </dd>
        <dd class="pact-send__price">
          <h6>单张预算</h6>
          <input type="text"
                 v-model="newData.lowcost"/>
          <i>-</i>
          <input type="text"
                 v-model="newData.highcost"/> 元/张
        </dd>
        <dd class="pact-send__need">
          <h6>需求张数</h6>
          <input ref="readonly" class="pact-send__input" type="text"
                 v-model="newData.imgcount"/>
          <label for="need" @click='handleEdit'>
            <input id="need" type="checkbox" v-model="checked">
            不限
          </label>
        </dd>
        <dd class="pact-send__time">
          <h6>截稿日期</h6>
          <DatePicker
            @onSubmitEndTime="onSubmitEndTime">
          </DatePicker>
        </dd>
        <dd class="pact-send__type">
          <h6>稿件类型</h6>
          <SelectView
            :options="options"
            @onSubmitSelect="onSubmitSelect">
          </SelectView>
        </dd>
        <dd class="pact-send__tags">
          <h6>&#12288;&#12288;标签</h6>
          <input v-model="paramsTag" type="text"/>
          <dl class="pact-send__tags-content">
            <dt>推荐标签</dt>
            <dd class="pact-send__tags-use" v-for="item in tags">
              <h6>{{item.title}}</h6>
              <section>
                <i
                  @click="handleGettags(list.tags,list.id)"
                  v-for="list in item.attr">{{list.tags}}</i>
              </section>
            </dd>
          </dl>
        </dd>
        <dd>
          <h6>约稿详情</h6>
          <section class="editor">
            <EditorView :bodyContent="propsContent" @onContent='onContent'></EditorView>
          </section>
        </dd>
        <dd class="pact-send__tags-btn"
            @click="handleSend">发布
        </dd>
      </dl>
    </section>
    <RestrictionView @setRes="setRes">
      <b slot="title">谁可以查看我的QQ号码</b>
    </RestrictionView>
  </div>
</template>

<!--数据交互-->
<script>
  import DatePicker from 'components/Common/DatePicker' //时间选择组件
  import SelectView from 'components/Common/Select' //选择框组件
  import EditorView from 'components/Common/Editor' //富文本编辑器组件
  import RestrictionView from 'components/Painting/Restriction' //权限控制组件

  export default {
    components: {
      DatePicker,
      SelectView,
      EditorView,
      RestrictionView

    },

    created(){
      this.getMarks();

      //判断用户是否登录
      this.isLogin();
    },
    data: () => ({
      isreadonly: false,
      propsContent: `<p>
                          <h6>稿件要求：</h6>
                       <br/>
                          <h6>稿件的用途：</h6>
                       <br/>
                          <h6>稿件的尺寸格式：</h6>
                       <br/>
                          <h6>需要几位画师：</h6>
                       <br/>
                         <h6>约稿流程：应征之后，我会选择合适的画师直接QQ联系或私信：</h6>
                       <br/>
                         <h6>结款时间与方式：</h6>
                       <br/>
                         <h6>参考例图：</h6>
                       <br/>
                      </p>
                     `,
      options: [{
        value: '1',
        label: '商业稿'
      }, {
        value: '2',
        label: '同人稿'
      }, {
        value: '3',
        label: '私人稿'
      },],
      tags: [{title: '稿件属性:', attr: []}, {title: '稿件用途:', attr: []}],
      paramsTag: [], //标签属性

      //用户输入数据
      newData: {
        title: [],
        lowcost: '',
        highcost: '',
        imgcount: '',
        notetype: '1',
        tagsname: [],
        content: '',
        permiss: 1,
        endtime: ''
      },
      checked: false,
    }),
    methods: {
      //获取权限
      setRes(val){
        console.log(val);
        this.newData.permiss = val.permiss;
      },
      //获取截稿日期
      onSubmitEndTime(t){
        this.newData.endtime = t;
        console.log(t);
      },
      //获取稿件类型
      onSubmitSelect(val){
        this.newData.notetype = val;
      },

      //获取标签
      getMarks(){
        this.getToken().then(token => {
          this.$http('common/gettags', {
            params: {
              access_token: token,
              tagtype: 1
            }
          }).then(res => {
            let data = res.data.data.list;
            data.forEach(item => {
              switch (item.type) {
                case '1':   //  属性便签
                  this.tags[0].attr.push(item);
                  break;
                case '2':  //用途
                  this.tags[1].attr.push(item);
                  break;
              }
            })
          }).catch(err => {
            console.log(err);
          })
        })
      },

      //获取富文本内容
      onContent(val){
        this.newData.content = val;
      },

      //选择标签
      handleGettags(name, id){
        console.log(name, id);
        this.paramsTag.push(name);
        this.newData.tagsname.push(id);
        this.paramsTag = [...new Set(this.paramsTag)];
        this.newData.tagsname = [...new Set(this.newData.tagsname)];
      },
      //发布约稿
      handleSend(){
        let _this_ = this;
        this.getToken().then(token => {
          this.$http('yuegao/addyuegao', {
            params: {
              access_token: token,
              uid: 18202,
              title: _this_.newData.title,
              lowcost: _this_.newData.lowcost,
              highcost: _this_.newData.highcost,
              imgcount: _this_.newData.imgcount,
              notetype: _this_.newData.notetype,
              tagsname: _this_.newData.tagsname,
              content: _this_.newData.content,
              permiss: _this_.newData.permiss,
              endtime: _this_.newData.endtime
            }
          }).then(res => {
            console.log(res);
            if (res.data.stats === '2000') {
              alert('发布成功')
            }
          }).catch(err => {
            console.log(err);
          })
        })
      },
      handleEdit(){
        if (this.checked) { //限制
          this.$refs.readonly.setAttribute('readonly', 'true');
          this.newData.imgcount = '';
        } else { //不限制
          this.$refs.readonly.removeAttribute('readonly');
        }
      }
    }
  }
</script>

<!--样式控制-->
<style lang="scss" scoped>
  @import "../../common/css/define";

  .pact-send {
    max-width: 1180px;
    margin: 0 auto;
    color: $black;
    .pact-send__title {
      font-size: 22px;
      font-weight: bold;
      margin: 40px 0 20px 0;
    }
    .pact-send__content {
      width: 760px;
      padding: 40px;
      background-color: #fff;
      margin-bottom: 20px;
      display: inline-block;
      @include wrapShow;

      > dl > dd {
        margin-bottom: 40px;
        font-size: 14px;
        > h6 {
          display: inline-block;
          font-size: 14px;
          font-weight: normal;
          margin-right: 18px;
        }
        .date {
          display: inline-block;
        }
      }
      .pact-send__name {
        > input {
          width: 478px;
          height: 36px;
          border: 1px solid #e6e6e6;
          border-radius: 2px;
          text-indent: 1em;
          font-size: 14px;
        }
      }
      .pact-send__need {
        .pact-send__input {
          width: 190px;
          height: 36px;
          border: 1px solid #e6e6e6;
          text-align: center;
          margin-right: 10px;
        }
      }
      .pact-send__price {
        > input {
          width: 94px;
          height: 36px;
          border-radius: 2px;
          border: 1px solid #e6e6e6;
          text-align: center;
        }
        > i {
          color: #e6e6e6;
          margin: 0 6px;
        }
      }
      .pact-send__tags {
        > input {
          width: 478px;
          height: 36px;
          text-indent: 1em;
          border-radius: 2px;
          border: 1px solid #e6e6e6;
        }
        .pact-send__tags-content {
          margin-left: 76px;
          margin-top: 20px;
          > dt {
            margin-bottom: 20px;
            font-size: 14px;
          }
          > dd {
            font-size: 0;
            > h6 {
              display: inline-block;
              font-size: 12px;
              font-weight: normal;
              vertical-align: top;
              line-height: 20px;
            }
            section {
              display: inline-block;
              width: 610px;
              vertical-align: top;
              margin-left: 4px;
              > i {
                display: inline-block;
                vertical-align: top;
                height: 20px;
                padding: 0 12px;
                border: 1px solid #e6e6e6;
                color: #666;
                font-size: 12px;
                border-radius: 2px;
                line-height: 20px;
                text-align: center;
                margin: 0 8px 8px 0;
                cursor: pointer;
              }
            }
          }
        }
      }
      .pact-send__tags-btn {
        width: 124px;
        height: 50px;
        border-radius: 4px;
        color: #fff;
        line-height: 50px;
        text-align: center;
        font-size: 16px;
        cursor: pointer;
        margin: 70px 0 0 76px;
        @include boxShow;
        @include linear;
      }
    }
    .editor {
      display: inline-block;
      width: 600px;
      vertical-align: top;
    }
  }
</style>



