<!-- 2017/4/10  私信列表与对话框汇总-->
<template>
  <section class="expression" :class="{active:!show}">
    <ul class="expression__list clearfix">
      <transition name="fade">
        <li class="expression__word" v-show="show" @click="openWord">
          <a href="javascript:void(0);" class="dialog-lev__word">
            <img src="http://mbpcos.image.alimmdn.com/static/img/works/expression.png" alt="">添加颜文字
          </a>
        </li>
      </transition>
      <transition name="fade">
        <section v-show="hide">
          <li><a class="expression__close" href="javascrip:void(0)" @click="closeWord"></a></li>
          <li class="expression__item" v-for="(item,index) in expressionList" @click="addExpression(item,index)">
            <a href="javascrip:void(0)">{{item}}</a>
          </li>
        </section>
      </transition>
    </ul>
  </section>

</template>


<script>
  export default {
    data: () => {
      return {
        show:true,
        hide:false,
        expressionList:['(。・∀・)ノ゛','(TωT)','(･ω-｡)','ヾ(≧∇≦*) ','ヾ(ﾟ∀ﾟ○)ﾂ','ヽ(*´Д｀*)ﾉ','(；ω；｀)','(/ω＼*)','(●＾U＾●)','(｡ゝω･)bﾞ','(;´ρ`)','（´・ω・｀）','∑(ﾟωﾟﾉ)ノ','(-д-；)','（＾ε＾）','(`_ゝ´)','（＞△＜）','m(_ _)m','┌(^q^┐)┐','_(:з」∠)_'],
      }
    },
    methods:{
      openWord(){
        this.word();
      },
      closeWord(){
        this.word();
      },
      word(){
        this.show = !this.show;
        this.hide = !this.hide
      },
      addExpression(item,index){ // item 代表表情符号
          this.$emit("addExpression",item,index)
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../common/css/reset.css";
  @import "../../common/css/define";

  .expression {
    width: 500px;
    padding: 25px 10px;
    z-index: 999;
  }
  .active {
    background-color: #ecf8fd;
  }
  .expression__list {
    width: 500px;
    box-sizing: border-box;
    position: relative;
    .expression__item {
      display: block;
      float: left;
      width: 100px;
      box-sizing: border-box;
      line-height: 27px;
      text-align: center;
      border: 1px solid #fce4ec;
      a {
        color: $black;
        font-size: 12px;
        &:hover {
          color: $pink;
        }
      }
    }
  }
  .expression__word {
    position: absolute;
    top: -15px;
    left: -10px;
    a {
      font-size: 12px;
      color: $pink;
      img {
        vertical-align: sub;
        margin-right: 15px;
      }
    }
  }
  .expression__close {
    width: 15px;
    height: 15px;
    background-image: url("./images/close.gif");
    display: inline-block;
    overflow: hidden;
    background-repeat: no-repeat;
    cursor: pointer;
    vertical-align: middle;
    position: absolute;
    top: -18px;
    right: 10px;
  }

  .fade-enter-active, .fade-leave-active {
    transition: opacity 1.0s
  }
  .fade-enter, .fade-leave-active {
    opacity: 0
  }

</style>



