import PaintingDetail from 'components/Painting/Detail'  //画作详情
import PaintingList from 'components/Painting/List'      //画作列表
import ActivityIndex from 'components/Activity/ActivityIndex'      //活动列表
import ActivityDetail from 'components/Activity/ActivityDetail'  //活动详情
import PaintingNew from 'components/Painting/Send'  //画作发布
import ActivitySend from 'components/Activity/ActivitySend'  //活动发布
import PactDetail from 'components/Pact/PactDetail'  //约稿详情
import PactRecruit from 'components/Pact/PactRecruit'  //约稿详情
import PactSend from 'components/Pact/PactSend'  //发布约稿
import NewCollect from 'components/Design/NewCollect'  //发布征稿
import PactPaintingNew from 'components/Pact/PactPaintingNew'  //发布画铺
import CollectInfoDetail from 'components/Design/CollectInfoDetail'  //征稿详情
import DesignNew from 'components/Design/DesignNew'  //征稿详情
import PactIndex from 'components/Pact/PactIndex'
import DesignList from 'components/Design/PendingSale' //待出售设计
import DesignIndex from 'components/Design/DesignIndex' //设计平台主页
import PersonalIndex from 'components/Personal/PersonalIndex' //用户主页
import GroupIndex from 'components/Group/GroupIndex' //Q群主页
import IndexVIew from 'components/Index/Index' //Q群主页
import MessageIndex from 'components/Message/MesSubject' //Q群主页
import SearchIndex from 'components/Search/SearchMain' //搜索页

//发布活动
const newActivity = [
  {
    path: '/new/painting',
    component: PaintingNew
  },
  {
    path: '/new/activity',
    component: ActivitySend
  },
  {
    path: '/new/pact',
    component: PactSend
  },
  {
    path: '/new/shop',
    component: PactPaintingNew
  },
  {
    path: '/new/design',
    component: DesignNew
  },
  {
    path: '/new/collect',
    component: NewCollect
  },
];

//约稿平台路由
const pact = [
  {
    path: '/pact/:id',
    component: PactDetail
  },
  {
    path: '/index',
    component: PactIndex
  },
  {
    path: '/recruit/:uid/:id',
    component: PactRecruit
  },
  {
    path: "/painting/:id",
    component: PaintingDetail
  },
  {
    path: '/',
    redirect: '/index'
  }
].concat(newActivity);//添加发布活动路由

//画作路由
const painting = [
  {
    path: '/index',
    component: PaintingList,
  },
  {
    path: '/',
    redirect: '/index',
  },
  {
    path: '/painting/:id',
    component: PaintingDetail,
  },
  {
    path: '/id/:id',
    component: PaintingNew,
  },
].concat(newActivity);

//活动路由
const activity = [
  {
    path: '/detail/:id',
    component: ActivityDetail
  },
  {
    path: '/index',
    component: ActivityIndex,
  },
  {
    path: '/id/:id',
    component: ActivitySend,
  },
  {
    path: '/painting/:id',
    component: PaintingDetail,
  },
  {
    path: '/',
    redirect: '/index',
  },
  {
    path: '/new/painting/:id',
    component: PaintingNew
  }
].concat(newActivity);

//设计平台
const design = [
  {
    path: '/',
    component: DesignIndex
  },
  {//已用设计
    path: '/design/:id',
    component: PaintingDetail
  },
  {//应征设计
    path: '/new/design/:id',
    component: DesignNew
  },
  {//征稿信息详情
    path: '/collect/:id',
    component: CollectInfoDetail
  },
].concat(newActivity);

//个人主页
const personal = [
  {
    path: '/',
    component: PersonalIndex
  },
  {
    path: '/accounts/:id',
    component: PersonalIndex
  },
  {
    path: '/accounts/products/:id',
    component: PersonalIndex
  },
].concat(newActivity);


//Q群
const Group = [
  {
    path: '/',
    component: GroupIndex
  }
].concat(newActivity);

//主页路由

const index = [
  {
    path: '/',
    component: IndexVIew
  }
].concat(newActivity);

// 消息页路由
const message = [
  {
    path: '/',
    component: MessageIndex
  },
  {
    path: '/private/privateType/:id&:name',
    component: MessageIndex
  },
].concat(newActivity);

//搜索页
const search = [
  {
    path: '/',
    component: SearchIndex
  },
  {
    path: '/searchType/:id&:name',
    component: SearchIndex
  },
].concat(newActivity);


//全局路由
