//     path: '/new/activity',
//     component: ActivitySend
//   },
//   // {
//   //   // path: '/pactDetail',
//   //   // component: PactDetail
//   // },
//   {
//     path: '/new/pact',
//     component: PactSend
//   },
//   {
//     path: '/new/newPainting',
//     component: PactPaintingNew
//   },
//   {
//     path: '/collect',
//     component: CollectInfoDetail
//   },
//   {
//     path: '/new/newDesign',
//     component: DesignNew
//   },
//   {
//     path: '/',
//     component: PaintingList
//   }
// ];

export {pact, painting, activity, design, personal, Group, index, message, search}



