<!--约稿详情画师组件-->
<template>
  <div class="pact-painter">

    <!--发稿方显示-->
    <ul class="pact-painter__orderBy">
      <li class="pact-painter__des">
        <i
          @click="handleChangeTable(index)"
          :class="{'is--active':item.flag}"
          v-for="(item,index) in orderBy">{{item.title}}</i>
        <span>如需帮助，可联系官方客服</span>
      </li>
      <li class="pact-painter__condition">
        <i
          @click="handleChangeCondition(index)"
          :class="{'is--active':item.flag}"
          v-for="(item,index) in condition ">{{item.title}}</i>
      </li>
    </ul>
    <h6 class="pact-painter__title">共{{painter.length}}位画师约稿</h6>
    <!--画师组件-->
    <PactPainter
      :painter="painter"
      :value="margin"
      :items="lists">
      <template
        scope="props"
        slot="price">
        <ul class="pact-painter__nav">
          <li class="pact-painter__price">
            <span>报价：￥<i>{{props.info.cost}}</i></span>
            <span>留言：<i>{{props.info.message}}</i></span>
          </li>
          <li class="pact-painter__btn">
           <span
             v-if="props.info.ustats === '0'">
             <i @click="handleChoose('1',props.info.bmuid)">婉拒</i>
             <i @click="handleChoose('2',props.info.bmuid)">初步选用</i>
           </span>
            <i
              :class="props.info.ustats==='1'?'un--choose':'has--choose'"
              v-else>{{props.info.ustats | isChoose}}</i>
          </li>
        </ul>
      </template>
    </PactPainter>
    <section v-if="painter.length===0">
      暂无数据。。。
    </section>
    <!--分页组件-->
    <Pagination
      @pageChange="pageChange"
      :pageSize="pageSize"
      :pageCount="pageCount"></Pagination>
  </div>
</template>

<!--数据交互-->
<script>
  import PactPainter from 'components/Index/Painter'
  import Pagination from 'components/Common/Pagination'

  export default {
    components: {
      PactPainter,
      Pagination
    },
    mounted(){
      this.getData();
    },

    //过滤器
    filters: {
      isChoose(val){ //是否选中
        if (val === '2') {
          return '已初步选用'
        } else if (val === '1') {
          return '已婉拒'
        }
      }
    },
    props: ['id'],
    data: () => ({
      margin: 0,//painter__pic 行间样式
      lists: [1, 3, 4],
      painter: [], //报名画师列表
      orderBy: [
        {title: '按应征时间', flag: true, orderBy: ''},
        {title: '按报价高点', flag: false, orderBy: 'cost'}
      ],
      condition: [  //筛选条件
        {title: '官方推荐', flag: false, orderBy: '1'},
        {title: '我关注的', flag: false, orderBy: '2'},
        {title: '初选画师', flag: false, orderBy: '3'}
      ],
      order: '', //排序方式
      conditionBy: '',  //筛选条件
      pageCount: 0, //总条数
      pageSize: 30, //每页展示条数
      pageIndex: 1,//当前页数
    }),
    methods: {
      getData(){ //获取应征画师列表
        this.getToken().then(token => {
          this.$http.get('yuegao/baomingpainterlist', {
            params: {
              access_token: token,
              order: this.order,
              uid: 18202,
              id: this.id,
              authorid: 18202,
              page: this.pageIndex
            }
          }).then(res => {
            this.painter = res.data.data.list;
            this.pageCount = parseInt(res.data.data.total);  //总页数
          }).catch(err => {
            console.log(err);
          })
        })
      },

      //排序切换
      handleChangeTable(index) {
        this.orderBy.forEach(item => {
          item.flag = false;
        });
        this.orderBy[index].flag = true;
        this.order = this.orderBy[index].orderBy;
        this.getData();
      },

      //条件筛选
      handleChangeCondition(index) {
        let status = this.condition[index].flag;
        this.condition.forEach(item => {
          item.flag = false;
        });

        if (status === true) {  //取消
          this.condition[index].flag = false;
          this.conditionBy = '';
        } else {  //选中
          this.condition[index].flag = true;
          this.conditionBy = this.condition[index].orderBy;
        }

      },

      //是否选中
      handleChoose(val, uid){
        this.getToken().then(token => {
          this.$http.get('yuegao/cancelyuegao', {
            params: {
              access_token: token,
              id: this.id,
              uid: uid,
              condition: 'result',
              stats: val
            }
          }).then(res => {
            this.getData();
          }).catch(err => {
            console.log(err);
          })
        })
      },

      //分页切换
      pageChange(val){
        this.pageIndex = val;
        this.getData();
      }
    }
  }
</script>

<!--组件样式-->

<style lang="scss" scoped>
  @import "../../common/css/define";

  .pact-painter {
    max-width: 1140px;
    margin: 0 auto;
    padding: 20px;
    background-color: #fff;
    color: $black;
    @include wrapShow;
  }

  .pact-painter__title {
    margin: 20px 0;
    font-weight: normal;
    font-size: 14px;
  }

  .pact-painter__orderBy {
    width: 1140px;
    padding: 0 20px;
    height: 54px;
    margin: -20px 0 0 -20px;
    background-color: #fafafa;
    line-height: 54px;

    > li {
      display: inline-block;
      font-size: 14px;
    }
    .pact-painter__des {
      > i {
        margin-right: 36px;
        color: $black;
        cursor: pointer;
        &.is--active {
          color: $pink;
          font-weight: bold;
        }
      }
      > span {
        color: $gray;
      }
    }
    .pact-painter__condition {
      float: right;
      > i {
        margin-left: 30px;
        color: #666;
        display: inline-block;
        width: 80px;
        text-align: right;
        cursor: pointer;
        background: url("../../components/Common/images/choose@bg.png") no-repeat 0 center;
        &.is--active {
          background: url("../../components/Common/images/choosed.png") no-repeat 0 center;
        }
      }
    }
  }

  .pact-painter__nav {
    margin-top: 20px;
    height: 40px;
    padding: 0 20px;
    background-color: #fafafa;
    > li {
      display: inline-block;
      line-height: 40px;
      font-size: 14px;
    }
    .pact-painter__price {
      > span {
        margin-right: 20px;
        color: #666;
        > i {
          color: #333;
        }
      }
    }
    .pact-painter__btn {
      float: right;
      font-size: 12px;
      > i, > span > i {
        display: inline-block;
        padding: 0 10px;
        color: #fff;
        height: 24px;
        line-height: 24px;
        background-color: $pink;
        border-radius: 2px;
        cursor: pointer;
        &:nth-of-type(1) {
          background-color: #ccc;
        }
        &.un--choose {
          background-color: #ccc;
          cursor: not-allowed;
        }
        &.has--choose {
          background-color: $pink;
          cursor: not-allowed;
        }
      }
    }
  }
</style>



