<!--最新画铺-->
<template>
  <ul class="goods">
    <li class="goods__list" v-for="item in goods">
      <section class="goods__info">
        <a
          :href="`painting.html#/painting/${item.id}`" target="_blank" class="goods__img">
          <img :src="JSON.parse(item.imgs)[0]" alt=""/>
          <i>6P</i>
        </a>
        <section class="goods__hot">
          <h6 :title="item.title">{{item.title}}</h6>
          <i class="goods__price">￥{{item.lowcost}}-{{item.highcost}}</i>
          <i class="goods__date">{{item.fastdesignday}}-{{item.longdesignday}}天</i>
        </section>
      </section>
      <section class="goods__tips">
        <a :href="'personal.html#/accounts/'+item.uid" class="goods__tips-img">
          <img :src="item.avatar" alt=""/>
        </a>
        <p :title="item.nickname">{{item.nickname}}</p>
        <i>{{item.createtime | UnixToNormalTime(2)}}</i>
      </section>
    </li>
  </ul>
</template>

<!--数据交互-->
<script>
  export default {
    name: "app-goods",
//    data: () => {
//      return {
//        list:[1,2,3,4,5]
//      }
//    }
    props: ['goods', 'link']
  }
</script>

<!--组件样式-->
<style lang="scss" scoped>
  @import "../../common/css/define";

  .goods {
    max-width: 1180px;
    margin: 0 auto
  }

  .goods__list {
    display: inline-block;
    margin: 0 20px 50px 0;
    &:nth-child(5n) {
      margin-right: 0;
    }
  }

  .goods__info {
    width: 220px;
    height: 388px;
    background-color: #fff;
    @include wrapShow;

    .goods__img {
      display: block;
      width: 220px;
      height: 330px;
      border-radius: 4px 4px 0 0;
      position: relative;
      overflow: hidden;

      > img {
        width: 100%;
      }
      > i {
        position: absolute;
        display: block;
        width: 32px;
        height: 20px;
        top: 0;
        left: 0;
        color: #fff;
        border-radius: 4px 0 4px 0;
        background-color: rgba(0, 0, 0, 0.3);
        text-align: center;
        line-height: 20px;
        font-size: 12px;
      }
    }
  }

  .goods__hot {
    font-size: 12px;
    padding: 0 12px;
    > h6 {
      width: 190px;
      line-height: 30px;
      font-size: 12px;
      font-weight: normal;
      color: $gray;
      @include beyondPass;
    }
    .goods__price {
      font-weight: bold;
      font-size: 14px;
      color: $pink;
    }
    .goods__date {
      float: right;
      color: $pink
    }
  }

  .goods__tips {
    margin-top: 18px;
    .goods__tips-img {
      background: red;
      width: 20px;
      height: 20px;
      border-radius: 50%;
      overflow: hidden;
      display: inline-block;
      vertical-align: middle;
      > img {
        width: 100%;
      }
    }
    > p {
      width: 50px;
      display: inline-block;
      vertical-align: middle;
      font-size: 12px;
      color: $black;
      margin: 0 16px;
      @include beyondPass;
    }
    > i {
      display: inline-block;
      vertical-align: middle;
      font-size: 12px;
      color: $gray;
    }
  }
</style>



