<!-- 2017/3/22- yyy -约稿信息 -->
<template>
  <section class="comtitle">
    <ul>
      <li class="comtitle__list" v-for="item,index in SubTitleList " @click="selectSub(index)"
          :class="{active:item.bol}">{{item.name}}
      </li>
    </ul>
  </section>
</template>

<script>
  export default {
    props: ["SubTitleList"],
    methods: {
      selectSub(index){
        this.$emit('SubTitleIcon', index);
      }
    }
  }
</script>

<style lang="scss" scoped>

  @import "../../common/css/reset.css";
  @import "../../common/css/define";

  .comtitle__list {
    display: inline-block;
    height: 25px;
    border-radius: 4px;
    border: 1px solid #e6e6e6;
    font-size: 12px;
    color: #666666;
    line-height: 25px;
    padding: 0 10px;
    margin: 0 8px 8px 0;
    cursor: pointer;
  }

  .active {
    background-color: $pink;
    color: $white;
  }

</style>



