<template>
  <section class="mes-nav">
    <p>我的消息</p>
    <ul class="mes-nav__ul">
      <li class="mes-nav__list" v-for="item,index in MesNav"  @click = "iconTabTitle(index,item.tab)">
        <span class="mes-nav__title" :class="{active:item.bol}">{{item.name}} <i v-if="item.bolNum">({{parseInt(item.num01)+parseInt(item.num02)}})</i><i v-if="item.storeBol">({{item.num}})</i></span>
      </li>
    </ul>
  </section>
</template>

<script>
  export default {
    props:["MesNav"],
    methods:{
      iconTabTitle(index,type){
        this.$emit('iconTabTitle',index,type);
      }
    }
  }

</script>

<style scoped lang="scss">
  @import "../../common/css/reset.css";
  @import "../../common/css/define";



    .mes-nav {
      width: 180px;
      min-height: 1200px;
      box-sizing: border-box;
      border-right: 1px solid $lightgrey;
      float: left;
      > p {
        height: 50px;
        font-size: 14px;
        font-weight: bold;
        line-height: 50px;
        padding-left: 20px;
        border-bottom: 1px solid $lightgrey;
      }
      .mes-nav__ul {
        margin: 15px 0;
      }
    }



  .mes-nav__list {
    height: 40px;
    margin: 5px 0;
    .mes-nav__title{
      display: block;
      width: 120px;
      height: 40px;
      line-height: 40px;
      font-size: 14px;
      cursor: pointer;
      color: $black;
      border-radius:4px;
      margin-left: 20px;
      i {
        color: $pink;
      }
    }
    .active {
      @include linear;
      color: $white;
      padding-left: 20px;
      i {
        color: $white;
      }
    }
  }

  .message-con__list {
    padding: 12px 30px 0 30px;
  }

</style>



