<!-- Created by yyy on 2017/3/20. 搜索页搜索框-->
<template>
  <div class="bg">
    <div class="searchbox">
      <div class="searchbox__txt clearfix">
        <input type="text" placeholder="请输入要搜索的内容" v-model="searchWord"  @keyup.enter="searchRange(name)" id="searchKeyWord">
        <span @click="searchRange(name)">搜索</span>
      </div>
      <ul class="searchbox__menu">
        <li v-for="item,index in searchList" @click="tabSwitch(index,item.tab)" :class="{active:item.bol}">{{item.name}}</li>
      </ul>
    </div>
  </div>
</template>

<!--数据交互-->
<script>
  export default {
    props:["searchList"],
    data:()=>{
      return {
        searchWord:""
      }
    },
    methods:{
      tabSwitch(index,type){
        this.$emit('searchTitle',index,type);
      },
      searchRange(name){
        this.$emit("searchRange",this.searchWord)
      }
    }
  }
</script>

<!--组件样式-->
<style lang="scss" scoped>
  @import "../../common/css/define";

  .bg {
    box-shadow:0 5px 15px rgba(0,0,0,0.08);
    transform:translate3d(0px,-2px,0px);
  }
  .searchbox {
    width: 1180px;
    margin: 0 auto;
    background-color: $white;

    .searchbox__txt {
      width: 575px;
      text-align:center;
      padding-top: 40px;
      margin: 0 auto 20px auto;
      input {
        width: 475px;
        height: 50px;
        box-sizing: border-box;
        border-top: 1px solid $lightgrey;
        border-bottom: 1px solid $lightgrey;
        border-left: 1px solid $lightgrey;
        text-indent: 1em;
        font-size: 14px;
        outline: none;
        border-top-left-radius: 4px;
        border-bottom-left-radius: 4px;
        float: left;
        &:focus {
          border-top: 1px solid #e26784;
          border-bottom: 1px solid #e26784;
          border-left: 1px solid #e26784;
          transition: all 0.3s linear;
          color: $black;
        }
      }
      span {
        display: inline-block;
        float: left;
        text-align: center;
        width: 100px;
        height: 50px;
        background-color: $pink;
        box-sizing: border-box;
        line-height: 50px;
        color:$white;
        cursor: pointer;
        border-top-right-radius: 4px;
        border-bottom-right-radius: 4px;
      }
    }

    .searchbox__menu {
      height: 40px;
      text-align:center;
      padding-bottom: 20px;
      li {
        display: inline-block;
        width: 100px;
        height: 40px;
        line-height:40px;
        cursor:pointer;
        margin-right: 10px;
        font-size: 14px;
        color: $black;
        border-radius: 4px;
        &:last-child {
          margin-right: 0;
        }
      }
      .active {
        background-color: $pink;
        @include  linear;
        color: $white;
      }
    }
  }
  .searchbox__result {
    width: 1180px;
    height: 40px;
    margin: 0 auto;
    line-height: 40px;
    background-color: $white;
  }

</style>



