<!--约稿详情-->
<template>
  <div class="pact-detail">
    <PactDetailTitle :items="items"></PactDetailTitle>
    <PactDetailNav :names="names" @change="pactDetail"></PactDetailNav>

    <!--动态组件-->
    <keep-alive>
      <component
        :userId="items.uid"
        :items="items"
        :id="id"
        :is="componentView">
      </component>
    </keep-alive>

  </div>
</template>

<!--数据交互-->
<script>
  import PactDetailTitle from 'components/Pact/PactDetailTitle'
  import PactDetailDesc from 'components/Pact/PactDetailDesc'
  import PactDetailPainter from 'components/Pact/PactDetailPainter'
  import PactDetailNav from 'components/Activity/ActivityNav' //公用导航

  export default {
    components: {
      PactDetailTitle,
      PactDetailNav,
      PactDetailDesc,
      PactDetailPainter
    },
    data: () => (
      {
        names: [{title: '约稿详情', isActive: true}, {title: '应征画师', isActive: false}],
        componentView: PactDetailDesc,
        items: [],//描述数据
        id: '',//约稿id
      }
    ),
    mounted(){
      this.id = this.$route.params.id;
      this.getData(this.id);
    },
    methods: {
      pactDetail(index){ //菜单切换
        this.names.forEach(item => {
          item.isActive = false;
        });
        this.names[index].isActive = true;
        switch (index) {
          case 0:
            this.componentView = PactDetailDesc;
            break;
          case 1:
            this.componentView = PactDetailPainter;
            break;
        }
      },

      //获取描述数据
      getData(id){
        let _this_ = this;
        this.getToken().then(token => {
          this.$http.get('yuegao/findyuegao', {
            params: {
              access_token: token,
              id: id,
            }
          }).then(res => {
            _this_.items = res.data.data.list;
          }).catch(err => {
            console.log(err);
          })
        })
      },
    },
  }
</script>

<style lang="scss" scoped>

</style>



