<!--点赞公共模块-->
<template>
  <i :class="collectUser | isPraise(collect.moduleid)"
     @click="handleCount">
    {{commitData.count | setNull}}
  </i>
</template>

<!--数据交互-->
<script>
  import {mapGetters} from 'vuex'

  export default {
    props: ['collect'],

    mounted(){
      if (this.collect.uid) {
        this.commitData = this.collect;
      }
      this.users = this.$store.getters.collectUser;
    },
    watch: {
      'collect'(val){//获取props数据
        this.commitData = val;
      },
      'collectUser'(prd) {
        this.users = prd;
      }
    },
    computed: mapGetters([
      'collectUser'
    ]),
    data: () => ({
      commitData: {}, //提交的数据
      users: [] //点赞用户
    }),

    filters: { //过滤点赞用户
      isPraise(arr, n){
        for (let item of arr) {
          if (item === n) {
            return 'is--collect';  //存在
          }
        }
        return '';  //不存在
      }
    },

    methods: {
      //点赞操作
      handleCount(){
        this.getToken().then(token => {
          this.$http.get('common/collect', {
            params: {
              access_token: token,
              moduleid: this.commitData.moduleid,
              uid: this.commitData.uid,
              authorid: this.commitData.authorid,
              name: this.commitData.name
            }
          }).then(res => {
            if (res.data.data.stats === '1') { //点赞
              this.users.push(this.commitData.moduleid);
              this.commitData.count++; //数量+1
              this.$store.dispatch('onCollectUsers', this.users)
            }
            if (res.data.data.stats === '2') { //取消
              this.commitData.count--;  //数量-1
              this.users.findIndex((value, index, arr) => { //查找位置并删除
                if (value === this.commitData.moduleid) {
                  arr.splice(index, 1);
                }
              });
              this.$store.dispatch('onCollectUsers', this.users)
            }
          }).catch(err => {
            console.log(err);
          })
        })
      },
    }
  }
</script>





