<!--约稿详情用户信息组件-->
<template>
  <div class="pact-detail__user">
    <a :href="`personal.html#/accounts/${uid}`" class="pact-detail__user-pic">
      <img :src="items.avatar" alt=""/>
    </a>
    <p class="pact-detail__user-name">{{items.nickname}}</p>
    <ul class="pact-detail__user-collect">
      <li>关注 <i>{{items.followcount}}</i></li>
      <li>|</li>
      <li>粉丝 <i>{{items.fanscount}}</i></li>
    </ul>
    <ul class="pact-detail__user-tools">
      <li class="pact-detail__user-tools__add">
        <i>+关注</i>
        <i>私信</i>
        <i>QQ</i>
      </li>
      <li class="pact-detail__user-tools__fans">
        <i>加关注</i>
        <i>长春小菊花</i>
        <i>(粉丝1979)</i>
      </li>
    </ul>
  </div>
</template>

<!--数据交互-->
<script>
  export default {
    props: ['uid'],
    data: () => ({
      items: [], //用户信息
    }),
    mounted(){

    },
    watch: {
      'uid'(uid){
        this.getUserInfo(uid);
      }
    },
    methods: {

      //获取用户信息
      getUserInfo(uid){
        this.getToken().then(token => {
          this.$http.get('user/finduserinfo', {
            params: {
              access_token: token,
              uid: uid
            }
          }).then(res => {
            this.items = res.data.data.list;
          }).catch(err => {
            console.log(err);
          })
        })
      }
    }
  }
</script>
<style lang="scss" scoped>
  @import "../../common/css/define";

  .pact-detail__user {
    width: 320px;
    display: inline-block;
    vertical-align: top;
    background-color: #fff;
    color: $black;
    margin-left: 14px;
    @include wrapShow;

    .pact-detail__user-pic {
      width: 80px;
      height: 80px;
      margin: 20px auto 0;
      display: block;
      overflow: hidden;
      border-radius: 50%;
      > img {
        width: 100%;
      }
    }
    .pact-detail__user-name {
      text-align: center;
      color: $black;
      font-size: 16px;
      line-height: 36px;
    }
    .pact-detail__user-collect {
      text-align: center;
      > li {
        display: inline-block;
        color: $gray;
        font-size: 12px;
        &:nth-of-type(2) {
          color: #e6e6e6;
        }
      }
    }
    .pact-detail__user-tools {
      width: 210px;
      margin: 0 auto;
    }
    .pact-detail__user-tools__add {
      margin: 20px 0;
      > i {
        display: inline-block;
        width: 64px;
        height: 32px;
        border: 1px solid #ccc;
        font-size: 12px;
        line-height: 32px;
        text-align: center;
        border-radius: 2px;
        &:nth-of-type(1) {
          background-color: $green;
          border-color: $green;
          color: #fff;
        }
      }
    }
    .pact-detail__user-tools__fans {
      padding-bottom: 20px;
      > i {
        font-size: 12px;
        &:nth-of-type(1) {
          display: inline-block;
          width: 60px;
          height: 20px;
          border: 2px solid #ccc;
          border-radius: 2px;
          line-height: 20px;
          text-align: center;
          color: $blue;
        }
        &:nth-of-type(2) {
          color: #1582bd;
          font-weight: bold;
        }
        &:nth-of-type(3) {
          color: #666;
        }
      }
    }
  }
</style>



