<!-- 2017/3/22- yyy 瀑布流 分组细化组件-->
<template>
  <section class="flow">
    <div v-for="item in Paint" class="flow__list-div">
      <a :href="'painting.html#/painting/'+item.id" target="_blank" class="flow__list-img">
        <img :src="item.cover==null?'http://img2.imgtn.bdimg.com/it/u=2966329046,1602124054&fm=23&gp=0.jpg':item.cover"alt="" :title="item.title">
        <span class="flow__t"></span><span class="flow__txt">{{JSON.parse(item.imgs).length}}</span>
      </a>
      <dl>
        <dd class="flow__per clearfix">
          <div style="float: left">
            <a :href="'personal.html#/accounts/products/'+item.uid" target="_blank"><span class="flow__head"><img
              :src="item.avatar" alt=""></span><i class="flow__name">{{item.nickname}}</i></a>
            <i>{{item.createtime | UnixToNormalTime(2)}}</i>
          </div>
          <div class="flow__icon" style="float: right">
            <i>{{item.bcount == null ? '0' : item.bcount}}</i>
            <PraiseCommon
              :praise="{moduleid:item.id,count:item.pcount,uid: '18202',authorid: item.uid,name: 'design_praise'}">
            </PraiseCommon>
          </div>
        </dd>
      </dl>
      <dl v-for="sonItem in item.comment">
        <dd class="compaint__mes clearfix flow__mes-bot">
          <span class="flow__head"><a :href="'personal.html#/accounts/products/'+item.uid"><img :src="sonItem.avatar" alt=""></a></span>
          <i><a :href="'personal.html#/accounts/products/'+item.uid">{{sonItem.nickname}}</a></i><i>&nbsp;:&nbsp;</i><i>{{sonItem.replymsg}}</i>
        </dd>
      </dl>
    </div>
  </section>
</template>

<script>
  import PraiseCommon from 'components/Common/PraiseCommon'
  export default {
    props: ["Paint"],
    mounted(){
      this.getModuleId();
    },
    components: {
      PraiseCommon
    },
    methods:{
      //获取点赞产品id
      getModuleId(){
        this.getUsers('1', { //点赞
          uid: 18202,
          name: 'picture_praise'
        }).then(res => {
          let data = res.data.data.list.map(item => {
            return item.moduleid
          });
          this.$store.dispatch('onPraiseUsers', data);
        });
      },
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../common/css/reset.css";
  @import "../../common/css/define";

  .flow__list-div {
    width: 280px;
    margin-bottom: 20px;
    @include wrapShow;
    .flow__list-img {
      position: relative;
      display: block;
      img {
        display: block;
        width: 280px;
        border-bottom: 1px solid rgba(128,128,128,0.2);
      }
      .flow__t {
        width: 40px;
        height: 20px;
        background-color: #4c4c4c;
        opacity: 0.8;
        position: absolute;
        top: 0;
        left: 0;
        border-radius: 4px 0 4px 0;
      }
      .flow__txt {
        width: 40px;
        height: 20px;
        font-size: 12px;
        color: $white;
        text-align: center;
        line-height: 20px;
        position: absolute;
        top: 0;
        left: 0;
      }
    }
    .flow__per {
      height: 50px;
      line-height: 50px;
      img {
        display: inline-block;
      }
      i {
        display: inline-block;
        font-size: 12px;
        color: #999999;
        vertical-align: middle;
        &:nth-of-type(1) {
          margin-right: 8px;
        }
        &:nth-of-type(2) {
          margin-right: 10px;
        }
      }
      .flow__name {
        width: 50px;
        @include beyondPass;
      }
      .flow__head {
        float: left;
        height: 50px;
        line-height: 48px;
        margin: 0 8px 0 10px;
        img {
          width: 20px;
          height: 20px;
          border-radius: 50%;
          vertical-align: middle;

        }
      }
      .flow__icon {
        height: 50px;
        line-height: 45px;
        margin-right: 6px;
        > i {
          display: inline-block;
          width: 30px;
          height: 100%;
          line-height: 50px;
          text-align: right;
          &:nth-of-type(1) {
            background: url("../../components/Common/images/person@hot.png") no-repeat 0 center;
          }
          &:nth-of-type(2) {
            cursor: pointer;
            background: url("../../components/Common/images/Parise@def.png") no-repeat 0 center;
          }
          &.is--praise {
            background: url("../../components/Common/images/praise@single.png") no-repeat 0 center;
          }
        }
      }
    }
    .flow__mes {
      width: 260px;
      padding: 10px;

      .flow__head {
        float: left;
        display: inline-block;
        vertical-align: middle;
        img {
          display: block;
          width: 20px;
          height: 20px;
          border-radius: 50%;
        }
      }
      i {
        float: left;
        font-size: 12px;
        color: $black;
        line-height: 18px;
        vertical-align: 18px;
        word-wrap: break-word;
        &:nth-of-type(1) {
          margin-left: 6px;
          a {
            color: $black;
            &:hover {
              color: $pink;
            }
          }
        }
      }
    }
    .flow__mes-bot {
      border-top: 1px solid #e6e6e6;
      border-bottom: 1px solid #e6e6e6;
    }
  }
</style>



