<!--千人计划组件-->
<template>
  <div class="pact-plan">
    <h6 class="pact-plan__title">
      【千人计划】：我们将从画萌网评选出1000位最优秀的画师，授予独一无二的数字ID，
      从001编号到999，计划将长期执行，更加优秀的画师会不断取代原来的画师，总的数量永远不会超过1000，最终留下的都将是大触画师！
    </h6>
    <ul>
      <li class="pact-plan__item" v-for="(item,index) in items">
        <a
          target="_blank"
          :href="`personal.html#/accounts/products/${item.uid}`"
          class="pact-plan__img">
          <img :src="item.img" alt=""/>
        </a>
        <p class="pact-plan__name">{{index >= 100 ? index + 1 : `00${index + 1}`}}. 次元梦境</p>
      </li>
    </ul>
    <Pagination
      :pageCount="pageCount"
      :pageSize="pageSize"
      @pageChange="pageChange">
    </Pagination>
  </div>
</template>

<!--数据交互-->
<script>
  import Pagination from 'components/Common/Pagination'

  export default {
    components: {
      Pagination,
    },
    data: () => ({
      items: [],
      pageSize: 30, // 每页显示的几条
      pageCount: 0,  // 总条数
      pageIndex: 1,  //当前页数
    }),
    mounted(){
      this.getData();
    },
    methods: {
      getData(){ //获取千人画师数据
        this.getToken().then(token => {
          this.$http.get('painter/qianrenplanlist', {
            params: {
              access_token: token,
              page: this.pageIndex
            }
          }).then(res => {
            this.items = res.data.data.list;
            this.pageCount = parseInt(res.data.data.total);
          }).catch(err => {
            console.log(err);
          })
        })
      },

      //分页调用
      pageChange(val) {
        this.pageIndex = val;
        this.getDate();
      }
    }
  }
</script>

<!--组件样式-->
<style scoped lang="scss">
  @import "../../common/css/define";

  .pact-plan {
    .pact-plan__title {
      max-width: 1140px;
      padding: 20px;
      font-size: 16px;
      color: #666;
      background-color: #fff;
      margin: 0 auto 40px;
      line-height: 28px;
      @include wrapShow;
    }
    > ul {
      max-width: 1180px;
      margin: 0 auto;
      font-size: 0;
    }
    .pact-plan__item {
      display: inline-block;
      width: 220px;
      margin: 0 20px 20px 0;
      border-radius: 4px;
      overflow: hidden;
      @include wrapShow;
      &:nth-of-type(5n) {
        margin-right: 0;
      }
      > a {
        display: block;
        overflow: hidden;
        width: 220px;
        height: 330px;
        > img {
          width: 100%;
        }
      }
      > p {
        font-size: 14px;
        font-weight: bold;
        line-height: 34px;
        color: $pink;
        text-indent: 10px;
      }
    }
    .block {
      margin-top: 50px;
    }
  }
</style>



