<!--活动列表项目组件-->
<template>
  <div>
    <section class="activity-item" v-for="(item,index) in lists">
      <router-link :to="'/detail/'+item.id">
        <section class="activity-item__pic">
          <img :src="item.imgurl" alt=""/>
        </section>
      </router-link>
      <section class="activity-item__info">
        <section class="l-wrap">
          <a
            :href="`personal.html#/accounts/${item.uid}`"
            class="activity-item__header">
            <img :src="item.avatar" alt=""/>
          </a>
          <i class="activity-item__name">{{item.nickname}}</i>
        </section>
        <section class="activity-item__desc">
          <h6>{{item.title}}</h6>
          <section class="activity-item__hot">
            <i>{{item.jcount}}人参与</i>
            <slot
              :info="{bcount:item.bcount,ccount:item.ccount,jcount:item.jcount}"
              name="hot"></slot>
          </section>
          <p v-html="item.content"></p>
        </section>
        <slot name="btn"></slot>
      </section>
      <section class="activity-item__state">
        <h6>{{item.stats | changeState(0)}}</h6>
        <p>{{item.endactivitytime | UnixToNormalTime(0)}}</p>
      </section>
    </section>
  </div>
</template>

<!--数据交互-->
<script>
  import Pagination from 'components/Common/Pagination' //分页组件
  export default {
    components: {
      Pagination
    },
    data: () => ({
      time: ''
    }),
    props: ['path', 'lists', 'pageCount', 'pageSize'],
  }
</script>
<!--组件样式-->
<style scoped lang="scss">
  @import "../../common/css/define";

  .activity-item {
    width: 1180px;
    margin: 18px auto;
    border-radius: 4px;
    position: relative;
    padding-bottom: 30px;
    @include wrapShow;

    .activity-item__pic {
      width: 1180px;
      height: 300px;
      border-radius: 4px;
      overflow: hidden;
      > img {
        width: 100%;
      }
    }
    .activity-item__info {
      font-size: 0;
      position: relative;
      .l-wrap {
        display: inline-block;
        vertical-align: top;
      }
      .activity-item__header {
        display: block;
        margin: -64px 22px 0 22px;
        width: 124px;
        height: 124px;
        background-color: #fff;
        border: 4px solid rgba(255, 255, 255, 0.4);
        background-clip: content-box;
        border-radius: 50%;
        overflow: hidden;
        position: relative; //提高层级
        z-index: 333;
        > img {
          width: 100%;
        }
      }
      .activity-item__name {
        display: inline-block;
        width: 166px;
        text-align: center;
        font-size: 14px;
        color: $black;

      }
      .activity-item__desc {
        display: inline-block;
        vertical-align: top;
        margin-top: 26px;
        > h6 {
          font-size: 22px;
          color: $black;
          display: inline-block;
          vertical-align: middle;
          margin-right: 20px;
        }
        .activity-item__hot {
          display: inline-block;
          > i {
            display: inline-block;
            font-size: 12px;
            vertical-align: middle;
            height: 22px;
            padding: 0 10px;
            background-color: $pink;
            color: #fff;
            line-height: 22px;
            text-align: center;
            border-radius: 2px;
          }
        }
        > p {
          font-size: 16px;
          color: #666;
          margin-top: 16px;
          text-indent: 8px;
          max-height: 56px;
          overflow: hidden;
          font-family: '微软雅黑', sans-serif;
        }
      }
    }
    .activity-item__state {
      width: 138px;
      height: 90px;
      background: url("./images/bg@act.png") no-repeat;
      background-size: cover;
      position: absolute;
      top: 0;
      right: 20px;
      > h6 {
        font-size: 22px;
        color: #fff;
        text-align: center;
        margin-top: 20px;
      }
      > p {
        font-size: 14px;
        color: #fff;
        text-align: center;
        line-height: 36px;
      }
    }
  }

</style>



