<!--发布画作组件-->
<template>
  <section class="new">
    <!--设计商用插槽-->
    <slot name="sale"></slot>
    <section class="new__title">
      <h6>作品</h6>
    </section>
    <UploadImg @getImgKey="getImgKey"></UploadImg>
    <dl class="new-content">
      <dd class="new-content__title">
        <em>画作标题</em>
        <input type="text" v-model="newData.title" placeholder="标题不超过25个字"/>
      </dd>
      <dd class="new-content__desc">
        <em>画作说明</em>
        <textarea v-model="newData.desc" placeholder="300字以内"></textarea>
      </dd>
      <dd class="new-content__types">
        <em>&#12288;&#12288;类型</em>
        <i :class="{'is--active':item.isActive}" v-for="(item,index) in type"
           @click="changeType(index)">{{item.title}}</i>
      </dd>
      <dd class="new-content__author" v-if="isShow">
        <em>&#12288;原作名</em>
        <input v-model="newData.author" type="text"/>
      </dd>
      <dd class="new-content__name" v-if="isShow">
        <em>&#12288;角色名</em>
        <input v-model="newData.role" type="text" placeholder="添加角色，回车输入"/>
      </dd>
      <dd class="new-content__add">
        <em>&#12288;&#12288;标签</em>
        <input v-model="newData.marks" type="text" placeholder="添加角色，回车输入"/>
      </dd>
    </dl>
    <dl class="new-marks">
      <dt>推荐标签:</dt>
      <dd v-for="(item,type) in tags">
        <h6>{{item.title}}</h6>
        <ul class="new-marks__item">
          <li v-for="(list,index) in item.attr" @click="addTags(index,type)">{{list.tags}}</li>
        </ul>
      </dd>
      <!--商用报价插槽-->
      <slot name="price"></slot>
      <dd class="new-marks__btn" @click="send">发布</dd>
    </dl>
  </section>
</template>

<script>
  import UploadImg from 'components/Common/UploadImg' //图片编辑公共组件
  export default {
    data: () => {
      return {
        isShow: true, //控制是否展示同人
        srcEl: null,
        filesList: [],
        imgs: [],
        newData: {imgList: [], pictype: '1', title: '', desc: '', author: '', role: '', marks: []}, //用户输入数据
        tags: [], //公共便签数据
        path: '',
      };
    },
    props: ['type'],
    components: {
      UploadImg
    },
    created(){
      this.path = this.$route.fullPath;
    },
    mounted(){

      this.getMarks();
    },
    methods: {
      //获取上传图片key
      getImgKey(key){
        this.newData.imgList.push(key);
        console.log(key);
      },
      send(){ //发布画作
        this.$emit('commit', this.newData);
      },

      //切换同人非同人
      changeType(index){
        this.type.forEach(item => {
          item.isActive = false;
        });
        this.type[index].isActive = true;
        this.newData.pictype = index + 1;
        if (index === 1) {
          this.isShow = false;
          this.newData.author = '';
          this.newData.role = '';
        } else {
          this.isShow = true;
        }
      },
      //获取标签
      getMarks(){
        this.getToken().then(token => {
          this.$http('common/gettags', {
            params: {
              access_token: token,
              tagtype: 1
            }
          }).then(res => {
            let data = res.data.data.list;

            if (this.path === '/new/design') { //设计稿
              this.tags = [{title: '', attr: []}];
              data.forEach(item => {
                switch (item.type) {
                  case '4':   //  属性便签
                    this.tags[0].attr.push(item);
                    break;
                }
              })
            } else { //画作
              this.tags = [{title: '稿件属性:', attr: []}, {title: '稿件用途:', attr: []}, {title: '稿件风格:', attr: []}];
              data.forEach(item => {
                switch (item.type) {
                  case '1':   //  属性便签
                    this.tags[0].attr.push(item);
                    break;
                  case '2':  //用途
                    this.tags[1].attr.push(item);
                    break;
                  case '3':  // 风格
                    this.tags[2].attr.push(item);
                    break;
                }
              })
            }

          }).catch(err => {
            console.log(err);
          })
        })
      },

      //设置标签
      addTags(index, type){
        switch (type) {
          case 0:
            this.newData.marks.push(this.tags[type].attr[index].tags);
            this.newData.marks = [...new Set(this.newData.marks)]; //数组去重
            break;
          case 1:
            this.newData.marks.push(this.tags[type].attr[index].tags);
            this.newData.marks = [...new Set(this.newData.marks)];
            break;
          case 2:
            this.newData.marks.push(this.tags[type].attr[index].tags);
            this.newData.marks = [...new Set(this.newData.marks)];
            break;
        }
      }
    },
  }
</script>

<!--组件样式-->
<style scoped lang="scss">
  @import "../../common/css/define";

  .new {
    width: 760px;
    padding: 40px 40px 30px 40px;
    display: inline-block;
    vertical-align: top;
    background-color: #fff;
    @include wrapShow;
  }

  .new__title {
    display: inline-block;
    vertical-align: middle;
    > h6 {
      font-size: 14px;
      font-weight: normal;
      display: inline-block;
      vertical-align: middle;
      margin-left: 28px;
    }
    .new__commit {
      display: inline-block;
      vertical-align: middle;
      > label {
        display: inline-block;
        color: #fff;
        text-align: center;
        font-size: 16px;
        width: 120px;
        height: 48px;
        line-height: 48px;
        border-radius: 4px;
        margin: 0 14px;
        cursor: pointer;
        @include boxShow;
        @include linear;
      }
      > p {
        display: inline-block;
        vertical-align: middle;
        font-size: 14px;
        color: #999;
      }
    }
    .new__pic {
      position: relative;
      width: 684px;
      padding: 20px;
      margin-left: 56px;
      > li, .marks {
        position: relative;
        display: inline-block;
        margin: 0px 8px 6px 0px;
        cursor: move;
        &:nth-of-type(5n) {
          margin-right: 0;
        }
        &:nth-of-type(1)::after {
          content: '封面';
          color: #fff;
          font-size: 14px;
          height: 30px;
          position: absolute;
          width: 100%;
          line-height: 30px;
          bottom: 0px;
          text-align: center;
          background-color: rgba(0, 0, 0, 0.5);
        }

        > .new__commit-img {
          width: 130px;
          height: 194px;
          overflow: hidden;
          > img {
            width: 100%;
          }
        }
        .new__bg {
          width: 130px;
          height: 194px;
          background: rgba(0, 0, 0, 0.4);
          position: absolute;
          left: 0;
          top: 0;
        }
        .new__marks {
          display: block;
          position: absolute;
          font-size: 12px;
          top: 10px;
          right: 10px;
          color: #fff;
          cursor: pointer;
        }
      }
    }
    .new__tips {
      width: 100%;
      text-align: center;
      font-size: 14px;
      color: $gray;
    }
  }

  .new-content {
    font-size: 14px;
    > dd {
      margin: 40px 0;
      > input {
        height: 40px;
        width: 480px;
        border: 1px solid #e6e6e6;
        border-radius: 4px;
        text-indent: 1em;
      }
      > em {
        margin-right: 16px;
      }
      &:nth-last-child(1) {
        margin-bottom: 20px;
      }
    }
    .new-content__desc {
      > textarea {
        width: 458px;
        height: 158px;
        resize: none;
        border-radius: 4px;
        border: 1px solid #E6E6E6;
        line-height: 20px;
        padding: 10px;
      }
      > em {
        vertical-align: top;
        display: inline-block;
        margin-top: 10px;
      }
    }
    .new-content__types {

      > i {
        width: 100px;
        height: 20px;
        display: inline-block;
        background: url("./images/select@16x16.png") no-repeat;
        text-indent: 1.8em;
        cursor: pointer;
        &.is--active {
          background: url("./images/selected@16x16.png") no-repeat;
        }
      }
    }
  }

  .new-marks {
    width: 684px;
    margin-left: 76px;
    font-size: 14px;
    > dt {
      margin-bottom: 16px;
    }
    > dd {
      margin-bottom: 12px;
      font-size: 0;
      &:nth-last-child(1) {
        margin-bottom: 0;
      }
      > h6 {
        font-weight: normal;
        font-size: 14px;
        display: inline-block;
        vertical-align: top;
      }
      .new-marks__item {
        display: inline-block;
        width: 610px;
        vertical-align: top;
        > li {
          font-size: 14px;
          height: 24px;
          padding: 0px 12px;
          display: inline-block;
          border: 1px solid #e6e6e6;
          border-radius: 2px;
          text-align: center;
          line-height: 24px;
          color: #666;
          margin: 0px 6px 6px 0px;
          cursor: pointer;
          transition: 0.3s;
          &:hover {
            background: $pink;
            color: #fff;
            border-color: #fff;
          }
          &.is--active {
            background: $pink;
            color: #fff;
            border-color: #fff;
          }
        }
      }
    }
    .new-marks__btn {
      width: 120px;
      height: 48px;
      border-radius: 4px;
      color: #fff;
      font-size: 16px;
      line-height: 48px;
      text-align: center;
      cursor: pointer;
      margin-top: 30px;
      @include linear;
      @include boxShow;
    }
  }
</style>



