<!--活动列表组件-->
<template>
  <div>
    <ActivityNav :menus="menus" @change="change" @search="search"></ActivityNav>
    <ActivityItem :lists="actData"></ActivityItem>
    <Pagination
      :pageCount="pageCount"
      :pageSize="pageSize"
      @pageChange="pageChange"></Pagination>
    <div class="loading"
         v-loading="loading"
         element-loading-text="加载中...">
    </div>
  </div>
</template>

<!--数据交互-->
<script>
  import ActivityNav from 'components/Common/Bar'
  import ActivityItem from 'components/Activity/ActivityItem'
  import Pagination from 'components/Common/Pagination' //分页组件
  export default {
    components: {
      ActivityNav,
      ActivityItem,
      Pagination
    },
    data: () => {
      return {
        menus: [
          {title: '全部活动', isActive: false},
          {title: '预热中', isActive: false},
          {title: '进行中', isActive: true},
          {title: '已结束', isActive: false}
        ],
        loading: true,
        activityState: 2, //进行中
        lists: [], //商品列表
        actData: [],//商品列表全部数据
        cat: ['', 'hot', 'being', 'over'], //菜单搜索标签
        pageSize: 30, // 每页显示的几条
        pageCount: 0,  // 总条数
        pageIndex: 1,
      }
    },
    mounted(){
      this.getDate(0);
//      location.search='?state=being';
    },
    methods: {
      change(index){ //菜单切换
        this.menus.forEach(item => {
          item.isActive = false;
        });
        this.menus[index].isActive = 'true';
        this.activityState = index;
        this.getDate();
      },
      getDate(){
        let _this_ = this;
        this.getToken().then(token => {
          this.$http.get('huodong/findallhuodong', {
            params: {
              access_token: token,
              cat: this.cat[this.activityState], //根据索引查找标签
              page: this.pageIndex
            }
          }).then(res => {
            this.loading = false;
            _this_.actData = res.data.data.list;
            _this_.pageCount = parseInt(res.data.data.total);
          }).catch(err => {
            console.log(err);
          })
        })
      },
      search(value){ //活动搜索
        let _this_ = this;
        this.getToken().then(token => {
          this.$http.get('huodong/search', {
            params: {
              access_token: token,
              key: value
            }
          }).then(res => {
            _this_.lists = res.data.data.list;
          }).catch(err => {
            console.log(err);
          })
        })
      },
      pageChange(val){ //分页调用
        this.pageIndex = val;
        this.getDate()
      }
    }
  }
</script>

<style scoped lang="scss">

</style>



