<!-- 2017/3/22- yyy -待出售设计-->
<template>
  <section>
    <section class="sale">
      <range
        :SearchRange="SearchRange"
        :IconRelease="IconRelease"></range>
      <ComSell
        :ComSell="pendingSale"
        class="l-mt"
        :link="link"></ComSell>

      <!--分页-->
      <Pagination
        :pageCount="pageCount"
        :pageSize="pageSize"
        @pageChange="pageChange"></Pagination>
    </section>
  </section>
</template>
<script>

  import range from 'components/Design/SearchRange';  // 搜索范围
  import ComSell from 'components/Common/ComSell';  // 个人待出售设计公共部分
  import Pagination from 'components/Common/Pagination' //分页组件
  //  import Event from './bus';

  export default {
    data: () => {
      return {
        SearchRange: [
          {name: "按时间", inputBol: false, bol: true},
          {name: "按点赞", inputBol: false, bol: false},
          {name: "按人气", inputBol: false, bol: false},
          {name: "全部设计", inputBol: true, bol: false},
          {name: "公开设计", inputBol: true, bol: false},
          {name: "仅商家可见", inputBol: true, bol: false},
        ],
        IconRelease: "发布设计",
        link: '/design',
        pageCount: 0,
        pageSize: 30,
        pageIndex: 1,
        promise: null,
        pendingSale: [] //未商用全部数据
      }
    },
    mounted(){
      this.getData();
    },
    components: {
      range,
      ComSell,
      Pagination
    },
    methods: {
      getData(){ //获取待出售设计数据
        let _this_ = this;
        this.getToken().then(token => {
          this.$http.get('sheji/findallsheji', {
            params: {
              access_token: token,
              busintype: '2',
              page: this.pageIndex
            }
          }).then(res => {
            this.pendingSale = res.data.data.list;
            _this_.pageCount = parseInt(res.data.data.total);//总页数

          }).catch(err => {
            console.log(err);
          })
        })
      },
      pageChange(val){ //分页调用
        this.pageIndex = val;
        this.getData();
      }
    }
  }

</script>

<style lang="scss" scoped>

  @import "../../common/css/reset.css";
  @import "../../common/css/define";

  .l-mt {
    margin-top: 30px;
  }
</style>



