<!--设计平台主页组件-->
<template>
  <div>
    <section class="bg">
      <section class="design__nav clearfix">
        <personalNav :ListComNav="ListComNav" class="design__nav-l" @homeCom="tabSwitch"></personalNav>
        <searchTxt class="design__nav-r" @searchIcon="searchIcon" :searchContent="searchContent"></searchTxt>
      </section>
    </section>
    <section class="design__content">
      <section class="design__icon">
        <keep-alive>
          <component :is='currentView' :ComCollect="Collect"></component>
        </keep-alive>
      </section>
    </section>
  </div>
</template>

<!--数据交互-->
<script>

  import personalNav from 'components/Personal/ComNav';  // 组件-来自个人主页导航
  import searchTxt from 'components/Design/ComSearch';  //搜索框
  import subTitle from 'components/Design/CommonTitle';  // 小标题

  import pendSale from 'components/Design/PendingSale';  // 待出售设计
  import commerDesign from 'components/Design/CommercialDesign';  // 已商用设计
  import collectInfo from 'components/Design/CollectInfo';  // 征稿信息

  let json = [
    {name: "卡牌", bol: true},
    {name: "CG原画", bol: true},
    {name: "等身抱枕", bol: false},
    {name: "周边", bol: false},
    {name: "服设", bol: false},
    {name: "柄图", bol: false},
    {name: "印花", bol: true},
    {name: "人设", bol: false},
    {name: "立绘", bol: true},
    {name: "纯场景", bol: false},
    {name: "同人本", bol: false},
    {name: "钥匙扣", bol: false},
    {name: "明信片", bol: false},
    {name: "海报", bol: false},
    {name: "胶带纸", bol: false},
    {name: "漫画", bol: false},
    {name: "头像", bol: false},
    {name: "轻小说", bol: false},
    {name: "排版", bol: false},
    {name: "商插", bol: false}
  ];

  export default {
    data: () => {
      return {
        ListComNav: [
          {name: "待出售设计", bol: true, tab: "pendSale"},
          {name: "已商用设计", bol: false, tab: "commerDesign"},
          {name: "征稿信息", bol: false, tab: "collectInfo"}
        ],
        searchContent: "卡牌",
        SubTitleList: json,
        currentView: pendSale
      }
    },
    components: {
      personalNav,
      searchTxt,
      subTitle,
      pendSale,
      commerDesign,
      collectInfo
    },
    mounted(){
      let type = window.location.search.substring(1);
      this.moreCollectInfo(type); //主页查看更多跳转
//      this.getPraise();
    },
    methods: {

      moreCollectInfo(type){
        switch (type) {
          case 'design':
            this.currentView = collectInfo;
            break;
          case 'daftSale':
            this.currentView = pendSale;
            break;
          case 'daftUsed':
            this.currentView = commerDesign;
            break;
        }
      },
      tabSwitch(index){
        for (let i = 0; i < this.ListComNav.length; i++) {
          this.ListComNav[i].bol = false;
          if (index === i) {
            this.ListComNav[i].bol = true;
            this.currentView = this.ListComNav[i].tab;
          }
        }
      },
      searchIcon(){  // 搜索框事件
        console.log("搜索框检索的内容")
      },
      SubTitleIcon(index){ // 点击切换小标题的姿态
        for (let i = 0; i < this.SubTitleList.length; i++) {
          if (index === i) {
            this.SubTitleList[i].bol = !this.SubTitleList[i].bol;
          }
        }
      },

      //获取会员点赞作品
      getPraise(){
        this.getUsers('1', {
          uid: 18202,
          name: 'design_praise'
        }).then(res => {
          this.$store.dispatch('onPraiseUsers', res.data.data.list);
        })
      }
    }
  }
</script>

<!--组件样式-->
<style lang="scss" scoped>

  @import "../../common/css/reset.css";
  @import "../../common/css/define";

  .bg {
    width: 100%;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
    transform: translate3d(0px, -2px, 0px);
    .design__nav {
      width: 1180px;
      margin: 0 auto;
      padding: 20px 0;
      .design__nav-l {
        float: left;
      }
      .design__nav-r {
        float: right
      }
    }
  }

  .design__bgsub {
    width: 100%;
    /*box-shadow: 0px -10px 20px -10px #e6e6e6;*/
    .design__sub {
      padding-top: 12px;
      width: 1180px;
      margin: 0 auto;
    }
  }

  .design__content {
    background-color: #fafafa;
    .design__icon {
      width: 1180px;
      margin: 0 auto;
    }
  }

  #app {
    background-color: #fafafa;
  }
</style>



