<!--发布画铺-->
<template>
  <div class="pact-new">
    <h6 class="pact-new__title">发布画铺</h6>
    <dl class="pact-new__content">
      <dd>
        <h6>&#12288;&#12288;作品</h6>
        <UploadImg @getImgKey="getImgKey"></UploadImg>
      </dd>
      <dd class="pact-new__name">
        <h6>画作标题</h6>
        <input v-model="dataShop.title" type="text" placeholder="标题不超过25个字"/>
      </dd>
      <dd class="pact-new__desc">
        <h6>画作说明</h6>
        <textarea v-model="dataShop.content" placeholder="300字以内"></textarea>
      </dd>
      <dd class="pact-new__price">
        <h6>&#12288;&#12288;报价</h6>
        <input
          v-model="dataShop.lowcost"
          type="text"
          placeholder="可以填写相同价格"/>&#12288;至&#12288;
        <input
          v-model="dataShop.highcost"
          type="text"
          placeholder="可以填写相同价格"/>&#12288;元
      </dd>
      <dd class="pact-new__time">
        <h6>创作周期</h6>
        <input type="text" v-model="dataShop.fastdesignday"/>&#12288;至&#12288;
        <input type="text" v-model="dataShop.longdesignday"/>&#12288;天
      </dd>
      <dd class="pact-new__btn" @click="handleCommit">发布</dd>
    </dl>
    <section class="pact-new__restriction">
      <h6>权限设置</h6>
      <ul>
        <li
          @click="handleChangeAct(index)"
          :class="{'is--active':item.flag}"
          v-for="(item,index) in dataActMenu">
          {{item.title}}
          <p>{{item.desc}}</p>
        </li>
      </ul>
    </section>
  </div>
</template>

<!--数据交互-->
<script>
  import UploadImg from 'components/Common/UploadImg' //图片编辑公共组件

  export default {
    components: {
      UploadImg
    },
    data: () => ({
      dataShop: {title: '', imgs: [], content: '', lowcost: '', highcost: '', fastdesignday: '', longdesignday: ''},
      dataActMenu: [
        {title: '允许右键', desc: '即允许用户通过右键下载保存你的作品', flag: true},
        {title: '添加水印', desc: '用户保存作品原图时，原图上将添加署名水印', flag: true}
      ],
      dataActState: ['1', '2']
    }),
    mounted(){
      //用户是否登录
      this.isLogin()
    },
    methods: {
      //获取上传图片key
      getImgKey(key){
        this.dataShop.imgs.push(key)
      },
      //设置权限
      handleChangeAct(index){
        this.dataActState = [];
        this.dataActMenu[index].flag = !this.dataActMenu[index].flag; //复选设置
        this.dataActMenu.forEach((item, index) => { //筛选为选中状态
          if (item.flag === true) {
            this.dataActState.push(index + 1);
          }
        });
        console.log(this.dataActState);
      },
      handleCommit(){
        let _this_ = this;
        let baseUrl = document.querySelector('#drawing').toDataURL("image/png"); //获取base64
        let base64Data = baseUrl.replace(/^data:image\/(png|jpg);base64,/, "");
        this.getToken().then(token => {
          const uploadeBase = () => {
            return new Promise(resolve => {
              this.$http('common/uptoken', {
                params: {
                  access_token: token
                }
              }).then(res => {
                //上传base64
                const token = res.data.data.uptoken;
                _this_.Qiniu_Base64(base64Data, token).then(res => {
                  console.log(res);
                  _this_.dataShop.imgs.push(res);
                  resolve(res);
                })
              });
            })
          };
          const sendPain = (async () => { //异步提交
            await uploadeBase();
            this.$http('huapu/addhuapu', {
              params: {
                access_token: token,
                uid: 18202,
                title: _this_.dataShop.title,
                imgs: _this_.dataShop.imgs,
                content: _this_.dataShop.content,
                lowcost: _this_.dataShop.lowcost,
                highcost: _this_.dataShop.highcost,
                fastdesignday: _this_.dataShop.fastdesignday,
                longdesignday: _this_.dataShop.longdesignday,
                act: _this_.dataActState,
              }
            }).then(res => {
              console.log(res);
              //todo 提交后清空数据
            }).catch(err => {
              console.log(err);
            })
          })();
        });
      }
    }
  }
</script>

<!--组件样式-->
<style scoped lang="scss">
  @import "../../common/css/define";

  .pact-new {
    max-width: 1180px;
    margin: 0 auto;
    color: $black;
    .pact-new__title {
      font-size: 22px;
      margin: 20px 0 20px 0;
    }
    .pact-new__content {
      width: 760px;
      padding: 40px;
      margin-bottom: 20px;
      background-color: #fff;
      display: inline-block;
      @include wrapShow;

      > dd {
        margin-bottom: 40px;
        font-size: 14px;
        > h6 {
          display: inline-block;
          vertical-align: top;
          line-height: 40px;
          font-size: 14px;
          font-weight: normal;
          margin-right: 16px;
        }
        > section {
          display: inline-block;
          > label {
            display: inline-block;
            width: 120px;
            height: 48px;
            border-radius: 4px;
            font-size: 16px;
            line-height: 48px;
            text-align: center;
            color: #fff;
            cursor: pointer;
            margin-right: 16px;
            @include wrapShow;
            @include linear;
          }
          > i {
            font-size: 14px;
            color: #666;
          }
        }
        &:nth-last-of-type(1) {
          margin-bottom: 0;
        }
      }
      .pact-new__name {
        > input {
          width: 478px;
          height: 36px;
          border-radius: 2px;
          border: 1px solid #e6e6e6;
          font-size: 14px;
          text-indent: 1em;
        }
      }
      .pact-new__desc {
        > textarea {
          width: 474px;
          height: 160px;
          border-radius: 2px;
          border: 1px solid #e6e6e6;
          font-size: 14px;
          resize: none;
          text-indent: 1em;
          padding-top: 16px;
        }
      }
      .pact-new__price, .pact-new__time {
        > input {
          width: 218px;
          height: 36px;
          border-radius: 2px;
          border: 1px solid #e6e6e6;
          font-size: 14px;
          text-indent: 1em;
        }
      }
      .pact-new__btn {
        width: 120px;
        height: 50px;
        border-radius: 4px;
        font-size: 16px;
        line-height: 50px;
        text-align: center;
        color: #fff;
        cursor: pointer;
        margin-left: 76px;
        @include wrapShow;
        @include linear;

      }
    }

    .pact-new__restriction {
      width: 320px;
      height: 310px;
      display: inline-block;
      vertical-align: top;
      margin-left: 10px;
      background-color: #fff;
      @include wrapShow;

      > h6 {
        font-size: 16px;
        width: 100%;
        height: 48px;
        line-height: 48px;
        background-color: $pink;
        color: #fff;
        border-radius: 4px 4px 0 0;
        font-weight: normal;
        text-indent: 1.5em;
      }
      > ul {
        padding: 40px;
        > li {
          margin-bottom: 16px;
          font-size: 14px;
          color: $black;
          cursor: pointer;
          &:nth-of-type(1), &:nth-of-type(2) {
            background: url("./images/choose@bg.png") no-repeat;
            text-indent: 1.5em;
          }
          &.is--active {
            background: url("./images/choosed.png") no-repeat;
          }
          > p {
            margin-top: 4px;
            width: 210px;
            margin-left: 1.5em;
            text-align: left;
            text-indent: 0;
            line-height: 20px;
            color: $gray;
            font-size: 12px;
          }
        }
      }
    }
  }
</style>



