<template>
  <section class="new__commit">
    <input type="file" id="pic" v-show="false" @change="getFile($event)"/>
    <label for="pic">上传图片</label>
    <p>为保证封面正常显示，请在电脑端上传图片</p>

    <ul class="new__pic" v-if="imgs.length>0">
      <li :key="item.id" v-for="(item,index) in imgs" @mouseenter="isSee(index)" @mouseleave="noSee(index)"
          @dragstart="fnDrag($event,index)"
          @dragover="allowDrop($event)"
          @drop.stop="fnDrop($event,index)"
          draggable="true">
        <section class="new__commit-img">
          <img :src="item"/>
        </section>
        <section class="new__bg" v-show="false" ref="mark">
          <i class="new__marks el-icon-close" @click="isDel(index)"></i>
        </section>
      </li>
    </ul>
    <section class="new__tips" v-if="imgs.length>0">拖动预览图可以调整图片顺序</section>
    <div v-show="imgs.length>0">
      <div id="new-edit">
        <div class="box" draggable="true">
          <img class="box-img" :src="imgs[0]">
        </div>
        <i class="marks"></i>
        <div class="move">
          <div>
            <img class="marks-bg" :src="imgs[0]">
          </div>
          <div class="move-face"></div>
          <div class="r"></div>
          <div class="b"></div>
          <div class="l"></div>
          <div class="t"></div>
          <div class="rb"></div>
          <div class="lt"></div>
          <div class="rt"></div>
          <div class="lb"></div>
        </div>
      </div>
      <div class="show">
        <img :src="imgs[0]" class="showImg">
      </div>
    </div>
    <!--图片裁剪区域-->
    <div class="canvas" v-show="false">
      <canvas ref="drawing" id="drawing" width="220" height="330">canvas</canvas>
    </div>
  </section>
</template>

<!--数据交互-->
<script>
  export default {
    data: () => ({
      imgs: [],
      path: ''
    }),
    created(){
      this.path = this.$route.fullPath;
    },
    methods: {
      isSee(index){ //显示
        this.$refs.mark[index].style.display = 'block'
      },
      noSee(index){ //隐藏
        this.$refs.mark[index].style.display = 'none';
      },
      isDel(index){ //删除
        this.imgs.splice(index, 1);
        console.log(index);
      },

      getFile(ev){ //获取图片信息
        let files = ev.target.files;
        let reader = new FileReader();
        let url = window.URL.createObjectURL(files[0]);
        let _this_ = this;
        this.imgs.push(url);

        //分发图片信息
        this.getToken().then(token => {
          this.$http.get('common/uptoken', {
            params: {
              access_token: token
            }
          }).then(res => {
            const token = res.data.data.uptoken;
            const url = 'http://up.qiniu.com';
            _this_.Qiniu_upload(files[0], token, url).then(res => {
              console.log(res);
//              _this_.newData.imgList.push(res);
              _this_.$emit('getImgKey', res)
            })
          });
        });
        //获取到图片后
        this.cropper(this.imgs[0]);
      },
      allowDrop(event){ //允许拖拽区
        event.preventDefault();
      },
      fnDrag(event, index) {//获取拖拽目标数据
        this.srcEl = event.target;
        event.dataTransfer.setData('Text', index);
      },
      fnDrop(event, index){ //设置拖拽区数据
        event.preventDefault();
        let temp = this.imgs[index];
        let data = event.dataTransfer.getData("Text");

        //重新添加数组并改变图片数组位置
        let imgList = this.imgs;
        imgList[index] = imgList[data];
        imgList[data] = temp;

        //删除原先数组，重新渲染数据
        this.imgs = [];
        this.imgs = imgList;
        this.cropper(this.imgs[0]);
      },
      cropper(img){ //图片编辑
        if (this.path === '/new/design') { //发送设计稿
          new this.Cropper(img, 264, 264, 220, 220);
        } else { //画作
          new this.Cropper(img, 170, 260);
        }
      },
    }
  }
</script>

<!--组件样式-->
<style scoped lang="scss">
  @import "../../common/css/define";

  #new-edit {
    position: relative;
    width: 330px;
    margin: 0 auto;
    display: inline-block;
    user-select: none;
  }

  .new__pic {
    position: relative;
    width: 684px;
    padding: 20px;
    margin-left: 56px;
    > li, .marks {
      position: relative;
      display: inline-block;
      margin: 0px 8px 6px 0px;
      cursor: move;
      &:nth-of-type(5n) {
        margin-right: 0;
      }
      &:nth-of-type(1)::after {
        content: '封面';
