        font-size: 14px;
        height: 30px;
        position: absolute;
        width: 100%;
        line-height: 30px;
        bottom: 0px;
        text-align: center;
        background-color: rgba(0, 0, 0, 0.5);
      }

      > .new__commit-img {
        width: 130px;
        height: 194px;
        overflow: hidden;
        > img {
          width: 100%;
        }
      }
      .new__bg {
        width: 130px;
        height: 194px;
        background: rgba(0, 0, 0, 0.4);
        position: absolute;
        left: 0;
        top: 0;
      }
      .new__marks {
        display: block;
        position: absolute;
        font-size: 12px;
        top: 10px;
        right: 10px;
        color: #fff;
        cursor: pointer;
      }
    }
  }

  .new__tips {
    width: 100%;
    text-align: center;
    font-size: 14px;
    color: $gray;
  }

  .new__commit {
    display: inline-block;
    vertical-align: middle;
    > label {
      display: inline-block;
      color: #fff;
      text-align: center;
      font-size: 16px;
      width: 120px;
      height: 48px;
      line-height: 48px;
      border-radius: 4px;
      margin: 0 14px;
      cursor: pointer;
      @include boxShow;
      @include linear;
    }
    > p {
      display: inline-block;
      vertical-align: middle;
      font-size: 14px;
      color: #999;
    }
  }

  .new__bg {
    width: 130px;
    height: 194px;
    background: rgba(0, 0, 0, 0.4);
    position: absolute;
