<!--选择器组件-->
<template>
    <el-select v-model="values" @change ='handleChange' placeholder="请选择">
        <el-option
                v-for="item in options"
                :label="item.label"
                :value="item.value">
        </el-option>
    </el-select>
</template>

<!--数据交互-->
<script>
  export default {
    props:['options'],
    data:()=>({values:'1'}),
    methods:{
      handleChange(){
        this.$emit('onSubmitSelect',this.values)
      }
    }
  }
</script>



