<!--待出售设计组件-->
<template>
  <ul class="daftSale">
    <li class="daftSale__list" v-for="item in daftSale">
      <section class="daftSale__info">

        <a
          target="_blank"
          :href="link+item.id" class="daftSale__img">
          <img src="./images/sale.png" alt=""/>
          <i>2P</i>
        </a>
        <section class="daftSale__hot">
          <i>{{item.bcount?item.bcount:'0'}}</i>
          <!--<i>{{item.pcount?item.pcount:'0'}}</i>-->
        </section>
      </section>
      <section class="daftSale__desc">
        <h6>{{item.title}}</h6>
        <b>￥{{item.lowcost | setNull}}~{{item.highcost | setNull}}</b>
      </section>
      <section class="daftSale__tips">
        <a :href="'personal.html#/accounts/'+item.uid" class="daftSale__tips-img">
          <img :src="item.avatar" alt=""/>
        </a>
        <p :title="item.nickname">{{item.nickname}}</p>
        <i>{{item.createtime |UnixToNormalTime(2)}}</i>
      </section>
    </li>
  </ul>
</template>

<!--数据交互-->
<script>
  export default {
    name: 'app-daft-sale',
    data: () => {
      return {
        // list:[1,2,3,4,5]
      };
    },
    props: ['daftSale', 'link']
  }
</script>

<style lang="scss" scoped>
  @import "../../common/css/define";

  .daftSale {
    max-width: 1180px;
    margin: 0 auto;

    .daftSale__list {
      width: 220px;
      display: inline-block;
      margin-right: 20px;
      &:nth-child(5) {
        margin-right: 0;
      }
    }
  }

  .daftSale__info {
    width: 220px;
    height: 256px;
    border-radius: 4px;
    position: relative;
    @include wrapShow;

    .daftSale__img {
      display: block;
      width: 220px;
      height: 220px;
      overflow: hidden;
      > img {
        width: 100%;
      }
      > i {
        display: block;
        width: 32px;
        height: 20px;
        background-color: rgba(0, 0, 0, 0.3);
        border-radius: 4px 0 4px 0;
        position: absolute;
        top: 0;
        left: 0;
        color: #fff;
        font-size: 12px;
        text-align: center;
        line-height: 20px;
      }
    }
    .daftSale__hot {
      line-height: 36px;
      font-size: 12px;
      color: $gray;
      text-align: right;
      padding: 0 12px;
      > i {
        display: inline-block;
        padding: 0 20px;
        height: 36px;
        margin: 0 10px;
        background: url("../../components/Common/images/person@hot.png") no-repeat 0 center;
      }
    }
  }

  .daftSale__desc {
    border-bottom: 1px solid #e6e6e6;
    padding-bottom: 12px;
    > h6 {
      font-size: 12px;
      font-weight: normal;
      color: $black;
      line-height: 32px;
    }
    > b {
      font-size: 14px;
      color: $pink;
    }
  }

  .daftSale__tips {
    margin-top: 12px;
    .daftSale__tips-img {
      width: 20px;
      height: 20px;
      border-radius: 50%;
      overflow: hidden;
      display: inline-block;
      vertical-align: middle;
      background: darkorange;
      > img {
        width: 100%;
      }
    }
    > p {
      width: 40px;
      display: inline-block;
      vertical-align: middle;
      font-size: 12px;
      color: $black;
      margin: 0 16px;
      @include beyondPass;
    }
    > i {
      display: inline-block;
      vertical-align: middle;
      font-size: 12px;
      color: $gray;
    }
  }
</style>



