<!--画作详情组件-->

<template>
  <div id="detail">
    <section class="detail__content">
      <section class="l-wrap">
        <dl class="detail__title">
          <dt>
            {{items.title}}
            <i>{{items.bcount | setNull}}</i>
          </dt>
          <dd class="detail__tips">
            [{{(items.pictype === 1) ? '同人' : '非同人'}}] {{imgs.length}}P <i>{{items.createtime | UnixToNormalTime(1)}}</i>
          </dd>
          <dd class="detail__marks">
            <i v-for="mark in marks">{{mark}}</i>
          </dd>
          <dd class="detail__types">
            <i v-for="type in types">{{type}}</i>
          </dd>
          <dd class="detail__hot">
            <PraiseCommon :praise="praise"></PraiseCommon>
            <CollectCommon :collect="collect"></CollectCommon>
          </dd>
        </dl>
        <section class="detail__pic">
          <!--设计平台详情插槽-->
          <section class="sale-detail" v-if="module">
            <h6>商用报价：<i>￥{{items.lowcost}}~{{items.highcost}}</i></h6>
            <p>商家如果看中此设计，可以直接与设计者联系-></p>
          </section>
          <h6>画作介绍：{{items.introduce}}</h6>
          <img
            v-for="img in imgs"
            :src="img" alt=""/>
        </section>
        <section class="detail__menu">
          <i>关注</i>
          <i>收藏</i>
          <i>分享</i>
        </section>
        <section class="detail__next">
          <i>上一篇</i>
          <i>下一篇</i>
        </section>
      </section>
      <section class="detail__ad">
        <a :href="`activity.html#/detail/${item.activityid}`" v-for="item in ad.huameco">
          <img :src="item.img" alt=""/>
        </a>
      </section>
      <section class="detail__comment">
        <textarea placeholder="你可在这里评论"
                  v-model="contentValue">
        </textarea>
        <section class="detail__btn">
          <i @click="handleComment({id:0,content:contentValue})">发表评论</i>
        </section>

        <!--评论列表-->
        <h6 class="detail__comment-type">共{{commentInfo ? commentInfo.length : 0}}条评论</h6>
        <Comment v-for="(item,index) in commentInfo"
                 @handleComment="handleComment"
                 @handleDelComment="handleDelComment"
                 :msg="item">
        </Comment>
      </section>
    </section>
    <UserInfo
      :userInfo='userInfo'
      :othersPainting='othersPainting'
      :ad="ad.others"></UserInfo>
  </div>
</template>

<!--数据交互-->
<script>
  import Comment from './Comment.vue'
  import UserInfo from './UserInfo.vue'
  import PraiseCommon from 'components/Common/PraiseCommon'
  import CollectCommon from 'components/Common/CollectCommon'

  export default {
    components: {
      Comment,
      UserInfo,
      PraiseCommon,
      CollectCommon,
    },
    props: ['title', 'desc'],
    data: () => {
      return {
        isShow: false,
        marks: ['同人本', '人设', '女性角色', '周边', '商插', '板绘'],
        types: ['勇者大冒险', '神荼', '勇者大冒险同人赛'],
        list: [1, 2, 3, 4],
        tiger: {title: '大花猫', desc: '这不是小花虎吗？'},
        cat: {title: '小花虎', desc: '这不是大花猫吗？'},
        id: '', //画作id
        items: {},
        imgs: [],
        hash: '',
        contentValue: '',
        commentInfo: [], //获取评论信息
        module: false, //控制设计详情是否显示价格
        getUrl: {painting: 'huazuo/findhuazuo', design: 'sheji/findsheji'},
        type: '1',
        comment: { //评论提交数据
          access_token: '',
          moduleid: '',
          type: '1',
          uid: '18202',
          pid: '0',
          nickname: '江江',
          avatar: 'http://tvax1.sinaimg.cn/crop.97.85.1450.1450.50/006ynCijly8fdwr3rribsj319q1bfamu.jpg',
          content: '',
          createtime: ''
        },
        userInfo: {},
        ad: {huameco: [], others: []}, //广告位数据
        othersPainting: [], //其他画作列表
        praise: {},
        collect: {},
        praiseUser: [],
        uid: '500'   //登录用户id
      }
    },
    filters: { //过滤空数据
      setNull(val){
        if (val) {
          return val
        } else {
          return 0
        }
      }
    },
    created(){
      this.hash = location.hash;
      this.id = this.$route.params.id;
      this.getHash(this.hash);
      this.getPraise(this.hash);
    },
    mounted(){
    },
    watch: {
      $route(){
        this.id = this.$route.params.id;
        this.getHash(this.hash);
      }
    },
    methods: {
      getHash(hash){ //根据hash判断页面详情
        if (hash.indexOf('painting') !== -1) {//画作详情
          this.fetchData(this.getUrl.painting);
          this.comment.type = '1'; //提交评论\
          this.type = '1';
        } else { //设计详情
          this.module = true;   //  控制设计详情是否显示价格
          this.fetchData(this.getUrl.design);
          this.comment.type = '4'; //提交评论
          this.type = '4';
        }
      },

      fetchData(url){
        let _this_ = this;
        this.getToken().then(token => {
          this.$http.get(url, { //获取详情数据
            params: {
              access_token: token,
              id: this.id
            }
          }).then(res => {
            _this_.items = res.data.data.list;
            _this_.imgs = JSON.parse(res.data.data.list.imgs);
            //点赞数据
            this.praise = { //点赞数据
              moduleid: this.items.id,
              count: this.items.pcount,
              uid: '18202',
              authorid: this.items.uid,
              name: 'picture_praise',
              praiseModuleid: [],  //已赞模块
            };
            //搜藏数据
            this.collect = { //点赞数据
              moduleid: this.items.id,
              count: this.items.ccount,
              uid: '18202', //TODO 获取缓存用户id
              authorid: this.items.uid,
              name: 'picture_praise',
            };
            if (this.type === '1') { //画作详情
              this.getOthersPainting(token, this.items.uid);
              this.praise.name = 'picture_praise'; //画作点赞
              this.collect.name = 'picture_collect'; //画作收藏
            } else {
              this.praise.name = 'design_praise'; //设计点赞
              this.collect.name = 'design_collect'; //设计收藏
            }
            this.getUserInfo(token, this.items.uid);
          }).catch(err => {
            console.log(err);
          });

          this.$http.get('common/showadvert', {//获取广告位
            params: {
              access_token: token,
              type: this.type
            }
          }).then(res => {
            this.ad = {huameco: [], others: []}; //清空数据
            res.data.data.list.forEach(item => {
              if (item.adtype === '1') { //本站数据
                this.ad.huameco.push(item);
              } else {
                this.ad.others.push(item);
              }
            });
          }).catch(err => {
            console.log(err);
          });
          this.getCommentList(token);

          //获取已关注id
          if (this.uid) { //是否登录
            this.getFollowId(this.uid, token);
          }
        })
      },

      getCommentList(token){//获取评论列表
        this.$http.get('Comment/showcomment', {
          params: {
            access_token: token,
            pid: 0,
            moduleid: this.id,
            type: this.type
          }
        }).then(res => {
          //数据不为null时赋值
          res.data.data.list ? this.commentInfo = res.data.data.list : this.commentInfo = [];
        }).catch(err => {
          console.log(err);
        })
      },

      getOthersPainting(token, uid){ //获取其他画作
        this.$http.get('huazuo/findotherhuazuo', {
          params: {
            access_token: token,
            uid: uid
          }
        }).then(res => {
          this.othersPainting = res.data.data.list;
        })
      },

      getUserInfo(token, uid){ //获取用户信息
        this.$http.get('user/finduserinfo', {
          params: {
            access_token: token,
            uid: uid
          }
        }).then(res => {
          this.userInfo = res.data.data.list;
          console.log(res.data.data.list);
        })
      },

      handleComment(val){ //发布评论
        let _this_ = this;
        this.comment.pid = val.id;
        this.comment.moduleid = this.id;
        this.comment.content = val.content;
        this.comment.createtime = (Date.now() / 1000);
        let inputInfo = this.comment;
        this.getToken().then(token => {
          _this_.comment.access_token = token;
          _this_.$http.get('comment/addcomment', { //提交评论
            params: this.comment
          }).then(res => {

            //发布成功重新拉取评论信息
            this.getCommentList(token);
            this.contentValue = ''; //清空数据
          }).catch(err => {
            console.log(err);
          });
        })
      },

      //删除评论
      handleDelComment(id){
        this.getToken().then(token => {
          this.$http.get('comment/delcomment', {
            params: {
              access_token: token,
              id: id
            }
          }).then(res => {
            console.log(res);
            //删除成功重新拉取评论信息
            this.getCommentList(token);
          }).catch(err => {
            console.log(err);
          })
        })
      },

      //获取点赞会员
      getPraise(hash){
        if (hash.indexOf('painting') !== -1) {
          var praiseName = 'picture_praise';
          var collectName = 'picture_collect';
        } else {
          praiseName = 'design_praise';
          collectName = 'design_collect';
        }
        this.getUsers('1', { //点赞
          uid: 18202,
          name: praiseName
        }).then(res => {
          let data = res.data.data.list.map(item => {
            return item.moduleid
          });
          this.$store.dispatch('onPraiseUsers', data);
        });

        this.getUsers('2', { //收藏
          uid: 18202,
          name: collectName
        }).then(res => {
          let data = res.data.data.list.map(item => {
            return item.moduleid
          });
          this.praise.praiseModuleid = data;
          this.$store.dispatch('onCollectUsers', data);
        })
      },
    }
  }
</script>

<!--组件样式-->
<style scoped lang="scss">
  @import "../../common/css/define";

  .detail__content {
    display: inline-block;
  }

  #detail {
    font-size: 0;
    max-width: 1180px;
    margin: 0 auto;
    .l-wrap {
      width: 800px;
      padding: 20px;
      margin-top: 40px;
      background-color: #fff;
      @include wrapShow;
    }
    .detail__title {
      position: relative;
      > dt {
        font-size: $titleSize;
        color: $black;
        font-weight: bold;

        > i {
          margin-left: 10px;
          padding: 0 16px;
          font-size: 14px;
          color: $pink;
          font-weight: normal;
          background: url("./images/hot.png") no-repeat left center;
        }
      }
      .detail__tips {
        font-size: 14px;
        color: #666;
        margin-top: 20px;
      }
      .detail__marks {
        margin-top: 20px;
        > i {
          display: inline-block;
          height: 22px;
          padding: 0 10px;
          font-size: 14px;
          border-radius: 2px;
          border: 1px solid #e6e6e6;
          line-height: 22px;
          text-align: center;
          margin-right: 10px;
          color: $gray;
          &:nth-last-child(1) {
            margin-right: 0;
          }
        }
      }
      .detail__types {
        margin-top: 20px;
        > i {
          display: inline-block;
          font-size: 14px;
          color: $black;
          padding: 0 20px;
          &:nth-of-type(1) {
            background: url("./images/type@01.png") no-repeat left center;
          }
          &:nth-of-type(2) {
            background: url("./images/type@02.png") no-repeat left center;
          }
          &:nth-of-type(3) {
            background: url("./images/type@03.png") no-repeat left center;
          }
        }

      }
      .detail__hot {
        position: absolute;
        right: 0px;
        top: 0px;
        > i {
          display: inline-block;
          width: 60px;
          height: 30px;
          border: 1px solid #ccc;
          background-color: #fafafa;
          color: #666;
          text-align: center;
          text-indent: 1.2em;
          line-height: 30px;
          cursor: pointer;
          font-size: 14px;
          border-radius: 2px;
          &:nth-of-type(1) {
            background: url("./images/icon.png") no-repeat 10px 11px;
            margin-right: 8px;
          }
          &:nth-of-type(2) {
            background: url("./images/icon.png") no-repeat 10px -11px;
          }
          &.is--praise {
            background: $pink url("../../components/Common/images/icon@praise.png") no-repeat 10px 8px;
            border: 1px solid $pink;
            color: #fff;
          }
          &.is--collect {
            background: $pink url("../../components/Common/images/icon@praise.png") no-repeat 10px -14px;
            border: 1px solid $pink;
            color: #fff;
          }
        }
      }
    }
    .detail__pic {
      width: 800px;
      margin-top: 20px;
      padding-top: 20px;
      border-top: 1px solid #e6e6e6;
      > h6 {
        font-size: 14px;
        color: #333;
        font-weight: normal;
        margin-bottom: 20px;
      }
      > img {
        width: 100%;
      }
    }
    .detail__menu {
      text-align: center;
      > i {
        display: inline-block;
        width: 48px;
        height: 48px;
        border-radius: 50%;
        background-color: #999;
        font-size: 0;
        margin-right: 16px;
        cursor: pointer;
        background: url("../../components/Common/images/icon.png") no-repeat;
        &:nth-of-type(1) {
          background-position: 0px 0px;
        }
        &:nth-of-type(2) {
          background-position: 0px 0px;
        }
        &:nth-of-type(3) {
          background-position: 0px 0px;
        }
      }
    }
    .detail__next {
      text-align: center;
      margin-top: 20px;
      > i {
        display: inline-block;
        width: 120px;
        height: 50px;
        border-radius: 4px;
        font-size: 14px;
        color: #fff;
        text-align: center;
        line-height: 50px;
        transition: 0.3s;
        margin-right: 14px;
        cursor: pointer;
        @include boxShow;
        @include linear;

        &:active {
          box-shadow: none;
        }
      }
    }
    .detail__ad {
      width: 100%;
      > a {
        width: 410px;
        margin: 20px 0;
        display: inline-block;
        > img {
          width: 100%;
        }
        &:nth-child(2) {
          float: right;
        }
      }
    }
    .detail__comment {
      background-color: #fff;
      width: 800px;
      padding: 20px;
      @include wrapShow;

      > textarea {
        width: 780px;
        height: 100px;
        border: 1px solid #e6e6e6;
        border-radius: 2px;
        font-size: 12px;
        resize: none;
        color: $black;
        padding: 10px;
      }
    }
    .detail__btn {
      text-align: right;
      > i {
        display: inline-block;
        margin: 12px 0 20px 0;
        width: 120px;
        height: 50px;
        border-radius: 4px;
        font-size: 16px;
        text-align: center;
        line-height: 50px;
        color: #fff;
        cursor: pointer;
        transition: 0.3s;
        @include boxShow;
        @include linear;
        &:active {
          box-shadow: none;
        }
      }
    }
    .detail__comment-type {
      font-size: 14px;
      border-top: 1px solid #e6e6e6;
      color: $black;
      font-weight: normal;
      padding-top: 20px;
    }
    .sale-detail {
      font-size: 14px;
      margin-bottom: 20px;
      > h6 {
        font-weight: normal;
        display: inline-block;
        > i {
          color: $pink;
        }
      }
      > p {
        display: inline-block;
        float: right;
      }
    }
  }
</style>



