<!-- 2017/3/22- yyy -征稿列表组件-->
<template>
  <section>
    <section class="commersale">
      <range :SearchRange="SearchRange" :IconRelease="IconRelease" @rangeIcon="rangeIcon"></range>
      <CollectList
        :lists="lists"
        :btn="btn">
        <!--反馈时间-->
        <template slot="feedback" scope="props">
          <li>已投稿件：<i>90 张</i></li>
          <li>反馈时间：<i>{{props.text}}天内</i></li>
        </template>
        <!--福利插槽-->
        <template scope="props" slot="gift">
          <section class="commersale__wf">福利：{{ props.gift }}</section>
        </template>
        <!--分页组件-->
        <Pagination
          :pageCount="pageCount"
          :pageSize="pageSize"
          @pageChange="pageChange"
          slot="page"></Pagination>
      </CollectList>
    </section>
  </section>
</template>

<!--数据交互-->
<script>

  import range from 'components/Design/SearchRange';  // 搜索范围
  import CollectList from 'components/Pact/PactItem' //征稿列表
  import Pagination from 'components/Common/Pagination' //分页组件

  export default {
    data: () => {
      return {
        SearchRange: [
          {name: "按发布时间", inputBol: false, bol: true, orderBy: 'time'},
          {name: "按截稿日期", inputBol: false, bol: false, orderBy: 'endtime'},
          {name: "按稿费高低", inputBol: false, bol: false, orderBy: 'cost'},
          {name: "正在进行", inputBol: true, bol: false, orderBy: 'progress'},
        ],
        IconRelease: "发布征稿",
        lists: {
          item: [],
          path: '/collect'
        },
        btn: '去投稿',
        orderBy: 'time',
        pageCount: 0,  //总条数
        pageSize: 30, //每页显示数
        pageIndex: 1,  //当前页
      }
    },
    components: {
      range,
      CollectList,
      Pagination
    },
    mounted(){
      this.getData();
    },
    methods: {

      //获取数据
      getData(){
        let _this_ = this;
        this.getToken().then(token => {
          this.$http.get('zhenggao/findallzhenggao', {
            params: {
              access_token: token,
              order: this.orderBy,
              page: this.pageIndex
            }
          }).then(res => {
            _this_.lists.item = res.data.data.list;
            this.pageCount = parseInt(res.data.data.total);
          }).catch(err => {
            console.log(err);
          })
        })
      },

      rangeIcon(index){ //菜单切换
        this.SearchRange.forEach(item => {
          item.bol = false;
        });
        this.SearchRange[index].bol = true;
        this.orderBy = this.SearchRange[index].orderBy;
        this.getData();
      },

      //分页调用
      pageChange(val){
        this.pageIndex = val;
        this.getData();
      }
    }
  }

</script>

<!--组件样式-->
<style lang="scss" scoped>

  @import "../../common/css/reset.css";
  @import "../../common/css/define";

  .commersale {

  }

  .commersale__wf {
    font-size: 14px;
    font-weight: bold;
    margin-top: 8px;
  }


</style>



