<!--发布设计组件-->
<template>
  <section class="design-new">
    <h6 class="design-new__title">发布设计</h6>
    <DesignNew
      @commit='commit'
      :type="type">
      <dl slot="sale" class="design-new__sale">
        <dt>是否已商用</dt>
        <dd
          @click="changeBtn(index)"
          v-for="(item,index) in isUsed"
          :class="{'is--active':item.flag}">
          {{item.title}}
        </dd>
      </dl>
      <dd slot="price" class="design-new__price">
        <h6>商用报价</h6>
        <input type="text" placeholder="可填写相同价格" v-model="designData.lowcost"/>
        <i>-</i>
        <input type="text" placeholder="可填写相同价格" v-model="designData.highcost"/>
      </dd>
    </DesignNew>
    <DesignRestriction @setRes='setRes'></DesignRestriction>
  </section>
</template>

<!--数据交互-->
<script>
  import DesignNew from 'components/Painting/New'
  import DesignRestriction from 'components/Painting/Restriction'

  export default {
    components: {
      DesignNew,
      DesignRestriction
    },
    data: () => ({
      isUsed: [{title: '未商用，待出售', flag: true}, {title: '已商用，可公开（建议同时上传设计稿和实体商品图）', flag: false}],
      type: [{title: '同人设计', isActive: true}, {title: '原创设计', isActive: false}],
      imgList: [],//上传图片
      permiss: {permiss: '1', act: ['1', '2']},//权限初始化数据
      papersid: '',//征稿id
      //用户输入数据
      designData: {
        busintype: 1,
        lowcost: '0',
        highcost: '0',
        uid: '', //登录用户id
      }
    }),
    mounted(){
      let papersid = this.$route.params.id;
      if (papersid) {
        this.papersid = this.$route.params.id;
      }
      //判断用户是否登录
      this.isLogin();
    },
    methods: {
      //获取权限
      setRes(value){
        this.permiss = value;
      },

      //设置是否商用
      changeBtn(index){
        this.isUsed.forEach(item => {
          item.flag = false
        });
        this.isUsed[index].flag = true;
        this.designData.busintype = index + 1;
        console.log(this.busintype);
      },
      //获取输入数据
      commit(value){
        let _this_ = this;
        let baseUrl = document.querySelector('#drawing').toDataURL("image/png"); //获取base64
        let base64Data = baseUrl.replace(/^data:image\/(png|jpg);base64,/, "");
        this.getToken().then(token => {
          const uploadeBase = () => {
            return new Promise(resolve => {
              this.$http('common/uptoken', {
                params: {
                  access_token: token
                }
              }).then(res => {
                //上传base64
                const token = res.data.data.uptoken;
                _this_.Qiniu_Base64(base64Data, token).then(res => {
                  value.imgList.push(res);
                  resolve(res);
                })
              });
            })
          };
          const sendPain = (async () => { //异步提交
            await uploadeBase();
            this.$http('sheji/addsheji', {
              params: {
                access_token: token,
                uid: this.uid,
                title: value.title,
                imgs: value.imgList,
                busintype: _this_.designData.busintype,
                introduce: value.desc,
                designtype: value.pictype,
                author: value.author,
                role: value.role,
                tagsname: value.marks,
                act: _this_.permiss.act,
                permiss: _this_.permiss.permiss,
                lowcost: _this_.designData.lowcost,
                highcost: _this_.designData.highcost,
                papersid: this.papersid, //设计id
              }
            }).then(res => {
              console.log(res);
            }).catch(err => {
              console.log(err);
            })
          })();
        })
      },
    }
  }
</script>

<!--组件样式-->
<style scoped lang="scss">
  @import "../../common/css/define";

  .design-new {
    max-width: 1180px;
    margin: 0 auto;
    color: $black;
    .design-new__title {
      font-size: 22px;
      margin: 40px 0 20px 0;
    }
    .design-new__sale {
      margin-bottom: 40px;
      > dt {
        display: inline-block;
        font-size: 14px;
        margin-right: 16px;
      }
      > dd {
        display: inline-block;
        font-size: 14px;
        background: url("./images/select@16x16.png") no-repeat left center;
        text-indent: 20px;
        cursor: pointer;
        &:nth-of-type(1) {
          margin-right: 20px;
        }
        &.is--active {
          background: url("./images/selected@16x16.png") no-repeat left center;
        }
      }
    }
    .design-new__price {
      margin-left: -76px;
      > h6 {
        line-height: 40px;
        margin-right: 16px;
      }
      > input {
        width: 118px;
        height: 36px;
        font-size: 12px;
        border-radius: 2px;
        text-align: center;
        border: 1px solid #e6e6e6;
      }
      > i {
        margin: 0 10px;
        color: #e6e6e6;
        font-size: 14px;
      }
    }
  }
</style>



