<!--富文本编辑器-->
<template>
  <div>
    <vue-html5-editor
      :content="bodyContent"
      :height="300"
      :show-module-name="showModuleName"
      @change="updateData" ref="editor">
    </vue-html5-editor>
  </div>
</template>

<!--数据交互-->
<script>
  export default {
    props: ['bodyContent'],
    data () {
      return {
        imgList: [],
        showModuleName: true,
        contentData: ''
      }
    },

    methods: {
      updateData: function (data) {
        let _this_ = this;
        let key = '';
        this.contentData = data;
        new Promise(resolve => {
          data.replace(/<img [^>]*src=['"]([^'"]+)[^>]*>/gi, function (match, content) {
            let base64Data = content.replace(/^data:image\/jpeg;base64,/, "");
            if (_this_.imgList.indexOf(content) === -1) { //数组不存在上传图片
              _this_.imgList.push(content);
              _this_.getToken().then(token => {
                _this_.$http.get('common/uptoken', {
                  params: {
                    access_token: token
                  }
                }).then(res => {
                  const token = res.data.data.uptoken;
                  _this_.Qiniu_Base64(base64Data, token).then(res => {
                    resolve(match.replace(content, `http://artwork.huamoe.com/${res}`))
                  });
                }).catch(err => {
                  console.log(err);
                })
              })
            }
          });
        }).then(res => {
          let data = _this_.contentData.replace(/<img [^>]*src=['"]([^'"]+)[^>]*>/gi, res);
          this.$emit('onContent', data);
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  .quill-editor {
    height: 200px;
  }

</style>



