<!--约稿详情介绍组件-->
<template>
  <div class="pact-detail__desc">
    <section class="pact-detail__desc-content">
      <PactDetailContent
        @onSubmitComment="onSubmitComment"
        :btnName="'我要应征'"
        :link="`/recruit/${userId}/${id}`"
        :content="items.content">
      </PactDetailContent>
      <h6 class="pact-comment__num">共{{comment.length}}条评论</h6>
      <PactDetailReturn v-for="item in comment"
                        @handleDelComment="handleDelComment"
                        :msg="item">
      </PactDetailReturn>
    </section>
    <PactDetailUserInfo
      :uid="userId"></PactDetailUserInfo>
    <!--<PactRecruit></PactRecruit>-->
  </div>
</template>

<!--数据交互-->
<script>
  import PactDetailReturn from 'components/Painting/Comment'  //公用评论
  import PactDetailUserInfo from 'components/Pact/PactDetailUserInfo' //用户信息
  import PactRecruit from 'components/Pact/PactRecruit' //应征约稿
  import PactDetailContent from 'components/Pact/PactDetailContent' //约稿详情内容

  export default {
    data: () => ({
      comment: [
        {
          "id": "1003",
          "type": "1",
          "uid": "18202",
          "parentid": "0",
          "nickname": "江江",
          "avatar": "http://tvax1.sinaimg.cn/crop.97.85.1450.1450.50/006ynCijly8fdwr3rribsj319q1bfamu.jpg",
          "readstats": "1",
          "content": "我是父级",
          "createtime": "1494748222",
          "children": [
            {
              "id": "1004",
              "type": "1",
              "uid": "18202",
              "parentid": "1003",
              "nickname": "江江",
              "avatar": "http://tvax1.sinaimg.cn/crop.97.85.1450.1450.50/006ynCijly8fdwr3rribsj319q1bfamu.jpg",
              "readstats": "1",
              "content": "我是子级",
              "createtime": "1494748239",
              "children": []
            }
          ]
        }
      ],
      //评论列表
      uid: '18202',
      avatar: 'http://tvax1.sinaimg.cn/crop.97.85.1450.1450.50/006ynCijly8fdwr3rribsj319q1bfamu.jpg',
      nickname: '江江'
    }),
    components: {
      PactDetailReturn,
      PactDetailUserInfo,
      PactRecruit,
      PactDetailContent
    },
    props: ['items', 'id', 'userId'],
    mounted(){
      this.getCommentList();
    },
    methods: {

      //发表评论
      onSubmitComment(val) {
        this.getToken().then(token => {
          this.$http.get('comment/addcomment', {
            params: {
              access_token: token,
              moduleid: this.id,
              type: '3',
              uid: this.uid,
              pid: '0',
              nickname: this.nickname,
              avatar: this.avatar,
              content: val,
              createtime: (Date.now() / 1000)
            }
          }).then(res => {
            console.log(res);
            this.getCommentList();  //发表成功再次拉取列表
          }).catch(err => {
            console.log(err);
          })
        })
      },

      //获取评论列表
      getCommentList(){
        this.getToken().then(token => {
          this.$http.get('comment/showcomment', {
            params: {
              access_token: token,
              pid: '0',
              moduleid: this.id,
              type: '3'
            }
          }).then(res => {
            console.log(res);
//            this.comment = res.data.data.list;
          }).catch(err => {
            console.log(err);
          })
        })
      },

      //删除评论
      handleDelComment(id){
        this.getToken().then(token => {
          this.$http.get('comment/delcomment', {
            params: {
              access_token: token,
              id: id
            }
          }).then(res => {
            console.log(res);
            //删除成功重新拉取评论信息
            this.getCommentList(token);
          }).catch(err => {
            console.log(err);
          })
        })
      },
    }
  }
</script>

<!--组件样式-->
<style scoped lang="scss">
  @import "../../common/css/define";

  .pact-detail__desc {
    max-width: 1180px;
    margin: 0 auto;
    color: $black;
    > .pact-detail__desc-content {
      max-width: 800px;
      background-color: #fff;
      padding: 40px 20px;
      display: inline-block;
      @include wrapShow;
    }
    .pact-comment__num {
      font-size: 14px;
      font-weight: normal;
    }
  }
</style>



