<!-- 2017/3/22- 活动组件  系统组件-->
<template>
  <section class="system" style="padding: 20px 40px 0 40px;">
    <section class="system__read" @click="readSystem">全部标记为已读</section>
    <ul>
      <li v-for="item in systemEveryList" class="system__list">
        <section class="system__detail"><i>{{item.content}} </i></section>
        <i class="system__date">{{item.createtime | UnixToNormalTime(1)}}</i>
      </li>
    </ul>
    <Pagination :pageCount="PageCount" :pageSize="PageSize" @pageChange="PageChange"></Pagination>
  </section>
</template>

<script>
  import Pagination from 'components/Common/Pagination' //分页组件
  export default {
    data:()=>{
        return{
          systemEveryList:[],
          PageCount:0,
          PageSize:20,
          pageIndex:1,
        }
    },
    components:{
      Pagination
    },
    mounted(){
      this.getSystemList();
    },
    methods:{
      PageChange(val){
        this.pageIndex = val;
        this.getSystemList();
      },
      getSystemList(){
        let _this = this;
        this.getToken().then(token=> {
          this.$http.get('message/shownotice', {
            params: {
              access_token: token,
              type:4,
              uid:20,
              page: _this.pageIndex,
              show: _this.PageSize
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this.systemEveryList = res.data.data.list;
              _this.PageCount = parseInt(res.data.data.total);
              let a = 0;  // 征稿通知-->投稿通知未读条数
              res.data.data.list.forEach((item,index)=>{
                if(res.data.data.list[index].readstats === '1'){
                  a++
                }else {
                  a = 0
                }
              })
              this.$store.dispatch('onSubmitSystem',a);
            }
          }).catch(err => {
            console.log(err);
          });
        })
      },
      readSystem(){
        let _this = this;
        this.getToken().then(token=> { // 统计系统信息已读
          this.$http.get('message/markreadnotice', {
            params: {
              access_token: token,
              type:4,
              uid:20
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              this.getSystemList()
            }
          }).catch(err => {
            console.log(err);
          });
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../common/css/reset.css";
  @import "../../common/css/define";

  .system__read {
    width: 120px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    font-size: 14px;
    cursor: pointer;
    border-radius:4px;
    font-size: 14px;
    color: $white;
    margin-bottom: 20px;
    @include linear;
  }
  .system__list {
    padding-bottom: 15px;
    margin-bottom: 18px;
    border-bottom: 1px solid $lightgrey;
    font-size: 14px;
  }

  .system__detail {
    width: 785px;
    display: inline-block;
    > i {
      color: $black;
    }
    > a {
      &:hover {
        color: $pink;
      }
    }
  }
  .system__date {
    display: inline-block;
    color: #999999;
    font-size: 12px;
  }
</style>



