<!--推荐画师组件-->
<template>
  <section class="painter">
    <ul class="painter__item">
      <li v-for="(item,index) in painter" :key="index">
        <a :href="'personal.html#/accounts/'+item.uid" class="painter__img">
          <img :src="item.avatar" alt=""/>
        </a>
        <section class="painter__info">
          <h6>{{item.nickname}}</h6>
          <i>同人稿，日系，女性角色，商插，板绘</i>
          <section class="painter__menu">
            <FollowUserCommon :authorId="item.uid">
            </FollowUserCommon>
            <i><a :href="'message.html#/privateType/'+item.id+'&'+item.nickname">私信</a></i>
            <!--<i :title="`我的QQ是${item.qq},点击发起QQ会话`"-->
            <!--@click="handleDialog(item.nickname,item.qq)">QQ</i>-->
            <CopyQqDialogue
              :info="{name:item.nickname,qq:item.qq}"
              class="painter__qq"></CopyQqDialogue>
          </section>
          <ul class="painter__desc" @click="setTab($event)">
            <li>
              <h6 class="is--active">接稿说明</h6>
              <section :title="item.introduce" class="painter__desc-content" v-show="true">
                {{item.introduce ? item.introduce : '无'}}
              </section>
            </li>
            <li>
              <h6>接稿经验（{{item.exper | types}}）</h6>
              <section :title='item.experdescrit' class="painter__desc-content" v-show="false">
                {{item.experdescrit ? item.experdescrit : '无'}}
              </section>
            </li>
            <li>
              <h6>其他网站（{{item.othersite ? '有' : '无'}}）</h6>
              <section class="painter__desc-content" v-show="false">
                {{item.othersite ? item.othersite : '无' }}
              </section>
            </li>
          </ul>
          <section class="painter__weiBo">
            <section class="painter__weiBo--unBind" v-if="isBind === false"><a href="#">未绑定微博</a></section>
            <section class="painter__weiBo--bind" v-else="isBind">
              <i>添加关注</i>
              <i>长春小菊花</i>
              <i class="painter__weiBo-fans">(粉丝1979)</i>
            </section>
          </section>
        </section>
        <ul class="painter__pic" :style="{marginLeft:value+'px'}">
          <li v-for="imgList in item.imgs"><img :src="imgList.imgs" alt=""/></li>
        </ul>
        <section class="painter__btn">
          <a target="_blank"
             :href="'personal.html#/accounts/products/'+item.uid">></a>
        </section>

        <!--应征约稿-->
        <slot name="price"
              :info="{bmuid:item.bmuid,cost:item.cost,message:item.message,stats:item.stats,ustats:item.ustats}"></slot>
      </li>
    </ul>
    <slot name="page"></slot>
  </section>
</template>

<!--数据交互-->
<script>
  import FollowUserCommon from 'components/Common/FollowUserCommon'
  import CopyQqDialogue from 'components/Common/CopyQqDialogue'

  export default {
    name: 'app-painter',
    data: () => {
      return {
        isBind: false,
        dialogVisible: false,
      }
    },
    filters: { //是否有接稿经验过滤器
      types(value){
        if (value === '2') {
          return '有'
        } else {
          return '无'
        }
      }
    },

    components: {
      FollowUserCommon,
      CopyQqDialogue
    },
    methods: {
      setTab($event){ //画师接稿tab切换
        let el = $event.target;
        if (el.tagName.toLowerCase() === 'h6') {
          let parentEl = el.parentNode.parentNode;
          let desc = parentEl.querySelectorAll('.painter__desc-content');
          let lists = parentEl.querySelectorAll('h6');
          for (let i = 0, l = lists.length; i < l; i++) {
            lists[i].classList.remove('is--active');
            desc[i].style.display = 'none';
          }
          el.classList.add('is--active');
          el.nextElementSibling.style.display = 'block';
        }
      }
    },
    props: ['value', 'painter', 'followId']
  }
</script>

<!--组件样式-->
<style lang="scss" scoped>
  @import "../../common/css/define";

  .dialog__copy {
    cursor: pointer;
    margin-left: 20px;
    &:hover {
      text-decoration: underline;
    }
  }

  .dialog__input {
    width: 100px;
    text-align: center;
  }

  .dialog__qq {
    > a {
      color: #333;
    }
  }

  .painter {
    max-width: 1180px;
    margin: 0 auto;
    padding-bottom: 30px;
  }

  .painter__item {
    display: inline-block;
    vertical-align: top;

    > li {
      border-bottom: 1px solid #ccc;
      padding: 28px 0;
      &:nth-last-child(1) {
        border-bottom: none;
      }
    }
    .painter__img {
      width: 80px;
      height: 80px;
      border-radius: 50%;
      overflow: hidden;
      display: inline-block;
      vertical-align: top;
      > img {
        width: 100%;
      }
    }
    .painter__info {
      display: inline-block;
      vertical-align: top;
      margin-left: 16px;

      > h6 {
        font-size: 14px;
      }
      > i {
        font-size: 12px;
        color: $gray;
        line-height: 30px;
      }
      .painter__menu {
        margin-bottom: 12px;

        > i, .painter__qq {
          width: 48px;
          height: 22px;
          font-size: 12px;
          text-align: center;
          line-height: 22px;
          display: inline-block;
          border-radius: 4px;
          border: 1px solid #ccc;
          color: $green;
          cursor: pointer;

          > a {
            color: $green;
          }
          &:nth-child(1) {
            background-color: $green;
            color: #fff;
            border: 1px solid $green;
          }
        }
      }
      .painter__desc {
        height: 120px;
        font-size: 0;
        position: relative;
        > li {
          width: 108px;
          height: 30px;
          line-height: 30px;
          font-size: 12px;
          color: $blue;
          text-align: center;
          display: inline-block;
          vertical-align: top;

          > h6 {
            font-size: 12px;
            border-bottom: 1px solid #ccc;
            cursor: pointer;
            &.is--active {
              border: 1px solid #ccc;
              border-radius: 4px 4px 0 0;
              border-bottom: none;
              color: $black;
            }
          }
        }
        .painter__desc-content {
          width: 282px;
          height: 46px;
          color: $black;
          border-radius: 0 0 4px 4px;
          border: 1px solid #ccc;
          border-top: none;
          line-height: 18px;
          text-align: left;
          padding: 20px;
          position: absolute;
          left: 0;
          overflow: hidden;
        }
      }
    }
    .painter__weiBo {
      .painter__weiBo--unBind > a, .painter__weiBo--bind {
        display: inline-block;
        margin-top: 12px;
        font-size: 12px;
        color: $blue;
      }
      .painter__weiBo-fans {
        color: $black;
      }
    }
  }

  .painter__pic {
    display: inline-block;
    vertical-align: top;
    /*margin-left: 30px;*/
    > li {
      width: 156px;
      height: 220px;
      border-radius: 4px;
      overflow: hidden;
      display: inline-block;
      vertical-align: top;
      margin-right: 10px;

      > img {
        width: 100%;
      }
    }
  }

  .painter__btn {
    width: 40px;
    height: 64px;
    background-color: $pink;
    border-radius: 4px;
    text-align: center;
    line-height: 64px;
    display: inline-block;
    margin-top: 82px;
    @include boxShow;
    @include linear;

    > a {
      color: #fff;
      display: block;
    }
  }

</style>



