<!--已商用设计组件-->
<template>
  <ul class="daftUsed">
    <li class="daftUsed__list" v-for="item in 5">
      <section class="daftUsed__info">

        <a
          target="_blank"
          :href="link+item.id" class="daftUsed__img">
          <img src="./images/used.png" alt=""/>
          <i>2P</i>
        </a>
        <section class="daftUsed__hot">
          <i>{{item.bcount ? item.bcount : '0'}}</i>
          <!--<i>{{item.ccount}}</i>-->
        </section>
      </section>
      <section class="daftUsed__desc">
        <h6>{{item.title}}</h6>
      </section>
      <section class="daftUsed__tips">
        <a :href="'personal.html#/accounts/'+item.uid" class="daftUsed__tips-img">
          <img :src="item.avatar" alt=""/>
        </a>
        <p :title="item.nickname">{{item.nickname}}</p>
        <i>{{item.createtime | UnixToNormalTime(3)}}</i>
      </section>
    </li>
  </ul>
</template>

<!--数据交互-->
<script>
  export default {
    name: 'app-daft-used',
    data: () => {
      return {
        // list:[1,2,3,4,5,]
      };
    },
    props: ['daftUsed', 'link']
  }
</script>

<style lang="scss" scoped>
  @import "../../common/css/define";

  .daftUsed {
    max-width: 1180px;
    margin: 0 auto;

    .daftUsed__list {
      width: 220px;
      display: inline-block;
      margin-right: 20px;
      &:nth-child(5) {
        margin-right: 0;
      }
    }
  }

  .daftUsed__info {
    width: 220px;
    height: 256px;
    border-radius: 4px;
    position: relative;
    @include wrapShow;

    .daftUsed__img {
      display: block;
      width: 220px;
      height: 220px;
      overflow: hidden;
      > img {
        width: 100%;
      }
      > i {
        display: block;
        width: 32px;
        height: 20px;
        background-color: rgba(0, 0, 0, 0.3);
        border-radius: 4px 0 4px 0;
        position: absolute;
        top: 0;
        left: 0;
        color: #fff;
        font-size: 12px;
        text-align: center;
        line-height: 20px;
      }
    }
    .daftUsed__hot {
      line-height: 36px;
      font-size: 12px;
      color: $gray;
      text-align: right;
      padding: 0 12px;
      > i {
        display: inline-block;
        padding: 0 20px;
        height: 36px;
        margin: 0 10px;
        background: url("../../components/Common/images/person@hot.png") no-repeat 0 center;
      }
    }
  }

  .daftUsed__desc {
    border-bottom: 1px solid #e6e6e6;
    > h6 {
      font-size: 12px;
      font-weight: normal;
      color: $black;
      line-height: 32px;
    }
  }

  .daftUsed__tips {
    margin-top: 12px;
    .daftUsed__tips-img {
      width: 20px;
      height: 20px;
      border-radius: 50%;
      overflow: hidden;
      display: inline-block;
      vertical-align: middle;
      > img {
        width: 100%;
      }
    }
    > p {
      width: 50px;
      display: inline-block;
      vertical-align: middle;
      font-size: 12px;
      color: $black;
      margin: 0 16px;
      @include beyondPass;
    }
    > i {
      display: inline-block;
      vertical-align: middle;
      font-size: 12px;
      color: $gray;
    }
  }
</style>



