<!--约稿首页-->
<template>
  <div class="pact">
    <NavView :menus="menus" @change="change" @search="search"></NavView>
    <keep-alive>
      <component :is="showComponent"></component>
    </keep-alive>
  </div>
</template>

<!--数据交互-->
<script>
  import NavView from 'components/Common/Bar'            //公共导航
  import PactInfo from 'components/Pact/PactInfo'        //约稿信息组件
  import PactPainter from 'components/Pact/PactPainter'  //接稿画师组件
  import PactPlan from 'components/Pact/PactPlan'        //千人计划组件
  import PactPaintingShop from 'components/Pact/PactPaintingShop'  //画铺组件


  export default {
    components: {
      NavView,
    },
    data: () => ({
      menus: [
        {
          title: '约稿信息',
          isActive: true,
        },
        {
          title: '接稿画师',
          isActive: false
        },
        {
          title: '千人计划',
          isActive: false
        },
        {
          title: '画铺',
          isActive: false
        }
      ],
      dataList: {
        item: [],
        path: '/pactDetail'
      },
      showComponent: PactInfo,
    }),
    mounted(){
      let type = window.location.search.substring(1);
      this.moreShop(type);
    },
    methods: {
      moreShop(type){
        switch (type) {
          case 'shop': //首页最新画铺查看更多
            this.showComponent = PactPaintingShop;
            break;
          case 'painter':  //首页画师推荐查看更多
            this.showComponent = PactPainter;
            break;
        }
      },
      change(index){//按钮切换
        this.menus.forEach(item => {
          item.isActive = false;
        });
        this.menus[index].isActive = true;
        switch (index) {
          case 0:
            this.showComponent = PactInfo;
            break;
          case 1:
            this.showComponent = PactPainter;
            break;
          case 2:
            this.showComponent = PactPlan;
            break;
          case 3:
            this.showComponent = PactPaintingShop;
            break;
        }
      },
      search(value){ //约稿搜索
        let _this_ = this;
        this.getToken().then(token => {
          this.$http.get('yuegao/search', {
            params: {
              access_token: token,
              key:value
            }
          }).then(res=>{
            console.log(res);
            _this_.dataList.item = res.data.data.list;
          }).catch(err=>{
              console.log(err)
          })
        })
      },
    }

  }
</script>

<!--组件样式-->
<style lang="scss" scoped>
  .pact {
    background-color: #fafafa;
    padding-bottom: 20px;
  }

</style>



