<template>
  <ul class="detail-user">
    <li>
      <!--评论展示-->
      <section class="l-wrap">
        <a :href="`personal.html#/accounts/${msg.uid}`" class="detail-user__header">
          <img :src="msg.avatar" alt=""/>
        </a>
        <section class="detail-user__content">
          <h6><a :href="`personal.html#/accounts/${msg.uid}`">{{msg.nickname}}</a></h6>
          <p>{{msg.content}}！</p>
          <p>{{msg.createtime | UnixToNormalTime(1)}}</p>
        </section>
        <section class="detail-user__edit">
          <i @click="commentShow(msg.id)">回复（{{msg.children ? msg.children.length : 0}}）</i>&#8194;|&#8194;
          <i @click="onDelComment(msg.id)">删除</i>
        </section>
      </section>

      <!--回复展示-->
      <section
        :class="{'is--hide':!flag}"
        class="detail__return">
        <section class="l-wrap"
                 v-for="item in msg.children">
          <a :href="`personal.html#/accounts/${item.uid}`" class="detail-user__header">
            <img :src="item.avatar" alt=""/>
          </a>
          <section class="detail-user__content">
            <h6><a :href="`personal.html#/accounts/${item.uid}`">{{item.nickname}}</a></h6>
            <p v-html="item.content"></p>
            <p>{{item.createtime | UnixToNormalTime(1)}}</p>
          </section>
          <section class="detail-user__edit">
            <i @click="handleReturn(item.nickname,msg.id,msg.uid)">回复</i>&#8194;|
            <i @click="onDelComment(item.id)">删除</i>
          </section>
        </section>

        <!--查看更多-->
        <div
          class="detail-user__more"
          :class="{'is--show':flag}"
          v-if="msg.children.length>2">
          <i>还有{{msg.children.length - 2}}条回复</i>，
          <span @click="handleMore">点击查看</span>
        </div>
      </section>

      <!--输入框-->
      <section class="input" v-show="showState">
        <textarea :placeholder="tips"
                  v-model="inputValue">
        </textarea>
        <section class="input__btn"><i @click="onComment">回复</i></section>
      </section>
    </li>
  </ul>
</template>

<!--数据交互-->

<script>

  export default {
    data: () => {
      return {
        flag: false,
        tips: '', //回复框提示语
        inputValue: '',
        commentUid: '',
        showState: false,
        currentView: '',
        commentVal: {id: '', content: ''},
      }
    },
    mounted(){
      console.log(this.msg);
    },
    props: ['msg'],
    methods: {
      //回复内容
      handleReturn(name, id, uid){
        this.commentUid = `回复<a href="personal.html#/accounts/${uid}" class="link">@${name}：</a>`;
        console.log(this.commentUid);
        this.showState = true;
        this.commentVal.id = id;
        this.tips = `回复 @${name}`;
      },
      commentShow: function (id) {
        this.commentVal.id = id;
        this.showState = !this.showState;
        this.tips = '您可以在这回复';
      },
      onComment(){
        this.commentVal.content = `${this.commentUid}${this.inputValue}`;
        this.$emit('handleComment', this.commentVal);
        this.inputValue = ''; //清空输入框
        this.tips = '';//清空提示
      },
      handleMore(){ //查看跟多
        this.flag = true;
      },

      //删除评论事件监听
      onDelComment(id){
        this.$emit('handleDelComment', id)
      }
    },
  }
</script>

<style lang="scss" scoped>
  @import "../../common/css/define";

  .detail-user {
    border-bottom: 1px solid #e6e6e6;
    &:nth-last-child(1) {
      border-bottom: none;
    }
    > li {
      .l-wrap {
        position: relative;
        padding: 10px 10px 20px 10px;
        &:nth-last-child(1) {

        }
      }
    }
    .detail-user__header {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      overflow: hidden;
      display: inline-block;
      vertical-align: top;
      > img {
        width: 100%;
      }
    }
    .detail-user__content {
      display: inline-block;
      vertical-align: top;
      margin-left: 6px;
      > h6 {
        display: block;
        font-size: 14px;
        color: $black;
        font-weight: normal;
        > a {
          color: #4083a9;
        }
      }
      > p {
        margin-top: 12px;
        font-size: 12px;
        color: $gray;
        &:nth-of-type(1) {
          color: #666;
        }
      }
    }
    .detail-user__edit {
      display: inline-block;
      vertical-align: top;
      font-size: 14px;
      color: $black;
      position: absolute;
      right: 0px;
      bottom: 20px;
      > i {
        cursor: pointer;
        &:nth-of-type(2) {
          margin-left: 4px;
        }
      }
    }
    .detail__return {
      width: 740px;
      background: #fafafa;
      margin-left: 56px;
      .input {
        border-bottom: none;
        margin-left: 0;
        > textarea {
          margin-left: 56px;
          width: 644px;
        }
      }
    }
  }

  //第三条一下全部隐藏
  .is--hide > section {
    &:nth-of-type(n+3) {
      display: none;
    }
  }

  .input {
    width: 720px;
    height: 130px;
    padding: 10px;
    background-color: #fafafa;
    margin-left: 56px;
    > textarea {
      resize: none;
      width: 700px;
      height: 46px;
      background-color: #fff;
      font-size: 14px;
      padding: 10px;
    }
    .input__btn {
      text-align: right;
      margin-top: 10px;
      > i {
        cursor: pointer;
        display: inline-block;
        width: 64px;
        height: 30px;
        border-radius: 4px;
        @include boxShow;
        @include linear;
        text-align: center;
        line-height: 30px;
        color: #fff;
        font-size: 14px;
      }
    }
  }

  .detail-user__more {
    height: 40px;
    color: #333;
    line-height: 40px;
    border-top: 1px solid #e6e6e6;
    font-size: 12px;
    padding: 0 10px;
    > span {
      cursor: pointer;
      color: #2db2ea;
      &:hover {
        text-decoration: underline;
      }
    }
  }

  .is--show {
    display: none;
  }
</style>



