<!--群导航-->
<template>
    <div class="group-nav">
      <ul class="group-nav__items">
        <li v-for="item,index in qqList" :class="{active:item.bol}" @click="switchTab(index)">{{item.name}}</li>
      </ul>
    </div>
</template>

<!--数据交互-->
<script>
    export default {
      props:['qqList'],
      methods:{
        switchTab(index){
          this.$emit("switchTab",index)
        }
      }
    }
</script>

<style scoped lang="scss">
    @import "../../common/css/define";
    .group-nav {
      height: 80px;
      line-height: 80px;
      background-color: #fff;
      @include wrapShow;
      .group-nav__items {
        width: 1180px;
        margin: 0 auto;
        height: 40px;
        text-align:center;
        padding-bottom: 20px;
        li {
          display: inline-block;
          padding: 0 30px;
          height: 40px;
          line-height:40px;
          cursor:pointer;
          margin-right: 10px;
          font-size: 14px;
          color: $black;
          border-radius: 4px;
          &:last-child {
            margin-right: 0;
          }
        }
        .active {
          background-color: $pink;
          @include  linear;
          color: $white;
        }
      }
    }
</style>



