<!--约稿项目组件-->
<template>
  <div class="manc-item clearfix">
    <dl v-for="item in ComManu" class="manc-item__list">
      <dt class="manc-item__img">
        <a :href="'personal.html#/accounts/products/'+item.uid" target="_blank"> <img :src="item.avatar" alt=""/></a>
      </dt>
      <dd class="manc-item__num">
        <img src="./images/toptr.png" alt="">
        <em>{{item.id}}</em>
      </dd>
      <dd class="manc-item__name">
        <h6 :title="item.nickname">
          <a :href="'personal.html#/accounts/products/'+item.uid" target="_blank">{{item.nickname}}</a>
        </h6>
        <p>+关注</p>
      </dd>
      <dd class="manc-item__info">
        <h6><a :href="'pact.html#/pactDetail/'+item.id" target="_blank">{{item.title}}</a></h6>
        <section class="manc-item__marks" v-if="item.tag == null">该约稿暂无标签</section>
        <section class="manc-item__marks" v-else v-for="value in item.tag">{{value.tags}}</section>
        <ul class="manc-item__hot">
          <li>截稿日期：<i>{{item.endtime | UnixToNormalTime(0)}}</i></li>
          <li>单张预算：<i>{{item.lowcost}}~{{item.highcost}}</i></li>
          <li>需求张数：<i>{{item.imgcount}}&#8197;张</i></li>
          <li>应征人数：<i>{{item.personcount}}&#8197;人</i></li>
        </ul>
      </dd>
      <dd class="manc-item__btn">
        <a :href="'pact.html#/pact/'+item.id" target="_blank">去应征</a>
      </dd>
    </dl>
    <Pagination :pageCount="manuPageCount" :pageSize="manuPageSize" @pageChange="manuPageChange"></Pagination>
  </div>
</template>

<!--数据交互-->
<script>
  import Pagination from 'components/Common/Pagination' //分页组件
  export default {
    props:["ComManu","manuPageCount","manuPageSize"],
    components:{
      Pagination
    },
    methods:{
      manuPageChange(val){
        this.$emit("manuPageChange",val)
      }
    }
  }
</script>

<!--组件样式-->
<style lang="scss" scoped>
  @import "../../common/css/define";
  .manc-item {
    width: 1180px;
    background-color: #fafafa;

    >dl {
      padding: 30px 20px 30px 85px;
      margin:20px 0 20px 0;
      @include wrapShow;
      position: relative;
    }
  }
  .manc-item__num {
    position: absolute;
    top: 0;
    left: 20px;
    img {
      display: block;
      width: 44px;
      height: 44px;
    }
    em {
      width: 44px;
      height: 40px;
      position: absolute;
      top: 0;
      left: 0;
      text-align: center;
      line-height: 40px;
      color: $white;
    }
  }
  .manc-item__img {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    overflow: hidden;
    display: inline-block;
    vertical-align: top;
    >img {
      width: 100%;
    }
  }
  .manc-item__name {
    display: inline-block;
    vertical-align: top;
    margin: 12px 60px 0 10px;
    >h6 {
      width: 80px;
      @include beyondPass;
      font-size: 14px;
      font-weight: normal;
      a {
        color: $black;
      }
    }
    >p {
      width: 48px;
      height: 22px;
      border-radius: 2px;
      background-color: $green;
      color: #fff;
      line-height: 22px;
      text-align: center;
      font-size: 12px;
      margin-top: 10px;
      cursor: pointer;
    }
  }
  .manc-item__info {
    display: inline-block;
    vertical-align: top;
    >h6 {
      font-size: 16px;
      font-weight: bold;
      a {
        color: $black;
      }
    }
    .manc-item__marks {
      font-size: 12px;
      color: #999;
      margin: 10px 0;
    }
    .manc-item__hot {
      >li {
        display: inline-block;
        font-size: 14px;
        margin-right: 16px;
        line-height: 30px;

        >i {
          color: $pink;
        }
      }
    }
  }
  .manc-item__btn {
    display: inline-block;
    vertical-align: top;
    font-size: 14px;
    width: 100px;
    height: 40px;
    line-height: 40px;
    transition: 0.3s;
    text-align: center;
    border-radius: 4px;
    cursor: pointer;
    color: $white;
    margin-top: 20px;
    float: right;
    @include boxShow;
    @include linear;
    &:active {
      box-shadow: none;
    }
    a {
      display: block;
      width: 100%;
      height: 100%;
      color: $white;
    }
  }
</style>



