<!--search-->
<template>
  <section class="search clearfix">
    <ul class="search__condition">
      <li>{{condition}}
        <i class="el-icon-arrow-down"></i>
      </li>
      <ul class="search__hover">
        <li v-for="item,index in list" @click="search(index)" :class="{active:item.bol}">{{item.name}}</li>
      </ul>
    </ul>
    <section class="search__input">
      <input type="text" placeholder="搜索画作、设计、约稿、用户等" id="searchWord" @keyup.enter="searchMain()"/>
      <span @click="searchMain()"></span>
    </section>
    <Prompt :hidePrompt="hidePrompt" :showNum="showNum" :promptLanguage="promptLanguage"></Prompt>
  </section>
</template>

<script>
  import Prompt from 'components/Common/Prompt' // 提示框
  export default {
    name: 'app-search',
    data: () => {
      return {
        condition: "画作",
        hidePrompt:false, // 弹窗默认是不可见的
        showNum:2, // 0 成功  1 错误 2 提示
        promptLanguage:"",
        list: [
          {name:"画作",bol:true},
          {name:"画铺",bol:false},
          {name:"设计",bol:false},
          {name:"约稿",bol:false},
          {name:"征稿",bol:false},
          {name:"活动",bol:false},
          {name:"用户",bol:false},
          ]
      }
    },
    components:{
      Prompt
    },
    methods:{
      search(index){
        for(let i = 0; i < this.list.length; i++){
          this.list[i].bol = false;
          if(index === i){
            this.list[index].bol = true;
            this.condition = this.list[index].name;
          }
        }
      },
      searchMain(){
          let type = 0;
          switch (this.condition){
            case "画作":
              type = 0;
              break;
            case "画铺":
              type = 1;
              break;
            case "设计":
              type = 2;
              break;
            case "约稿":
              type = 3;
              break;
            case "征稿":
              type = 4;
              break;
            case "活动":
              type = 5;
              break;
            case "用户":
              type = 6;
              break;
          }
          if(document.getElementById("searchWord").value === ""){
            this.promptPopup(2,"搜索内容不能为空");
            return false;
          }else {
            window.location.href = "http://localhost:8080/module/search.html#/searchType/"+type+"&"+document.getElementById("searchWord").value;
          }
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../../common/css/define";

  .search {
    .search__condition {
      width: 68px;
      height: 40px;
      border: 2px solid $pink;
      line-height: 40px;
      display: inline-block;
      vertical-align: top;
      border-radius: 4px 0px 0px 4px;
      background-color: #fff;
      cursor: pointer;
      position: relative;
      float: left;
       >li {
         font-size: 14px;
         font-weight: bold;
         text-indent: 10px;
        > i {
          font-size: 12px;
          color: $gray;
          text-align: right;
          text-indent: 2px;
        }
      }
      .search__hover {
        display: none;
        width: 68px;
        border: 2px solid $pink;
        background-color: $white;
        position: absolute;
        top: 40px;
        left: -2px;
        z-index: 888;
        text-align: center;
        font-size: 14px;
        li {
          border-bottom: 2px solid $pink;;
          &:last-child {
            border-bottom: none;
          }
          &:hover {
            background-color: #ecf8fd;
          }
        }
        .active {
          font-weight: bold;
          background-color: #ecf8fd;
        }
      }
      &:hover {
        border-radius: 4px 0 0 0;
        ul {
          display: block;
        }
      }
    }
    .search__input {
      display: inline-block;
      vertical-align: top;
      float: left;
      > input {
        display: inline-block;
        width: 250px;
        height: 38px;
        border: 2px solid $pink;
        border-left: none;
        font-size: 14px;
        border-radius: 0 4px 4px 0;
        text-indent: 1em;
        position: relative;
      }
      > span {
        position: absolute;
        width: 38px;
        height: 40px;
        display: block;
        background: #fff url("./image/icon@search.png") no-repeat center;
        top: 2px;
        right: 2px;
        cursor: pointer;
      }
    }
  }
</style>



