<!--活动列表组件-->
<template>
  <section>
    <section class="activity" v-for="item in ActivityList">
      <section class="activity__pic">
        <a :href="'activity.html#/detail/'+item.id" target="_blank"><img :src="item.imgurl" alt=""/></a>
      </section>
      <section class="activity__info">
        <section class="activity__auhter">
          <section class="activity__header">
            <a :href="'personal.html#/accounts/products/'+item.uid" target="_blank"><img :src="item.avatar" alt=""/></a>
          </section>
          <i class="activity__name">{{item.author}}</i>
        </section>
        <section class="activity__desc">
          <h6>{{item.title}}</h6>
          <section class="activity__hot">
            <i>{{item.jcount==null?'0':item.jcount}}人参与</i>
          </section>
          <p v-html="item.content" class="activity__sub"></p>
        </section>
      </section>
      <section class="activity__state" :class="item.stats | changeState(5)">
        <h6>{{item.stats | changeState(4)}}</h6>
        <p>截稿:{{item.endactivitytime | UnixToNormalTime(0)}}</p>
      </section>
    </section>
    <Pagination :pageCount="actPageCount" :pageSize="actPageSize" @pageChange="actPageChange"></Pagination>
  </section>
</template>

<!--数据交互-->
<script>
  import Pagination from 'components/Common/Pagination' //分页组件
  export default {
    props:["ActivityList","actPageCount","actPageSize"],
    components:{
      Pagination
    },
    methods:{
      actPageChange(val){
        this.$emit("actPageChange",val)
      }
    }
  }
</script>
<!--组件样式-->
<style scoped lang="scss">
  @import "../../common/css/activity.css";
</style>



