<!-- Created by yyy on 2017/4/17. -->
<template>
  <div>
    <searchBox :searchList="searchList" @searchTitle="tabSwitch" @searchRange="searchRange"></searchBox>
    <!-- 搜索导航切换内容区域 -->
    <section class="search">
      <section class="search__list">
        <p class="search__show">共搜索到<i>{{searchResultShowNum}}</i>{{searchResultShow}}</p>
        <CssLoading :styleObj="{width:'1180px',height:'500px'}" :showLoad="showLoad"></CssLoading>
        <Nosearch :showNoImage="showNoImage" :styleObj="{width:'1180px'}"></Nosearch>
        <keep-alive>
          <component :is='currentView'
                     :loadOver="loadOver"
                     :loadMoreShow="loadMoreShow"
                     @loadMore="loadMore"
                     :items="Paint"
                     :ComShop="ShopEveryList"
                     :shopPageCount="shopPageCount"
                     :shopPageSize="shopPageSize"
                     @shopPageChange="shopPageChange"
                     :ComSell="SellEveryList"
                     :sellPageCount="sellPageCount"
                     :sellPageSize="sellPageSize"
                     @sellPageChange="sellPageChange"
                     :ComManu="ManuEveryList"
                     :manuPageCount="manuPageCount"
                     :manuPageSize="manuPageSize"
                     @manuPageChange="manuPageChange"
                     :ComCollect="CollectEveryList"
                     :collectPageCount="collectPageCount"
                     :collectPageSize="collectPageSize"
                     @collectPageChange="collectPageChange"
                     :ActivityList="ActivityEveryList"
                     :actPageCount="actPageCount"
                     :actPageSize="actPageSize"
                     @actPageChange="actPageChange"
                     :UserList="UserEveryList"
                     :userPageCount="userPageCount"
                     :userPageSize="userPageSize"
                     @userPageChange="userPageChange"
          ></component>
        </keep-alive>
      </section>
    </section>
    <Prompt :hidePrompt="hidePrompt" :showNum="showNum" :promptLanguage="promptLanguage"></Prompt>
  </div>

</template>

<script>
  import searchBox from 'components/Search/Searchbox' // 搜索
  import ComPaint from 'components/Common/Flow';  //  画作-公共部分
  import ComShop from 'components/Common/ComShop';  //  画铺-公共部分
  import ComSell from 'components/Common/ComSell';  // 个人待出售设计公共部分
  import ComManuscripts from 'components/Search/ComManuscripts';  // 约稿组件
  import ComCollect from 'components/Search/ComCollect' // 征稿组件
  import ComActivity from 'components/Search/Activity' // 活动组件
  import user from 'components/Search/User' // 用户组件
  import Prompt from 'components/Common/Prompt' // 提示框
  import CssLoading from 'components/Common/CssLoading' // 加载动画
  import Nosearch from 'components/Common/Nosearch' // 没有搜索内容的时候，展示的内容
  export default {
    name: 'app',
    data: () => {
      return {
        searchResultShowNum: "0", // 默认是0篇
        searchResultShow: "篇画作", // 默认显示第一个，即‘篇画作’
        searchIndex: 0, // 默认显示画作搜索  对应tab切换的小角标
        showLoad: false, // 隐藏加载动画
        showNoImage: true, // 返回值为空时展示的内容，提示搜索为空
        searchList: [
          {name: "画作", bol: true, tab: "ComPaint"},
          {name: "画铺", bol: false, tab: "ComShop"},
          {name: "设计", bol: false, tab: "ComSell"},
          {name: "约稿", bol: false, tab: "ComManuscripts"},
          {name: "征稿", bol: false, tab: "ComCollect"},
          {name: "活动", bol: false, tab: "ComActivity"},
          {name: "用户", bol: false, tab: "user"},
        ],
        loadOver:"加载更多...",
        loadMoreShow:false, // 当加载成功时候，出现
        hidePrompt: false,
        promptLanguage: "", // 提示框出现的提示语
        showNum: 2, // 0 成功  1 错误 2 提示
        currentView: ComPaint,
        Paint: [],
        paintPagesize:60,
        paintPageIndex:1,
        ShopEveryList:[],
        shopPageSize: 30, // 画铺每页显示几条
        shopPageCount:0, // 画铺总页数
        shopPageIndex:1,
        SellEveryList:[],
        sellPageSize: 30, // 设计每页显示的几条
        sellPageCount: 0,  // 设计总页数
        sellPageIndex:1,
        ManuEveryList:[],
        manuPageSize:20,  // 约稿每页显示的几天
        manuPageCount:0, // 约稿搜索总页数
        manuPageIndex:1,
        Collect: [],
        CollectEveryList:[],
        collectPageSize:20,  // 征稿每页显示几条数据
        collectPageCount:0, // 征稿总页数
        collectPageIndex:1,
        ActivityEveryList:[],
        actPageSize:10,  // 活动每页显示的数
        actPageCount:0, // 活动总页数
        actPageIndex:1,
        UserEveryList:[],
        userPageCount:0, // 所有用户的个数
        userPageSize:30, // 用户每页展示几个
        userPageIndex:1,
      }
    },
    components: {
      searchBox,
      ComPaint,
      ComShop,
      ComSell,
      ComManuscripts,
      ComCollect,
      ComActivity,
      user,
      Prompt,
      CssLoading,
      Nosearch,
    },
    mounted(){
      this.fromOhterPage();
    },
    methods: {
      fromOhterPage(){ // 从其他页面调整进来时加载的效果
        let _this = this;
        if(document.location.href.indexOf("searchType") !== -1){
          let sid = this.$route.fullPath.substring(this.$route.fullPath.lastIndexOf("/")+1,this.$route.fullPath.indexOf("&"));
          let searchContent = this.$route.fullPath.substring(this.$route.fullPath.indexOf("&")+1);
          for (let i = 0; i < this.searchList.length; i++) {
            this.searchList[i].bol = false;
          }
          this.currentView = this.searchList[sid].tab;
          this.searchList[sid].bol = true;
          switch (sid) {
            case '0':
              // 1.画作
              this.getToken().then(token => {
                _this.$http.get("huazuo/search", {
                  params: {
                    access_token: token,
                    key: searchContent
                  }
                }).then(res => {
                  if (res.data.stats === '2000') {
                    if (res.data.data.list.length === '0') {
                      _this.showNoImage = true; // 搜索内容为空时，展示提示‘内容为空’
                      _this.showLoad = false;
                      _this.Paint = [];
                    } else {
                      _this.showNoImage = false;
                      _this.showLoad = false;
                      _this.Paint = res.data.data.list;
                      _this.searchResultShowNum = res.data.data.list.length;
                    };
                  }
                }).catch(err => {
                  console.log("数据请求出错，请联系管理员!")
                })
              })
              break;
            case '1':
              // 2.画铺
              _this.getToken().then(token => {
                _this.$http.get('huapu/search', {
                  params: {
                    access_token: token,
                    key: searchContent,
                    page:this.shopPageIndex
                  }
                }).then(res => {
                  if (res.data.stats === '2000') {
                    if (res.data.data.total === '0') {
                      _this.showNoImage = true;
                      _this.showLoad = false;
                      _this.ShopEveryList = [];
                    } else {
                      _this.showLoad = false; // 加载成功时，动画消失
                      _this.showNoImage = false;
                      _this.ShopEveryList = res.data.data.list;
                      _this.searchResultShowNum = parseInt(res.data.data.total);
                      _this.shopPageCount = parseInt(res.data.data.total);
                    }
                  }
                }).catch(err => {
                  console.log(err);
                });
              });
              break;
            case '2':
              // 3.设计
              this.getToken().then(token => {
                this.$http.get('sheji/search', {
                  params: {
                    access_token: token,
                    key: searchContent,
                    page:_this.sellPageIndex
                  }
                }).then(res => {
                  if (res.data.stats === '2000') {
                    if (res.data.data.total === '0') {
                      _this.showNoImage = true;
                      _this.showLoad = false;
                      _this.SellEveryList = [];
                    } else {
                      _this.showLoad = false; // 加载成功时，动画消失
                      _this.showNoImage = false;
                      _this.SellEveryList = res.data.data.list;
                      _this.searchResultShowNum = parseInt(res.data.data.total);
                      _this.sellPageCount = parseInt(res.data.data.total);
                    }
                  }
                }).catch(err => {
                  console.log(err);
                });
              })
              break;
            case '3':
              // 4.约稿
              this.getToken().then(token => {
                this.$http.get('yuegao/search', {
                  params: {
                    access_token: token,
                    key: searchContent,
                    page:this.manuPageIndex
                  }
                }).then(res => {
                  if (res.data.stats === '2000') {
                    if (res.data.data.total === '0') {
                      _this.showNoImage = true;
                      _this.showLoad = false;
                      _this.ManuEveryList = [];
                    } else {
                      _this.showLoad = false; // 加载成功时，动画消失
                      _this.showNoImage = false;
                      _this.ManuEveryList = res.data.data.list;
                      _this.searchResultShowNum =  parseInt(res.data.data.total);
                      _this.manuPageCount =  parseInt(res.data.data.total);
                    }
                  }
                }).catch(err => {
                  console.log(err);
                });
              })
              break;
            case '4':
              // 5.征稿
              this.getToken().then(token => {
                this.$http.get('zhenggao/search', {
                  params: {
                    access_token: token,
                    key: searchContent,
                    page:this.collectPageIndex
                  }
                }).then(res => {
                  if (res.data.stats === '2000') {
                    if (res.data.data.total === '0') {
                      _this.showNoImage = true;
                      _this.showLoad = false;
                      _this.CollectEveryList
                    } else {
                      _this.showLoad = false;
                      _this.showNoImage = false;
                      _this.CollectEveryList = res.data.data.list;
                      _this.searchResultShowNum = parseInt(res.data.data.total);
                      _this.collectPageCount = parseInt(res.data.data.total);
                    }
                  }
                }).catch(err => {
                  console.log(err);
                });
              })
              break;
            case '5':
              // 6.活动
              this.getToken().then(token => {
                this.$http.get('huodong/search', {
                  params: {
                    access_token: token,
                    key: searchContent,
                    page:_this.actPageIndex
                  }
                }).then(res => {
                  if (res.data.stats === '2000') {
                    if (res.data.data.total === '0') {
                      _this.showNoImage = true;
                      _this.showLoad = false;
                      _this.ActivityEveryList = [];
                    } else {
                      _this.showLoad = false;
                      _this.showNoImage = false;
                      _this.ActivityEveryList = res.data.data.list;
                      _this.searchResultShowNum = parseInt(res.data.data.total);
                      _this.actPageCount = parseInt(res.data.data.total);
                    }
                  }
                }).catch(err => {
                  console.log(err);
                });
              })
              break;
            case '6':
              // 7.用户
              this.getToken().then(token => {
                this.$http.get('user/search', {
                  params: {
                    access_token: token,
                    key: searchContent,
                    page:_this.userPageIndex
                  }
                }).then(res => {
                  if (res.data.stats === '2000') {
                    if (res.data.data.total === '0') {
                      _this.showNoImage = true;
                      _this.showLoad = false;
                      _this.UserEveryList = [];
                    } else {
                      _this.showNoImage = false;
                      _this.showLoad = false;
                      _this.UserEveryList = res.data.data.list;
                      _this.searchResultShowNum = parseInt(res.data.data.total);
                      _this.userPageCount = parseInt(res.data.data.total);
                    }
                  }
                }).catch(err => {
                  console.log(err);
                });
              })
              break;
          }
        }else {
          return false
        }
      },
      shopPageChange(val){
        this.shopPageIndex = val;
        this.searchRange(document.getElementById("searchKeyWord").value);
      },
      sellPageChange(val){
        this.sellPageIndex = val;
        this.searchRange(document.getElementById("searchKeyWord").value);
      },
      manuPageChange(val){
        this.manuPageIndex = val;
        this.searchRange(document.getElementById("searchKeyWord").value);
      },
      collectPageChange(val){
        this.collectPageIndex = val;
        this.searchRange(document.getElementById("searchKeyWord").value);
      },
      actPageChange(val){
        this.actPageIndex = val;
        this.searchRange(document.getElementById("searchKeyWord").value);
      },
      userPageChange(val){
        this.userPageIndex = val;
        this.searchRange(document.getElementById("searchKeyWord").value);
      },
      loadMore(){ // 加载更多图片
        this.paintPageIndex++;
        this.searchRange(document.getElementById("searchKeyWord").value);
      },
      searchRange(name){
        let _this = this;
        if (name === "" || name === undefined) {
          _this.promptLanguage = "搜索内容不能为空"
          _this.hidePrompt = true;
          setTimeout(function () {
            _this.hidePrompt = false;
          }, 2500)
          return false; // 不在继续下面的操作
        } else {
          _this.showNoImage = false;
          _this.showLoad = true;
          switch (_this.searchIndex) {
            case 0:
              // 1.画作
              this.getToken().then(token => {
                _this.$http.get("huazuo/search", {
                  params: {
                    access_token: token,
                    key: name,
                    page: _this.paintPageIndex,
                    show: _this.paintPagesize
                  }
                }).then(res => {
                  if (res.data.stats === '2000') {
                    if (res.data.data.total === '0') {
                      _this.showNoImage = true; // 搜索内容为空时，展示提示‘内容为空’
                      _this.showLoad = false;
                      _this.Paint = [];
                    } else {
                      _this.loadMoreShow = true;
                      _this.showNoImage = false;
                      _this.showLoad = false;
                      _this.Paint = res.data.data.list;
                      _this.searchResultShowNum = parseInt(res.data.data.total);
                      if(res.data.data.list.length === 0){
                        this.loadOver = "全部加载完毕或暂无数据";
                      }
                    };
                    // 通过hash值来提示搜索人检索的主题和检索字
                    window.location.hash = "?title=" + decodeURI("huazuo") + "&key=" + decodeURI(name);
                  }
                }).catch(err => {
                  console.log("数据请求出错，请联系管理员!")
                })
              })
              break;
            case 1:
              // 2.画铺
              _this.getToken().then(token => {
                _this.$http.get('huapu/search', {
                  params: {
                    access_token: token,
                    key: name,
                    page:this.shopPageIndex,
                    show:_this.shopPageSize
                  }
                }).then(res => {
                  if (res.data.stats === '2000') {
                    if (res.data.data.total === '0') {
                      _this.showNoImage = true;
                      _this.showLoad = false;
                      _this.ShopEveryList = [];
                    } else {
                      _this.showLoad = false; // 加载成功时，动画消失
                      _this.showNoImage = false;
                      _this.ShopEveryList = res.data.data.list;
                      _this.searchResultShowNum = parseInt(res.data.data.total);
                      _this.shopPageCount = parseInt(res.data.data.total);
                    }
                    window.location.hash = "?title=huapu" + "&key=" + decodeURI(name);
                  }
                }).catch(err => {
                  console.log(err);
                });
              });
              break;
            case 2:
              // 3.设计
              this.getToken().then(token => {
                this.$http.get('sheji/search', {
                  params: {
                    access_token: token,
                    key: name,
                    page:_this.sellPageIndex,
                    show:_this.sellPageSize
                  }
                }).then(res => {
                  if (res.data.stats === '2000') {
                    if (res.data.data.total === '0') {
                      _this.showNoImage = true;
                      _this.showLoad = false;
                      _this.SellEveryList = [];
                    } else {
                      _this.showLoad = false; // 加载成功时，动画消失
                      _this.showNoImage = false;
                      _this.SellEveryList = res.data.data.list;
                      _this.searchResultShowNum = parseInt(res.data.data.total);
                      _this.sellPageCount = parseInt(res.data.data.total);
                    }
                    window.location.hash = "?title=sheji" + "&key=" + decodeURI(name);
                  }
                }).catch(err => {
                  console.log(err);
                });
              })
              break;
            case 3:
              // 4.约稿
              this.getToken().then(token => {
                this.$http.get('yuegao/search', {
                  params: {
                    access_token: token,
                    key: name,
                    page:this.manuPageIndex,
                    show:_this.manuPageSize
                  }
                }).then(res => {
                  if (res.data.stats === '2000') {
                    if (res.data.data.total === '0') {
                      _this.showNoImage = true;
                      _this.showLoad = false;
                      _this.ManuEveryList = [];
                    } else {
                      _this.showLoad = false; // 加载成功时，动画消失
                      _this.showNoImage = false;
                      _this.ManuEveryList = res.data.data.list;
                      _this.searchResultShowNum =  parseInt(res.data.data.total);
                      _this.manuPageCount =  parseInt(res.data.data.total);
                    }
                    window.location.hash = "?title=yuegao" + "&key=" + decodeURI(name);
                  }
                }).catch(err => {
                  console.log(err);
                });
              })
              break;
            case 4:
              // 5.征稿
              this.getToken().then(token => {
                this.$http.get('zhenggao/search', {
                  params: {
                    access_token: token,
                    key: name,
                    page:this.collectPageIndex,
                    show:_this.collectPageSize
                  }
                }).then(res => {
                  if (res.data.stats === '2000') {
                    if (res.data.data.total === '0') {
                      _this.showNoImage = true;
                      _this.showLoad = false;
                      _this.CollectEveryList
                    } else {
                      _this.showLoad = false;
                      _this.showNoImage = false;
                      _this.CollectEveryList = res.data.data.list;
                      _this.searchResultShowNum = parseInt(res.data.data.total);
                      _this.collectPageCount = parseInt(res.data.data.total);
                    }
                    window.location.hash = "?title=zhenggao" + "&key=" + decodeURI(name);
                  }
                }).catch(err => {
                  console.log(err);
                });
              })
              break;
            case 5:
              // 6.活动
              this.getToken().then(token => {
                this.$http.get('huodong/search', {
                  params: {
                    access_token: token,
                    key: name,
                    page:_this.actPageIndex,
                    show:_this.actPageSize
                  }
                }).then(res => {
                  if (res.data.stats === '2000') {
                    if (res.data.data.total === '0') {
                      _this.showNoImage = true;
                      _this.showLoad = false;
                      _this.ActivityEveryList = [];
                    } else {
                      _this.showLoad = false;
                      _this.showNoImage = false;
                      _this.ActivityEveryList = res.data.data.list;
                      _this.searchResultShowNum = parseInt(res.data.data.total);
                      _this.actPageCount = parseInt(res.data.data.total);
                    }
                    window.location.hash = "?title=huodong" + "&key=" + decodeURI(name);
                  }
                }).catch(err => {
                  console.log(err);
                });
              })
              break;
            case 6:
              // 7.用户
              this.getToken().then(token => {
                this.$http.get('user/search', {
                  params: {
                    access_token: token,
                    key: name,
                    page:_this.userPageIndex,
                    show:_this.userPageSize
                }
                }).then(res => {
                  if (res.data.stats === '2000') {
                    if (res.data.data.total === '0') {
                      _this.showNoImage = true;
                      _this.showLoad = false;
                    } else {
                      _this.showLoad = false;
                      _this.showNoImage = false;
                      _this.UserEveryList = res.data.data.list;
                      _this.searchResultShowNum = parseInt(res.data.data.total);
                      _this.userPageCount = parseInt(res.data.data.total);
                    }
                    window.location.hash = "?title=user" + "&key=" + decodeURI(name);
                  }
                }).catch(err => {
                  console.log(err);
                });
              })
              break;
          }
        }
      },
      tabSwitch(index,type){
        for (let i = 0; i < this.searchList.length; i++) {
          this.searchList[i].bol = false;
          if (index === i) {
            this.showNoImage = false;
            this.searchList[index].bol = true;
            this.currentView = this.searchList[index].tab;
            this.searchIndex = index;
            switch (index){
              case 0:
                this.searchResultShowNum = this.Paint.length;
                this.searchResultShow = "篇画作";
                this.noPictureShowImage(this.Paint);
                break;
              case 1:
                this.searchResultShowNum = this.shopPageCount;
                this.searchResultShow = "篇画铺";
                this.noPictureShowImage(this.ShopEveryList);
                break;
              case 2:
                this.searchResultShowNum = this.sellPageCount;
                this.searchResultShow = "篇设计";
                this.noPictureShowImage(this.SellEveryList);
                break;
              case 3:
                this.searchResultShowNum = this.manuPageCount;
                this.searchResultShow = "篇约稿";
                this.noPictureShowImage(this.ManuEveryList);
                break;
              case 4:
                this.searchResultShowNum = this.collectPageCount;
                this.searchResultShow = "篇征稿";
                this.noPictureShowImage(this.CollectEveryList);
                break;
              case 5:
                this.searchResultShowNum = this.actPageCount;
                this.searchResultShow = "个活动";
                this.noPictureShowImage(this.ActivityEveryList);
                break;
              case 6:
                this.searchResultShowNum = this.userPageCount;
                this.searchResultShow = "个用户";
                this.noPictureShowImage(this.UserEveryList);
                break;
            }
          }
        }
      },
      noPictureShowImage(type){
        if(type == ""  | type == null){
          this.showNoImage = true;
        }else {
          this.showNoImage = false;
        }
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../../common/css/define";

  .search {
    width: 100%;
    padding-top: 40px;
    padding-bottom: 10px;
    margin-bottom: 30px;
    background-color: #fafafa;
    .search__list {
      width: 1180px;
      margin: 0 auto;
      .search__show {
        margin-bottom: 20px;
        color: $black;
        font-size: 14px;
        letter-spacing: 0.2em;
        i {
          color: $pink;
        }
      }
    }
  }

</style>



