        this.everyList = this.bigGroup;
      },
      switchTab(index){
        this.qqList.forEach((a, i) => {
          this.qqList[i].bol = false;
          if (index === i) {
            this.qqList[index].bol = true;
          }
        })
        switch (index) {
          case 0:
            this.everyList = this.bigGroup;
            break;
          case 1:
            this.everyList = this.smallGroup[0];
            break;
          case 2:
            this.everyList = this.smallGroup[1];
            break;
          case 3:
            this.everyList = this.smallGroup[2];
            break;
          case 4:
            this.everyList = this.smallGroup[3];
            break;
          case 5:
            this.everyList = this.smallGroup[4];
            break;
          case 6:
            this.everyList = this.smallGroup[5];
            break;
          case 7:
            this.everyList = this.smallGroup[6];
            break;
        }
      }
