<!-- 2017/3/21-用户 -->
<template>
  <section class="user">
    <ul class="user__ul">
      <li class="user__list clearfix" v-for="item,index in UserList">
        <a :href="'personal.html#/accounts/products/'+item.id" class="user__head">
          <img :src="item.avatar" alt="">
        </a>
        <section class="user__name">
          <div>
            <a :href="'personal.html#/accounts/products/'+item.id">{{item.nickname}}</a>
            <a href="javascript:void(0)" v-if="item.gender==1"><img src="./images/boy.png" alt=""></a>
            <a href="javascript:void(0)" v-else-if="item.gender==2"><img src="./images/girl.png" alt=""></a>
            <a href="javascript:void(0)" v-else-if="item.gender==3"><img src="" alt=""></a>
          </div>
          <p v-if="item.tagsname==null"><i>该用户暂无标签</i></p>
          <p v-else><i v-for="val in item.tagsname">{{val.tags}}</i></p>
        </section>
        <section class="user__dyn">
          <span>+关注</span>
          <span><a :href="'message.html#/privateType/'+item.id+'&'+item.nickname" >私信</a> </span>
          <CopyQqDialogue :info="{name:item.nickname,qq:item.qq}" class="user__qq"></CopyQqDialogue>
        </section>
      </li>
    </ul>
    <Pagination :pageCount="userPageCount" :pageSize="userPageSize" @pageChange="userPageChange"></Pagination>
  </section>
</template>

<script>
  import Pagination from 'components/Common/Pagination' //分页组件
  import CopyQqDialogue from 'components/Common/CopyQqDialogue' // 复制qq时，弹窗
  export default {
    props:["UserList","userPageCount","userPageSize"],
    components:{
      Pagination,
      CopyQqDialogue
    },
    methods:{
      userPageChange(val){
        this.$emit("userPageChange",val)
      },
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../common/css/reset.css";
  @import "../../common/css/define";

  .user {
    width: 100%;
    background-color: #fafafa;
    .user__ul {
      width: 1180px;
      margin: 0 auto;
      box-sizing: border-box;
    }
  }
  .user__list {
    width: 580px;
    height: 120px;
    display: inline-block;
    box-sizing: border-box;
    margin: 0 20px 20px 0;
    padding: 20px;
    border: 1px solid $lightgrey;
    background-color: $white;
    border-radius:4px;
    position: relative;
    @include wrapShow;
    &:nth-of-type(2n){
       margin-right:0;
     }
    .user__head {
      float: left;
      display: inline-block;
      width: 80px;
      height: 80px;
      overflow: hidden;
      border-radius: 50%;
      margin-right: 20px;

      > img {
        width: 100%;
      }
    }
    .user__name {
      float: left;
      margin-top: 20px;
      a {
        font-size: 14px;
        color: $black;
        font-weight: bold;
      }
      img {
        margin-left: 6px;
        vertical-align: baseline;
      }
      p {
        font-size: 12px;
        color: #999999;
        margin-top: 8px;
      }
    }
    .user__dyn {
      float: right;
      margin-top: 38px;
      span {
        display: inline-block;
        height: 20px;
        border-radius: 2px;
        font-size: 14px;
        color: #8bc34a;
        line-height: 20px;
        padding: 0 6px;
        border: 1px solid $lightgrey;
        cursor: pointer;
        a{
          color: #8bc34a;
        }
        &:first-child {
          border: 1px solid #8bc34a;
          background-color: #8bc34a;
          color: $white;
        }
        &:nth-of-type(2) {
          margin: 0 8px;
        }
      }
      .user__qq {
        display: inline-block;
        height: 20px;
        border-radius: 2px;
        font-size: 14px;
        color: #8bc34a;
        line-height: 20px;
        padding: 0 6px;
        border: 1px solid $lightgrey;
        cursor: pointer;
      }
    }
  }

</style>



