<!--QQ弹窗，复制qq,点击QQ号码启用聊天qq-->
<template>
  <section>
    <i @click="qqDialog" >QQ</i>
    <transition name="fade">
      <section class="copy" v-if="qqShowPop">
        <div class="copy__con">
          <section class="copy__top clearfix">
            <i>提示</i>
            <i @click="closeCopy">X</i>
          </section>
          <section class="copy__qq">
            <span>{{info.name}}</span>
            <span>的QQ是:</span>
            <span><a :href="'http://wpa.qq.com/msgrd?v=3&uin='+info.qq+'&site=qq&menu=yes'" target="_blank">{{info.qq===null?'暂未填写QQ':info.qq}}</a></span>
            <span v-if="info.qq===null"></span>
            <span @click="copyContent" v-else>{{copySwitch}}</span>
          </section>
          <textarea id="user__copy__content">{{info.qq==null?'暂未填写QQ':info.qq}}</textarea>
        </div>
      </section>
    </transition>
  </section>
</template>

<!--数据交互-->
<script>
  export default {
    props:['info'],
    data:()=>{
      return {
        qqShowPop:false,
        copySwitch:"复制"
      }
    },
    methods:{
      qqDialog(){
        this.qqShowPop = true;
      },
      closeCopy(){
        this.qqShowPop = false;
        this.copySwitch = "复制"
      },
      copyContent(){
        let copy = document.getElementById("user__copy__content");
        copy.select(); // 选择对象
        document.execCommand("Copy"); // 执行浏览器复制命令
        if(this.copySwitch == "复制"){
          this.copySwitch = "已复制"  // 已复制-->复制
        }
      }
    }
  }

</script>

<!--组件样式-->
<style lang="scss" scoped>
  @import "../../common/css/reset.css";
  @import "../../common/css/define";

  .fade-enter-active, .fade-leave-active {
    transition: opacity 0.3s
  }
  .fade-enter, .fade-leave-active {
    opacity: 0
  }
  .copy {
    position: fixed;
    top: 0;
    left:0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.1);
    z-index: 99999;
    .copy__con {
      position: fixed;
      top: 50%;
      left: 50%;
      margin: -70px 0 0 -170px;
      z-index: 999;
      .copy__top {
        width: 320px;
        height: 40px;
        background-color: $pink;
        border-radius: 4px 4px 0 0;
        padding: 0 10px;
        i {
          line-height: 40px;
          font-weight: bold;
          font-size: 14px;
          color: $white;
          float: left;
          &:last-child {
            float: right;
            cursor: pointer;
          }
        }
      }
      .copy__qq {
        width: 340px;
        height: 100px;
        font-size: 14px;
        color: $black;
        line-height: 100px;
        text-align: center;
        background-color: $white;
        border-radius: 0 0 4px 4px;
        span {
          display: inline-block;
          &:first-child {
            color: $pink;
          }
          &:nth-of-type(3){
            font-weight: 600;
            text-decoration:underline;
          }
          &:last-child {
            cursor: pointer;
            margin-left: 6px;
            color: #03a9f4;
          }
        }
      }
    }
    textarea {
      width: 0;
      height: 0;
      resize: none;
      visibility: hidden;
    }
  }

</style>



