<!-- 2017/3/22- yyy -已商用设计-->

<template>
  <section>
    <section class="commersale">

      <range
        :SearchRange="SearchRange"
        :IconRelease="IconRelease"
        @rangeIcon="rangeIcon"
        @relDesign="relDesign"></range>
    </section>

    <!--商用设计组件-->
    <ComSell
      class="l-mt"
      :ComSell="sell"
      :link="link"></ComSell>
    <!--分页组件-->
    <Pagination
      :pageCount="pageCount"
      :pageSize="pageSize"
      @pageChange="pageChange">
    </Pagination>
  </section>

</template>


<script>

  import range from 'components/Design/SearchRange'  // 搜索范围
  import ComSell from 'components/Common/ComSell'  // 设计公共部分
  import Pagination from 'components/Common/Pagination' //分页组件

  export default {
    data: () => {
      return {
        SearchRange: [
          {name: "按时间", inputBol: false, bol: true},
          {name: "按点赞", inputBol: false, bol: false},
          {name: "按人气", inputBol: false, bol: false},
        ],
        IconRelease: "发布设计",
        link: '/design',
        pageCount: 0,
        pageSize: 30,
        pageIndex: 1,
        promise: null,
        sell: [] //未商用全部数据
      }
    },
    components: {
      range,
      ComSell,
      Pagination
    },
    mounted(){
      this.getData();
    },
    methods: {
      getData(){ //获取已出售设计数据
        let _this_ = this;
        this.getToken().then(token => {
          this.$http.get('sheji/findallsheji', {
            params: {
              access_token: token,
              busintype: '1',
              page: this.pageIndex
            }
          }).then(res => {
            this.sell = res.data.data.list;
            _this_.pageCount = parseInt(res.data.data.total);//总页数

          }).catch(err => {
            console.log(err);
          })
        })
      },

      //分页控制
      pageChange(val){
        this.pageIndex = val;
        this.getData();
      },
      rangeIcon(){

      },
      relDesign(){

      },
    }
  }

</script>

<style lang="scss" scoped>

  @import "../../common/css/reset.css";
  @import "../../common/css/define";

  .l-mt {
    margin-top: 30px;
  }


</style>



