<!--时间组件-->

<template>
  <div class="block date">
    <el-date-picker
      v-model="value"
      type="date"
      @change="handleChangeDate"
      placeholder="选择日期"
      :picker-options="pickerOptions0">
    </el-date-picker>
  </div>
</template>


<!--数据交互-->
<script>
  export default {
    data() {
      return {
        pickerOptions0: {
          disabledDate(time) {
            return time.getTime() < Date.now() - 8.64e7;
          }
        },
        value: '',
      };
    },
    methods: {
      handleChangeDate(){
       if (this.value) {
         let t = Date.parse(new Date(this.value)) / 1000;
         this.$emit('onSubmitEndTime',t)
       }
      }
    }
  };
</script>



