<!-- 2017/3/22- 活动组件  获奖结果组件-->
<template>
  <section class="przie">
    <ul>
      <li v-for="item in colPrize" class="prize__list">
        <section class="prize__detail"><i>你参加的活动</i><a :href="'activity.html#/detail/'+item.moduleid" target="_blank">{{item.title}}</a> <i>已经颁奖了，快去看看吧</i></section>
        <i class="prize__date">{{item.createtime | UnixToNormalTime(1)}}</i>
      </li>
    </ul>
    <Pagination :pageCount="prizePageCount" :pageSize="prizePageSize" @pageChange="prizePageChange"></Pagination>
  </section>
</template>

<script>
  import Pagination from 'components/Common/Pagination' //分页组件
  export default {
    props:["colPrize","prizePageCount","prizePageSize"],
    components:{
      Pagination
    },
    methods:{
      prizePageChange(val){
        this.$emit("prizePageChange",val)
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../../common/css/reset.css";
  @import "../../../common/css/define";

  .przie {
    width: 920px;
  }
  .prize__list {
    padding-bottom: 15px;
    margin-bottom: 18px;
    border-bottom: 1px solid $lightgrey;
    font-size: 14px;

  }

  .prize__detail {
    width: 785px;
    display: inline-block;
    > i {
      color: #666666;
    }
    > a {
      color: $black;
      font-weight: bold;
    }
  }
  .prize__date {
    display: inline-block;
    color: #666666;
    font-size: 12px;
  }
</style>



