<!--约稿详情标题组件-->
<template>
  <div class="pact-detail__title">
    <dl>
      <dd class="pact-detail__name">
        <h6> {{items.title}}</h6>
        <i class="pact-detail__hot">{{items.bcount | setNull}}</i>
        <i class="pact-detail__collect">{{items.ccount | setNull}}</i>
      </dd>
      <dd class="pact-detail__time">2016-11-12 10:45:45</dd>
      <dd class="pact-detail__tags">
        <i>同人本</i>
        <i>同人</i>
        <i>女性角色</i>
        <i>周边</i>
      </dd>
      <dd class="pact-detail__tips">
        <section class="pact-detail__price">单张预算：<i>{{items.lowcost}}~{{items.highcost}}元</i></section>
        <section class="pact-detail__number">需求张数：<i>{{items.imgcount}}张</i></section>
      </dd>
      <dd class="pact-detail__state">
        <h6>{{items.stats | changeState(0)}}</h6>
        <p>截稿：{{items.endtime | UnixToNormalTime(0)}}</p>
      </dd>
    </dl>
  </div>
</template>

<!--数据交互-->
<script>
  export default {
    props: ['items']
  }
</script>

<!--组件样式-->
<style scoped lang="scss">
  @import "../../common/css/define";

  .pact-detail__title {
    margin-top: 40px;
    dl {
      max-width: 1140px;
      margin: 0 auto;
      padding: 20px;
      position: relative;
      background: #fff;
      @include wrapShow;
    }
    .pact-detail__name {
      > h6 {
        font-size: 22px;
        display: inline-block;
        vertical-align: middle;
      }
      .pact-detail__hot {
        font-size: 14px;
        color: $pink;
        display: inline-block;
        padding: 0 20px;
        text-align: right;
        vertical-align: middle;
        margin: 0 16px;
        background: url("../../components/Common/images/hot.png") no-repeat 0 center;
      }
      .pact-detail__collect {
        font-size: 14px;
        display: inline-block;
        vertical-align: middle;
        width: 32px;
        padding: 0 15px;
        height: 30px;
        color: #666;
        border-radius: 2px;
        border: 1px solid #ccc;
        line-height: 30px;
        text-align: right;
        background: url("../../components/Common/images/icon.png") 15px -11px;
        cursor: pointer;
        &.is--collect {
          border-color: $pink;
          color: #fff;
          background: $pink url("../../components/Common/images/icon@praise.png") no-repeat 15px -14px;
        }
      }

    }
    .pact-detail__time {
      font-size: 14px;
      color: $gray;
      line-height: 40px;
    }
    .pact-detail__tags {
      margin: 6px 0 20px 0;
      > i {
        display: inline-block;
        padding: 0 10px;
        height: 22px;
        border: 1px solid #e6e6e6;
        border-radius: 2px;
        font-size: 14px;
        line-height: 22px;
        color: #666;
      }
    }
    .pact-detail__tips {
      .pact-detail__price {
        display: inline-block;
        font-size: 14px;
        margin-right: 22px;
        > i {
          color: $pink;
        }
      }
      .pact-detail__number {
        display: inline-block;
        font-size: 14px;
        > i {
          color: $pink;
        }
      }
    }
    .pact-detail__state {
      position: absolute;
      top: 0px;
      right: 20px;
      width: 138px;
      height: 94px;
      background: url("./images/act@bg.png") no-repeat;
      > h6 {
        font-size: 22px;
        text-align: center;
        margin: 20px 0 10px 0;
        color: #fff;
      }
      > p {
        font-size: 14px;
        text-align: center;
        color: #fff;
      }
    }
  }
</style>



