<!-- 2017/3/22- 个人主页  个人首页-->
<template>
  <section class="bg">
    <section class="homepage clearfix">
      <section class="homepage-left">
        <Nosearch :showNoImage="showNoImage" :styleObj="{width:'840px'}"></Nosearch>
        <!-- 发布画作 -->
        <ul v-for="item in huazuoList">
          <span class="homepage-left__d"><i>发布画作</i><i>{{item.createtime | UnixToNormalTime(2)}}</i></span>
          <li class="homepage-left__list">
            <dl>
              <dd><em>同人</em>&#8194;:&#8194;<i>{{item.title}}</i></dd>
              <dd v-if="item.original==null?false:true"><em>原作</em>&#8194;:&#8194;<i>{{item.original}}</i></dd>
              <dd><em>角色</em>&#8194;:&#8194;<i>{{item.role}}</i></dd>
              <dd><em>标签</em>&#8194;:&#8194;<i v-for="val in item.tagsname">{{val.tags}}</i></dd>
              <dd><em>简介</em>&#8194;:&#8194;<i>{{item.introduce==null?'主人很懒，什么也没有留下':item.introduce}}</i></dd>
            </dl>
            <section class="homepage-left__img">
              <a :href="'painting.html#/detail/'+item.actionid"><img :src="item.imgs[1]" alt=""></a>
            </section>
          </li>
        </ul>

        <!-- 发布画铺 -->
        <ul v-for="item in huapuList">
          <span class="homepage-left__d"><i>发布画铺</i><i>{{item.createtime | UnixToNormalTime(2)}}</i></span>
          <li class="homepage-left__list">
            <dl>
              <dd><em>标题</em>&#8194;:&#8194;<i>{{item.title}}</i></dd>
              <dd><em>报价</em>&#8194;:&#8194;<i>￥{{item.lowcost}}~{{item.highcost}}</i></dd>
              <dd><em>周期</em>&#8194;:&#8194;<i>{{item.fastdesignday}}~{{item.longdesignday}}天</i></dd>
              <dd><em>简介</em>&#8194;:&#8194;<i>{{item.introduce==null?'主人很懒，什么也没有留下':item.introduce}}</i></dd>
            </dl>
            <section class="homepage-left__img">
              <a :href="'pact.html#/painting/'+item.actionid"><img :src="item.content" alt=""></a>
            </section>
          </li>
        </ul>

        <!-- 发布设计 -->
        <ul v-for="item in shejiList">
          <span class="homepage-left__d"><i>发布设计</i><i>{{item.createtime | UnixToNormalTime(2)}}</i></span>
          <li class="homepage-left__list">
            <dl>
              <dd><em>标题</em>&#8194;:&#8194;<i>{{item.title}}</i></dd>
              <dd><em>报价</em>&#8194;:&#8194;<i>￥{{item.lowcost}}~{{item.highcost}}</i></dd>
              <dd><em>周期</em>&#8194;:&#8194;<i>{{item.fastdesignday}}~{{item.longdesignday}}天</i></dd>
              <dd><em>简介</em>&#8194;:&#8194;<i>{{item.content==null?'主人很懒，什么也没有留下':item.content}}</i></dd>
            </dl>
            <section class="homepage-left__img ">
              <a href="#"><img src="./images/per.png" alt=""></a>
            </section>
          </li>
        </ul>

        <!-- 发布约稿 -->
        <ul v-for="item in yuegaoList">
          <span class="homepage-left__d"><i>发布约稿</i><i>{{item.createtime | UnixToNormalTime(2)}}</i></span>
          <li class="homepage-left__list">
            <a :href="'pact.html#/pactDetail/'+item.actionid" class="homepage-left__title">{{item.title}}</a>
            <span class="homepage-left__sty" v-if="item.tag==null"><i>该约稿暂无标签</i></span>
            <span class="homepage-left__sty" v-else><i v-for="value in item.tag">{{value.tags}}</i></span>
            <table class="homepage-left__table" cellspacing="0" cellpadding="0">
              <tr>
                <td><i>截稿日期&#8194;:&#8194;</i><i>{{item.endtime | UnixToNormalTime(0)}}</i></td>
                <td><i>应征人数&#8194;:&#8194;</i><i>{{item.personcount}}人</i></td>
              </tr>
              <tr>
                <td><i>单张预算&#8194;:&#8194;</i><i>{{item.lowcost}}~{{item.highcost}}</i></td>
                <td><i>需求张数&#8194;:&#8194;</i><i>{{item.imgcount}}张</i></td>
              </tr>
            </table>
          </li>
        </ul>

        <!-- 发布征稿 -->
        <ul v-for="item in zhenggaoList">
          <span class="homepage-left__d"><i>发布征稿</i><i>{{item.createtime | UnixToNormalTime(2)}}</i></span>
          <li class="homepage-left__list">
            <a :href="'design.html#/collectDet/'+item.actionid" class="homepage-left__title">{{item.title}}</a>
            <span class="homepage-left__sty" v-if="item.tag==null"><i>该征稿暂无标签</i></span>
            <span class="homepage-left__sty" v-else><i v-for="value in item.tag">{{value.tags}}</i></span>
            <table class="homepage-left__table" cellspacing="0" cellpadding="0">
              <tr>
                <td><i>截稿日期&#8194;:&#8194;</i><i>{{item.endtime | UnixToNormalTime(0)}}</i></td>
                <td><i>已投稿件&#8194;:&#8194;</i><i>{{item.personcount}}张</i></td>
              </tr>
              <tr>
                <td><i>单张预算&#8194;:&#8194;</i><i>{{item.lowcost}}~{{item.highcost}}</i></td>
                <td><i>反馈时间&#8194;:&#8194;</i><i>{{item.replaytime}}天</i></td>
              </tr>
              <tr>
                <td><i>福利&#8194;:&#8194;</i><i>{{item.gift}}</i></td>
              </tr>
            </table>
          </li>
        </ul>

        <!-- 发布活动 -->
        <ul v-for="item in activeList">
          <span class="homepage-left__d"><i>发布活动</i><i>{{item.createtime | UnixToNormalTime(2)}}</i></span>
          <li class="homepage-left__list">
            <span class="homepage-left__title"><a :href="'activity.html#/detail/'+item.actionid">{{item.title}}</a>&#12288;&#12288;{{item.startactivitytime | UnixToNormalTime(0)}}~{{item.endactivitytime | UnixToNormalTime(0)}}</span>
            <a :href="'activity.html#/detail/'+item.actionid" class="homepage-left__activity">
              <img :src="item.imgurl" alt="">
            </a>
          </li>
        </ul>
      </section>

      <section class="homepage-right">
        <ul>
          <li class="homepage-right__list">
            <span class="homepage-right__tit">个人资料</span>
            <dl>
              <dd><em>性别&#8194;:&#8194;</em><i>{{perSex | sexState}}</i></dd>
              <dd><em>地区&#8194;:&#8194;</em><i>{{perProvince}}&#12288;{{perCity}}</i></dd>
              <dd><em>简介&#8194;:&#8194;</em><i v-html="perIntroduce==null?'主人很懒，什么也没有留下':perIntroduce"></i></dd>
            </dl>
          </li>
          <li class="homepage-right__list">
            <span class="homepage-right__tit">个人微博</span>
            <dl>
              <span class="homepage-right__fan"><img src="./images/hm.png" alt=""></span><i class="homepage-right__num">(粉丝{{fanscount==null?0:fanscount}}人)</i>
            </dl>
          </li>
          <li class="homepage-right__list">
            <span class="homepage-right__tit">接稿经验</span>
            <dl>
              <i v-html="experdescrit == null?'主人很懒，什么也没有留下':experdescrit"></i>
            </dl>
          </li>
          <li class="homepage-right__list">
            <span class="homepage-right__tit">其他图站</span>
            <dl>
              <i v-html="perOthersite == null ? '主人很懒，什么也没有留下':perOthersite"></i>
            </dl>
          </li>
        </ul>
      </section>
    </section>
  </section>
</template>

<script>
  function scrollRise() {
    let top = window.scrollY;
    let home = document.getElementsByClassName("homepage-right")[0];
    if(home == undefined){
        return false; // 避免其他页面加载时出现错误提示
    }
    let w = document.documentElement.clientWidth | document.body.clientWidth
    let l = (w-1180)/2;
    if(top>=480){
      if(l>=0){
        home.style.position = "fixed";
        home.style.top = 0;
        home.style.right = l+"px";
      }else {
        home.style.position = "absolute";
        home.style.top = 0;
        home.style.right= 0;
      }
    }else {
      home.style.position = "absolute";
      home.style.top = 0;
      home.style.right= 0;
    }
  }
  window.onscroll = scrollRise;
  window.onresize = scrollRise;
  import Nosearch from 'components/Common/Nosearch' // 没有搜索内容的时候，展示的内容
  export default {
    data: () => {
      return {
        huazuoList:[],  // 发布画作
        huapuList:[],
        shejiList:[], // 发布设计
        yuegaoList:[], // 发布约稿
        zhenggaoList:[], // 发布征稿
        activeList:[],  // 发布活动
        perNickname:"",
        perSex:"",  // 性别
        perIntroduce:"",  // 个人简介
        perProvince:"",
        perCity:"",
        fanscount:"",
        experdescrit:"", // 个人接稿经验
        perOthersite:"", // 其他网站
        showNoImage:false,
      }
    },
    components:{
      Nosearch
    },
    filters:{
      sexState:function (val) {
        let state = ['男','女','保密'];
        return state[val-1];
      }
    },
    mounted(){
      this.getPersonalZiliao();
    },
    methods:{
      getPersonalZiliao(){
        let $uid = this.$route.fullPath.substring(this.$route.fullPath.lastIndexOf("/")+1);
        let _this = this;
        _this.getToken().then(token => { // 个人资料 右边栏
          _this.$http.get('user/findUserInfo', {
            params: {
              access_token: token,
              uid: $uid, // 作者本人  用户详细信息接口
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this.perNickname = res.data.data.list.nickname;
              _this.perSex = res.data.data.list.gender;
              _this.perIntroduce = res.data.data.list.introduce;
              _this.experdescrit   = res.data.data.list.experdescrit;
              _this.fanscount = res.data.data.list.fanscount;
              _this.perProvince = res.data.data.list.province;
              _this.perCity = res.data.data.list.city;
              _this.perOthersite = res.data.data.othersite;
            }
          }).catch(err => {
            console.log(err);
          });

          this.$http.get('user/finduserupdates', {
            params: {
              access_token: token,
              uid: $uid // 作者本人  用户详细信息接口
            }
          }).then(res => {
            if (res.data.stats === '2000' && res.data.msg === '成功') {
              if(res.data.data.list == null){
                this.showNoImage = true;
              }else {
                this.showNoImage = false;
                for(let i = 0; i < res.data.data.list.length; i++){
                  switch (res.data.data.list[i].type){
                    case '1': // 画作
                      _this.huazuoList.push({
                        actionid:res.data.data.list[i].actionid,
                        createtime:res.data.data.list[i].createtime,
                        title:res.data.data.list[i].title,
                        role:res.data.data.list[i].role,
                        introduce:res.data.data.list[i].introduce,
                        original:res.data.data.list[i].original,
                        tagsname:res.data.data.list[i].tagsname,
                        imgs:res.data.data.list[i].imgs
                      })
                      break;
                    case '2': //活动
                      _this.activeList.push({
                        actionid:res.data.data.list[i].actionid,
                        createtime:res.data.data.list[i].createtime,
                        title:res.data.data.list[i].title,
                        startactivitytime:res.data.data.list[i].startactivitytime,
                        endactivitytime:res.data.data.list[i].endactivitytime,
                        imgurl:res.data.data.list[i].imgurl
                      })
                      break;
                    case '3': //约稿
                      _this.yuegaoList.push({
                        actionid:res.data.data.list[i].actionid,
                        createtime:res.data.data.list[i].createtime,
                        title:res.data.data.list[i].title,
                        imgcount:res.data.data.list[i].imgcount,
                        lowcost:res.data.data.list[i].lowcost,
                        highcost:res.data.data.list[i].highcost,
                        endtime:res.data.data.list[i].endtime,
                        personcount:res.data.data.list[i].personcount,
                        tag:res.data.data.list[i].tag
                      })
                      break;
                    case '4':　// 设计
                      _this.shejiList.push({
                        title:res.data.data.list[i].title,
                        actionid:res.data.data.list[i].actionid,
                        createtime:res.data.data.list[i].createtime,
                        fastdesignday:res.data.data.list[i].fastdesignday,
                        longdesignday:res.data.data.list[i].longdesignday,
                        lowcost:res.data.data.list[i].lowcost,
                        highcost:res.data.data.list[i].highcost,
                        content:res.data.data.list[i].content,
                        imgs:res.data.data.list[i].imgs
                      })
                      break;
                    case '5': //征稿
                      _this.zhenggaoList.push({
                        actionid:res.data.data.list[i].actionid,
                        createtime:res.data.data.list[i].createtime,
                        title:res.data.data.list[i].title,
                        replaytime:res.data.data.list[i].replaytime,
                        lowcost:res.data.data.list[i].lowcost,
                        highcost:res.data.data.list[i].highcost,
                        endtime:res.data.data.list[i].endtime,
                        personcount:res.data.data.list[i].personcount,
                        tag:res.data.data.list[i].tag,
                        gift:res.data.data.list[i].gift
                      })
                      break;
                    case '6': //画铺
                      _this.huapuList.push({
                        title:res.data.data.list[i].title,
                        actionid:res.data.data.list[i].actionid,
                        createtime:res.data.data.list[i].createtime,
                        fastdesignday:res.data.data.list[i].fastdesignday,
                        longdesignday:res.data.data.list[i].longdesignday,
                        lowcost:res.data.data.list[i].lowcost,
                        highcost:res.data.data.list[i].highcost,
                        content:res.data.data.list[i].content,
                        imgs:res.data.data.list[i].imgs
                      })
                      break;
                  }
                }
              }
            }
          }).catch(err => {
            console.log(err);
          });
        })
      },
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../common/css/reset.css";
  @import "../../common/css/define";
  .bg {
    width: 100%;
    background-color: #fafafa;
    .homepage {
      width: 1180px;
      margin: 0 auto;
      padding: 0 0 20px 0;
      position: relative;
      .homepage-left {
        width: 840px;
        min-height: 550px;
        @include wrapShow;
      }
      .homepage-right {
        width: 320px;
        position: absolute;
        top: 0;
        right:0;
        @include wrapShow;
      }
    }
  }

  .homepage-left__d {
    height: 40px;
    line-height: 40px;
    padding-left: 20px;
    > i {
      font-size: 14px;
      color: $black;
      &:last-child {
        font-size: 12px;
        color: #999999;
        margin-left: 12px;
      }
    }
  }
  .homepage-left__list {
    background-color: $white;
    border-radius: 4px;
    padding-left: 20px;

    dl {
      dd {
        font-size: 12px;
        &:first-child {
          padding-top: 20px;
        }
        &:nth-of-type(2n) {
          margin: 12px 0;
        }
        em {
          color: #666666;
        }
        i {
          color: $black;
        }
      }
    }
    .homepage-left__img {
      margin-top: 20px;
      padding-bottom: 20px;
      img {
        width: 120px;
      }
    }
    .homepage-left__table {
      padding-bottom: 20px;
      tr {
        td {
          width: 275px;
          height: 25px;
          i {
            font-size: 14px;
            color: $black;
            &:last-child {
              color: $pink;
            }
          }
        }
      }
    }
    .homepage-left__title {
      display: block;
      padding-top: 20px;
      font-size: 16px;
      color: $black;
      font-weight: bold;
      a {
        color: $black;
      }
    }
    .homepage-left__sty {
      display: block;
      font-size: 12px;
      color: #999999;
      margin: 10px 0 14px;
    }
    .homepage-left__activity {
      display: block;
      width: 800px;
      height: 200px;
      margin-top: 12px;
      padding-bottom: 20px;
      > img {
        width: 100%;
        border: 1px solid rgba(128,128,128,0.2);
      }
    }
  }

  .homepage-right__list {

    .homepage-right__tit {
      font-size: 14px;
      color: $black;
      height: 40px;
      line-height: 40px;
      padding-left: 20px;
    }
    dl {
      background-color: $white;
      padding:20px 20px;
      font-size: 12px;
      dd {
        em {
          display: inline-block;
          color: #666666;
          line-height: 20px;
        }
        i {
          display: inline-block;
          color: $black;
          line-height: 20px;
        }
        &:nth-of-type(2) {
          margin: 8px 0;
        }
        &:last-child {
          em {
            vertical-align: top;
          }
          i {
            width: 240px;
            text-align: justify;
          }
        }
      }
      .homepage-right__fan {
        display: inline-block;
        height: 22px;
        vertical-align: middle;
        > img {
          height: 100%;
          vertical-align: middle;
        }
      }
      .homepage-right__num {
        vertical-align: middle;
      }
    }
    &:nth-of-type(3),&:last-child {
      i {
        line-height: 20px;
        text-align: justify;
      }
    }
  }
</style>



