<!-- 2017/3/22- 约稿通知 应征通知-->
<template>
  <section class="join">
    <ul>
      <li class="join__list clearfix" v-for="item in actList">
        <section style="height: 60px">
          <section class="join__head"><img :src="item.avatar" alt=""></section>
          <section class="join__l">
            <div class="join__per"><a :href="'personal.html#/accounts/products/'+item.uid" target="_blank">{{item.nickname}}</a><i>参加了我的活动</i><a :href="'activity.html#/detail/'+item.moduleid" target="_blank">{{item.title}}</a></div>
            <div class="join__author">{{item.pictit}}</div>
          </section>
          <section class="join__r">{{item.createtime | UnixToNormalTime(1)}}</section>
        </section>

        <section class="join__work">
          <a :href="'activity.html#/detail/'+item.moduleid" target="_blank"><img :src="JSON.parse(item.imgs)[0]" alt=""></a>
        </section>
      </li>
    </ul>
    <Pagination :pageCount="actPageCount" :pageSize="actPageSize" @pageChange="actPageChange"></Pagination>
  </section>

</template>

<script>
  import Pagination from 'components/Common/Pagination' //分页组件
  export default {
    props:["actList","actPageCount","actPageSize"],
    components:{
      Pagination
    },
    methods:{
      actPageChange(val){
        this.$emit("actPageChange",val)
      }
    }
  }
</script>


<style lang="scss" scoped>
  @import "../../../common/css/reset.css";
  @import "../../../common/css/define";

  .join {
    width: 920px;
  }
  .join__list {

    padding-bottom: 20px;
    margin-bottom: 20px;
    border-bottom: 1px solid $lightgrey;
    .join__head {
      float: left;
      > img {
        width: 40px;
        height: 40px;
        border-radius:50%
      }
    }
  }

  .join__l {
    float: left;
    margin-left:16px;

  }
  .join__per {
    >i {
      margin: 0 15px;
      font-size: 12px;
      color: #a3a3a3;
    }
    > a {
      font-weight: bold;
      font-size: 14px;
      color: $black;
    }
  }
  .join__author {
    font-size: 14px;
    color: #666666;
    margin-top: 6px;
  }
  .join__r {
    float: right;
    display: block;
    height: 18px;
    line-height: 18px;
    font-size: 12px;
    color: #999999;
    text-align: center;
  }

  .join__work {
    padding-left: 55px;
     img {
      width: 130px;
      height: 190px;
      display: inline-block;
    }
  }
</style>



