<!-- 2017/3/22- yyy -约稿信息 -->
<template>
  <section class="search clearfix">
    <input type="text" name="" id="" v-model="searchContent"  value="" placeholder="请输入要搜索的内容"/>
    <span @click="searchResult"></span>
  </section>
</template>

<script>
  export default {
    props:["searchContent"],
    methods:{
      searchResult(){
        this.$emit('searchIcon');
      }
    }
  }
</script>

<style lang="scss" scoped>

  @import "../../common/css/reset.css";
  @import "../../common/css/define";

  .search {
    height: 40px;
    input {
      width: 365px;
      height: 40px;
      box-sizing: border-box;
      border: 1px solid #e6e6e6;
      border-right: none;
      float: left;
      border-top-left-radius: 4px;
      border-bottom-left-radius: 4px;
      font-size: 12px;
      color: #cccccc;
      padding-left: 10px;
      &:focus {
        color: $black;
        border:1px solid $pink;
        transition: all 0.3s linear;
      }
    }
    span {
      display: inline-block;
      float: left;
      width: 65px;
      height: 40px;
      cursor: pointer;
      background:url("images/search.png") center no-repeat;
      background-color: $pink;
      border-top-right-radius: 4px;
      border-bottom-right-radius: 4px;
    }
  }

</style>



