<!-- created by YYY 2017.3.28 -->
<template>
  <section class="personal-nav">
    <img class="personal__img" src="./images/bg.png" alt="">
    <section class="clearfix">
      <ul class="personal__ul">
        <li v-for="item,index in PersonalNav" class="personal__list" @click="tabSwitch(index,item.tab)" :class="{active:item.bol}">{{item.name}}</li>
      </ul>
      <div class="personal__fn">
        <i>关注</i><i>{{followcount==null?0:followcount}}</i><i>粉丝</i><i>{{fanscount==null?0:fanscount}}</i>
      </div>
    </section>


    <!-- 定位的部分 -->
    <section class="personal__loc">
      <span class="personal__head"><img :src="avatar" alt=""></span>
      <section class="personal__author">
        <div><h4>{{nickname}}</h4><span><img :src="perSex | sexState"></span></div>
        <p>{{role | identity}}</p>
        <p v-html="introduce==null?'主人很懒，什么也没有留下':introduce"></p>
      </section>
      <section class="personal__contact">
        <span>+关注</span>
        <span><a :href="'message.html#/privateType/'+uid+'&'+nickname">私信</a></span>
        <CopyQqDialogue :info="{name:nickname,qq:qq}" class="personal__qq"></CopyQqDialogue>
      </section>
      <span class="personal__bg"></span>
    </section>
  </section>

</template>

<script>
  import CopyQqDialogue from 'components/Common/CopyQqDialogue' // 复制qq时，弹窗
  export default {
    props:["PersonalNav"],
    data:()=>{
      return {
        perSex:"",
        nickname:"",
        introduce:"",
        avatar:"",
        fanscount:"",
        followcount:"",
        role:"",
        uid:"",
      }
    },
    components:{
      CopyQqDialogue
    },
    filters:{
      sexState:function (val) {
        let state = ['http://www.easyicon.net/api/resizeApi.php?id=522315&size=64','http://www.easyicon.net/api/resizeApi.php?id=522314&size=64',''];
        return state[val-1]
      },
      identity:function (val) {
        let state = ['画手','商家','其他'];
        return state[val-1]
      }
    },
    mounted(){
      this.getPersonalZiliao();
    },
    methods:{
      tabSwitch(index,type){
        this.$emit('HomepageNav',index,type);
      },
      getPersonalZiliao(){
        let $uid = this.$route.fullPath.substring(this.$route.fullPath.lastIndexOf("/")+1);
        let _this = this;
        _this.getToken().then(token => { // 发布画作
          _this.$http.get('user/findUserInfo', {
            params: {
              access_token: token,
              uid: $uid, // 作者本人  用户详细信息接口
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this.perSex = res.data.data.list.gender;
              _this.nickname = res.data.data.list.nickname;
              _this.introduce = res.data.data.list.introduce;
              _this.avatar = res.data.data.list.avatar;
              _this.fanscount = res.data.data.list.fanscount;
              _this.followcount = res.data.data.list.followcount;
              _this.role = res.data.data.list.role;
              _this.qq = res.data.data.list.qq;
            }
          }).catch(err => {
            console.log(err);
          });
        })
      },
    }
  }
</script>

<style lang="scss" scoped>

  @import "../../common/css/reset.css";
  @import "../../common/css/define";
  .fade-enter-active, .fade-leave-active {
    transition: opacity 0.3s
  }
  .fade-enter, .fade-leave-active {
    opacity: 0
  }
  .personal-nav {
    border: 1px solid $lightgrey;
    border-radius:4px;
    @include wrapShow;
    position: relative;
    .personal__img {
      width: 1180px;
      height: 200px;
      display: block;
    }
    .personal__ul {
      float: left;
      padding-left: 145px;
      height: 40px;
      .personal__list {
        width: 90px;
        text-align: center;
        display: inline-block;
        border-bottom: 2px solid transparent;
        line-height: 40px;
        font-size: 14px;
        color: $black;
        cursor: pointer;
        &:hover {
          color: #e86987;
          font-weight: bold;
          border-bottom: 2px solid #e86987;
          transition: 0.5s;
        }
      }
      .active {
        color: #e86987;
        font-weight: bold;
        border-bottom: 2px solid #e86987;
        transition: 0.5s;
      }
    }
    .personal__fn {
      float: right;
      height: 40px;
      >i {
        display: inline-block;
        line-height: 40px;
        font-size: 12px;
        &:nth-of-type(2n-1) {
          color: #999999;
          margin-right: 8px;
        }
        &:nth-of-type(2n) {
          color: #e86998;
          font-weight: bold;
          margin-right: 20px;
        }
        &:nth-of-type(3):before{
          content: "|";
          padding-right: 20px;
        }
      }
    }
    .personal__loc {
      .personal__head {
        display: block;
        width: 120px;
        height: 120px;
        border-radius: 50%;
        position: absolute;
        border: 5px solid rgba(150,150,150,0.2);
        top:105px;
        left:20px;
        z-index: 10;
        >img {
          display: block;
          width: 120px;
          height: 120px;
          border-radius: 50%;
        }
      }
      .personal__author {
        position: absolute;
        top: 105px;
        left: 165px;
        z-index: 10;
        > div {
          h4 {
            display: inline-block;
            font-size: 22px;
            color: $white;
            margin: 0 0 10px 0;
          }
          span {
            display: inline-block;
            width: 12px;
            height: 12px;
            padding: 2px;
            background-color: #03a9f4;
            border-radius: 2px;
            vertical-align: text-bottom;
            margin-left: 10px;
            > img {
              display: inline-block;
              width: 12px;
              height: 12px;
              vertical-align: super;
            }
          }
        }
        p {
          color: $white;
          font-size: 12px;
          &:last-child {
            margin-top: 15px;
            font-size: 14px;
          }
        }
      }
      .personal__contact {
        position: absolute;
        right: 20px;
        top: 155px;
        z-index: 10;
        span {
          display: inline-block;
          font-size: 12px;
          color: $white;
          text-align: center;
          border-radius: 4px;
          cursor: pointer;
          &:first-child {
            width: 48px;
            height: 22px;
            line-height: 22px;
            background-color: #8bc34a;
          }
          &:nth-of-type(2){
            width: 36px;
            height: 22px;
            background-color: rgba(50,25,35,0.6);
            box-sizing: border-box;
            line-height: 22px;
          }
          &:nth-of-type(2) {
            margin: 0 8px;
            a {
              color: $white;
            }
          }
        }
        .personal__qq {
          cursor: pointer;
          font-size: 13px;
          border-radius: 4px;
          color: $white;
          display: inline-block;
          width: 36px;
          height: 22px;
          background-color: rgba(50,25,35,0.6);
          box-sizing: border-box;
          line-height: 20px;
          text-align: center;

        }
      }
      textarea {
        width: 0;
        height: 0;
        overflow: hidden;
        resize: none;
      }
      .personal__bg {
        width: 1180px;
        height: 200px;
        display: block;
        position: absolute;
        top: 0;
        left:0;
        background-image: linear-gradient(to top,rgba(196,196,196,0.3) 0,rgba(250,250,250,0.1) 100%);
        z-index: 5;
      }
    }
  }

</style>




