<!-- 2017/3/21- 提醒关注我的 -->
<template>
  <section class="payme">
    <ul>
      <li class="payme__list clearfix" v-for="item in payMeList">
        <section class="payme__l">
          <div class="payme__head"><a :href="'personal.html#/accounts/products/'+item.uid" target="_blank"><img :src="item.avatar" alt=""></a></div>
          <a :href="'personal.html#/accounts/products/'+item.uid" target="_blank">{{item.nickname}}</a>
          <i>关注了我</i>
        </section>
        <section class="payme__r">{{item.createtime | UnixToNormalTime(1)}}</section>
      </li>
    </ul>
    <Pagination :pageCount="payPageCount" :pageSize="payPageSize" @pageChange="payPageChange"></Pagination>
  </section>
</template>

<script>
  import Pagination from 'components/Common/Pagination' //分页组件
  export default {
    props:["payMeList","payPageCount","payPageSize"],
    components:{
      Pagination
    },
    methods:{
      payPageChange(val){
        this.$emit("payPageChange",val)
      }
    }
  }

</script>

<style lang="scss" scoped>
  @import "../../../common/css/reset.css";
  @import "../../../common/css/define";

  .payme__list {
    padding-bottom: 20px;
    margin-bottom: 20px;
    border-bottom: 1px solid $lightgrey;
  }

  .payme__head {
    overflow: hidden;
    display: inline-block;
    vertical-align: middle;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    overflow: hidden;
    a {
      display: block;
      width: 100%;
      height: 100%;
      > img {
        width: 100%;
        height: 100%;
      }
    }
  }
  .payme__l {
    float: left;
    > a {
        display: inline-block;
        font-size: 14px;
        color: $black;
        font-weight: bold;
        vertical-align: middle;
        margin: 0 16px;
      }
    > i {
        display: inline-block;
        vertical-align: middle;
        font-size: 14px;
        color: #666666;
      }
  }

  .payme__r {
    font-size: 12px;
    color: #999999;
    line-height: 40px;
    float: right;
  }
</style>




