<!--发布活动组件-->
<template>
  <div class="activity-send">
    <h6 class="activity-send__title">发布活动</h6>
    <section class="l-activity">
      <dl class="activity-send__item">
        <dd class="activity-send__pic">
          <h6 class="activity-send__h6">&#12288;&#12288;封面</h6>
          <section class="activity-send__commit">
            <input @change='handleChange($event)' type="file" v-show="false" id="pic"/>
            <label for="pic">上传图片</label>
            <p>请上传1200*360像素的图片</p>
          </section>
          <section class="activity-send__rest"
                   v-if="imgList.length >0">
            <section>
              <img v-for="item in imgList" :src="item" alt=""/>
            </section>
            <p>点击上传封面可重新上传</p>
          </section>
        </dd>
        <dd class="activity-send__name">
          <h6 class="activity-send__h6">活动标题</h6>
          <input type="text" placeholder="标题不超过25个子"
                 v-model="submitData.title"/>
        </dd>
        <dd class="activity-send__start">
          <h6 class="activity-send__h6">&#12288;投稿期</h6>
          <DatePicker @onSubmitEndTime="onSendTimeStart"></DatePicker>
          -
          <DatePicker @onSubmitEndTime="onSendTimeEnd"></DatePicker>
        </dd>
        <dd class="activity-send__end">
          <h6 class="activity-send__h6">&#12288;评选期</h6>
          <DatePicker @onSubmitEndTime="onChooseTimeStart"></DatePicker>
          -
          <DatePicker @onSubmitEndTime="onChooseTimeEnd"></DatePicker>
        </dd>
        <dd class="activity-send__notice">
          <h6 class="activity-send__h6">公布日期</h6>
          <DatePicker @onSubmitEndTime='onOpenTime'></DatePicker>
        </dd>
        <dd class="activity-send__detail">
          <h6 class="activity-send__h6">活动详情</h6>
          <section class="activity-send__content">
            <Editor @onContent='onContent'></Editor>
          </section>
        </dd>
        <dd class="activity-send__btn" @click="handleSendActivity">发布</dd>
      </dl>
    </section>
  </div>
</template>

<!--数据交互-->
<script>
  import DatePicker from '../../components/Common/DatePicker.vue'
  import Editor from '../../components/Common/Editor.vue'
  import {Message} from 'element-ui';

  export default {
    components: {
      DatePicker,
      Editor
    },
    data: () => ({
      content: '',
      file: '',
      uid: '', //登录用户id
      imgList: [], //上传图片
      submitData: {
        access_token: '',     //接口认证
        uid: '',          //登录用户id
        title: "",              //活动标题
        imgurl: '',             //封面图
        content: '',             //活动内容
        startactivitytime: '',   //活动投稿期开始时间
        endactivitytime: '',     //活动投稿期结束时间
        startchosetime: '',      //评选期开始时间
        endchosetime: '',      //评选期结束时间
        publictime: '',      //公布日期
      }
    }),
    created(){
      //判断用户是否登录
      this.isLogin();
    },
    methods: {
      //封面图片上传
      handleChange(event){
        this.file = event.target.files[0];
        let url = window.URL.createObjectURL(event.target.files[0]);
        this.imgList.splice(0, 1, url);
      },

      //监听富文本框内容
      onContent(data){
        this.submitData.content = data;
      },
      handleSendActivity(){
        let _this_ = this;
        this.getToken().then(token => {
          _this_.submitData.access_token = token;
          new Promise(resolve => { //图片提交成功后才能提交接口数据
            this.$http.get('common/uptoken', {
              params: {
                access_token: token
              }
            }).then(res => {
              const token = res.data.data.uptoken;
              const url = 'http://up.qiniu.com';
              _this_.Qiniu_upload(this.file, token, url).then(res => {
                resolve(res);
              })
            })
          }).then(res => {
            _this_.submitData.imgurl = res;
            _this_.submitData.uid = _this_.uid;
            this.$http('huodong/addhuodong', {
              params: _this_.submitData
            }).then(res => {

              if (res.data.stats === '2000') { //发布成功跳转到详情页
                location.href = `/module/activity.html#/detail/${res.data.data.id}`;
              } else if (res.data.data.error === '用户未登录') { //发布未登录
                Message({
                  message: '登录状态已过期，请先登录！',
                  type: 'error',
                  duration: 2000
                });
                setTimeout(() => {
                  location.href = '/module/loginRegister.html#/';
                }, 2000)
              } else {
                Message({ //其他错误提示
                  message: `${res.data.msg}`,
                  type: 'error',
                  duration: 2000
                });
              }
            }).catch(err => {
              console.log(err);
            })
          });
        })
      },
      onSendTimeStart(val){ //投稿期开始时间
        this.submitData.startactivitytime = val;
      },
      onSendTimeEnd(val){ //投稿结束时间
        this.submitData.endactivitytime = val;
      },
      onChooseTimeStart(val){ //评选开始时间
        this.submitData.startchosetime = val;
      },
      onChooseTimeEnd(val){ //评选结束时间
        this.submitData.endchosetime = val;
      },
      onOpenTime(val){ //评选公开时间
        this.submitData.publictime = val;
      }
    }
  }
</script>

<!--组件样式-->
<style lang="scss" scoped>
  @import "../../common/css/define";

  .activity-send {
    .activity-send__h6 {
      display: inline-block;
      font-size: 14px;
      color: $black;
      margin-right: 16px;
    }
  }

  .l-activity {
    padding: 40px;
    max-width: 1100px;
    background-color: #fff;
    margin: 0 auto;
    @include wrapShow;
  }

  .activity-send__title {
    font-size: 20px;
    max-width: 1180px;
    margin: 40px auto 20px;
  }

  .activity-send__item {
    max-width: 600px;
    margin: 0 auto;
    > dd {
      margin-bottom: 40px;
      &:nth-last-child(1) {
        margin-bottom: 0;
      }
    }
    .activity-send__pic {
      .activity-send__commit {
        display: inline-block;
        > label {
          cursor: pointer;
          display: inline-block;
          border-radius: 4px;
          width: 120px;
          height: 50px;
          font-size: 14px;
          line-height: 50px;
          text-align: center;
          color: #fff;
          margin: 0 20px;
          @include boxShow;
          @include linear;
        }
        > p {
          display: inline-block;
          color: $gray;
          font-size: 14px;
        }
      }
      .activity-send__rest {
        margin: 20px 0 0 0px;
        width: 560px;
        height: 164px;
        padding: 10px 20px;
        background-color: #fafafa;
        > section {
          width: 560px;
          height: 140px;
          overflow: hidden;
          > img {
            width: 100%;
          }
        }
        > p {
          font-size: 14px;
          color: $gray;
          text-align: center;
          line-height: 30px;
        }
      }
    }
    .activity-send__name {
      > input {
        width: 480px;
        height: 40px;
        border: 1px solid #e6e6e6;
        border-radius: 4px;
        font-size: 14px;
        text-indent: 1em;
      }
    }
    .activity-send__start {
      .date {
        display: inline-block;
      }
    }
    .activity-send__end {
      .date {
        display: inline-block;
      }
    }
    .activity-send__notice {
      .date {
        display: inline-block;
      }
    }
    .activity-send__detail {
      .activity-send__content {
        display: inline-block;
        width: 480px;
        vertical-align: top;
      }
    }
    .activity-send__btn {
      width: 120px;
      height: 50px;
      line-height: 50px;
      font-size: 14px;
      color: #fff;
      text-align: center;
      border-radius: 4px;
      margin-left: 76px;
      cursor: pointer;
      @include boxShow;
      @include linear;
    }
  }
</style>



