<template>
  <section>
    <section class="workicon">
      <personalNav :ListComNav="ListComNav" @homeCom="tabSwitch" class="workicon__nav"></personalNav>
      <keep-alive>
        <component :is='currentView'
                   :loadOver="loadOver"
                   @loadMore="loadMore"
                   :items="lists"
                   :loadMoreShow="loadMoreShow"
                   :ComShop="ShopEveryList"
                   :shopPageCount="shopPageCount"
                   :shopPageSize="shopPageSize"
                   @shopPageChange="shopPageChange"
                   :ComSell="SellEveryList"
                   :sellPageCount="sellPageCount"
                   :sellPageSize="sellPageSize"
                   :link="link"
                   @sellPageChange="sellPageChange"
                   :ComCommercial="CommercialEveryList"
                   :commerPageCount="commerPageCount"
                   :commerPageSize="commerPageSize"
                   @commerPageChange="commerPageChange"
        ></component>
      </keep-alive>
      <div class="workicon__propmt" v-if="workiconPropmt">暂无数据</div>
    </section>
  </section>
</template>


<script>
  import personalNav from 'components/Personal/ComNav';
  import ComPaint from 'components/Common/Flow';  // 画作公共部分
  import ComShop from 'components/Common/ComShop';  // 画铺公共部分
  import ComSell from 'components/Common/ComSell';
  import ComCommercial from 'components/Personal/ComCommercial';  // 已出售设计
  export default {
    data: () => {
      return {
        ListComNav:[
          {name:"画作",bol:true,tab:"ComPaint"},
          {name:"画铺",bol:false,tab:"ComShop"},
          {name:"待出售设计",bol:false,tab:"ComSell"},
          {name:"已商用设计",bol:false,tab:"ComCommercial"},
        ],
        currentView:ComPaint,
        workiconPropmt:false,
        loadOver:"点击加载更多...",
        loadMoreShow:false,
        lists:[],  // 动态获取数据
        paintPagesize:8,
        paintIndex:1,
        paintLastCount:0,
        ShopEveryList:[],
        shopPageSize:30,  // 个人主页-->作品-->画铺每页个数
        shopPageCount:0, // 个人主页-->作品-->画铺总个数
        shopPageIndex:1,
        shopLastCount:0,
        link:"/design",
        SellEveryList:[],
        sellPageSize:30,  // 个人主页-->作品-->待出售设计每页个数
        sellPageCount:0, //个人主页-->作品-->待出售设计总个数
        sellPageIndex:1,
        sellLastCount:0,
        CommercialEveryList:[],
        commerPageSize:30,  // 个人主页-->作品-->已商用每页个数
        commerPageCount:0, // 个人主页-->作品-->已商用总个数
        commerPageIndex:1,
        commerLastCount:0,
      }
    },
    components:{
      personalNav,
      ComPaint,
      ComShop,
      ComSell,
      ComCommercial
    },
    mounted(){
      this.getWorkList();
      this.getPaintList();
    },
    methods:{
      tabSwitch(index){
        for(let i = 0; i < this.ListComNav.length; i++){
          this.ListComNav[i].bol = false;
          if(index === i){
            this.ListComNav[i].bol = true;
            this.currentView = this.ListComNav[i].tab;
          }
        }
        switch (index){
          case 0:
            this.workiconPropmt = false;
            break;
          case 1:
            (this.shopLastCount === 0)? (this.workiconPropmt = true) : (this.workiconPropmt = false);
            break;
          case 2:
            (this.sellLastCount === 0)? (this.workiconPropmt = true) : (this.workiconPropmt = false);
            break;
          case 3:
            (this.commerLastCount === 0)? (this.workiconPropmt = true) : (this.workiconPropmt = false);
            break;
        }
      },
      shopPageChange(val){
        this.shopPageIndex = val;
        this.getWorkList();
      },
      sellPageChange(val){
        this.sellPageIndex = val;
        this.getWorkList();
      },
      commerPageChange(val){
        this.commerPageIndex = val;
        this.getWorkList();
      },
      loadMore(){ // 加载更多图片
        this.paintIndex++;
        this.getPaintList();
      },
      getPaintList(){
        let _this = this;
        _this.getToken().then(token => {
          this.$http.get('huazuo/findAllhuazuo', {  // 作品-->画作
            params: {
              access_token: token,
              uid: 8, // 作者本人  用户详细信息接口
              order: 'createtime',
              page: _this.paintIndex,
              show: _this.paintPagesize
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              this.lists = res.data.data.list;
              this.workiconPropmt = false;
              this.loadMoreShow = true;
              this.paintLastCount = res.data.data.list.length;
              if(res.data.data.list.length === 0){
                this.loadOver = "全部加载完毕或暂无数据";
              }
            }
          }).catch(err => {
            console.log(err);
          });
        })
      },
      getWorkList(){
        let _this = this;
        _this.getToken().then(token => {
          this.$http.get('huapu/findAllhuapu', {  // 作品-->画铺
            params: {
              access_token: token,
              uid: 64, // 作者本人  用户详细信息接口
              page:_this.shopPageIndex,
              show: _this.shopPageSize
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this.ShopEveryList = res.data.data.list;
              _this.shopPageCount = parseInt(res.data.data.total);
              _this.shopLastCount = res.data.data.list.length;
            }
          }).catch(err => {
            console.log(err);
          });

          this.$http.get('sheji/findallsheji', {  // 作品--> 待出售设计
            params: {
              access_token: token,
              busintype: 1, //  待出售设计为  1
              uid:9529,
              page: _this.sellPageIndex,
              show: _this.sellPageSize
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this.SellEveryList = res.data.data.list;
              _this.sellPageCount = parseInt(res.data.data.total);
              _this.sellLastCount = res.data.data.list.length;
            }
          }).catch(err => {
            console.log(err);
          });

          this.$http.get('sheji/findallsheji', {  // 作品--> 待出售设计
            params: {
              access_token: token,
              busintype: 2, // 已商用设计为 2
              uid:9529,
              page: _this.commerPageIndex,
              show: _this.commerPageSize
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this.CommercialEveryList = res.data.data.list;
              _this.commerPageCount = parseInt(res.data.data.total);
              _this.commerLastCount = res.data.data.list.length;
            }
          }).catch(err => {
            console.log(err);
          });

        })
      }
    }
  }

</script>

<style lang="scss" scoped>

  @import "../../common/css/reset.css";
  @import "../../common/css/define";

  .workicon__nav {
    margin: 40px 0 20px 0;
  }
  .workicon__propmt {
    width: 1180px;
    text-align: center;
    margin: 30px auto;
    font-size: 16px;
    font-style: italic;
    color: $paleBlue;
    cursor: pointer;
  }

</style>



