<!-- 2017/3/21- 赞 -->
<template>
  <section class="payme">
    <ul>
      <li class="payme__list clearfix" v-for="item in receiveList">
        <section class="payme__l">
          <div class="payme__head"><a :href="'personal.html#/accounts/products/'+item.uid" target="_blank"><img :src="item.avatar" alt=""></a></div>
          <a :href="'personal.html#/accounts/products/'+item.uid" target="_blank">{{item.nickname}}</a>
          <span v-if="item.type === 'design'"> <!-- 设计收到的赞 -->
            <i>赞了我的设计</i>
            <a :href="'design.html#/pendingDet/'+item.moduleid" target="_blank">{{item.title}}</a>
          </span>
          <span v-if="item.type === 'activity'"> <!-- 活动收到的赞 -->
            <i>赞了我的活动</i>
            <a :href="'activity.html#/detail/'+item.moduleid" target="_blank">{{item.title}}</a>
          </span>
          <span v-if="item.type === 'notes'"> <!-- 约稿收到的赞 -->
            <i>赞了我的约稿</i>
            <a :href="'pact.html#/pactDetail/'+item.moduleid" target="_blank">{{item.title}}</a>
          </span>
          <span v-if="item.type === 'paintshop'"> <!-- 画铺收到的赞 -->
            <i>赞了我的画铺</i>
            <a :href="'pact.html#/painting/'+item.moduleid" target="_blank">{{item.title}}</a>
          </span>
          <span v-if="item.type === 'papers'"> <!-- 征稿收到的赞 -->
            <i>赞了我的征稿</i>
            <a :href="'design.html#/collectDet'+item.moduleid" target="_blank">{{item.title}}</a>
          </span>
          <span v-if="item.type === 'picture'"><!-- 画作收到的赞 ,调到画作详情页面-->
            <i>赞了我的画作</i>
            <a :href="'painting.html#/detail/'+item.moduleid" target="_blank">{{item.title}}</a>
          </span>
        </section>
        <section class="payme__r">{{item.createtime | UnixToNormalTime(1)}}</section>
      </li>
    </ul>
    <Pagination :pageCount="receivePageCount" :pageSize="receivePageSize" @pageChange="receivePageChange"></Pagination>
  </section>
</template>

<script>
  import Pagination from 'components/Common/Pagination' //分页组件
  export default {
    props:["receiveList","receivePageCount","receivePageSize"],
    components:{
      Pagination
    },
    methods:{
      receivePageChange(val){
        this.$emit("receivePageChange",val)
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../../common/css/reset.css";
  @import "../../../common/css/define";

  .payme__list {
    padding-bottom: 20px;
    margin-bottom: 20px;
    border-bottom: 1px solid $lightgrey;
  }


  .payme__l {
    float: left;
    a {
      display: inline-block;
      font-size: 14px;
      color: $black;
      font-weight: bold;
      vertical-align: middle;
      margin: 0 16px;
    }
    i {
      display: inline-block;
      vertical-align: middle;
      font-size: 14px;
      color: #a3a3a3;
    }
    .payme__head {
      overflow: hidden;
      display: inline-block;
      vertical-align: middle;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      a {
        margin: auto;
      }
      > img {
        width: 100%;
      }
    }
  }

  .payme__r {
    font-size: 12px;
    color: #999999;
    line-height: 40px;
    float: right;
  }
</style>




