<!--header-->
<template>
    <section class="l-wrap">
      <img src="./image/banner-bot.png">
      <section class="l-header">
        <section class="header clearfix">
          <ul class="header__menu">
            <li v-for="(item,key) in menu"><a :href='item'>{{key}}</a></li>
          </ul>
          <ul class="header__login clearfix" v-if="LoginStats">
            <li>发布
              <ul class="header__hover">
                <li><a href="painting.html#/new/painting">画作</a></li>
                <li><a href="painting.html#/new/shop">画铺</a></li>
                <li><a href="painting.html#/new/design">设计</a></li>
                <li><a href="painting.html#/new/pact">约稿</a></li>
                <li><a href="painting.html#/new/collect">征稿</a></li>
                <li><a href="painting.html#/new/activity">活动</a></li>
              </ul>
            </li>
            <li>消息 <img src="./image/bell.png" alt="" :class="{iconAnimatedBell:bellBol}">
              <ul class="header__hover" v-if="messageBol">
                <li><a href="message.html#/private">私信<i v-if="privateAllNum>0?true:false">({{privateAllNum}})</i></a></li>
                <li><a href="message.html#/remind">提醒<i v-if="remindAllNum>0?true:false">({{remindAllNum}})</i></a></li>
                <li><a href="message.html#/yuegao">约稿通知<i v-if="(parseInt(manuscriptsYingzheng)+parseInt(manuscriptsAllResfue))>0?true:false">({{manuscriptsYingzheng+manuscriptsAllResfue}})</i></a></li>
                <li><a href="message.html#/zhenggao">征稿通知<i v-if="(parseInt(collectmanuAllTougao)+parseInt(collectmanuAllResfue))>0?true:false">({{parseInt(collectmanuAllTougao)+parseInt(collectmanuAllResfue)}})</i></a></li>
                <li><a href="message.html#/activity">活动通知<i v-if="(parseInt(activityAllJoin)+parseInt(activityAllWin))>0?true:false">({{parseInt(activityAllJoin)+parseInt(activityAllWin)}})</i></a></li>
                <li><a href="message.html#/system">系统<i v-if="systemAllNum>0?true:false">({{systemAllNum}})</i></a></li>
              </ul>
            </li>
            <li>次元梦境
              <ul class="header__hover">
                <li><a :href="'personal.html#/accounts/products/'+uid">个人主页</a></li>
                <li><a href="management.html">管理中心</a></li>
                <li @click="logOff"><a href="javascript:void(0)">退出登录</a></li>
              </ul>
            </li>
          </ul>
          <ul class="header__login header__notlogin clearfix" v-if="notLoginStats">
            <li><a href="loginRegister.html">登录</a></li>
          </ul>
          <app-search></app-search>
        </section>
      </section>
      <transition name="fade">
        <DeleteYesNo :deleteYesNo="deleteYesNo" :deletePrompt="deletePrompt" @deleteYes="deleteYes(type)" @deleteNo="deleteNo"></DeleteYesNo>
      </transition>
    </section>
</template>

<script>
  import appSearch from './Search'
  import store from '../../common/store/'
  import DeleteYesNo from 'components/Common/DeleteYesNo' // 弹窗
  export default {
    name: 'app-header',
    components: {
      appSearch,
      DeleteYesNo
    },
    data: () => {
      return {
        menu: {
          '首页': 'index.html', '画作': 'painting.html',
          '活动': 'activity.html', 'Q群': 'group.html', '约稿平台': 'pact.html',
          '设计平台': 'design.html'
        },
        uid:"11648",    // 自己登录者的id
        isShow: false,
        LoginStats: true, // 登录时显示的状态
        notLoginStats: false, // 登录状态下切换到(个人主页、管理中心、推出)
        messageBol:true, // 当显示不是个人信息页的时候，true；  否则未false
        bellBol:false, // 当有消息时，铃铛晃动，消息未0时，铃铛不晃动
        privateAllNum:0, // 记录未读私信总个数
        remindAllNum:0, // 评论未读条数
        manuscriptsYingzheng:0,
        manuscriptsAllResfue:0,
        collectmanuAllTougao:0,
        collectmanuAllResfue:0,
        activityAllJoin:0,
        activityAllWin:0,
        systemAllNum:0,
        deleteYesNo:false,
        deletePrompt:"是否退出登录", //删除时弹窗的提示语
      }
    },
    mounted(){
//      this.getNotRead();  // 用时在打开
      this.judgment();
    },
    methods: {
      judgment(){
        // 如果在message.html页面，不显示下拉私信列表，否则显示
        if(document.location.href.indexOf("message.html") !== -1){
          this.messageBol = false
        }else {
          this.messageBol = true;
        }
        // 通过cookie里面是否存在mshm_cxk，判断是否登录,如果存在表示已经登录，否则未登录
        if(this.Cookie.Read_cookie('mshm_cxk')){
          this.LoginStats = true;
          this.notLoginStats = false;
        }else {
          this.LoginStats = true;//是否显示次元梦境
          this.notLoginStats = false;//是否显示登录按钮
        }
      },
      logOff(){
        this.deleteYesNo = true;
      },
      deleteYes(){
        this.getToken().then(token=> {
          this.$http.get('login/logout', {  //  确认退出接口
            params: {
              access_token: token,
            }
          }).then(res => {
            if (res.data.stats === '2000'&& res.data.msg === "成功") {
              this.deleteYesNo = false;
              this.LoginStats = false;  // 未登录状态  切换状态
              this.notLoginStats = true; // 登录状态
              // 清楚执行清楚cookie操作
              if(document.location.href.indexOf("painting.html") !== -1 || document.location.href.indexOf("message.html") !== -1 || document.location.href.indexOf("management.html") !== -1){
                window.location.href = 'http://localhost:8080/module/loginRegister.html#/';
              }else{
                window.location.reload(); // 执行这一步之前先清除cookie，再刷新本页面，即未登录状态
              }
            }
          }).catch(err => {
            console.log(err);
          });
        })
      },
      deleteNo(){
        this.deleteYesNo = false;
      },
      getNotRead(){
        let _this = this;
        this.getToken().then(token => {
          this.$http.get('message/countLetterRead', {  // 私消未读通知
            params: {
              access_token: token,
              uid: 8922,
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this.privateAllNum = res.data.data.count;
              localStorage.privateAllNum = res.data.data.count;
            }
          }).catch(err => {
            console.log(err);
          });

          this.$http.get('comment/showmycomment', {  // 评论
            params: {
              access_token: token,
              pid:0,
              uid:7
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              let a = 0; // 评论未读条数
              res.data.data.list.forEach((item,index)=>{
                if(res.data.data.list[index].readstats === "1"){
                  a++;
                }else{
                  a = 0
                }
              });
              _this.remindAllNum = a;
              localStorage.remindAllNum = a;
            }
          }).catch(err => {
            console.log(err);
          });

          this.$http.get('message/shownotice', { // 约稿-->应征通知
            params: {
              access_token: token,
              uid:20,
              type:1
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              let a = 0;  // 约稿通知-->应征通知未读条数
              res.data.data.list.forEach((item,index)=>{
                if(res.data.data.list[index].readstats === '1'){
                  a++
                }else{
                  a = 0
                }
              });
              _this.manuscriptsYingzheng = a;
              localStorage.manuscriptsYingzheng = a;
            }
          }).catch(err => {
            console.log(err);
          });
          this.$http.get('yuegao/getyuegaonotice', { // 约稿-->选用/婉拒
            params: {
              access_token: token,
              uid:1766
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              let a = 0;  // 约稿通知-->选用拒绝通知未读条数
              res.data.data.list.forEach((item,index)=>{
                if(res.data.data.list[index].readstats === '1'){
                  a++
                }else{
                  a = 0
                }
              });
              _this.manuscriptsAllResfue = a;
              localStorage.manuscriptsAllResfue = a;
            }
          }).catch(err => {
            console.log(err);
          });

          this.$http.get('message/shownotice', {  // 获取征稿-->投稿通知总条数
            params: {
              access_token: token,
              uid:20,
              type:2
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              let a = 0;  // 征稿通知-->投稿通知未读条数
              res.data.data.list.forEach((item,index)=>{
                if(res.data.data.list[index].readstats === '1'){
                  a++
                }else{
                  a = 0
                }
              });
              _this.collectmanuAllTougao = a;
              localStorage.collectmanuAllTougao = a;
            }
          }).catch(err => {
            console.log(err);
          });

          this.$http.get('zhenggao/getzhengaonotice', {  // 获取征稿-->选用婉拒总条数
            params: {
              access_token: token,
              uid:11117
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              let a = 0;  // 征稿通知-->投稿通知未读条数
              res.data.data.list.forEach((item,index)=>{
                if(res.data.data.list[index].readstats === '1'){
                  a++
                }else{
                  a = 0
                }
              });
              _this.collectmanuAllResfue = a;
              localStorage.collectmanuAllResfue = a;
            }
          }).catch(err => {
            console.log(err);
          });

          this.$http.get('message/shownotice', {  // 活动通知
            params: {
              access_token: token,
              uid:20,
              type:3
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              let a = 0;  // 征稿通知-->投稿通知未读条数
              res.data.data.list.forEach((item,index)=>{
                if(res.data.data.list[index].readstats === '1'){
                  a++
                }else {
                  a=0;
                }
              });
              _this.activityAllJoin = a;
              localStorage.activityAllJoin = a;
            }
          }).catch(err => {
            console.log(err);
          });
          this.$http.get('huodong/winResult', {  // 获奖结果通知
            params: {
              access_token: token,
              uid:94,
              condition:1
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              let a = 0;  // 征稿通知-->投稿通知未读条数
              res.data.data.list.forEach((item,index)=>{
                if(res.data.data.list[index].readstats === '1'){
                  a++
                }else {
                  a = 0;
                }
              });
              _this.activityAllWin = a;
              localStorage.activityAllWin = a;
            }
          }).catch(err => {
            console.log(err);
          });

          this.$http.get('message/shownotice', {
            params: {
              access_token: token,
              type:4
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              let a = 0;  // 征稿通知-->投稿通知未读条数
              res.data.data.list.forEach((item,index)=>{
                if(res.data.data.list[index].readstats === '1'){
                  a++
                }else{
                  a = 0
                }
              });
              _this.systemAllNum = a;
              localStorage.systemAllNum = a;

              // 判断铃铛动否 放到最后一步，异步处理
              console.log(this.privateAllNum,this.remindAllNum,this.manuscriptsYingzheng,this.manuscriptsAllResfue,this.collectmanuAllTougao,this.collectmanuAllResfue,this.activityAllJoin,this.activityAllWin,this.systemAllNum);
              if(this.privateAllNum !== 0 || this.remindAllNum !== 0 || this.manuscriptsYingzheng !== 0 || this.manuscriptsAllResfue !== 0 || this.collectmanuAllTougao !== 0 || this.collectmanuAllResfue !== 0 || this.activityAllJoin !== 0 || this.activityAllWin !== 0 || this.systemAllNum !== 0){
                this.bellBol = true;
              }else {
                this.bellBol = false;
              }
            }
          }).catch(err => {
            console.log(err);
          });
        })
      },
    },
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
    @import "../../common/css/define";
    .l-wrap {

        >img {
          display: block;
          width: 100%;
          height: 150px;
        }
        width: 100%;
        height: 198px;
        /*background: url("./image/banner-bot.png") no-repeat;*/
        background-size: 100%;
        position: relative;
    }

    .l-header {
        width: 100%;
        height: 48px;
        background: #333;
        position: absolute;
        bottom: 0;
    }

    .header {
        width: 1180px;
        position: relative;
        margin: 0 auto;
    }

    .header__menu {
        display: inline-block;
    }

    .header__menu > li {
        display: inline-block;
        color: #fff;
        line-height: 46px;
        border-bottom: 2px solid transparent;
        padding: 0 30px;
        text-align: center;
        > a {
            display: block;
            color: $gray;
        }
        &:hover {
            border-bottom: 2px solid $pink;
            transition: 0.5s;
        }

        &:hover a {
            color: #fff;
            transition: 0.5s;
        }
    }

    .header__login {
      display: inline-block;
      color: #fff;
      float: right;
      font-size: 16px;
      margin-right: 20px;
    }
    .header__login > li {
      display: inline-block;
      line-height: 48px;
      width: 100px;
      text-align: center;
      cursor: pointer;
      position: relative;
      float: left;
        &:first-child {
          &:hover {
            ul {
              display: block;
            }
          }
        }
        &:nth-of-type(2){
          width: 130px;
          position: relative;
          img {
            width: 15px;
            height: 18px;
            vertical-align: middle;
            margin-left: 5px;
          }
          &:hover {
            ul {
              display: block;
            }
          }
        }
        &:last-child {
          max-width: 120px;
          &:hover {
            ul {
              display: block;
            }
          }
        }
      .header__hover {
        display: none;
        width: 100%;
        border: 1px solid rgba(128,128,128,0.2);
        border-top: none;
        position: absolute;
        top: 48px;
        left:0;
        z-index: 999;
        background-color: $white;
        li {
          font-size: 14px;
          color: $black;
          height: 40px;
          line-height: 40px;
          a {
            display: block;
            width: 100%;
            color: $black;
            i {
              color: $pink;
            }
          }
          border-bottom: 1px solid rgba(128,128,128,0.2);
          &:last-child {
            border: none;
          }
          &:hover {
            background-color: #ecf8fd;
          }
        }
      }
    }

    .header__login > li:nth-child(1) {
        background-color: $pink;
    }

    .header__notlogin {
      li {
        width: 92px;
        padding: 0;
        a{
          display: inline-block;
          width: 100%;
          height: 100%;
          color: $white;
        }
      }
    }

    .search {
        position: absolute;
        right: 0;
        top: -60px;
    }

    .iconAnimatedBell { // 如果有消息，铃铛一直晃动
      animation: ringing 2.0s ease-in-out 1.0s infinite;
      transform-origin: 50% 0%;
    }
    @keyframes ringing {
      0% {
        transform: rotate(-15deg);
      }
      2% {
        transform: rotate(15deg);
      }
      4% {
        transform: rotate(-18deg);
      }
      6% {
        -moz-transform: rotate(18deg);
      }
      8% {
        transform: rotate(-22deg);
      }
      10% {
        transform: rotate(22deg);
      }
      12% {
        transform: rotate(-18deg);
      }
      14% {
        transform: rotate(18deg);
      }
      16% {
        transform: rotate(-12deg);
      }
      18% {
        transform: rotate(12deg);
      }
      20% {
        transform: rotate(0deg);
      }
    }
</style>



