<!--点赞公共模块-->
<template>
  <i :class="hasPraise"
     @click="handleCount">
    <slot name="title"
          :title="status"></slot>
    {{commitData.count | setNull}}
  </i>
</template>

<!--数据交互-->
<script>
  import {mapGetters} from 'vuex'

  export default {
    props: ['praise'],
    mounted(){
      if (this.praise.uid) {
        this.commitData = this.praise;
      }
      this.users = this.$store.getters.praiseUser;
    },
    watch: {
      'praise'(val){//获取props数据
        this.commitData = val;
      },
      'praiseUser'(prd) {
        this.users = prd;
      }
    },

    computed: {
      ...mapGetters([
        'praiseUser'
      ]),

      //过滤状态
      hasPraise(){
        for (let item of this.praiseUser) {
          if (item === this.praise.moduleid) {
            this.status = '已投票';
            return 'is--praise';  //存在
          }
        }
        this.status = '点击投票';
        return '';  //不存在
      }
    },
    data: () => ({
      commitData: {}, //提交的数据
      users: [], //点赞用户
      status: '投票'  //投票状态
    }),

    methods: {
      //点赞操作
      handleCount(){
        this.getToken().then(token => {
          this.$http.get('common/praise', {
            params: {
              access_token: token,
              moduleid: this.commitData.moduleid,
              uid: this.commitData.uid,
              authorid: this.commitData.authorid,
              name: this.commitData.name
            }
          }).then(res => {
            console.log(this.users);
            if (res.data.data.stats === '1') { //点赞
              this.users.push(this.commitData.moduleid);
              this.commitData.count++; //数量+1
              this.$store.dispatch('onPraiseUsers', this.users)
            }
            if (res.data.data.stats === '2') { //取消
              this.commitData.count--;  //数量-1
              this.users.findIndex((value, index, arr) => { //查找位置并删除
                if (value === this.commitData.moduleid) {
                  arr.splice(index, 1);
                }
              });
              this.$store.dispatch('onPraiseUsers', this.users)
            }
          }).catch(err => {
            console.log(err);
          })
        })
      },
    }
  }
</script>





