<!--最新约稿组件-->
<template>
  <section class="custom">
    <ul>
      <li class="custom__list" v-for="item in custom">
        <dl class="custom__person">
          <dt class="custom__img">
            <a :href="'personal.html#/accounts/'+item.uid">
              <img :src="item.avatar" alt=""/>
            </a>
          </dt>
          <dd class="custom__name">{{item.nickname}}</dd>
          <dd class="custom__collect">
            <FollowUserCommon :authorId="item.uid"></FollowUserCommon>
          </dd>
        </dl>
        <dl class="custom__info">
          <dt>{{item.title}}</dt>
          <dd class="custom__marks">{{item.notetype}}</dd>
          <dd>截稿日期：<i>{{item.endtime | UnixToNormalTime(0)}}</i></dd>
          <dd>单张预算：<i>{{item.lowcost}}~{{item.highcost}}</i></dd>
          <dd>需求张数：<i>{{item.imgcount}}</i></dd>
          <dd>应征人数：<i>{{item.personcount}}人</i></dd>
        </dl>
        <section class="custom__btn"><a
          target="_blank"
          :href="'pact.html#/pact/'+item.id">去应征</a></section>
      </li>
    </ul>
  </section>
</template>

<!--数据交互-->
<script>
  import FollowUserCommon from 'components/Common/FollowUserCommon'
  export default {
    name: 'app-custom',
    data: () => {
      return {
        // custom: []
      }
    },
    components:{
      FollowUserCommon
    },
    props: ['custom','followId'],
    mounted(){
//       this.getCustom();
    },
  }
</script>

<!--样式-->
<style scoped lang="scss">
  @import "../../common/css/define";

  .custom {
    max-width: 1180px;
    margin: 0 auto;
    > ul {
      font-size: 0;
    }
    .custom__list {
      width: 240px;
      height: 330px;
      padding: 20px;
      @include wrapShow;
      display: inline-block;
      margin-right: 20px;

      &:nth-child(4) {
        margin-right: 0;
      }
      .custom__img, a {
        width: 40px;
        height: 40px;
        border-radius: 50%;
        overflow: hidden;
        display: inline-block;
        vertical-align: middle;
        > img {
          width: 100%;
        }
      }
      .custom__name {
        display: inline-block;
        vertical-align: middle;
        font-size: 14px;
        color: $black;
        margin-left: 12px;
      }
      .custom__collect {
        display: inline-block;
        vertical-align: middle;
        width: 50px;
        height: 20px;
        background: $green;
        border-radius: 4px;
        color: #fff;
        font-size: 12px;
        text-align: center;
        line-height: 20px;
        cursor: pointer;
        float: right;
        margin-top: 10px;
        >i{
          display: block;
        }
      }
      .custom__info {
        border-top: 1px solid #e6e6e6;
        margin-top: 20px;

        > dt {
          font-size: 16px;
          font-weight: bold;
          color: $black;
          margin: 20px 0 10px 0;
        }
        .custom__marks {
          font-size: 12px;
          color: #999;
          margin-bottom: 10px;
        }
        > dd {
          font-size: 14px;
          line-height: 24px;
          > i {
            color: $pink;
          }
        }
      }
      .custom__btn > a {
        display: block;
        width: 120px;
        height: 50px;
        border-radius: 4px;
        @include linear;
        @include boxShow;
        font-size: 16px;
        text-align: center;
        line-height: 50px;
        color: #fff;
        margin: 40px auto;
        cursor: pointer;
        transition: 0.3s;
        &:active {
          box-shadow: none;
        }
      }
    }
  }
</style>



