<!-- 2017/3/22- 约稿通知 选用/婉拒通知-->
<template>
  <section class="declined">
    <ul>
      <li class="declined__list" v-for="item in refuseTongzhiList">
        <section class="declined__head"><img :src="item.avatar" alt=""></section>
        <section class="declined__intro" v-if="item.stats == 1">
          <a :href="'personal.html#/accounts/products/'+item.uid" target="_blank">{{item.nickname}}</a><i>在约稿</i><a :href="'pact.html#/pactDetail/'+item.moduleid">{{item.title}}</a><i>暂没有通过，期待以后合作</i>
        </section>
        <section class="declined__intro" v-if="item.stats == 2">
          <a :href="'personal.html#/accounts/products/'+item.uid" target="_blank">{{item.nickname}}</a><i>在约稿</i><a :href="'pact.html#/pactDetail/'+item.moduleid">{{item.title}}</a><i>初步选用了你，你尽快联系</i>
        </section>
        <i class="declined__date">{{item.createtime | UnixToNormalTime(1)}}</i>
      </li>
    </ul>
    <Pagination :pageCount="refusePageCount" :pageSize="refusePageSize" @pageChange="refusePageChange"></Pagination>
  </section>
</template>


<script>
  import Pagination from 'components/Common/Pagination' //分页组件
  export default {
    props:["refuseTongzhiList","refusePageCount","refusePageSize"],
    components:{
      Pagination
    },
    methods:{
      refusePageChange(val){
        this.$emit("refusePageChange",val)
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../../common/css/reset.css";
  @import "../../../common/css/define";

  .declined__list {
    padding-bottom: 20px;
    margin-bottom: 20px;
    border-bottom: 1px solid $lightgrey;
  }

  .declined__head {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    overflow: hidden;
    display: inline-block;
    vertical-align: middle;
    > img {
      width: 100%;
    }
  }

  .declined__intro {
    display: inline-block;
    vertical-align: middle;
    font-size: 14px;
    color: #666666;
    a {
      color: $black;
      font-weight: bold;
      margin: 0 10px;
    }
  }

  .declined__date {
    float: right;
    display: inline-block;
    line-height: 40px;
    font-size: 12px;
    color: $gray;
  }
</style>




