<!-- 2017/3/22- 活动通知-->
<template>
  <section id="activity" style="padding: 20px 40px 0 40px;">
    <SubNav :ListSubNav="ListSubNav" @subNav="tabSwitch"></SubNav>
    <keep-alive>
      <component :is='currentView'
                 :actList="tongzhiEveryList"
                 :actPageCount="actPageCount"
                 :actPageSize="actPageSize"
                 @actPageChange="actPageChange"
                 :colPrize="PrizeEveryList"
                 :prizePageCount="prizePageCount"
                 :prizePageSize="prizePageSize"
                 @prizePageChange="prizePageChange"
      ></component>
    </keep-alive>
  </section>
</template>

<script>
  import SubNav from 'components/Message/SubNav' // 关注组件
  import Join from './Activity/Join' // 参加活动组件
  import Prize from './Activity/Prize' // 获奖结果组件
  import {mapGetters} from "vuex"
  export default {
    name:"activity",
    data: () => {
      return {
        currentView:Join,
        ListSubNav:[
          {name:"参加活动",bol:true,tab:"Join",num:0},
          {name:"获奖结果通知",bol:false,tab:"Prize",num:0}
        ],
        tongzhiEveryList:[],
        actPageSize:3, // 参加活动每一页的个数
        actPageCount:0, // 参加活动总个数
        actPageIndex:1,
        PrizeEveryList:[],
        prizePageSize:20,  // 获奖结果通知每一页个数
        prizePageCount:0, // 获奖结果通知总个数
        prizePageIndex:1,
      }
    },
    components:{
      SubNav,
      Join,
      Prize
    },
    mounted(){
      this.getActivityList();
      this.readMessage();
    },
    computed: mapGetters(['activityJoinNum','activityPrizeNum']),
    watch: {
      activityJoinNum(val){
        this.ListSubNav[0].num = val;
      },
      activityPrizeNum(val){
        this.ListSubNav[1].num = val;
      }
    },
    methods : {
      readMessage(){
        let subnav = document.getElementsByClassName("subnav")[0].getElementsByTagName("ul")[0].getElementsByTagName("li")[0];
        if(subnav){
          let _this = this;
          this.getToken().then(token=> { // 统计系统信息已读
            this.$http.get('message/markreadnotice', {
              params: {
                access_token: token,
                type:3,
                uid:20
              }
            }).then(res => {
              if (res.data.stats === '2000') {
                this.getActivityList()
              }
            }).catch(err => {
              console.log(err);
            });
          })
        }
      },
      tabSwitch(index) {
        for(var i = 0; i < this.ListSubNav.length; i++){
          this.ListSubNav[i].bol = false;
          if(index == i){
            this.ListSubNav[i].bol = true;
            this.currentView = this.ListSubNav[i].tab;
          }
        }
        if(index === 1) {
          this.getToken().then(token=> { // 统计系统信息已读
            this.$http.get('huodong/markread', {
              params: {
                access_token: token,
                uid:94
              }
            }).then(res => {
              if (res.data.stats === '2000') {
                this.getActivityList();
              }
            }).catch(err => {
              console.log(err);
            });
          })
        }
      },
      actPageChange(val){
        this.actPageIndex = val;
        this.getActivityList();
      },
      prizePageChange(val){
        this.prizePageIndex = val;
        this.getActivityList();
      },
      getActivityList(){
        let _this = this;
        this.getToken().then(token=> {
          this.$http.get('message/shownotice', {  // 活动通知
            params: {
              access_token: token,
              uid:20,
              type:3,
              page: _this.actPageIndex,
              show: _this.actPageSize
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this.tongzhiEveryList = res.data.data.list;
              _this.actPageCount = parseInt(res.data.data.total);
              let a = 0;  // 征稿通知-->投稿通知未读条数
              res.data.data.list.forEach((item,index)=>{
                if(res.data.data.list[index].readstats === '1'){
                  a++
                }else {
                  a=0
                }
              })
              this.$store.dispatch('onSubmitActivityJoin',a);
            }
          }).catch(err => {
            console.log(err);
          });
          this.$http.get('huodong/winResult', {  // 获奖结果通知
            params: {
              access_token: token,
              uid:94,
              condition:1,
              page: _this.prizePageIndex,
              show: _this.prizePageSize
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this.PrizeEveryList = res.data.data.list;
              _this.prizePageCount = parseInt(res.data.data.total);
              let a = 0;  // 征稿通知-->投稿通知未读条数
              res.data.data.list.forEach((item,index)=>{
                if(res.data.data.list[index].readstats === '1'){
                  a++
                }else {
                  a = 0
                }
              })
              this.$store.dispatch('onSubmitActivityPrize',a);
            }
          }).catch(err => {
            console.log(err);
          });

        })
      }
    }
  }
</script>

<style lang="scss" scoped>

  @import "../../common/css/reset.css";
  @import "../../common/css/define";

  .activity-nav {
    margin-bottom: 20px;
  }
  .activity__list {
    width: 120px;
    height: 40px;
    display: inline-block;
    margin: 5px 0 5px 20px;
    line-height: 40px;
    font-size: 14px;
    cursor: pointer;
    border-radius:4px;
    text-align: center;
    > i {
      color: $pink;
    }
    &:hover {
      @include linear;
      color: $white;
      i {
        color: $white;
      }
    }
  }


</style>



