<!--约稿项目组件-->
<template>
  <div class="pact-item">
    <dl v-for="(item,key) in lists.item">
      <dt class="pact-item__img">
        <img :src="item.avatar" alt=""/>
      </dt>
      <dd class="pact-item__name">
        <h6 :title="item.nickname">{{item.nickname}}</h6>
        <p>+关注</p>
      </dd>
      <dd class="pact-item__info">
        <h6>{{item.title}}</h6>
        <section class="pact-item__marks">
          {{item.notetype}}
        </section>

        <ul class="pact-item__hot">
          <li>截稿日期：<i>{{item.endtime | UnixToNormalTime(0)}}</i></li>
          <li>单张预算：<i>{{item.lowcost}}~{{item.highcost}}</i></li>
          <!--选项插槽-->
          <slot name="feedback" :hot="item.personcount" :count="item.imgcount"></slot>
        </ul>
        <!--征稿福利插槽-->
        <slot :gift="item.gift" name="gift"></slot>
      </dd>
      <dd class="pact-item__btn">

        <router-link target='_blank' :to="lists.path+'/'+item.id">{{btn}}</router-link>
      </dd>
    </dl>
    <!--分页-->
    <slot name="page"></slot>
    <section
      v-if="lists.item.length===0">
      暂无数据。。。
    </section>
  </div>
</template>

<!--数据交互-->
<script>

  export default {
    props: ['lists', 'btn']
  }
</script>

<!--组件样式-->
<style lang="scss" scoped>
  @import "../../common/css/define";

  .pact {
    margin-bottom: 100px;
  }

  .pact-item {
    max-width: 1140px;
    margin: 20px auto 0px;
    padding: 20px;
    background-color: #fff;
    color: $black;
    @include wrapShow;
    > dl {
      border-bottom: 1px solid #e6e6e6;
      padding-bottom: 40px;
      margin-top: 40px;
      &:nth-of-type(1) {
        margin-top: 0px;
      }
      &:nth-last-of-type(1) {
        border-bottom: none;
      }
    }
  }

  .pact-item__img {
    width: 56px;
    height: 56px;
    border-radius: 50%;
    overflow: hidden;
    display: inline-block;
    vertical-align: top;
    > img {
      width: 100%;
    }
  }

  .pact-item__name {
    display: inline-block;
    vertical-align: top;
    margin: 6px 50px 0 10px;
    > h6 {
      width: 70px;
      font-size: 14px;
      font-weight: normal;
      @include beyondPass;
    }
    > p {
      width: 48px;
      height: 20px;
      border-radius: 2px;
      background-color: $green;
      color: #fff;
      line-height: 20px;
      text-align: center;
      font-size: 12px;
      margin-top: 10px;
    }
  }

  .pact-item__info {
    display: inline-block;
    vertical-align: top;
    > h6 {
      font-size: 16px;
      font-weight: bold;
    }
    .pact-item__marks {
      font-size: 12px;
      color: #999;
      margin: 10px 0;
    }
    .pact-item__hot {
      > li {
        display: inline-block;
        font-size: 14px;
        margin-right: 16px;
        line-height: 30px;

        > i {
          color: $pink;
        }
      }
    }
  }

  .pact-item__btn {
    display: inline-block;
    vertical-align: top;
    font-size: 14px;
    width: 98px;
    height: 40px;
    line-height: 40px;
    transition: 0.3s;
    text-align: center;
    border-radius: 4px;
    cursor: pointer;
    margin-top: 34px;
    float: right;
    > a {
      color: #fff;
      display: block;
    }
    @include boxShow;
    @include linear;

    &:active {
      box-shadow: none;
    }
  }
</style>



