<!--征稿列表组件-->
<template>
  <section class="collect-item">
    <h6 class="collect-item__title">共{{items.length}}个设计</h6>
    <ul>
      <li v-for=" item in items">
        <section class="collect-item__img">
          <a href="javascript:;">
            <img :src="item.imgs&&JSON.parse(item.imgs)[1]"/>
          </a>
          <i>{{item.title}}</i>
        </section>
        <section class="collect-item__employ">
         <span v-if="item.jstats==='1'">
           <i @click="handleChoose('2',item.id)">采纳</i>
           <i @click="handleChoose('3',item.id)">婉拒</i>
         </span>
          <i
            :class="item.jstats==='2'?'is--choose':'un--choose'"
            v-else>{{item.jstats === '2' ? '已采纳' : '已婉拒'}}</i>
        </section>
        <section class="collect-item__name">
          <a
            :href="`personal.html#/accounts/${item.uid}`">
            <img :src="item.avatar"/>
          </a>
          <i>{{item.nickname}}</i>
        </section>
      </li>
    </ul>
  </section>
</template>

<!--数据交互-->
<script>
  export default {

    props: ['items'],
    methods: {
      //采纳画作
      handleChoose(stats, id){
        let prams = {stats: stats, id: id};
        this.$emit('onSubmitChoose', prams)
      }
    }
  }
</script>

<!--组件样式-->
<style scoped lang="scss">
  @import "../../common/css/define";

  .collect-item {
    padding-bottom: 60px;
    color: $black;
    > ul > li {
      display: inline-block;
      margin: 0 20px 34px 0;
      &:nth-of-type(5n) {
        margin-right: 0;
      }
    }
    .collect-item__title {
      font-size: 12px;
      margin: 20px 0 10px 0;
      color: #666;
      font-weight: normal;
    }
    .collect-item__img {
      width: 220px;
      height: 256px;
      background-color: #fff;
      border-radius: 4px;
      overflow: hidden;
      > a {
        display: block;
        width: 220px;
        height: 220px;
        overflow: hidden;
        > img {
          width: 100%;
        }
      }
      > i {
        padding: 0 10px;
        line-height: 36px;
        font-size: 12px;
        &:nth-of-type(2) {
          float: right;
          color: $gray;
        }
      }
    }
    .collect-item__employ {
      width: 220px;
      font-size: 12px;
      line-height: 34px;
      border-bottom: 1px solid #ccc;
      > i, span > i {
        margin-right: 10px;
        color: #fff;
        padding: 4px 10px;
        border-radius: 2px;
        cursor: pointer;
        background-color: $pink;
        &:nth-of-type(2) {
          background-color: #ccc;
        }
        &.is--choose {
          background-color: $pink;
          cursor: not-allowed;
        }
        &.un--choose {
          background-color: #ccc;
          cursor: not-allowed;
        }
      }
    }
    .collect-item__name {
      margin-top: 10px;
      font-size: 12px;
      > a {
        display: inline-block;
        width: 20px;
        height: 20px;
        border-radius: 50%;
        overflow: hidden;
        vertical-align: middle;
        > img {
          width: 100%;
        }
      }
      > i {
        display: inline-block;
        height: 20px;
        line-height: 20px;
        vertical-align: middle;
        margin-left: 12px;
      }
    }
  }
</style>



