<!-- 2017/3/22- 约稿通知 应征通知-->
<template>
  <section class="notapp">
    <ul>
      <li class="notapp__list clearfix" v-for="item in yingzhengTongzhiList">
        <section class="notapp__head"><img :src="item.avatar" alt=""></section>
        <section class="notapp__l">
          <div class="notapp__per"><a :href="'personal.html#/accounts/products/'+item.uid" target="_blank">{{item.nickname}}</a><i>应征了我的约稿</i><a :href="'pact.html#/pactDetail/'+item.moduleid" target="_blank">{{item.title}}</a></div>
          <div class="notapp__prize"><i>报价&nbsp;:&nbsp;{{item.cost}}</i><i>留言&nbsp;:&nbsp;{{item.message}}</i></div>
        </section>
        <section class="notapp__r">{{item.createtime | UnixToNormalTime(1)}}</section>
      </li>
    </ul>
    <Pagination :pageCount="yingzhengPageCount" :pageSize="yingzhengPageSize" @pageChange="yingzhengPageChange"></Pagination>
  </section>

</template>


<script>
  import Pagination from 'components/Common/Pagination' //分页组件
  export default {
    props:["yingzhengTongzhiList","yingzhengPageCount","yingzhengPageSize"],
    components:{
      Pagination
    },
    methods:{
      yingzhengPageChange(val){
        this.$emit("yingzhengPageChange",val)
      }
    }
  }
</script>


<style lang="scss" scoped>
  @import "../../../common/css/reset.css";
  @import "../../../common/css/define";

  .notapp__list {
    padding-bottom: 20px;
    margin-bottom: 20px;
    border-bottom: 1px solid $lightgrey;
    .notapp__head {
      float: left;
      > img {
        width: 40px;
        height: 40px;
        border-radius:50%
      }
    }
  }

  .notapp__l {
    float: left;
    margin-left:15px;

  }
  .notapp__per {
    >i {
      margin: 0 16px;
      font-size: 14px;
      color: #a3a3a3;
    }
    > a {
      font-weight: bold;
      font-size: 14px;
      color: $black;
    }
  }
  .notapp__prize {
    margin-top: 6px;
    > i {
      font-size: 14px;
      color: #666666;

      &:first-child {
        margin-right: 25px;
      }
    }
  }
  .notapp__r {
    float: right;
    line-height: 20px;
    font-size: 12px;
    color: #999999;
    text-align: center;
  }
</style>



