<!--qq群组件-->
<template>
    <div class="group-qq">
      <table border="0" cellspacing="0" cellpadding="0" v-for="item in allList">
        <tr>
          <th colspan="4" style="background-color: #ffffff;font-size: 14px;">{{item.name}}</th>
        </tr>
        <tr>
          <th>序号</th>
          <th>群名称</th>
          <th>群号</th>
          <th>操作</th>
        </tr>
        <tr v-for="(val,index) in item.icon">
          <td>{{index+1}}</td>
          <td>{{val.name}}</td>
          <td>{{val.qq}}</td>
          <td><a :href="val.href" target="_blank">一键加群</a></td>
        </tr>
      </table>
    </div>
</template>
<script>
  export default {
    props:['allList'],
  }
</script>
<style scoped lang="scss">
    @import "../../common/css/define";
    .group-qq {
      width: 1180px;
      margin: 20px auto;
        table {
          margin: 0 auto;
          border-top: 1px solid rgba(128,128,128,0.2);
          th {
            height: 40px;
            background-color: #e6e6e6;
            font-size: 12px;
            text-align: center;
            border-bottom: 1px solid rgba(128,128,128,0.2);
            &:first-child{
              border-left: 1px solid rgba(128,128,128,0.2);
              border-right: 1px solid rgba(128,128,128,0.2);
            }
            &:nth-of-type(2),&:nth-of-type(3){
              border-right: 1px solid rgba(128,128,128,0.2);
            }
          }
          td {
            height: 40px;
            width: 360px;
            text-align: center;
            font-size: 12px;
            background-color: #ffffff;
            border-bottom: 1px solid rgba(128,128,128,0.2);
            letter-spacing: 0.1em;
            color: $black;
            &:nth-of-type(2n-1) {
              background-color: #fafafa;
            }
            &:first-child {
              width: 210px;
              border-left: 1px solid rgba(128,128,128,0.2);
              border-right: 1px solid rgba(128,128,128,0.2);
            }
            &:last-child {
              width: 210px;
              border-right: 1px solid rgba(128,128,128,0.2);
            }
            &:nth-of-type(2),&:nth-of-type(3){
              border-right: 1px solid rgba(128,128,128,0.2);
            }
            a {
              color: $paleBlue;
            }
          }
        }
    }
</style>




