<!-- 2017/3/21- 提醒  评论我的 -->
<template>
  <section class="comment">
    <ul>
      <li class="comment__list clearfix" v-for="item in commonList">
        <section class="comment__head"> <a :href="'personal.html#/accounts/products/'+item.uid" target="_blank"><img :src="item.avatar" alt=""></a></section>
        <section class="comment__con">
          <section class="clearfix">
            <div class="comment__author">
              <a :href="'personal.html#/accounts/products/'+item.uid" target="_blank">{{item.nickname}}</a>
              <!-- 评论的类型 1、画作 2、活动 3、约稿 4、设计 5、征稿 -->
              <span v-if="item.type === '1'">
                <i>评论了我的画作</i>
                <a :href="'painting.html#/detail/'+item.moduleid" target="_blank">{{item.title}}</a>
              </span>
              <span v-if="item.type === '2'">
                <i>评论了我的活动</i>
                <a :href="'activity.html#/detail/'+item.moduleid" target="_blank">{{item.title}}</a>
              </span>
              <span v-if="item.type === '3'">
                <i>评论了我的约稿</i>
                <a :href="'pact.html#/pactDetail/'+item.moduleid" target="_blank">{{item.title}}</a>
              </span>
              <span v-if="item.type === '4'">
                <i>评论了我的设计</i>
                <a :href="'design.html#/pendingDet/'+item.moduleid" target="_blank">{{item.title}}</a>
              </span>
              <span v-if="item.type === '5'">
                <i>评论了我的征稿</i>
                <a :href="'design.html#/collectDet/'+item.moduleid" target="_blank">{{item.title}}</a>
              </span>
            </div>
            <p class="comment__date">{{item.createtime | UnixToNormalTime(1)}}</p>
          </section>
          <p class="comment__com">{{item.content}}</p>
        </section>
      </li>
    </ul>
    <Pagination :pageCount="commonPageCount" :pageSize="commonPageSize" @pageChange="commonPageChange"></Pagination>
  </section>
</template>

<script>
  import Pagination from 'components/Common/Pagination' //分页组件
  export default {
    props:["commonList","commonPageCount","commonPageSize"],
    components:{
      Pagination
    },
    methods:{
      commonPageChange(val){
        this.$emit("commonPageChange",val)
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../../common/css/reset.css";
  @import "../../../common/css/define";

  .comment__list {
    padding-bottom: 20px;
    margin-bottom: 20px;
    border-bottom: 1px solid $lightgrey;
  }
  .comment__head {
    float: left;
    overflow: hidden;
    display: inline-block;
    vertical-align: middle;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    overflow: hidden;
     img {
      width: 100%;
      border: 1px solid rgba(128,128,128,0.2);
      }
  }

  .comment__con {
    width: 860px;
    margin-left: 16px;
    float: left;
  }

  .comment__author {
    float: left;
    a {
       display: inline-block;
       font-size: 14px;
       color: $black;
       font-weight: bold;
       vertical-align: middle;
     }
     i {
       display: inline-block;
       vertical-align: middle;
       font-size: 12px;
       color: #666666;
        margin: 0 15px;
      }
  }
  .comment__date {
    font-size: 12px;
    color: #999999;
    line-height: 20px;
    float: right;
  }

  .comment__com {
    font-size: 14px;
    color: $black;
    line-height: 18px;
    margin-top: 10px;
    text-align: justify;
  }
</style>



