import * as types from './types'

export default {
  add: ({commit}) => {
    commit(types.ADD);
  },
  // loading控制
  showLoading: ({commit}) => {
    commit(types.SHOW_LOADING);
  },
  hideLoading: ({commit}) => {
    commit(types.HIDE_LOADING)
  },

  //未读消息控制
  onSubmitPrivate: ({commit}, val) => {
    commit(types.PRIVATE_NUM, val)
  },
  onSubmitComment: ({commit}, val) => { // 提醒
    commit(types.REMIND_NUM, val)
  },
  onSubmitYuegaoYingzheng: ({commit}, val) => {
    commit(types.MANUSCRIPTSYINGZHENG_NUM, val)
  },
  onSubmitYuegaoRefuse: ({commit}, val) => {
    commit(types.MANUSCRIPTSREFUSE_NUM, val)
  },
  onSubmitCollectTougao: ({commit}, val) => {
    commit(types.COLLECTMANUTOUGAO_NUM, val)
  },
  onSubmitCollectResfue: ({commit}, val) => {
    commit(types.COLLECTMANUREFUSE_NUM, val)
  },
  onSubmitActivityJoin: ({commit}, val) => {
    commit(types.ACTIVITYJOIN_NUM, val)
  },
  onSubmitActivityPrize: ({commit}, val) => {
    commit(types.ACTIVITYPRIZE_NUM, val)
  },
  onSubmitSystem: ({commit}, val) => {
    commit(types.SYSTEM_NUM, val)
  },

  //点赞数量
  onPraiseUsers: ({commit}, val) => {
    commit(types.PRAISE_USER, val);
  },

  //收藏数量
  onCollectUsers: ({commit}, val) => {
    commit(types.COLLECT_USER, val)
  },

  //关注
  onFollow: ({commit}, val) => {
    commit(types.FOLLOW_ID, val)
  },

  //登录url
  onRecordUrl: ({commit}, val) => {
    commit(types.LOGIN_URL, val)
  },
}





