<!-- 2017/3/22-私信 -->
<template>
  <section class="priletter">
    <p class="priletter-btn" @click="unreadMessage">全部标记为已读</p>
    <section class="priletter-con">
      <ul class="priletter-con__ul">
        <li class="priletter-con__list clearfix" v-for="item,index in PriLetter" @click="readPriDiaLog(index)">
            <section class="priletter-con__head"><img :src="item.avatar" alt=""></section>
            <section class="priletter-con__l">
              <a href="javascript:void(0)">{{item.nickname}}</a>
              <i>{{(item.content=='')?'暂无私信':item.content}}</i>
            </section>
            <section class="priletter-con__r">
              <i>{{item.createtime | UnixToNormalTime(1)}}</i>
              <i v-if="item.rcount<=0?false:true" class="priletter-con__r-num">{{item.rcount}}</i>
            </section>
        </li>
      </ul>
      <slot name="private"></slot>
    </section>
  </section>

</template>

<script>
  export default {
    props:["PriLetter"],
    methods:{
      unreadMessage(){
        this.$emit('unreadMessage');
      },
      readPriDiaLog(index){
        this.$emit('readPriDiaLog',index);
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../common/css/reset.css";
  @import "../../common/css/define";

  .priletter {
    width: 920px;
    padding: 20px 40px 0 40px;
  }
  .priletter-btn {
    width: 120px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    font-size: 14px;
    cursor: pointer;
    border-radius:4px;
    font-size: 14px;
    color: $white;
    @include linear;
  }

  .priletter-con {
    margin-top: 40px;
  }

  .priletter-con__list {
    padding-bottom: 20px;
    margin-bottom: 20px;
    border-bottom: 1px solid rgba(215, 215, 215, 1);
    position: relative;
    cursor: pointer;
  }
  .priletter-con__head {
    float: left;
    > img {
        width: 40px;
        height: 40px;
        border-radius:50%
      }
  }
  .priletter-con__l {
    float: left;
    margin-left:15px;
    > a {
        display: block;
        font-size: 14px;
        color: $black;
        &:hover {
          color: $pink;
         }
      }
    > i {
        width: 750px;
        overflow: hidden;
        text-overflow:ellipsis;
        white-space: nowrap;
        display: block;
        font-size: 12px;
        color: #666666;
        margin-top: 10px;
      }
  }
  .priletter-con__r {
    float: right;
    >i {
      display: block;
      line-height: 25px;
      font-size: 12px;
      color: #999999;
      text-align: center;
      line-height: 16px;
     }
    .priletter-con__r-num {
      padding: 0 10px;
      height: 20px;
      border-radius:4px;
      background-color: $pink;
      line-height: 20px;
      color: $white;
      position: absolute;
      right: 0;
      bottom: 20px;
    }
  }


</style>



