<!--活动详情组件-->
<template>
  <div id="activity-detail">
    <ActivityItem
      :lists="items">
      <template scope="props"
                slot="hot">
        <section class="activity-detail__hot">
          <i>{{props.info.bcount}}</i>
          <span>
          <i class="el-icon-star-on"></i>
          {{props.info.ccount}}
        </span>
        </section>
      </template>
      <router-link
        :to="`/new/painting/${id}`"
        slot="btn"
        class="activity-detail__btn">
        我要参加
      </router-link>
    </ActivityItem>

    <ActivityNav :names='names' @change="change"></ActivityNav>
    <keep-alive>
      <component
        :lists="lists"
        :is="currentView"></component>
    </keep-alive>
  </div>
</template>

<!--数据交互-->
<script>
  import ActivityItem from './ActivityItem.vue';
  import ActivityNav from './ActivityNav.vue';
  import ActivityDesc from 'components/Activity/ActivityDesc';
  import ActivityLists from 'components/Activity/ActivityLists';
  import ActivityResult from 'components/Activity/ActivityResult';

  export default {
    components: {
      ActivityItem,
      ActivityNav,
      ActivityDesc,
    },
    data: () => {
      return {
        names: [{title: '活动详情', isActive: true}, {title: '作品列表', isActive: false}, {title: '获奖结果', isActive: false}],
        currentView: ActivityDesc,
        lists: {},
        items: [],
        id: 0, //活动id
      }
    },
    mounted(){
      let id = this.$route.params.id;
      this.id = id;
      this.getData(id);
    },
    watch: {  //路由监控
      $route(to){
        let id = this.$route.params.id;
        if (to.fullPath.indexOf('detail') !== -1 && id) { //返回活动列表页id值不存在
          this.getData(id);
        }
      }
    },
    methods: {
      change(index){ //菜单切换
        this.names.forEach(item => {
          item.isActive = false;
        });
        this.names[index].isActive = true;
        this.showComponent(index);
      },
      showComponent(index){ //tab切换
        switch (index) {
          case 0:
            this.currentView = ActivityDesc;
            break;
          case 1:
            this.currentView = ActivityLists;
            break;
          case 2:
            this.currentView = ActivityResult;
            break;
        }
      },

      //获取活动
      getData(id){
        let _this_ = this;
        this.items = [];
        this.getToken().then(token => {
          this.$http.get('huodong/findhuodong', {
            params: {
              access_token: token,
              id: id,
            }
          }).then(res => {
            _this_.lists = res.data.data.id;
            this.items.push(res.data.data.id);
            console.log(res.data.data.id);
          }).catch(err => {
            console.log(err);
          })
        })
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../../common/css/define";

  .activity-detail__btn {
    width: 120px;
    height: 46px;
    display: block;
    border-radius: 4px;
    font-size: 16px;
    text-align: center;
    color: #fff;
    position: absolute;
    line-height: 46px;
    cursor: pointer;
    top: 30px;
    right: 20px;
    @include boxShow;
    @include linear;
  }

  .activity-detail__hot {
    display: inline-block;
    vertical-align: middle;
    margin-left: 20px;
    > i {
      font-size: 14px;
      display: inline-block;
      padding: 0 10px;
      color: $pink;
      background: url("./images/hot.png") no-repeat left center;
      text-indent: 8px;
    }
    > span {
      display: inline-block;
      font-size: 14px;
      padding: 0 20px;
      text-align: center;
      line-height: 28px;
      height: 28px;
      border: 1px solid #ccc;
      background-color: #fafafa;
      border-radius: 2px;
      margin-left: 10px;
      color: $black;

      > i {
        color: #999;
      }
    }
  }
</style>



