<!-- 2017/3/22- 征稿通知-->
<template>
  <section class="collentmanu" style="padding: 20px 40px 0 40px;">
    <SubNav :ListSubNav="ListSubNav" @subNav="tabSwitch"></SubNav>
    <keep-alive>
      <component :is='currentView'
                 :colSunmission="SunmissionEveryList"
                 :subPageCount="subPageCount"
                 :subPageSize="subPageSize"
                 @subPageChange="subPageChange"
                 :colRefuse="RefuseEveryList"
                 :refusePageCount="refusePageCount"
                 :refusePageSize="refusePageSize"
                 @refusePageChange="refusePageChange"
      ></component>
    </keep-alive>

  </section>

</template>

<script>
  import SubNav from 'components/Message/SubNav' // 关注组件
  import Submission from './Collectmanu/Submission' // 应征通知组件
  import Refuse from './Collectmanu/Refuse' // 选用/婉拒组件
  import {mapGetters} from "vuex"

  export default {
    data: () => {
      return {
        ListSubNav:[
          {name:"投稿通知",bol:true,tab:"Submission",num:0},
          {name:"选用/婉拒通知",bol:false,tab:"Refuse",num:0}
        ],
        currentView:Submission,
        SunmissionEveryList:[],
        subPageSize:3,  // 投稿通知每页的个数
        subPageCount:0, // 投稿通知总个数
        subPageIndex:1,
        RefuseEveryList:[],
        refusePageSize:3,  // 选用/婉拒通知每页的个数
        refusePageCount:0, // 选用/婉拒通知总个数
        refusePageIndex:1,
      }
    },
    components:{
      SubNav,
      Submission,
      Refuse,
    },
    mounted(){
      this.getCollentList();
      this.readMessage();
    },
    computed: mapGetters(['collectmanuTougaoNum','collectmanuRefuseNum']),
    watch: {
      collectmanuTougaoNum(val){
        this.ListSubNav[0].num = val;
      },
      collectmanuRefuseNum(val){
        this.ListSubNav[1].num = val;
      }
    },
    methods : {
      readMessage(){ // 进来默认已读操作
        let subnav = document.getElementsByClassName("subnav")[0].getElementsByTagName("ul")[0].getElementsByTagName("li")[0];
        if(subnav){
          let _this = this;
          this.getToken().then(token=> { // 统计系统信息已读
            this.$http.get('message/markreadnotice', {
              params: {
                access_token: token,
                type:2,
                uid:20
              }
            }).then(res => {
              if (res.data.stats === '2000') {
//                this.getCollentList(); //重新赋值
              }
            }).catch(err => {
              console.log(err);
            });
          })
        }
      },
      tabSwitch(index) {
        for(var i = 0; i < this.ListSubNav.length; i++){
          this.ListSubNav[i].bol = false;
          if(index == i){
            this.ListSubNav[i].bol = true;
            this.currentView = this.ListSubNav[i].tab;
          }
        }
        if(index === 1){
          this.getToken().then(token=> { // 统计系统信息已读
            this.$http.get('zhenggao/markread', {
              params: {
                access_token: token,
                uid:11117
              }
            }).then(res => {
              if (res.data.stats === '2000') {
                this.getManuList();
              }
            }).catch(err => {
              console.log(err);
            });
          })
        }
      },
      subPageChange(val){
        this.subPageIndex = val;
        this.getCollentList();
      },
      refusePageChange(val){
        this.refusePageIndex = val;
        this.getCollentList();
      },
      getCollentList(){
        let _this = this;
        this.getToken().then(token=> {
          this.$http.get('message/shownotice', {
            params: {
              access_token: token,
              uid:20,
              type:2,
              page: _this.subPageIndex,
              show: _this.subPageSize
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this.SunmissionEveryList = res.data.data.list;
              _this.subPageCount = parseInt(res.data.data.total);
              let a = 0;  // 征稿通知-->投稿通知未读条数
              res.data.data.list.forEach((item,index)=>{
                if(res.data.data.list[index].readstats === '1'){
                  a++
                }else {
                  a = 0
                }
              })
              this.$store.dispatch('onSubmitCollectTougao',a);
            }
          }).catch(err => {
            console.log(err);
          });

          this.$http.get('zhenggao/getzhengaonotice', {
            params: {
              access_token: token,
              uid:11117,
              page:_this.refusePageIndex,
              show:_this.refusePageSize
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this.RefuseEveryList = res.data.data.list;
              _this.refusePageCount = parseInt(res.data.data.total);
              let a = 0;  // 征稿通知-->投稿通知未读条数
              res.data.data.list.forEach((item,index)=>{
                if(res.data.data.list[index].readstats === '1'){
                  a++
                }else{
                  a=0
                }
              })
              this.$store.dispatch('onSubmitCollectResfue',a);
            }
          }).catch(err => {
            console.log(err);
          });
        })
      }
    }
  }
</script>

<style lang="scss" scoped>

  @import "../../common/css/reset.css";
  @import "../../common/css/define";

</style>



