import {
  ADD,
  SHOW_LOADING,
  HIDE_LOADING,
  PRIVATE_NUM,
  REMIND_NUM,
  MANUSCRIPTSYINGZHENG_NUM,
  MANUSCRIPTSREFUSE_NUM,
  COLLECTMANUTOUGAO_NUM,
  COLLECTMANUREFUSE_NUM,
  ACTIVITYJOIN_NUM,
  ACTIVITYPRIZE_NUM,
  SYSTEM_NUM,
  PRAISE_USER,
  COLLECT_USER,
  FOLLOW_ID,
  LOGIN_URL,
} from './types'

import getters from './getters'

// mutations
const state = {
  isShow: false,
  count: 0,
  loading: false,
  privateNum: 0, //私信未读数量
  remindNum: 0, //提醒未读数量
  manuscriptsYingzhengNum: 0, //约稿通知-->应征通知未读数量
  manuscriptsRefuseNum: 0, // 约稿通知-->选用/拒绝通知未读数量
  collectmanuTougaoNum: 0, //征稿通知-->投稿通知未读数量
  collectmanuRefuseNum: 0, // 征稿通知-->选用/婉拒数量
  activityJoinNum: 0, //活动通知-->参加活动未读数量
  activityPrizeNum: 0, //活动通知-->获奖结果通知未读数量
  systemNum: 0, //系统未读数量
  praiseUser: [],//已赞用户
  collectUser: [],//收藏用户
  followId: [], //已关的用户
  url: '' //登录url
};

const mutations = {
  [ADD](state) {
    state.count++;
  },
  [HIDE_LOADING](state) {
    state.loading = false;
  },
  [SHOW_LOADING](state) {
    state.loading = true;
  },
  [PRIVATE_NUM](state, val) {
    state.privateNum = val;
  },
  [REMIND_NUM](state, val){ // 提醒-->评论未读条数
    state.remindNum = val;
  },
  [MANUSCRIPTSYINGZHENG_NUM](state, val){  // 约稿通知-->应征通知未读条数
    state.manuscriptsYingzhengNum = val;
  },
  [MANUSCRIPTSREFUSE_NUM](state, val){  // 约稿通知-->选用拒绝未读条数
    state.manuscriptsRefuseNum = val;
  },
  [COLLECTMANUTOUGAO_NUM](state, val){ //征稿通知-->投稿通知未读数量
    state.collectmanuTougaoNum = val;
  },
  [COLLECTMANUREFUSE_NUM](state, val){
    state.collectmanuRefuseNum = val; // 征稿通知-->选用/婉拒数量
  },
  [ACTIVITYJOIN_NUM](state, val){
    state.activityJoinNum = val; //活动通知-->参加活动未读数量
  },
  [ACTIVITYPRIZE_NUM](state, val){
    state.activityPrizeNum = val; //活动通知-->获奖结果通知未读数量
  },
  [SYSTEM_NUM](state, val){
    state.systemNum = val; //系统通知未读数量
  },

  [PRAISE_USER](state, val) { //已赞用户
    state.praiseUser = val;
  },

  [COLLECT_USER](state, val) { //收藏用户
    state.collectUser = val;
  },

  [FOLLOW_ID](state, val) {
    state.followId = val;
  },
  [LOGIN_URL](state, val) {
    state.url = val;
  }
};
export default {
  mutations,
  getters,
  state
}



