<!-- 2017/3/22- yyy -约稿信息 -->
<template>
  <section class="range">
    <ul class="range__ul clearfix">
      <li class="range__list" v-for="(item,index) in SearchRange" @click="rangeIcon(index)"
          :class="{active:item.bol}">
        <span :class=" index <= 2 ? '' : 'noactive'">
          <input type='radio' v-if="item.inputBol" name="timeIng"/>
          {{item.name}}
        </span>
      </li>
      <li class="range__last" @click="relDesign">{{IconRelease}}</li>
    </ul>
  </section>
</template>

<script>

  import ComSell from 'components/Common/ComSell'; //商用设计组件
  import Pagination from 'components/Common/Pagination' //分页组件
  export default {
    props: ["SearchRange", "IconRelease"],
    methods: {
      rangeIcon(index){
        this.$emit('rangeIcon', index);
      },
      relDesign(){
        this.$emit('relDesign');
      }
    },
    components: {
      ComSell,
    },
    data: () => ({})
  }
</script>

<style lang="scss" scoped>

  @import "../../common/css/reset.css";
  @import "../../common/css/define";

  .range {
    padding-top: 30px;
    .l-mt {
      margin-top: 30px;
    }
  }

  .range__ul {
    width: 1180px;
    height: 80px;
    background-color: $white;
    border-radius: 4px;
    @include wrapShow;
    .range__list {
      float: left;
      line-height: 80px;
      padding-left: 20px;
      margin-right: 20px;
      cursor: pointer;
      input {
        vertical-align: middle;
      }
      span {
        color: $black;
        font-size: 14px;
        cursor: pointer;
      }
    }
    .active {
      span {
        color: $pink;
      }
    }
    .range__last {
      width: 100px;
      height: 40px;
      border-radius: 4px;
      text-align: center;
      line-height: 40px;
      float: right;
      color: $white;
      font-size: 14px;
      cursor: pointer;
      @include linear;
      margin: 20px 20px 0 0;
    }
    .noactive {
      color: $black !important;
    }
  }
</style>



