<!--设计征稿组件-->
<template>
  <ul class="design">
    <li v-for="item in design">
      <section class="design__title">{{item.gift}}</section>
      <section class="l-border">
        <section class="design__person">
          <a :href="'personal.html#/accounts/'+item.uid" class="design__img"><img :src="item.avatar" alt=""/></a>
          <p>{{item.nickname}}</p>
          <!--<section class="design__collect"-->
                   <!--@click="handleFollow(item.uid)">-->
            <!--{{item.uid | isFollow(followId)}}-->
          <!--</section>-->
          <section class="design__collect">
            <FollowUserCommon :authorId ='item.id'></FollowUserCommon>
          </section>
        </section>
        <dl class="design__info">
          <dt>{{item.title}}</dt>
          <dd class="design__marks">{{item.tag}}</dd>
          <dd>截稿日期：<i>{{item.endtime | UnixToNormalTime(0)}}</i></dd>
          <dd>单稿预算：<i>{{item.lowcost}}~{{item.highcost}}</i></dd>
          <dd>已投稿件：<i>{{item.ycount ? item.ycount : '0'}} 张</i></dd>
          <dd>反馈时间：<i>{{item.replaytime}}天内</i></dd>
        </dl>
        <!--todo 路由待设置-->
        <section class="design__btn"><a
          target="_blank"
          :href="'design.html#/collect/'+item.id">去投稿</a></section>
      </section>
    </li>
  </ul>
</template>

<!--数据交互-->
<script>
  import FollowUserCommon from 'components/Common/FollowUserCommon'

  export default {
    name: 'app-design',
    data: () => {
      return {
        // design: [],
      }
    },
    props: ['design', 'followId'],
    components: {
      FollowUserCommon
    },
  }
</script>

<!--组件样式-->
<style scoped lang="scss">
  @import "../../common/css/define";

  .design {
    max-width: 1180px;
    margin: 0 auto;
    font-size: 0;

    > li {
      display: inline-block;
      &:nth-child(1) {
        margin-right: 18px;
      }
    }
    .design__title {
      width: 180px;
      height: 32px;
      background-color: $yellow;
      font-size: 14px;
      font-weight: bold;
      color: #fff;
      text-align: center;
      line-height: 32px;
      border-radius: 4px 4px 0 0;
    }
    .l-border {
      width: 540px;
      height: 140px;
      padding: 20px;
      position: relative;
      background-color: #fff;
      @include wrapShow;
    }
    .design__person {
      width: 140px;
      display: inline-block;
      font-size: 14px;
      color: $black;
      vertical-align: top;

      > p {
        width: 120px;
        padding: 0 10px;
        text-align: center;
        margin: 10px 0;
        @include beyondPass;
      }

      .design__collect {
        width: 48px;
        height: 22px;
        border-radius: 4px;
        background-color: $green;
        color: #fff;
        font-size: 12px;
        text-align: center;
        line-height: 22px;
        cursor: pointer;
        margin: 0 auto;
        >i {
          display: block;
        }
      }
    }
    .design__img {
      display: block;
      width: 80px;
      height: 80px;
      border-radius: 50%;
      overflow: hidden;
      margin: 0 auto;
      > img {
        width: 100%;
      }
    }
    .design__info {
      display: inline-block;
      vertical-align: top;

      > dt {
        font-size: 16px;
        font-weight: bold;
        color: $black;
      }
      .design__marks {
        font-size: 12px;
        color: #999;
        margin: 10px 0;
      }
      > dd {
        line-height: 21px;
        font-size: 14px;
        color: $black;
        > i {
          color: $pink;
        }
      }
    }
    .design__btn > a {
      display: inline-block;
      position: absolute;
      width: 120px;
      height: 48px;
      color: #fff;
      text-align: center;
      line-height: 48px;
      font-size: 16px;
      border-radius: 4px;
      cursor: pointer;
      right: 20px;
      bottom: 66px;
      transition: 0.3s;
      @include linear;
      @include boxShow;

      &:active {
        box-shadow: none;
      }
    }
  }
</style>




