<!--分页组件-->

<template>
  <div class="block" :style="{position:position}">
    <el-pagination
      layout="prev, pager, next"
      :page-size="pageSize"
      @current-change='currentChange'
      :total="pageCount">
    </el-pagination>
  </div>
</template>

<style lang="scss" scoped>
  @import "../../common/css/define";

  .block {
    text-align: center;
    margin-top: 25px;
    @include pageFixed;
  }

  .el-pager > li.active {
    background-color: $pink;
    border-color: $pink;
  }
</style>


<!--数据交互-->
<script>
  export default {

    props: ['pageCount', 'pageSize'],
    data:()=>{
      return {
        position:(document.location.href.indexOf("management.html") !== -1 || document.location.href.indexOf("message.html") !== -1)?"static":"fixed"
      }
    },
    methods: {
      currentChange(val){  // 传type值，是为了一个tab页面可以引入一个 页面组件 多次使用的目的
        this.$emit('pageChange', val)
      },
    }
  }
</script>



