<!--画作详情用户信息组件-->

<template>
  <section class="detail-bar">
    <section class="detail-bar__user">
      <a :href="`personal.html#/accounts/${userInfo.id}`" class="detail-bar__img">
        <img :src="userInfo.avatar" alt=""/>
      </a>
      <p class="detail-bar__name">{{userInfo.nickname}}</p>
      <section class="detail-bar__hot">
        <i>关注 {{userInfo.followcount}}</i>
        <i>|</i>
        <i>粉丝 {{userInfo.fanscount}}</i>
      </section>
      <ul class="detail-bar__menu">
        <li>
          <FollowUserCommon :authorId="userInfo.id"></FollowUserCommon>
        </li>
        <li><a :href="'message.html#/privateType/'+userInfo.id+'&'+userInfo.nickname">+私信</a></li>
        <li><CopyQqDialogue :info="{name:userInfo.nickname,qq:userInfo.qq}"></CopyQqDialogue></li>
      </ul>
    </section>
    <ul class="detail-bar__type">
      <li v-for="type in types">{{type}}</li>
    </ul>
    <section class="detail-bar__fans">
      <i>加关注</i>
      <i>长春小菊花</i>
      <i>(粉丝1979)</i>
    </section>
    <section class="detail-bar__desc">
      <ul>
        <li
          :class="{'is--active':flag.desc}" @click="handleShow('d')">接稿说明
        </li>
        <li
          :class="{'is--active':flag.explain}" @click="handleShow('h')">接稿经验（{{userInfo.introduce > 0 ? '有' : '无'}}）
        </li>
        <li
          :class="{'is--active':flag.others}" @click="handleShow('n')">其他网站（{{userInfo.othersite > 0 ? '有' : '无'}}）
        </li>
      </ul>
      <section class="detail-bar__show">
        <p
          :title="userInfo.explain"
          v-show="flag.desc">{{userInfo.explain | setNull}}</p>
        <p
          :title="userInfo.introduce"
          v-show="flag.explain">{{userInfo.introduce | setNull}}</p>
        <p
          :title="userInfo.othersite"
          v-show="flag.others">{{userInfo.othersite | setNull}}</p>
      </section>
    </section>
    <ul class="detail-bar__pic">
      <router-link
        :to="`/painting/${item.id}`"
        tag="li"
        v-for="item in othersPainting">
        <img :src="JSON.parse(item.imgs)[0]" alt=""/>
      </router-link>
    </ul>
    <a class="detail-bar__ad"
       v-for="item in ad"
       :href="item.linkurl">
      <img :src="item.img" alt=""/>
    </a>
  </section>
</template>

<!--数据交互-->
<script>
  import FollowUserCommon from 'components/Common/FollowUserCommon'
  import CopyQqDialogue from 'components/Common/CopyQqDialogue'
  export default {
    data: () => {
      return {
        flag: {desc: true, explain: false, others: false},
        types: [],
      }
    },

    filters: { //数据null过滤器
      setNull(val){
        return val ? val : '暂无数据';
      }
    },

    components: {
      FollowUserCommon,
      CopyQqDialogue
    },
    props: ['ad', 'othersPainting', 'userInfo', 'msg'],
    methods: {
      handleShow: function (type) {
        this.flag = {desc: false, explain: false, others: false};
        switch (type) {
          case 'd':
            this.flag = {desc: true, explain: false, others: false};
            break;
          case 'h':
            this.flag = {desc: false, explain: true, others: false};
            break;
          case 'n':
            this.flag = {desc: false, explain: false, others: true};
            break;
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../common/css/define";

  .detail-bar {
    width: 320px;
    display: inline-block;
    font-size: 16px;
    vertical-align: top;
    background-color: #fff;
    margin: 40px 0 0 20px;
    @include wrapShow;

    .detail-bar__user {
      padding: 20px;
      border-bottom: 1px solid #e6e6e6;
      > a {
        display: block;
        width: 80px;
        height: 80px;
        margin: 0 auto;
        border-radius: 50%;
        overflow: hidden;
        > img {
          width: 100%;
        }
      }
      > p {
        text-align: center;
        color: $black;
        font-size: 16px;
        line-height: 36px;
      }
      .detail-bar__hot {
        text-align: center;
        i {
          color: #999;
          font-size: 14px;
          &:nth-of-type(2) {
            color: #e6e6e6;
            margin: 0 6px;
          }
        }
      }
      .detail-bar__menu {
        text-align: center;
        margin-top: 20px;
        > li {
          display: inline-block;
          width: 62px;
          height: 30px;
          border: 1px solid #ccc;
          border-radius: 2px;
          text-align: center;
          line-height: 30px;
          font-size: 14px;
          color: #666;
          cursor: pointer;
          >i {
            display: block;
          }
          >a {
            color: #333;
          }
          &:nth-of-type(1) {
            background-color: $green;
            border-color: $green;
            color: #fff;
          }
        }
      }
    }
    .detail-bar__type {
      padding: 20px;
      > li {
        display: inline-block;
        padding: 0 6px;
        height: 22px;
        border: 1px solid #e6e6e6;
        color: #666;
        border-radius: 2px;
        vertical-align: top;
        font-size: 14px;
        text-align: center;
        line-height: 22px;
        margin: 0 8px 8px 0;
      }
    }
    .detail-bar__fans {
      text-align: left;
      padding-left: 20px;

      > i:nth-of-type(1) {
        display: inline-block;
        border-radius: 2px;
        color: $blue;
        font-size: 14px;
        width: 60px;
        height: 20px;
        text-align: center;
        line-height: 20px;
        border: 1px solid #b5b5b5;
        background-color: #efefef;
        cursor: pointer;
      }
      > i:nth-of-type(2) {
        font-size: 14px;
        color: $blue;
        margin: 0 6px;
      }
      > i:nth-of-type(3) {
        font-size: 14px;
        color: #666;
      }
    }
    .detail-bar__desc {
      width: 278px;
      margin: 20px auto;
      > ul {
        font-size: 0;
        margin-bottom: -1px;
        > li {
          height: 30px;
          line-height: 30px;
          padding: 0 4px;
          text-align: center;
          display: inline-block;
          font-size: 14px;
          color: $blue;
          border-bottom: 1px solid #e6e6e6;
          cursor: pointer;
          &.is--active {
            border: 1px solid #e6e6e6;
            border-bottom: none;
            border-radius: 4px 4px 0 0;
            color: $black;
          }
        }
      }
      .detail-bar__show {
        height: 86px;
        width: 276px;
        border: 1px solid #e6e6e6;
        border-top: none;
        border-radius: 0 0 4px 4px;
        > p {
          font-size: 12px;
          color: $black;
          height: 36px;
          overflow: hidden;
          padding: 20px;
          line-height: 18px;
        }
      }
    }
    .detail-bar__pic {
      padding: 20px;
      border-top: 1px solid #e6e6e6;
      font-size: 0;
      cursor: pointer;
      > li {
        margin: 0 8px 8px 0;
        width: 136px;
        height: 204px;
        overflow: hidden;
        display: inline-block;
        > img {
          width: 100%;
        }
        &:nth-child(2n) {
          margin-right: 0;
        }
      }
    }
    .detail-bar__ad {
      display: block;
      padding: 20px;
      border-top: 1px solid #e6e6e6;
      overflow: hidden;
      > img {
        width: 100%;
      }
    }
  }
</style>



