<!-- 2017/4/11-yyy-提示 错误、成功、 -->
<template>
  <section class="prompt" v-if="hidePrompt">
    <div v-if="showNum == 0" class="prompt__list prompt__list-yes">
      <span>
        <img src="./images/Yes_32px_1099215_easyicon.net.png" alt="">
      </span>
      <p>{{promptLanguage}}</p>
    </div>
    <div v-else-if="showNum == 1" class="prompt__list prompt__list-no">
      <span>
        <img src="./images/No_32px_1099189_easyicon.net.png" alt="">
      </span>
      <p>{{promptLanguage}}</p>
    </div>
    <div v-else-if="showNum == 2" class="prompt__list prompt__list-pro">
      <span>
        <img src="./images/Alert_32px_1093642_easyicon.net.png" alt="">
      </span>
      <p>{{promptLanguage}}</p>
    </div>
  </section>
</template>

<script>
  export default{
    // 用法提示: hidePrompt当需要的时候让此值为true  showNum是那种类型(0,是成功；1是错误；2是提示)  promptLanguage是提示语
    props:["hidePrompt","showNum","promptLanguage"],
    data:()=>{
      return {
        bol:false,
      }
    },
    created(){
      let _this = this;
      if(!_this.bol){
        _this.bol = true;
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../common/css/reset.css";
  @import "../../common/css/define";

  .prompt__list {
    min-width: 200px;
    height: 50px;
    border: 1px solid #e6e6e6;
    border-radius: 4px;
    position: fixed;
    top: 50%;
    left:50%;
    margin-top: -25px;
    margin-left: -100px;
    span {
      display: inline-block;
      width: 25px;
      height: 25px;
      margin-right: 10px;
      padding-left: 10px;
      img {
        width: 25px;
        height: 25px;
        vertical-align:middle
      }
    }
    p {
      min-width: 115px;
      text-align: center;
      display: inline-block;
      font-size: 14px;
      line-height: 50px;
      font-weight: bold;
      padding-right: 10px;
    }
  }
  .prompt__list-yes {
    background-color: #dff0d8;
  }
  .prompt__list-no {
    background-color: #f2dede;
  }
  .prompt__list-pro {
    background-color: #d9edf7;
  }
  .prompt {
    animation:showOfHide 1.5s ease-out 1.0s 1 both;
  }

  @keyframes showOfHide{
    from{
      opacity: 1;
    }
    to{
      opacity: 0;
    }
  }
</style>



