<!-- 2017/3/22- yyy -个人主页 作品-待出售设计公共部分 -->
<template>
  <section class="comsell">
    <ul class="comsell__ul">
      <li class="comsell__list" v-for="item in ComCommercial">
        <section class="comsell__icon">
          <!--<a href="javascript:void(0)">-->
          <!--todo 路由参数id待配置-->
          <router-link :to="link+'/'+item.id" class="comsell__img" target="_blank">
            <img
              :src="item.cover==null?'http://img2.imgtn.bdimg.com/it/u=2966329046,1602124054&fm=23&gp=0.jpg':item.cover"
              alt="">
            <span class="comsell__t"></span><span class="comsell__txt">{{item.imgs && JSON.parse(item.imgs).length}}</span>
          </router-link>
          <!--</a>-->
          <div class="comsell__fh">
            <span><img src="../Personal/images/fire.png" alt=""></span>
            <i>{{(item.bcount == null || item.bcount == "0") ? "0" : item.bcount}}</i> <!-- 浏览数 -->
            <PaiseCom class="comsell__heart"
                      :praise="{moduleid:item.id,count:item.pcount,uid: '18202',authorid: item.uid,name: 'design_praise'}"></PaiseCom>
          </div>
        </section>
        <div class="comsell__hot clearfix">
          <h6>{{item.title}}</h6>
          <i class="comsell__price">￥{{item.lowcost | setNull}}~{{item.highcost | setNull}}</i>
        </div>
        <section class="comsell__bot">
          <div class="comsell__bot-img"><a :href="'personal.html#/accounts/products/'+item.uid" target="_blank"><img
            :src="item.avatar" alt=""/></a></div>
          <a :href="'personal.html#/accounts/products/'+item.uid" target="_blank">{{item.nickname}}</a>
          <i>{{item.createtime | UnixToNormalTime(2)}}</i>
        </section>
      </li>
    </ul>
    <Pagination :pageCount="commerPageCount" :pageSize="commerPageSize" @pageChange="commerPageChange"></Pagination>
  </section>

</template>


<script>
  import Pagination from 'components/Common/Pagination' //分页组件
  import PaiseCom from 'components/Common/PraiseCommon'
  export default {
    props:["ComCommercial","commerPageCount","commerPageSize"],
    components:{
      Pagination,
      PaiseCom
    },
    mounted(){
      this.getPraise();
    },
    methods:{
      commerPageChange(val){
        this.$emit("commerPageChange",val)
      },

      //获取会员点赞作品
      getPraise(){
        this.getUsers('1', {
          uid: 18202,
          name: 'design_praise'
        }).then(res => {
          let data = res.data.data.list.map(item => {
            return item.moduleid
          });
          this.$store.dispatch('onPraiseUsers', data);
        })
      }
    }
  }

</script>


<style lang="scss" scoped>

  @import "../../common/css/reset.css";
  @import "../../common/css/define";

  .comsell__list {
    width: 220px;
    display: inline-block;
    margin: 0 20px 40px 0;
    &:nth-of-type(5n) {
      margin-right: 0;
    }

    .comsell__icon {
      background-color: $white;
      border-radius: 4px;
      @include wrapShow;
      .comsell__img {
        display: block;
        position: relative;
        width: 100%;
        height: 225px;
        img {
          display: block;
          width: 100%;
          height: 225px;
          border-bottom: 1px solid rgba(128, 128, 128, 0.2);
        }
        .comsell__t {
          width: 40px;
          height: 20px;
          background-color: #4c4c4c;
          opacity: 0.8;
          position: absolute;
          top: 0;
          left: 0;
          border-radius: 4px 0 4px 0;

        }
        .comsell__txt {
          width: 40px;
          height: 20px;
          font-size: 12px;
          color: $white;
          text-align: center;
          line-height: 20px;
          position: absolute;
          top: 0;
          left: 0;
        }
      }
    }
  }

  .comsell__fh {
    height: 35px;
    text-align: right;
    padding-right: 10px;
    span {
      width: 12px;
      height: 35px;
      margin-right: 6px;
      display: inline-block;
      line-height: 35px;
      > img {
        width: 12px;
        height: 12px;
      }
    }
    i {
      font-size: 12px;
      color: #999999;
      line-height: 35px;
      &:nth-of-type(1) {
        margin-right: 10px;
      }
    }
    .comsell__heart {
      background: url("../Personal/images/heart.png") no-repeat 0 center;
      display: inline-block;
      width: 30px;
      height: 35px;
      line-height: 35px;
      text-align: right;
      vertical-align: middle;
      cursor: pointer;
    }
    .is--praise {
      background: url("../Common/images/praise@single.png") no-repeat 0 center;
    }
  }

  .comsell__hot {
    font-size: 12px;
    padding: 0 12px 15px 12px;
    border-bottom: 1px solid #e6e6e6;

    > h6 {
      line-height: 30px;
      font-size: 12px;
      font-weight: normal;
      color: $black;
      @include beyondPass;
      width: 180px;
    }
    > .comsell__price {
      font-weight: bold;
      font-size: 14px;
      color: $pink;
    }
  }

  .comsell__bot {
    margin-top: 10px;
    a {
      display: inline-block;
      vertical-align: middle;
      font-size: 12px;
      color: $black;
      margin: 0 8px;
      @include beyondPass;
      max-width: 100px;
    }
    i {
      display: inline-block;
      vertical-align: middle;
      font-size: 12px;
      color: #999999;
    }
  }

  .comsell__bot-img {
    overflow: hidden;
    display: inline-block;
    vertical-align: middle;
    a {
      display: block;
    }
    img {
      width: 20px;
      height: 20px;
      border-radius: 50%;
      border: 1px solid rgba(128, 128, 128, 0.2);
    }
  }

</style>



