<!--最赞画作-->
<template>
  <ul class="draw">
    <li class="draw__list" v-for="item in draw">
      <section class="draw__info">
        <a class="draw__img"
           :href="link+item.id">
          <img :src="JSON.parse(item.imgs)[0]" alt=""/>
          <i>{{item.imgs ? JSON.parse(item.imgs).length : 0}}</i>
        </a>
        <section class="draw__hot">
          <i>{{item.bcount | setNull}}</i>
          <PraiseCommon
            :praise="{moduleid:item.id,count:item.pcount,uid: '18202',authorid: item.uid,name: 'design_praise'}">
          </PraiseCommon>
        </section>
      </section>
      <section class="draw__tips">
        <a
          :href="'personal.html#/accounts/'+item.uid"
          class="draw__tips-img">
          <img :src="item.avatar" alt=""/>
        </a>
        <p :title="item.nickname">{{item.nickname}}</p>
        <i>{{item.createtime | UnixToNormalTime(2)}}</i>
      </section>
    </li>
  </ul>
</template>

<!--数据交互-->
<script>
  import PraiseCommon from 'components/Common/PraiseCommon'

  export default {
    name: "app-draw",
    data: () => {
      return {
        // list:[1,2,3,4,5]
      }
    },
    components: { //组件
      PraiseCommon
    },
    mounted(){
    },
    props: ['draw', 'link'],
    methods: {}
  }
</script>

<!--组件样式-->
<style lang="scss" scoped>
  @import "../../common/css/define";

  .draw {
    max-width: 1180px;
    margin: 0 auto
  }

  .draw__list {
    display: inline-block;
    margin-right: 20px;
    &:nth-child(5) {
      margin-right: 0;
    }
  }

  .draw__info {
    width: 220px;
    height: 364px;
    @include wrapShow;

    .draw__img {
      display: block;
      width: 220px;
      height: 330px;
      border-radius: 4px 4px 0 0;
      position: relative;
      overflow: hidden;

      > img {
        width: 100%;
      }
      > i {
        position: absolute;
        display: block;
        width: 32px;
        height: 20px;
        top: 0;
        left: 0;
        color: #fff;
        border-radius: 4px 0 4px 0;
        background-color: rgba(0, 0, 0, 0.3);
        text-align: center;
        line-height: 20px;
        font-size: 12px;
      }
    }
  }

  .draw__hot {
    font-size: 12px;
    color: $gray;
    padding: 0 12px;
    line-height: 36px;
    text-align: right;
    > i {
      width: 30px;
      height: 36px;
      display: inline-block;
      margin: 0 10px;
      &:nth-of-type(1) {
        background: url("../../components/Common/images/person@hot.png") no-repeat 0 center;
      }
      &:nth-of-type(2) {
        cursor: pointer;
        background: url("../../components/Common/images/Parise@def.png") no-repeat 0 center;
      }
      &.is--praise {
        background: url("../../components/Common/images/praise@single.png") no-repeat 0 center;
      }
    }
  }

  .draw__tips {
    margin-top: 18px;
    .draw__tips-img {
      width: 20px;
      height: 20px;
      border-radius: 50%;
      overflow: hidden;
      display: inline-block;
      vertical-align: middle;
      background: darkgoldenrod;
      > img {
        width: 100%;
      }
    }
    > p {
      width: 50px;
      display: inline-block;
      vertical-align: middle;
      font-size: 12px;
      color: $black;
      margin: 0 16px;
      @include beyondPass;
    }
    > i {
      display: inline-block;
      vertical-align: middle;
      font-size: 12px;
      color: $gray;
    }
  }
</style>



