<!--个人中心主页组件-->
<template>
  <div>
    <section class="bg">
      <section class="personal">
        <HomepageNav :PersonalNav="PersonalNav" @HomepageNav="tabSwitch"></HomepageNav>
        <section class="personal-content">
          <keep-alive>
            <component :is='currentView' ></component>
          </keep-alive>
        </section>
      </section>
    </section>
  </div>
</template>

<!--数据交互-->
<script>
  import HomepageNav from 'components/Personal/PersonalNav' // 个人首页
  import Homepage from 'components/Personal/Homepage' //底部组件
  import Workpaint from 'components/Personal/Workpaint' //底部组件
  import Demand from 'components/Personal/Workdemand' //底部组件
  import Workactivity from 'components/Personal/Workactivity' //底部组件
  import Workcollection from 'components/Personal/Workcollection' //底部组件

  export default {
    data: () => {
      return {
        PersonalNav:[
          {name:"主页",bol:true,tab:"Homepage"},
          {name:"作品",bol:false,tab:"Workpaint"},
          {name:"需求",bol:false,tab:"Demand"},
          {name:"活动",bol:false,tab:"Workactivity"},
          {name:"收藏",bol:false,tab:"Workcollection"},
        ],
        currentView:Homepage,
      }
    },
    components: {
      HomepageNav,
      Homepage,
      Workpaint,
      Demand,
      Workactivity,
      Workcollection
    },
//    beforeRouteEnter(to, from, next){
//      next(vm => { //推荐画师跳转到主页作品
//        let path = to.fullPath;
//        if (path.indexOf('products') !== -1)
//          vm.currentView = appWorkpaint;
//        vm.tabSwitch(0); //设置active
//      })
//    },
    methods:{
      tabSwitch(index){
        for(let i = 0; i < this.PersonalNav.length; i++){
          this.PersonalNav[i].bol = false;
          if(index === i){
            this.PersonalNav[i].bol = true;
            this.currentView = this.PersonalNav[i].tab;
          }
        }
      },
    }
  }
</script>

<!--组件样式-->
<style lang="scss" scoped>

  @import "../../common/css/reset.css";
  @import "../../common/css/define";

  .bg {
    width: 100%;
    background-color: #fafafa;
    .personal {
      width: 1180px;
      background-color: $white;
      margin: 20px auto 20px auto;

    }
  }



  .personal-content {
    width: 1180px;
    margin:20px auto 0 auto;
  }

</style>



