<!--作品列表组件-->
<template>
  <div id="activity-list">
    <ul class="activity-list__nav">
      <li v-for="(item,index) in orderByActive" :class="{'is--active':item.flag}" @click="change(index)">
        {{item.title}}
      </li>
    </ul>
    <ul class="activity-list__items">
      <li v-for="item in items">
        <router-link
          :to="'/painting/'+item.pictureid" class="activity-list__pic">
          <img :src='item.imgs&&JSON.parse(item.imgs)[0]'/>
        </router-link>
        <section class="activity-list__user">
          <a
            :href="`personal.html#/accounts/${item.uid}`"
            class="activity-list__header">
            <img :src="item.avatar">
          </a>
          <section class="activity-list__info">
            <i
              :title="item.nickname"
              class="activity-list__name">{{item.nickname}}</i>
            <i class="activity-list__time">{{item.createtime | UnixToNormalTime(2)}}</i>
            <i class="activity-list__ticket"
               @click="handleShare(item.title,item.imgs)">拉票</i>
          </section>
          <section class="activity-list__id">
            <h6>作品编号{{item.pictureid}}</h6>
            <section class="activity-list__btn">
              <PraiseCommon
                :praise="{moduleid:item.pictureid,count:item.pcount,uid: '18202',authorid: item.uid,name: 'picture_praise'}">
                <template scope="props" slot="title">
                  <span>{{props.title}}</span>
                </template>
              </PraiseCommon>
            </section>
          </section>
          <section class="activity-list__num">{{item.imgs && JSON.parse(item.imgs).length}}</section>
        </section>
      </li>
    </ul>
    <!--分页组件-->
    <Pagination :pageCount="pageCount"
                :pageSize="pageSize"
                @pageChange="pageChange">
    </Pagination>
  </div>
</template>

<!--数据交互-->
<script>
  import Pagination from 'components/Common/Pagination';
  import PraiseCommon from 'components/Common/PraiseCommon'

  export default {
    components: {
      Pagination,
      PraiseCommon
    },
    data: () => ({
      orderBy: 'createtime',
      orderByActive: [{title: '最新作品', flag: true, orderBy: 'createtime'}, {
        title: '投票排名',
        flag: false,
        orderBy: 'votes'
      }],
      id: '',
      items: [], //作品列表
      uid: '18202', //用户id
      pageSize: 30, // 每页显示的几条
      pageCount: 0,  // 总条数
      pageIndex: 1,  //当前页数
    }),
    mounted(){
      this.id = this.$route.params.id;
      this.getList(this.id);
      this.getPraise();
    },
    methods: {
      getList(id){ //获取画作列表
        let _this_ = this;
        this.getToken().then(token => {
          this.$http.get('huodong/findworks', {
            params: {
              access_token: token,
              id: id,
              order: _this_.orderBy,
              page: this.pageIndex
            }
          }).then(res => {
            this.items = res.data.data.list;
            this.pageCount = parseInt(res.data.data.total);
          }).catch(err => {
            console.log(err);
          })
        })
      },

      //获取已赞id
      getPraise(){
        this.getUsers('1', { //点赞
          uid: 18202,
          name: 'picture_praise'
        }).then(res => {
          let data = res.data.data.list.map(item => {
            return item.moduleid
          });
          this.$store.dispatch('onPraiseUsers', data);
        });
      },

      //分享到微博
      handleShare(name, img){
        var _shareUrl = 'http://v.t.sina.com.cn/share/share.php?&appkey=649394723';     //真实的appkey ，必选参数
        _shareUrl += '&url=' + encodeURIComponent(document.location);     //参数url设置分享的内容链接|默认当前页location，可选参数
        _shareUrl += '&title=' + encodeURIComponent(name || document.title);    //参数title设置分享的标题|默认当前页标题，可选参数
        _shareUrl += '&content=' + 'utf-8';   //参数content设置页面编码gb2312|utf-8，可选参数
        _shareUrl += '&pic=' + encodeURIComponent(JSON.parse(img)[0] || '');  //参数pic设置图片链接|默认为空，可选参数
        window.open(_shareUrl, '_blank', 'toolbar=no,menubar=no,scrollbars=no,resizable=1,location=no,status=0,' +
          'width=' + 660 + ',height=' + 550 + ',top=' + (screen.height - 550) / 2 + ',left=' + (screen.width - 660) / 2);
      },
      change(index){//排序切换
        this.orderByActive.forEach(item => {
          item.flag = false;
        });
        this.orderByActive[index].flag = true;
        this.orderBy = this.orderByActive[index].orderBy;
        this.getList(this.id);
      },

      //分页调用
      pageChange(val){
        this.pageIndex = val;
        this.getList(id);
      }
    }
  }
</script>

<!--组件样式-->
<style scoped lang="scss">
  @import "../../common/css/define";

  #activity-list {
    color: $black;
    margin-bottom: 20px;

    .activity-list__nav {
      max-width: 1180px;
      margin: 20px auto;
      > li {
        display: inline-block;
        font-size: 14px;
        margin-right: 32px;
        cursor: pointer;
        &.is--active {
          font-weight: bold;
          color: $pink;
        }
      }
    }
    .activity-list__items {
      max-width: 1180px;
      margin: 0 auto;
      > li {
        vertical-align: top;
        position: relative;
        width: 220px;
        border-radius: 4px;
        overflow: hidden;
        background-color: #fff;
        display: inline-block;
        margin: 0 20px 20px 0;
        &:nth-of-type(5n) {
          margin-right: 0;
        }
        @include wrapShow;

        .activity-list__pic {
          width: 100%;
          display: block;
          height: 330px;
          overflow: hidden;
          > img {
            width: 100%;
          }
        }
        .activity-list__user {
          > a {
            display: block;
          }
          padding: 20px 8px;
          .activity-list__header {
            width: 20px;
            height: 20px;
            border-radius: 50%;
            margin-bottom: 16px;
            overflow: hidden;
            > img {
              width: 100%;
            }
            display: inline-block;
            vertical-align: middle;
          }
          .activity-list__info {
            height: 20px;
            width: 176px;
            line-height: 20px;
            font-size: 12px;
            display: inline-block;
            vertical-align: middle;
            padding-bottom: 16px;
            > i {
              display: inline-block;
              vertical-align: top;
            }
            .activity-list__name {
              width: 60px;
              margin: 0 6px;
              @include beyondPass;
            }
            .activity-list__time {
              color: #999;
            }
            .activity-list__ticket {
              float: right;
              color: $pink;
              width: 40px;
              text-align: right;
              cursor: pointer;
              background: url("./images/share@12x10.png") no-repeat 0 center;
            }
          }
          .activity-list__id {
            border-top: 2px solid #e6e6e6;
            > h6 {
              text-align: center;
              font-weight: normal;
              margin: 24px 0;
              font-size: 14px;
            }
            .activity-list__btn {
              text-align: center;
              cursor: pointer;
              > i {
                display: inline-block;
                width: 120px;
                height: 40px;
                line-height: 40px;
                color: #fff;
                font-size: 14px;
                border-radius: 4px;
                text-align: center;
                background: #e6e6e6;

                &.is--praise {
                  @include boxShow;
                  @include linear;
                }
              }
            }
          }
          .activity-list__num {
            width: 30px;
            height: 20px;
            background-color: rgba(0, 0, 0, 0.3);
            font-size: 12px;
            position: absolute;
            color: #fff;
            border-radius: 4px 0 4px 0;
            text-align: center;
            line-height: 20px;
            left: 0;
            top: 0;
          }
        }
      }

    }
  }
</style>



