<!--约稿信息组件-->
<template>
  <div class="pact-info">
    <!--导航组件-->
    <PactNav
      :linkNav="'/new/pact'"
      :menus="lists"
      @change="change"></PactNav>
    <PactItem :lists="dataList" :btn="btn">
      <template slot="feedback" scope="props">
        <li>需求张数：<i>{{props.hot}} 张</i></li>
        <li>应征人数：<i>{{props.count}} 人</i></li>
      </template>
      <Pagination
        :pageCount="pageCount"
        :pageSize="pageSize"
        @pageChange="pageChange"
        slot="page"></Pagination>
    </PactItem>
  </div>
</template>

<!--数据交互-->
<script>
  import Pagination from 'components/Common/Pagination'
  import PactNav from 'components/Pact/PactNav'
  import PactItem from 'components/Pact/PactItem'

  export default {
    components: {
      PactNav,
      Pagination,
      PactItem
    },
    data: () => ({
      lists: {
        menu: [
          {title: '按发布时间', isActive: true, orderBy: ''},
          {title: '按截稿日期', isActive: false, orderBy: 'over'},
          {title: '按稿费高低', isActive: false, orderBy: 'cost'},
          {title: '正在进行', isActive: false, orderBy: 'being'}
        ], btn: "发布约稿"
      },

      dataList: {
        item: [],
        path: '/pact' //跳转路由
      },
      btn: '去应征',
      orderBy: '',
      pageSize: 30, // 每页显示的几条
      pageCount: 0,  // 总条数
      pageIndex: 1,  //当前页数
    }),
    mounted(){
      this.getList();
    },
    activated(){
    },
    methods: {
      change(index){ //菜单切换
        this.lists.menu.forEach(item => {
          item.isActive = false;
        });
        this.lists.menu[index].isActive = true;
        this.orderBy = this.lists.menu[index].orderBy;
        this.getList();
      },
      getList(){ //获取约稿列表
        let _this_ = this;
        this.getToken().then(token => {
          this.$http.get('yuegao/findallyuegao', {
            params: {
              access_token: token,
              order: this.orderBy,
              page: this.pageIndex,
            }
          }).then(res => {
            _this_.dataList.item = res.data.data.list;
            this.pageCount = parseInt(res.data.data.total);

          }).catch(err => {
            console.log(err);
          })
        })
      },

      //分页调用
      pageChange(val){
        this.pageIndex = val;
        this.getList();
      }
    }
  }
</script>





