<!-- 2017/3/22-私信对话框-->
<template>
  <section class="dialog">
    <section class="dialog-tit">
      <span @click="returnDiaLogList()">&lt;返回列表</span>
      <span>与<i>{{priLetterNickname}}</i>对话</span>
      <span v-if="false">清空对话&gt;</span>
    </section>
    <section class="dialog-con">
      <ul v-if="(DialogEveryList.length === 0)?false:true" class="dialog-con__ul">
        <li class="dialog-con__list" v-for="item in DialogEveryList">
          <section class="clearfix">
            <span class="dialog-con__head" :class="{dialogCon__headOther:(item.uid===item.mid?true:false)}">
              <img :src="item.avatar" alt="">
            </span>
            <section class="dialog-con__details":class="{dialogCon__detailsOther:(item.uid===item.mid?true:false)}">
              <em>&#9670;</em><i>&#9670;</i>
              <span :class="(item.content.length > 70 && item.uid===item.mid) ? '':dialogRight">{{item.content}}</span>
            </section>
          </section>
          <span class="dialog-con__time" :class="{dialogCon__timeOther:(item.uid===item.mid?true:false)}">{{item.createtime | UnixToNormalTime(1)}}</span>
        </li>
      </ul>
    </section>
    <section class="dialog-lev">
      <textarea name="" rows="" cols="" v-model="commontContent" placeholder="请输入要发送的内容" id="dialog-lev__comment" @keyup.enter="sendPrivate"></textarea>
      <span class="dialog-lev__reply" @click="sendPrivate">回复</span>
      <Expression @addExpression="addExpression"></Expression>
    </section>
  </section>

</template>

<script>
  import Expression from 'components/Common/Expression' // 表情组件
  export default {
    props:['priLetterNickname','DialogEveryList'],
    data: () => {
      return {
        commontContent:"",
        dialogRight:"dialogRight"
      }
    },
    components: {
      Expression
    },
    methods:{
      returnDiaLogList(index){   // 返回列表
        this.$emit('returnDiaLogList',index);
      },
      sendPrivate(content){  // 发送私细
        this.$emit('sendPrivate',this.commontContent)
      },
      addExpression(item,index){
        this.$emit("addExpression",item,index)
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../common/css/reset.css";
  @import "../../common/css/define";

  .dialog {
    .dialog-con__list,.dialog-con__list-o {
      margin-bottom: 20px;
    }
    .dialog-tit {
      width: 960px;
      height: 50px;
      border-bottom: 1px solid #e6e6e6;
      padding-left: 40px;
      span {
        display: inline-block;
        line-height: 50px;
        &:first-child, &:last-child {
          width: 60px;
          font-size: 12px;
          color: #03a9f4;
          cursor: pointer;
        }
        &:nth-of-type(2) {
          width: 800px;
          font-size: 14px;
          color: $black;
          text-align: center;
          letter-spacing:0.2em;
          i {
            font-weight: bold;
            color: $pink;
            cursor: pointer;
          }
        }
        &:last-child {
          color: #999999;
        }
      }
    }

    .dialog-con {

      .dialog-con__ul {
        width: 920px;
        overflow-y: scroll;
        padding: 40px 40px 20px 40px;
        background-color: #fafafa;
        border-bottom: 1px solid #e6e6e6;
        height: 550px;
      }
      .dialog-con__head {
        display: block;
        float: left;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        overflow: hidden;
        margin-right: 15px;
        img {
          width: 100%;
        }
      }
      .dialogCon__headOther {
        float: right;
        margin: 0 0 0 15px;
      }
      .dialog-con__details{
        float: left;
        position: relative;
        em,i {
          font-style: normal;
          font-size: 32px;
          position: absolute;
          left: -10px;
          top: 0;
          color: rgba(230,230,230,0.3);
        }
        i {
          left: -8px;
          color: white;
        }
        span {
          max-width: 620px;
          display: block;
          box-sizing: border-box;
          font-size: 12px;
          color: #666666;
          background-color: $white;
          line-height: 18px;
          @include wrapShow;
          border-radius: 4px;
          padding: 10px 15px;
        }
      }
      .dialogCon__detailsOther {
        float: right;
        em,i {
          right: -10px;
          text-align: right;
        }
        i {
          color: $pink;
        }
        span {
          color: $white;
          background-color: $pink;
        }
        .dialogRight {
          text-align: right;
        }
      }
      .dialog-con__time {
        display: block;
        font-size: 12px;
        color: #999999;
        margin: 10px 0 0 56px;
      }
      .dialogCon__timeOther {
        text-align: right;
        margin-right: 56px;
      }
    }
    .dialog-con__ul::-webkit-scrollbar-track {
      box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
      background-color: #F5F5F5;
    }
    .dialog-con__ul::-webkit-scrollbar {
      width: 6px;
      background-color: #F5F5F5;
    }
    .dialog-con__ul::-webkit-scrollbar-thumb {
      background-color: #AAA;
      border-radius: 10px;
      background-image: -webkit-linear-gradient(90deg, rgba(0, 0, 0, .1) 25%, transparent 25%, transparent 50%, rgba(0, 0, 0, .1) 50%, rgba(0, 0, 0, .1) 75%, transparent 75%, transparent)
    }

    .dialog-lev {
      width: 920px;
      margin: 40px auto 0 auto;
      position: relative;
      textarea {
        width: 920px;
        height: 100px;
        border: 1px solid #e6e6e6;
        border-radius: 4px;
        resize:none;
        font-size: 14px;
        color: $black;
        line-height: 18px;
        padding:10px;
        box-sizing: border-box;
        transition: all 0.3s;
        &:focus {
          border: 1px solid $pink;
        }
      }
      .dialog-lev__reply {
        display: inline-block;
        width: 100px;
        height: 40px;
        line-height: 40px;
        text-align: center;
        color: $white;
        font-size: 16px;
        border-radius: 4px;
        @include linear;
        cursor: pointer;
        transition: all 0.3s;
        position: absolute;
        top: 120px;
        right:0;
        &:active {
          box-shadow: none;
        }
      }
    }
  }
</style>



