<!-- 2017/3/22- yyy -个人主页 约稿-->
<template>
  <section>
    <section class="workicon">
      <personalNav :ListComNav="ListComNav" @homeCom="tabSwitch" class="workicon__nav"></personalNav>
      <keep-alive>
        <component :is='currentView'
                   :ComManu="manuEveryList"
                   :manuPageCount="manuPageCount"
                   :manuPageSize="manuPageSize"
                   @manuPageChange="manuPageChange"
                   :ComCollect="CollectEveryList"
                   :collectPageCount="collectPageCount"
                   :collectPageSize="collectPageSize"
                   @collectPageChange="collectPageChange"
        ></component>
      </keep-alive>
      <div class="workicon__propmt" v-if="workiconPropmt">暂无数据</div>
    </section>
  </section>
</template>

<script>
  import personalNav from 'components/Personal/ComNav';
  import ComYuegao from 'components/Search/ComManuscripts';  // 约稿组件
  import ComCollect from 'components/Search/ComCollect' // 征稿组件
  export default {
    data: () => {
      return {
        ListComNav:[
          {name:"约稿",bol:true,tab:"ComYuegao"},
          {name:"征稿",bol:false,tab:"ComCollect"},
        ],
        currentView:ComYuegao,
        workiconPropmt:false,
        manuEveryList:[],
        manuPageCount:0, //  需求-->约稿总页数
        manuPageSize: 20,  // 需求-->约稿总每页的个数
        manuPageIndex:1,
        manuLastCount:0,
        CollectEveryList:[],
        collectPageCount:0, // 需求-->征稿总页数
        collectPageSize:20,  // 需求-->征稿每页的个数
        collectPageIndex:1,
        collectLastCount:0,
      }
    },
    components:{
      personalNav,
      ComYuegao,
      ComCollect,
    },
    mounted(){
      this.getDemandList();
    },
    methods:{
      tabSwitch(index){
        for(let i = 0; i < this.ListComNav.length; i++){
          this.ListComNav[i].bol = false;
          if(index === i){
            this.ListComNav[i].bol = true;
            this.currentView = this.ListComNav[i].tab;
          }
        }
        switch (index){
          case 0:
            (this.manuLastCount === 0)? (this.workiconPropmt = true) : (this.workiconPropmt = false);
            break;
          case 1:
            (this.collectLastCount === 0)? (this.workiconPropmt = true) : (this.workiconPropmt = false);
            break;
        }
      },
      manuPageChange(val){
        this.manuPageIndex = val;
        this.getDemandList();
      },
      collectPageChange(val){
        this.collectPageIndex = val;
        this.getDemandList();
      },
      getDemandList(){
        let _this = this;
        _this.getToken().then(token => {
          this.$http.get('yuegao/findallyuegao', {  //需求--->约稿
            params: {
              access_token: token,
              uid: 8, // 作者本人  用户详细信息接口
              page: _this.manuPageIndex,
              show: _this.manuPageSize
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this.manuEveryList = res.data.data.list;
              _this.manuPageCount = parseInt(res.data.data.total);
              _this.manuLastCount = res.data.data.list.length;
              if(res.data.data.list.length === 0){
                this.workiconPropmt = true
              }
            }
          }).catch(err => {
            console.log(err);
          });

          this.$http.get('zhenggao/findallzhenggao', {  // 需求--->征稿
            params: {
              access_token: token,
              uid: 8, // 作者本人  用户详细信息接口
              order:'time',
              page: _this.collectPageIndex,
              show: _this.collectPageSize
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this.CollectEveryList = res.data.data.list;
              _this.collectPageCount = parseInt(res.data.data.total);
              _this.collectLastCount = res.data.data.list.length;
            }
          }).catch(err => {
            console.log(err);
          });
        })
      }
    }
  }

</script>

<style lang="scss" scoped>

  @import "../../common/css/reset.css";
  @import "../../common/css/define";

  .workicon__nav {
    margin: 40px 0 20px 0;
  }

  .workicon__propmt {
    width: 1180px;
    text-align: center;
    margin: 30px auto;
    font-size: 16px;
    font-style: italic;
    color: $paleBlue;
    cursor: pointer;
  }

</style>




