<!--征稿信息详情-->
<template>
  <section class="collect-detail">
    <CollectTitle
      :items="items"></CollectTitle>
    <section class="collect-detail__content">
      <CollectContent

        :btnName="'我要投稿'"
        :link="`/new/design/${id}`"
        :content="items.content"
        :menus="menus">
        <dd slot="title" class="collect-detail__price">
          <i>稿费：{{items.lowcost}}～{{items.highcost}}元</i>
          <i>福利：{{items.gift}}</i>
        </dd>
      </CollectContent>
    </section>
    <CollectUserInfo
      :uid="items.uid"
      class="l-mt"></CollectUserInfo>
    <CollectDetailNav
      @change="onChangeTable"
      :names="lists"></CollectDetailNav>
    <CollectDetailItem
      :items="designLists"
      @onSubmitChoose="onSubmitChoose"></CollectDetailItem>
    <Pagination
      @pageChange="pageChange"
      :pageSize="pageSize"
      :pageCount="pageCount"></Pagination>
  </section>
</template>

<!--数据交互-->
<script>
  import CollectTitle from 'components/Pact/PactDetailTitle'        //抬头组件
  import CollectContent from 'components/Pact/PactDetailContent'    //描述内容组件
  import CollectUserInfo from 'components/Pact/PactDetailUserInfo'  //用户信息组件
  import CollectDetailNav from 'components/Activity/ActivityNav'    //公用导航
  import CollectDetailItem from 'components/Design/CollectDetailItem'     //征稿详情设计组件
  import Pagination from 'components/Common/Pagination' //分页组件

  export default {
    components: {
      CollectTitle,
      CollectContent,
      CollectUserInfo,
      CollectDetailNav,
      CollectDetailItem,
      Pagination
    },
    data: () => ({
      menus: ['要求', '回报', '投稿加群', '注意'],
      lists: [{title: '全部征稿', isActive: true}, {title: '采纳征稿', isActive: false}],
      id: 0,  //征稿id
      items: [],
      designLists: [], //征稿设计列表
      designListsAll: [],  //征稿设计列表原始数据
      pageCount: 0,
      pageIndex: 1,
      pageSize: 30,
    }),
    mounted(){
      this.id = this.$route.params.id;
      this.getData();
      this.getDesignList();
    },
    methods: {
      //获取数据
      getData(){
        this.getToken().then(token => {
          this.$http.get('zhenggao/findzhenggao', {
            params: {
              access_token: token,
              id: this.id
            }
          }).then(res => {
            this.items = res.data.data.list;
          }).catch(err => {
            console.log(err);
          })
        })
      },

      //获取征稿list
      getDesignList(){
        this.getToken().then(token => {
          this.$http.get('zhenggao/zhenggaolist', {
            params: {
              access_token: token,
              id: this.id,
              page: this.pageIndex,
            }
          }).then(res => {
            this.designLists = res.data.data.list;
            this.designListsAll = res.data.data.list;
            this.pageCount = parseInt(res.data.data.total);
          }).catch(err => {
            console.log(err);
          })
        })
      },

      //设计选择
      onSubmitChoose(val){
        this.getToken().then(token => {
          this.$http.get('zhenggao/zhengaoresult', {
            params: {
              access_token: token,
              id: val.id,
              stats: val.stats,
            }
          }).then(res => {
            this.getDesignList();
          }).catch(err => {
            console.log(err);
          })
        })
      },

      //导航切换
      onChangeTable(val){
        this.lists.forEach(item => {
          item.isActive = false;
        });
        this.lists[val].isActive = true;
        if (val === 0) {
          this.designLists = this.designListsAll;
        } else if (val === 1) { //已采纳数据
          let arr = [];
          this.designLists.forEach(item => {
            if (item.jstats === '2') {
              arr.push(item);
            }
          });
          this.designLists = arr;
          this.pageCount = arr.length;
        }
      },

      //分页调用
      pageChange(val){
        this.pageIndex = val;
        this.getDesignList();
      }
    }
  }
</script>

<!--组件样式-->
<style scoped lang="scss">
  @import "../../common/css/define";

  .collect-detail {
    max-width: 1180px;
    margin: 0 auto;
  }

  .collect-detail__content {
    width: 800px;
    margin-top: 20px;
    background-color: #fff;
    padding: 40px 20px;
    display: inline-block;
    @include wrapShow;
  }

  .l-mt {
    margin-top: 20px;
  }

  .collect-detail__price {
    width: 840px;
    height: 56px;
    line-height: 56px;
    background-color: #fafafa;
    margin: -40px 0 0 -20px;
    text-indent: 20px;
    > i {
      font-size: 16px;
      font-weight: bold;
      &:nth-of-type(1) {
        margin-right: 26px;
      }
    }
  }
</style>



