<!--菜单导航组件-->
<template>
    <section class="l-wrap">
        <ul class="menu">
            <li :class='{active:menu.isActive}' class="menu__best" v-for="(menu,index) in menus" @click="change(index)">{{menu.title}}</li>
            <li class="menu__search">
                <input type="text" placeholder="搜画作" v-model="searchText"/>
                <i class="menu__icon el-icon-search" @click="search"></i>
            </li>
        </ul>
    </section>
</template>

<!--数据交互-->
<script>
  export default {
    name: 'app-menu',
    data: () => {
      return {
          searchText :''
      }
    },
    props:['menus'],
    methods:{
      change(index){
        this.$emit('change',index)
      },
      search(){
        this.$emit('search',this.searchText)
      }
    }
  }
</script>

<!--组件样式-->
<style lang="scss" scoped>
    @import "../../common/css/define";

    .l-wrap {
        box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.038);
        background-color: #fff;
        margin-bottom: 40px;
    }
    .menu {
        font-size: 0;
        padding: 20px 0;
        width: 1180px;
        margin: 0 auto;
        > li {
            display: inline-block;
            height: 38px;
            font-size: 14px;
            color: $black;
            line-height: 38px;
            width: 98px;
            text-align: center;
            cursor: pointer;
            transition: 0.3s;

            &.active {
                border-radius: 4px;
                text-align: center;
                color: #fff;
                @include boxShow;
                @include linear;
            }
            &:active {
                box-shadow: none;
            }
        }
        .menu__search {
            width: 430px;
            float: right;
            position: relative;
            > input {
                height: 36px;
                width: 362px;
                border: 1px #e6e6e6 solid;
                border-radius: 4px;
                text-indent: 1em;
                line-height: 36px;
            }
            .menu__icon {
                display: block;
                width: 64px;
                height: 40px;
                background-color: #e86987;
                position: absolute;
                top: 0;
                right: 4px;
                cursor: pointer;
                font-size: 20px;
                color: #fff;
                line-height: 40px;
            }
        }
    }
</style>



