<!-- 2017/3/22- yyy -个人主页 作品-画作-公共部分 -->
<template>
  <section class="comnav">
    <ul>
      <li v-for="item,index in ListComNav" @click="tabSwitch(index,item.tab)" class="comnav__list" :class="{active:item.bol}">{{item.name}}</li>
    </ul>
  </section>
</template>

<script>
  export default {
    props:["ListComNav"],
    methods:{
      tabSwitch(index,type){
        this.$emit('homeCom',index,type);
      }
    }
  }
</script>

<style lang="scss" scoped>

  @import "../../common/css/reset.css";
  @import "../../common/css/define";

  .comnav__list {
    height: 40px;
    line-height: 40px;
    display: inline-block;
    border-radius: 4px;
    font-size: 14px;
    color: $black;
    padding: 0 30px;
    margin-right: 20px;
    cursor: pointer;
    &:last-child {
      margin-right: 0;
    }
  }
  .active {
    @include linear;
    color: $white;
    font-weight: bold;
  }

</style>



