<!-- 2017/4/13 yyy 当点击取消的时候，确认弹窗是否执行的组件-->
<template>
  <section class="delete" v-if="deleteYesNo">
    <section class="delete__con">
      <p><i>温馨提示:</i></p>
      <span class="delete__close" @click="deleteNo(type)">X</span>
      <div>{{deletePrompt}}</div>
      <section class="delete__pro">
        <div><span @click="deleteYes(type)">确认</span><span @click="deleteNo(type)">取消</span></div>
      </section>
    </section>
  </section>
</template>
<script>
  export default{
    props:["deletePrompt","deleteYesNo"],
    methods:{
      deleteYes(type){ // 确认
        this.$emit("deleteYes",type)
      },
      deleteNo(type){  // 取消
        this.$emit("deleteNo",type)
      }
    }
  }
</script>

<style lang="scss" scoped>

  @import "../../common/css/reset.css";
  @import "../../common/css/define";

  .delete {
    position: fixed;
    top: 0;
    left:0;
    width: 100%;
    height: 100%;
    z-index: 9999;
    background-color: rgba(0,0,0,0.15);
    .delete__con {
      width: 320px;
      height: 148px;
      @include wrapShow;
      position: fixed;
      top: 50%;
      left: 50%;
      margin-top: -74px;
      margin-left: -160px;
      .delete__close {
        font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
        font-size: 14px;
        color: #ffffff;
        position: absolute;
        top: 0;
        right: 15px;
        cursor: pointer;
        line-height: 32px;
      }
      p {
        width: 320px;
        height: 32px;
        background-color: $pink;
        border-radius: 4px 4px 0 0;
        i {
          font-size: 14px;
          color: $white;
          font-weight: bold;
          line-height: 32px;
          padding-left: 10px;
          letter-spacing: 0.1em;
        }
      }
      >div {
        width: 320px;
        height: 60px;
        text-align: center;
        line-height: 60px;
        font-size: 16px;
        color: $black;
        background-color: $white;
        letter-spacing: 0.1em;
      }
      .delete__pro {
        width: 320px;
        height: 56px;
        background-color: $white;
        border-radius: 0 0 4px 4px;
        >div {
          width: 160px;
          height: 56px;
          margin: 0 auto;
          span {
            display: inline-block;
            width: 64px;
            height: 32px;
            background-color: $pink;
            text-align: center;
            line-height: 32px;
            border-radius: 4px;
            font-size: 14px;
            color: $white;
            cursor: pointer;
            @include linear;
            letter-spacing: 0.2em;
            box-shadow: none;
            &:last-child {
              margin-left: 32px;
              background: #cccccc;
            }
          }
        }
      }
    }

  }

</style>



