// 活动详情导航
<template>
  <div id='activity-nav'>
    <ul class='activity-nav__list'>
      <li
        v-for='(name,index) in names'
        @click="change(index)"
        :class={active:name.isActive}>{{name.title}}
      </li>
    </ul>
  </div>
</template>

// 数据交互
<script>
  export default {
    props: ['names'],
    methods: {
      change(index){
        this.$emit('change', index);
      }
    }
  }
</script>

// 组件样式
<style lang="scss" scoped>
  @import "../../common/css/define";

  .activity-nav__list {
    max-width: 1180px;
    height: 50px;
    margin: 20px auto;
    border-radius: 4px;
    overflow: hidden;
    background-color: #fff;
    @include wrapShow;

    > li {
      display: inline-block;
      padding: 0 20px;
      line-height: 50px;
      font-size: 14px;
      cursor: pointer;
      color: $black;
      &.active {
        background-color: $pink;
        color: #fff;
      }
    }
  }

</style>



