<!-- 2017/3/28- yyy -个人主页 活动-->
<template>
  <section>
    <section style="margin: 40px 0 20px 0;">
      <personalNav :ListComNav="ListComNav" @homeCom="tabSwitch" class="workicon__nav"></personalNav>
      <keep-alive>
        <component :is='currentView'
                   :ActivityList="allEveryList"
                   :actPageCount="actPageCount"
                   :actPageSize="actPageSize"
                   @actPageChange="actPageChange"
                   :FabuActivityList="fabuEveryList"
                   :fabuPageCount="fabuPageCount"
                   :fabuPageSize="fabuPageSize"
                   @fabuPageChange="fabuPageChange"
                   :JoinActivityList="joinEveryList"
                   :joinPageCount="joinPageCount"
                   :joinPageSize="joinPageSize"
                   @joinPageChange="joinPageChange"
        ></component>
      </keep-alive>
      <div class="workicon__propmt" v-if="workiconPropmt">暂无数据</div>
    </section>
  </section>
</template>
<script>
  import personalNav from 'components/Personal/ComNav';
  import ComActivity from 'components/Search/Activity'; // 全部活动
  import MeFabuActivity from 'components/Personal/MeFabuActivity';  // 我发布的活动
  import MeJoinActivity from 'components/Personal/MeJoinActivity';  // 我参与的活动
  export default {
    data: () => {
      return {
        ListComNav:[
          {name:"全部",bol:true,tab:"ComActivity"},
          {name:"我发布的",bol:false,tab:"MeFabuActivity"},
          {name:"我参与的",bol:false,tab:"MeJoinActivity"},
        ],
        currentView:ComActivity,
        workiconPropmt:true,
        allEveryList:[],
        actPageCount:0, // 我发布的所有活动总个数
        actPageSize:10, // 我发布的所有活动每页的个数
        actPageIndex:1,
        actLastCount:0,
        fabuEveryList:[],
        fabuPageCount:0,  // 我发布的活动总个数
        fabuPageSize:10,  // 我发布的活动每页个数
        fabuPageIndex:1,
        fabuLastCount:0,
        joinEveryList:[],
        joinPageCount:0, // 我参与的活动总个数
        joinPageSize:10,  // 我参与活动每页的个数
        joinPageIndex:1,
        joinLastCount:0,
      }
    },
    components:{
      personalNav,
      ComActivity,
      MeFabuActivity,
      MeJoinActivity
    },
    mounted(){
      this.getActivityList()
    },
    methods:{
      tabSwitch(index){
        for(let i = 0; i < this.ListComNav.length; i++){
          this.ListComNav[i].bol = false;
          if(index === i){
            this.ListComNav[i].bol = true;
            this.currentView = this.ListComNav[i].tab;
          }
        }
        switch (index){
          case 0:
            (this.actLastCount === 0)? (this.workiconPropmt = true) : (this.workiconPropmt = false);
              break;
          case 1:
            (this.fabuLastCount === 0)? (this.workiconPropmt = true) : (this.workiconPropmt = false);
              break;
          case 2:
            (this.joinLastCount === 0)? (this.workiconPropmt = true) : (this.workiconPropmt = false);
              break;
        }
      },
      actPageChange(val){
        this.actPageIndex = val;
        this.getActivityList();
      },
      fabuPageChange(val){
        this.fabuPageIndex = val;
        this.getActivityList();
      },
      joinPageChange(val){
        this.joinPageIndex = val;
        this.getActivityList();
      },
      getActivityList(){
        let _this = this;
        this.getToken().then(token => {

          this.$http.get('huodong/findallhuodong', { // 所有的活动
            params: {
              access_token: token,
              uid:8,
              condition:"all",
              page: _this.actPageIndex,
              show: _this.actPageSize
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this.allEveryList = res.data.data.list;
              _this.actPageCount = parseInt(res.data.data.total);
              _this.actLastCount = res.data.data.list.length;
              if(res.data.data.list.length === 0){
                this.workiconPropmt = true
              }
            }
          }).catch(err => {
            console.log(err);
          });

          this.$http.get('huodong/findallhuodong', { // 我发布的活动
            params: {
              access_token: token,
              uid:18202,
              condition:"my",
              page: _this.fabuPageIndex,
              show: _this.fabuPageSize
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this.fabuEveryList = res.data.data.list;
              _this.fabuPageCount = parseInt(res.data.data.total);
              _this.fabuLastCount = res.data.data.list.length;
            }
          }).catch(err => {
            console.log(err);
          });

          this.$http.get('huodong/findallhuodong', { // 我参与的活动
            params: {
              access_token: token,
              uid:8,
              condition:"partake",
              page: _this.joinPageIndex,
              show: _this.joinPageSize
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this.joinEveryList = res.data.data.list;
              _this.joinPageCount = parseInt(res.data.data.total);
              _this.joinLastCount = res.data.data.list.length;
            }
          }).catch(err => {
            console.log(err);
          });
        })
      }
    }
  }
</script>

<style lang="scss" scoped>

  .workicon__propmt {
    width: 1180px;
    text-align: center;
    margin: 30px auto;
    font-size: 16px;
    font-style: italic;
    color: #03a9f4;
    cursor: pointer;
  }

</style>




