<!--用户关注-->
<template>
  <i @click="handleFollow(authorId)">{{authorId | isFollow(followId)}}</i>
</template>

<!--数据交互-->
<script>
  import {mapGetters} from 'vuex'

  export default {
    props: ['authorId'],
    computed: mapGetters([
      'followId'
    ]),
    methods: {
      //监听关注事件
      handleFollow(val){
        let _this_ = this;
        this.getToken().then(token => {
          this.$http.get('user/userfollow', {
            params: {
              access_token: token,
              uid: 500,
              fromuid: val
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              if (res.data.data.id === 2) { //取消关注
                _this_.followId.findIndex((value, index, arr) => { //查找数组位置
                  if (value === val) { //删除数组元素
                    arr.splice(index, 1);
                  }
                })
              } else { //关注
                _this_.followId.push(val);
              }
            }
          }).catch(err => {
            console.log(err);
          })
        })
      },
    }
  }
</script>



