const changeState = (value, type) => {
  let index = value - 1;
  let state;
  switch (type) {
    case 0:  //活动状态过滤器
      state = ['审核中', '预热中', '进行中', '已结束,待颁奖', '已结束,已颁奖'];
      break;
    case 1:  // 画作状态过滤器
      state = ['正常', '已删除', '系统删除'];
      break;
    case 2:  // 管理中心--> 约稿平台管理-->我发布的约稿 && 我应征的约稿 1、进行中 2、已删除  3、已结束  4、系统删除
      state = ['进行中', '已删除', '已结束', '系统删除'];
      break;
    case 3: // 消息页面--> 征稿通知 --> 选用/婉拒通知
      state = ['未采纳你的投稿，不要灰心继续加油', '中选中了你的投稿，请保持手机畅通', '已婉拒你的投稿，期待下次与你合作'];
      break;
    case 4:  // 全局所有活动的活动状态
      state = ['进行中', '审核中', '已结束', '预热中'];
      break;
    case 5:  // 全局所有活动状态对应的背景图 jx进行中  sh审核中  js已结束   yr预热中  此背景css在common里面
      state = ['activity__state-jx', 'activity__state-sh', 'activity__state-js', 'activity__state-yr'];
      break;
    case 6: // 管理中心-->活动管理-->我参加的活动
      state = ['进行中', '已结束，待颁奖', '已结束，已颁奖'];
      break;
  }
  return state[index];
};

/**
 *  后台返回的时间戳转化正常时间格式过滤器
 * @param type  时间类型
 * @param unixtime  后台返回的时间戳
 * @returns {string} 返回的格式为2016-12-31  或者 2016-12-31 12:30
 * @constructor
 */
const UnixToNormalTime = (unixtime, type) => {
  let unix = unixtime * 1000;
  let commonTime = new Date(unix), y = commonTime.getFullYear(), m = commonTime.getMonth() + 1,
    d = commonTime.getDate(), h = commonTime.getHours(), f = commonTime.getMinutes();

  let minute = 1000 * 60, hour = minute * 60, day = hour * 24, month = day * 30, year = month * 30;
  let nowTime = new Date().getTime(),
    diffValue = nowTime - unix; //得到发布到现在的时间差值
  // if(diffValue < 0){return;}
  let monthC = diffValue / month,
    weekC = diffValue / (7 * day),
    dayC = diffValue / day,
    hourC = diffValue / hour,
    minC = diffValue / minute,
    yearC = diffValue / year,
    secondC = diffValue / 1000,
    result = null;
  // 可根据type类型来输出不同格式的时间类型
  switch (type) {
    case 0:
      return y + '-' + (m < 10 ? '0' + m : m) + '-' + (d < 10 ? '0' + d : d); //格式 2016-12-31
      break;
    case 1:
      return y + '-' + (m < 10 ? '0' + m : m) + '-' + (d < 10 ? '0' + d : d) + ' ' +
        (h < 10 ? '0' + h : h) + ":" + (f < 10 ? '0' + f : f); //格式 2016-12-31 12:30
      break;
    case 2:
      if (yearC > 1) {
        result = `${parseInt(yearC)}年前`;
      }
      else if (monthC <= 6 && monthC >= 1) {
        result = "" + parseInt(monthC) + "个月前";
      }
      else if (weekC >= 1) {
        result = "" + parseInt(weekC) + "周前";
      }
      else if (dayC >= 1) {
        result = "" + parseInt(dayC) + "天前";
      }
      else if (hourC >= 1) {
        result = "" + parseInt(hourC) + "小时前";
      }
      else if (minC >= 1) {
        result = "" + parseInt(minC) + "分钟前";
      } else if (secondC >= 1) {
        result = `${parseInt(secondC)}秒前`;
      }
      return result;
      break;
    case 3:
      let duration = (Date.now() - unixtime) / 1000;
      return duration > 60 ? `${Math.round(duration / 60)} 分钟前` : `${Math.round(Math.max(duration, 1))} 秒前`
  }
};

/**
 * 过滤是否关注
 * @param n    元素
 * @param arr  数组
 * @returns {*}
 */
const isFollow = (n, arr) => {
  for (let item of arr) {
    if (item === n) {
      return '已关注';
    }
  }
  return '+关注';
};

/**
 * 处理空数据
 * @param val   数值
 * @param type  转换类型默认值：2， ‘1-返回暂无数据‘，’2-返回0‘
 * @returns {*}
 */
const setNull = (val, type=2) => {
  switch (type) {
    case 1:
      return val ? val : '暂无数据';
      break;
    case 2:
      return val ? val : '0';
  }
};

export {changeState, UnixToNormalTime, isFollow, setNull};



