<!-- 2017/3/22- yyy -作品-画铺-公共部分 -->
<template>
  <section class="comshop">
    <ul class="comshop__ul">
      <li class="comshop__list" v-for="item in ComShop">
        <section class="comshop__icon">
          <!-- 跳转到画铺详情页面 -->
          <a :href="'pact.html#/painting/'+item.id" class="comshop__img" target="_blank">
            <img :src="item.cover" alt="">
            <span class="comshop__t"></span><span class="comshop__txt">{{JSON.parse(item.imgs).length}}</span>
          </a>
          <div class="comshop__hot clearfix">
            <h6>{{item.title}}</h6>
            <i class="comshop__price">￥{{item.lowcost}}-{{item.highcost}}</i>
            <i class="comshop__date">{{item.longdesignday}}-{{item.fastdesignday}}天</i>
          </div>
        </section>
        <section class="comshop__bot">
          <div class="comshop__bot-img"><a :href="'personal.html#/accounts/products/'+item.uid" target="_blank"><img :src="item.avatar" alt=""/></a></div>
          <a :href="'personal.html#/accounts/products/'+item.uid" target="_blank">{{item.nickname}}</a>
          <i>{{item.createtime | UnixToNormalTime(2)}}</i>
        </section>
      </li>
    </ul>
    <Pagination :pageCount="shopPageCount" :pageSize="shopPageSize" @pageChange="shopPageChange"></Pagination>
  </section>

</template>


<script>
  import Pagination from 'components/Common/Pagination' //分页组件
  export default {
    props:["ComShop","shopPageCount","shopPageSize"],
    components:{
      Pagination
    },
    methods:{
      shopPageChange(val){
        this.$emit("shopPageChange",val)
      }
    }
  }

</script>


<style lang="scss" scoped>

  @import "../../common/css/reset.css";
  @import "../../common/css/define";

  .comshop__list {
    width: 220px;
    display: inline-block;
    margin:0 20px 20px 0;
    overflow: hidden;
    @include wrapShow;
    &:nth-of-type(5n){
      margin-right: 0;
    }

    .comshop__icon {
      background-color: $white;
      border-radius:4px;

      .comshop__img {
        display: block;
        position: relative;
        width: 100%;
        height: 330px;
        img {
          display: block;
          width: 100%;
          height: 330px;
          box-sizing: border-box;
          border: 1px solid rgba(128,128,128,0.2);
        }
        .comshop__t {
          width: 40px;
          height: 20px;
          background-color: #4c4c4c;
          opacity: 0.8;
          position: absolute;
          top: 0;
          left: 0;
          border-radius:4px 0 4px 0;

        }
        .comshop__txt {
          width: 40px;
          height: 20px;
          font-size: 12px;
          color: $white;
          text-align: center;
          line-height: 20px;
          position: absolute;
          top: 0;
          left: 0;
        }
      }
    }
  }

  .comshop__hot {
    font-size: 12px;
    padding: 0 12px 15px 12px;
    >h6 {
      width: 180px;
      line-height: 30px;
      font-size: 12px;
      font-weight: normal;
      color: $black;
      @include beyondPass;
    }
    >.comshop__price {
      font-weight: bold;
      font-size: 14px;
      color: $pink;
    }
    >.comshop__date {
      float: right;
      color: $pink
    }
  }

  .comshop__bot {
    margin: 10px 0;
    a{
      max-width: 100px;
      display: inline-block;
      vertical-align: middle;
      font-size: 12px;
      color: $black;
      margin: 0 8px;
      @include beyondPass;
    }
    i {
      display: inline-block;
      vertical-align: middle;
      font-size: 12px;
      color: #999999;
    }
  }
  .comshop__bot-img {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    overflow: hidden;
    display: inline-block;
    vertical-align: middle;
    img {
      width: 100%;
    }
  }

</style>



