<!-- 2017/3/21- 提醒  评论我的 -->
<template>
  <section class="comment">
    <ul>
      <li class="comment__list" v-for="item in replyList">
        <section class="clearfix">
          <section class="comment__head"><a :href="'personal.html#/accounts/products/'+item.uid" target="_blank"><img
            :src="item.avatar" alt=""></a></section>
          <section class="comment__con">
            <section class="clearfix">
              <div class="comment__author">
                <a :href="'personal.html#/accounts/products/'+item.uid" target="_blank">{{item.nickname}}</a>
                <!-- 评论的类型 1、画作 2、活动 3、约稿 4、设计 5、征稿 -->
                <span v-if="item.type === '1'">
                  <i>回复了我的画作</i>
                  <a :href="'painting.html#/detail/'+item.moduleid" target="_blank">{{item.title}}</a>
                </span>
                <span v-if="item.type === '2'">
                  <i>回复了我的活动</i>
                  <a :href="'activity.html#/detail/'+item.moduleid" target="_blank">{{item.title}}</a>
                </span>
                <span v-if="item.type === '3'">
                  <i>回复了我的约稿</i>
                  <a :href="'pact.html#/pactDetail/'+item.moduleid" target="_blank">{{item.title}}</a>
                </span>
                <span v-if="item.type === '4'">
                  <i>回复了我的设计</i>
                  <a :href="'design.html#/pendingDet/'+item.moduleid" target="_blank">{{item.title}}</a>
                </span>
                <span v-if="item.type === '5'">
                  <i>回复了我的征稿</i>
                  <a :href="'design.html#/collectDet'+item.moduleid" target="_blank">{{item.title}}</a>
                </span>
              </div>
              <p class="comment__date">{{item.createtime | UnixToNormalTime(1)}}</p>
            </section>
            <p class="comment__com">{{item.content}}</p>
          </section>
        </section>

        <section class="comment__discuss">
          <a href="javascript:void(0)">我&nbsp;:</a>
          <p>{{item.pcontent}}</p>
        </section>

      </li>
    </ul>
    <Pagination :pageCount="replyPageCount" :pageSize="replyPageSize" @pageChange="replyPageChange"></Pagination>
  </section>
</template>

<script>
  import Pagination from 'components/Common/Pagination' //分页组件
  export default {
    props: ["replyList", "replyPageCount", "replyPageSize"],
    components: {
      Pagination
    },
    methods: {
      replyPageChange(val){
        this.$emit("replyPageChange", val)
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../../common/css/reset.css";
  @import "../../../common/css/define";

  .comment__list {
    padding-bottom: 15px;
    margin-bottom: 15px;
    border-bottom: 1px solid $lightgrey;
  }

  .comment__head {
    float: left;
    overflow: hidden;
    display: inline-block;
    vertical-align: middle;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    overflow: hidden;
    img {
      width: 100%;
      border: 1px solid rgba(128, 128, 128, 0.2);
    }
  }

  .comment__con {
    width: 860px;
    float: left;
    margin-left: 16px;

  }

  .comment__author {
    float: left;
    a {
      display: inline-block;
      font-size: 14px;
      color: $black;
      font-weight: bold;
      vertical-align: middle;
    }
    i {
      display: inline-block;
      vertical-align: middle;
      font-size: 12px;
      color: #666666;
    }
  }

  .comment__date {
    float: right;
    font-size: 12px;
    color: #999999;
    line-height: 20px;
  }

  .comment__com {
    font-size: 14px;
    color: $black;
    margin-top: 5px;
    text-align: justify;
  }

  .comment__discuss {
    margin-top: 10px;
    background-color: #fafafa;
    font-size: 14px;
    color: $black;
    padding: 12px 0;
    > a {
      display: inline-block;
      padding: 0 15px;
      color: $black;
    }
    > p {
      display: inline-block;
    }
  }
</style>



