<!--抽奖结果组件-->
<template>
  <div class="activity-result">
    <h6 class="activity-result__title">
      <i>icon</i>
      <span>获奖结果</span>
    </h6>
    <div class="activity-result__item" v-if="list.length > 0">
        {{list}}
    </div>
    <div v-else>
      暂无数据。。。
    </div>
  </div>
</template>


<!--数据交互-->
<script>
  export default {
    data: () => {
      return {
        list: '',//获奖结果
      }
    },
    mounted(){
      let id = this.$route.params.id;
      this.getResult(id);
    },
    methods: {
      getResult(id){ //获奖结果数据
        this.getToken().then(token => {
          this.$http.get('huodong/winresult', {
            params: {
              access_token: token,
              id: id
            }
          }).then(res => {
            this.list = res.data.data.list;
          }).catch(err => {
            console.log(err);
          })
        })
      }
    }
  }
</script>

<!--组件样式-->
<style lang="scss" scoped>
  @import "../../common/css/define";

  .activity-result {
    max-width: 1140px;
    margin: 0 auto 20px;
    padding: 40px 20px;
    background-color: #fff;
    @include wrapShow;
  }

  .activity-result__title {
    max-width: 1180px;
    margin-bottom: 36px;
    > i {
      display: inline-block;
      width: 4px;
      height: 24px;
      background-color: $pink;
      border-radius: 4px;
      font-size: 0;
      vertical-align: middle;
    }
    > span {
      font-size: 20px;
      font-weight: bold;
      line-height: 22px;
      display: inline-block;
      vertical-align: top;
      margin-left: 4px;
    }
  }

  .activity-result__item {
    > dd {
      margin-bottom: 36px;
      > h6 {
        width: 254px;
        height: 56px;
        background: url("./images/actr@bg.png") no-repeat;
        text-align: center;
        line-height: 56px;
        font-size: 20px;
        color: #fff;
        font-weight: bold;
      }
      > p {
        margin-top: 36px;
        font-size: 16px;
        font-weight: bold;
      }
    }
  }

  .activity-result__No {
    background-color: #fff;
    text-align: center;
    margin-top: 50px;
    > img {
      width: 170px;
      height: 154px;
    }
    > p {
      color: #666;
      font-size: 16px;
      font-weight: bold;
      text-align: center;
      margin-top: 16px;
    }
  }
</style>



