// 应征约稿组件
<template>
  <div class='pact-recruited'>
    <section class="pact-recruited__bg">
      <h6 class="pact-recruited__title">
        应征约稿
      </h6>
      <dl>
        <dd class="pact-recruited__price">
          <h6>我的报价</h6>
          <input
            v-model="cost"
            type="text"
            placeholder="输入单张预算">
        </dd>
        <dd class="pact-recruited__desc">
          <h6>我的留言</h6>
          <textarea
            v-model="message"
            type="text"
            placeholder="给约稿方的留言"/>
        </dd>
        <dd class="pact-recruited__show">
          <h6>展示作品</h6>
          <p>画作不够？先去
            <router-link to="/new/painting">上传画作</router-link>
            ，再来应征
          </p>
          <ul class="pact-recruited__menu"
              ref="group">
            <li
              :key="item.id"
              :class="index===0?'is--active':''"
              @click="changeTable($event,item.id)"
              v-for=" (item,index) in items">{{item.name}}
            </li>
          </ul>
          <ul class="pact-recruited__pro">
            <li
              ref="chooseImg"
              @click="handleChoosePic(index,img.id)"
              :key="img.id"
              v-for="(img,index) in imgs">
              <img
                :src="img.cover"/>
              <i>icon</i>
            </li>
          </ul>
        </dd>
        <dd class="pact-recruited__btn"
            @click="handleCommit">
          应征
        </dd>
      </dl>
    </section>
  </div>
</template>

<!--数据交互-->
<script>
  export default {
    data: () => ({
      uid: 64,//用户uid
      items: [], //分组数据
      imgs: [], //分组图片
      groupIndex: '1', //分组id
      hasChooseImg: [], //已选中的图片
      index: 0,
      cost: '', //报价
      message: '', //留言
      id: 0,  //约稿id
      authorid: 0,//发布者id
    }),
    mounted(){
      this.getGroup(1);
      this.id = this.$route.params.id;
      this.authorid = this.$route.params.uid
    },
    methods: {
      //获取图片分组
      getGroup(index){
        this.getToken().then(token => {
          //获取分组
          this.$http.get('Manage/findPictureGroup', {
            params: {
              access_token: token,
              uid: this.uid
            }
          }).then(res => {
            this.items = res.data.data.list;
          }).catch(err => {
            console.log(err);
          });
          this.getPic(index, token);
        })
      },

      //获取分组图片
      getPic(index, token) {
        this.$http.get('Manage/findPictures', {
          params: {
            access_token: token,
            uid: this.uid,
            groupid: index
          }
        }).then(res => {
          this.imgs = res.data.data.list.pictures;
        }).catch(err => {
          console.log(err);
        })
      },
      //改变分组
      changeTable(event, index){
        [...this.$refs.group.children].forEach(item => { //清空class
          item.classList.remove('is--active');
        });

        //添加class
        event.target.classList.add('is--active');
        this.getToken().then(token => {
          this.getPic(index, token);
        })
      },

      //选择图片
      handleChoosePic(index, id){
        let el = this.$refs.chooseImg[index];
        let len = this.hasChooseImg.length;

        if (el.className === 'is--choose') { //取消
          el.classList.remove('is--choose');
          this.hasChooseImg.findIndex((value, i, arr) => { //删除指定img
            if (value === id) {
              arr.splice(i, 1);
            }
          });
        } else if (len < 4) { //选中
          el.classList.add('is--choose');
          this.hasChooseImg.push(id);
        } else {
          alert('只能选择4张图')
        }
        console.log(this.hasChooseImg);
      },

      //提交应征
      handleCommit(){
        this.getToken().then(token => {
          this.$http.get('yuegao/baomingyuegao', {
            params: {
              access_token: token,
              uid: '12382',
              id: this.id,
              cost: this.cost,
              authorid: this.authorid,
              groupid: this.hasChooseImg,
              message: this.message,
            }
          }).then(res => {
            if (res.data.stats === '2000') {
                alert('应征成功');
               history.go(-1);
            } else {
                alert(res.data.msg);
            }
          }).catch(err => {
            console.log(err);
          })
        })
      },
    }
  }
</script>

<!--组件样式-->
<style scoped lang="scss">
  @import "../../common/css/define";

  .pact-recruited {
    .pact-recruited__bg {
      max-width: 1100px;
      border-radius: 4px;
      background-color: #fff;
      padding: 40px;
      margin: 20px auto;
      @include wrapShow;
      .pact-recruited__title {
        font-size: 22px;
        color: $black;
      }
      > dl {
        margin-top: 40px;
        > dd {
          margin-bottom: 40px;
          &:nth-last-of-type(1) {
            margin-bottom: 0;
          }
          > h6 {
            display: inline-block;
            font-weight: normal;
            font-size: 14px;
            vertical-align: middle;
          }
          > input {
            margin-left: 20px;
            width: 638px;
            height: 40px;
            border: 1px solid #e6e6e6;
            font-size: 14px;
            border-radius: 2px;
            text-indent: 16px;
            line-height: 40px;
          }
          > textarea {
            display: inline-block;
            margin-left: 20px;
            vertical-align: top;
            width: 634px;
            height: 172px;
            border-radius: 2px;
            border: 1px solid #e6e6e6;
            font-size: 14px;
            text-indent: 16px;
            line-height: 20px;
            resize: none;
          }
          > p {
            display: inline-block;
            margin-left: 20px;
            font-size: 14px;
            vertical-align: middle;
            color: $gray;
            > a {
              color: $pink;
              &:hover {
                text-decoration: underline;
              }
            }
          }
          .pact-recruited__menu {
            margin-top: 20px;
            > li {
              display: inline-block;
              height: 32px;
              padding: 0 17px;
              text-align: center;
              border-radius: 4px;
              font-size: 14px;
              line-height: 32px;
              transition: 0.3s;
              cursor: pointer;
              &.is--active {
                color: #fff;
                @include linear;
                @include boxShow;
              }
            }
          }
          .pact-recruited__pro {
            margin-top: 20px;
            font-size: 0;
            > li {
              width: 128px;
              height: 190px;
              display: inline-block;
              overflow: hidden;
              position: relative;
              margin: 0 10px 10px 0;
              cursor: pointer;
              &:nth-of-type(8n) {
                margin-right: 0;
              }
              > img {
                width: 100%;
              }
              > i {
                display: inline-block;
                width: 16px;
                height: 16px;
                position: absolute;
                top: 0;
                left: 0;
                background: url("./images/choose@bg.png") no-repeat;
              }
              &.is--choose {
                box-sizing: border-box;
                border: 1px solid $pink;
                > i {
                  background: url("../../components/Common/images/choosed.png") no-repeat;
                }
              }
            }
          }
        }
        .pact-recruited__btn {
          width: 120px;
          height: 48px;
          color: #fff;
          font-size: 16px;
          line-height: 48px;
          border-radius: 4px;
          text-align: center;
          cursor: pointer;
          transition: 0.3s;
          @include linear;
          @include boxShow;
          &:active {
            box-shadow: none;
          }
        }
      }
    }
  }
</style>



