<!-- 2017/4/10  私信列表与对话框汇总-->
<template>
  <section style="position: relative;">
    <transition name="fade">
      <PrivateLetter class="private-list" v-show="show"
                     :PriLetter="PriEverylist"
                     @unreadMessage="unreadMessage"
                     @readPriDiaLog="readPriDiaLog">
        <Pagination :pageCount="pageCount" :pageSize="pageSize" @pageChange="pageChange" slot="private"></Pagination>
      </PrivateLetter>
    </transition>
    <transition name="fade">
      <PrivateDialog class="private-per" v-show="hide"
                     :priLetterNickname="priLetterNickname"
                     :DialogEveryList="DialogEveryList"
                     @returnDiaLogList="returnDiaLogList"
                     @sendPrivate="sendPrivate"
                     @addExpression="addExpression">
      </PrivateDialog>
    </transition>
    <Prompt :hidePrompt="hidePrompt" :showNum="showNum" :promptLanguage="promptLanguage"></Prompt>
  </section>
</template>


<script>

  import PrivateLetter from 'components/Message/PrivateLetter' //私信组件
  import PrivateDialog from 'components/Message/PrivateDialog' // 对话框组件
  import Prompt from 'components/Common/Prompt' // 提示框
  import Pagination from 'components/Common/Pagination' //分页组件
  let closeTime; // 关闭定时器
  export default {
    data: () => {
      return {
        hidePrompt: false, // 弹窗默认是不可见的
        showNum: 2, // 0 成功  1 错误 2 提示
        promptLanguage: "发送内容不能为空",
        show: true, // 默认展示私信列表
        hide: false, // 默认隐藏私信列表
        priLetterUid:"", // 存储对应点击进去时，对应的uid
        priLetterNickname:"", // 记录点击那个私信时，获取到对应的名字
        priLetterIndex:"",
        DialogEveryList:[],  // 对应点击作者与每一个人详细的对话内容
        PriEverylist: [],  //每一页展示几条数据
        pageSize: 12, // 每页显示的几条
        pageCount: 0,  // 总页数
        pageIndex:1,// 私信读取带第几页
        priLitterUid:"",
      }
    },
    components: {
      PrivateLetter,
      PrivateDialog,
      Prompt,
      Pagination
    },
    mounted(){
      this.getPriLetter();
      this.privateLetter();
    },
    methods: {
      privateLetter(){
         // 通过privateType来判断是否从别的页面私信来的
        if(document.location.href.indexOf("privateType") !== -1){
          this.show = false;
          this.hide = true;
          let id = this.$route.fullPath.substring(this.$route.fullPath.lastIndexOf("/")+1,this.$route.fullPath.indexOf("&"));
          this.priLetterNickname = this.$route.fullPath.substring(this.$route.fullPath.indexOf("&")+1)
          let _this = this;
          _this.getToken().then(token => {
            _this.$http.get('message/showletter',{
              params: {
                access_token: token,
                uid: 8922,  // 发送用户的id
                sid: id,  // 点击哪一个人，记录他的receiver，这个receiver的id在私信列表获取，点击哪一个获取那一个
              }
            }).then(res => {
              if (res.data.stats === '2000') {
                _this.DialogEveryList = res.data.data.list.reverse(); // 倒序输出
              }
            }).catch(err => {
              console.log(err);
            });
          })
        }
      },
      unreadMessage() { // 处理已读私信息事件
        this.getToken().then(token=> { // 统计系统信息已读
          this.$http.get('message/markread', {
            params: {
              access_token: token,
              uid:8922
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              this.getPriLetter();
            }
          }).catch(err => {
            console.log(err);
          });
        })
      },
      getPriLetter(){ // 此接口获取所有人对我发送的个人私人分组列表
        let _this = this;
        this.getToken().then(token => {
          this.$http.get('message/showUser',{
            params: {
              access_token: token,
              uid: 8922, // 接受用户的id
              page: _this.pageIndex,
              show: _this.pageSize,
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this.PriEverylist = res.data.data.list;
              let a = 0;
              res.data.data.list.forEach(function (a1, i, a3) {
                  a += parseInt(a1.rcount);
              });
              this.$store.dispatch('onSubmitPrivate',a);
              _this.pageCount = parseInt(res.data.data.total);
            }
          }).catch(err => {
            console.log(err);
          });
        })
      },
      readPriDiaLog(index){  // 读取私人信息(点击那个读取那个信息)
        let _this = this;
        _this.show = !this.show;
        _this.hide = !this.hide;
        _this.priLetterIndex = index;
        _this.priLitterUid = _this.PriEverylist[index].uid;
        _this.priLetterNickname = _this.PriEverylist[index].nickname;
//         closeTime = setInterval(function () {
            _this.getToken().then(token => {
              _this.$http.get('message/showletter', {
                params: {
                  access_token: token,
                  uid: 8922,  // 发送用户的id
                  sid: _this.PriEverylist[index].uid,  // 点击哪一个人，记录他的receiver，这个receiver的id在私信列表获取，点击哪一个获取那一个
                }
              }).then(res => {
                if (res.data.stats === '2000') {
                  _this.DialogEveryList = res.data.data.list.reverse(); // 倒序输出
                }
              }).catch(err => {
                console.log(err);
              });
            })
//          },2000);

        _this.getToken().then(token => {
          _this.$http.get('message/markread', { // 标记未读状态-->结果返回给服务器，变成已读状态
            params: {
              access_token: token,
              uid: 8922,  // 发送用户的id
              aid: _this.PriEverylist[index].uid,  // 64点击哪一个人，记录他的receiver，这个receiver的id在私信列表获取，点击哪一个获取那一个
            }
          }).then(res => {
            if (res.data.stats === '2000') {

            }
          }).catch(err => {
            console.log(err);
          });
        })

      },
      returnDiaLogList(){ // 返回私人列表
        this.show = !this.show;
        this.hide = !this.hide;
        clearInterval(closeTime);
        this.getPriLetter();
        this.pageChange(this.pageNumber);
      },
      sendPrivate(content){  // 发送私人信息
        let _this = this;
        if (document.getElementById("dialog-lev__comment").value.length == 0) {
          this.hidePrompt = true;
          setTimeout(function () {
            _this.hidePrompt = false; // 让这个元素在页面上消失 time 是这个消失的动画需要2.0s时间
          }, 2000)
          return false; // 不在继续下面的操作
        } else {
          let $id = "";
          let priId = this.$route.fullPath.substring(this.$route.fullPath.lastIndexOf("/")+1,this.$route.fullPath.indexOf("&"));
          let a = /privateType/g; // 通过privateType来判断是否从私信以外的页面，如果不是就获取priLitterUid
          if(a.test(document.location.href)){
              $id = priId;
          }else {
              $id = _this.priLitterUid;
          }
          this.getToken().then(token => {
            this.$http.get('message/sendletter', {
              params: {
                access_token: token,
                uid: 8922, // 作者本人
                receiver:$id,  // 作者发给接受人的私信
                content:document.getElementById("dialog-lev__comment").value
              }
            }).then(res => {
              if (res.data.stats === '2000') {
                _this.DialogEveryList.push({  // 此数据同步更新到数据库和DialogEveryList中，给人的印象是没有卡顿现象
                  content:document.getElementById("dialog-lev__comment").value,
                  createtime:(new Date()).getTime()/1000,
                  uid:8922, // 发送者本人，此举只要目的是对话为组件 PrivateDialog中判断uid使用
                  mid:8922,  // 通过mid和uid对比，是否相等返回bol值，true我是发送人，false我是接受人
                  avatar:"http://obzco6ci9.bkt.clouddn.com/a7e6e0d3e80c59151cce9d740cb98102.jpg" // 注册成功，此值修改为cookie里面本人的头像
                });
                document.getElementById("dialog-lev__comment").value = "";  // 发送成功之后清空对话框里面的内容
              }
            }).catch(err => {
              console.log(err);
            });
          })
        }
      },
      addExpression(item, index){ // 发送自己给对面，添加表情
        document.getElementById("dialog-lev__comment").value += item;
      },
      pageChange(val){ //分页调用
        this.pageIndex = val;
        this.getPriLetter();
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../common/css/reset.css";
  @import "../../common/css/define";

  .fade-enter-active, .fade-leave-active {
    transition: opacity .8s
  }

  .fade-enter, .fade-leave-active {
    opacity: 0
  }

  .private-list, .private-per {
    position: absolute;
    top: 0;
    left: 0;
  }

</style>



