import axios from 'axios'
import Vue from 'vue'

// axios的一些配置，比如发送请求显示loading，请求回来loading消失之类的
axios.interceptors.request.use(function (config) {  //配置发送请求的信息
  // store.dispatch('showLoading');
  return config;
}, function (error) {
  return Promise.reject(error);
});

axios.interceptors.response.use(function (response) { //配置请求回来的信息
  // store.dispatch('hideLoading');
  return response;
}, function (error) {
  return Promise.reject(error);
});


axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';//post请求头部
axios.defaults.baseURL = 'http://jk.mshmtest.com/frontservicev1'; //测试URL
// axios.defaults.baseURL = 'http://api.mtlaser.cn//frontservicev1'; //公网URL
// axios.defaults.withCredentials = true;
Vue.prototype.$http = axios;  //其他页面在使用axios的时候直接  this.$http就可以了



