<!--画铺组件-->
<template>
  <div class="pact-shop">
    <PactNav
      :linkNav="linkNav"
      :menus="lists"
      class="l-marginB"
      @change="change"></PactNav>
    <PactShop :goods="goods" :link="link"></PactShop>
    <Pagination
      :pageCount="pageCount"
      :pageSize="pageSize"
      @pageChange="pageChange">
    </Pagination>
  </div>
</template>

<!--数据交互-->
<script>
  import PactNav from 'components/Pact/PactNav'
  import PactShop from 'components/Index/Goods'
  import Pagination from 'components/Common/Pagination'

  export default {
    components: {
      PactNav,
      PactShop,
      Pagination
    },
    data: () => ({
      linkNav: '/new/shop',//导航路由
      lists: {
        menu: [
          {title: '按实际', isActive: true, orderBy: ''},
          {title: '按报价', isActive: false, orderBy: 'cost'},
          {title: '按速度', isActive: false, orderBy: 'fast'},
          {title: '按人气', isActive: false, orderBy: ''},
          {title: '推荐画铺', isActive: false, orderBy: 'being'},
          {title: '我收藏的', isActive: false, orderBy: 'being'} //todo 我的收藏先判断用户是否存在
        ], btn: "发布画铺"
      },
      goods: [],
      orderBy: '',
      link: 'painting.html#/detail/',
      pageSize: 30, // 每页显示的几条
      pageCount: 0,  // 总条数
      pageIndex: 1,  //当前页数
    }),
    mounted(){
      this.getData();
    },
    methods: {
      change(index){ //菜单切换
        this.lists.menu.forEach(item => {
          item.isActive = false;
        });
        this.lists.menu[index].isActive = true;
        this.orderBy = this.lists.menu[index].orderBy;
        this.getData();
      },
      getData(){//获取数据
        this.getToken().then(token => {
          let _this_ = this;
          this.$http.get('huapu/findAllhuapu', {
            params: {
              access_token: token,
              uid: '',
              order: this.orderBy,
              page: this.pageIndex
            }
          }).then(res => {

            _this_.goods = res.data.data.list;
            this.pageCount = parseInt(res.data.data.total);
          }).catch(err => {
            console.log(err);
          })
        })
      },

      //分页调用
      pageChange(val){
        this.pageIndex = val;
        this.getData();
      }
    }
  }
</script>

<!--组件样式-->
<style lang="scss" scoped>
  .l-marginB {
    margin-bottom: 40px;
  }
</style>



