<!-- 2017/3/22- 约稿通知 应征通知-->
<template>
  <section class="submission">
    <ul>
      <li class="submission__list clearfix" v-for="item in colSunmission">
        <section style="height: 60px">
          <section class="submission__head"><img :src="item.avatar" alt=""></section>
          <section class="submission__l">
            <div class="submission__per">
              <a :href="'personal.html#/accounts/products/'+item.uid" target="_blank">{{item.nickname}}</a>
              <i>投稿了我的征稿</i>
              <a :href="'design.html#/collectDet/'+item.moduleid" target="_blank">{{item.title==null?'暂无标题':item.title}}</a></div>
            <div class="submission__author">{{item.pictit}}</div>
          </section>
          <section class="submission__r">{{item.createtime | UnixToNormalTime(1)}}</section>
        </section>
        <section class="submission__work">
          <a :href="'design.html#/collectDet/'+item.moduleid" target="_blank"><img :src="JSON.parse(item.imgs)[0]" alt=""></a>
        </section>
      </li>
    </ul>
    <Pagination :pageCount="subPageCount" :pageSize="subPageSize" @pageChange="subPageChange"></Pagination>
  </section>

</template>


<script>
  import Pagination from 'components/Common/Pagination' //分页组件
  export default {
    props:["colSunmission","subPageCount","subPageSize"],
    components:{
      Pagination
    },
    methods:{
      subPageChange(val){
        this.$emit("subPageChange",val)
      }
    }
  }
</script>


<style lang="scss" scoped>
  @import "../../../common/css/reset.css";
  @import "../../../common/css/define";

  submission {
    width: 920px;
  }
  .submission__list {
    padding-bottom: 20px;
    margin-bottom: 20px;
    border-bottom: 1px solid $lightgrey;
    .submission__head {
      float: left;
      > img {
        width: 40px;
        height: 40px;
        border-radius:50%
      }
    }
  }

  .submission__l {
    float: left;
    margin-left:16px;

  }
  .submission__per {
    >i {
      margin: 0 15px;
      font-size: 12px;
      color: #a3a3a3;
    }
    > a {
      font-weight: bold;
      font-size: 14px;
      color: $black;
    }
  }
  .submission__author {
    font-size: 14px;
    color: #666666;
    margin-top: 6px;
  }
  .submission__r {
    float: right;
    display: block;
    height: 18px;
    line-height: 18px;
    font-size: 12px;
    color: #999999;
    text-align: center;
  }

  .submission__work {
    padding-left: 55px;
     img {
      width: 130px;
      height: 190px;
      display: inline-block;
    }
  }
</style>



