<!--权限设置组件-->
<template>
  <section class="restriction">
    <h6>权限设置</h6>
    <slot name="title"></slot>
    <ul>
      <li v-for="(item,index) in menuFlag.select"
          @click="change(index,'select')"
          class="restriction__select"
          :class="{'is--active':item.flag}">
        {{item.title}}
      </li>
      <li v-for="(item,index) in menuFlag.choose"
          @click="change(index,'choose')"
          class="restriction__choose"
          :class="{'is-choose':item.flag}">
        {{item.title}}
        <p>{{item.desc}}</p>
      </li>
    </ul>
  </section>
</template>

<!--数据交互-->
<script>
  export default {
    name: 'app-restriction',
    created(){
      this.path = this.$route.fullPath;
      this.handleChangeMenu();
    },
    data: () => {
      return {
        //菜单切换
        menuFlag: {
          select: [
            {title: '所有人可见', flag: true},
            {title: '粉丝可见', flag: false}],
          choose: [
            {title: '允许右键', desc: '即允许用户通过右键下载保存你的作品', flag: true},
            {title: '添加水印', desc: '用户保存作品原图时，原图上将添加署名水印', flag: true}
          ]
        },
        permiss: {permiss: '1', act: ['1', '2']}, //权限初始化数据
        path: ''
      }
    },
    methods: {
      change(index, type) { //权限选择切换
        switch (type) {
          case 'select':
            this.menuFlag.select.forEach(item => {
              item.flag = false;
            });
            this.menuFlag.select[index].flag = true;
            this.permiss.permiss = index + 1;
            break;
          case 'choose':
            this.menuFlag.choose[index].flag = !this.menuFlag.choose[index].flag;
            this.permiss.act = []; //数组重置
            this.menuFlag.choose.forEach((item, index) => { //找出选中的权限
              if (item.flag === true) {
                this.permiss.act.push(index + 1)
              }
            });
            break;
        }
        this.$emit('setRes', this.permiss)
      },
      //当为约稿时菜单切换
      handleChangeMenu(){
        if (this.path === '/new/pact') {
          this.menuFlag = {
            select: [
              {title: '所有应征画师', flag: true},
              {title: '应征画师中我关注的', flag: false},
              {title: '不公开QQ', flag: false},
            ],
          }
        }
      }
    }
  }
</script>

<!--组件样式-->
<style scoped lang="scss">
  @import "../../common/css/define";

  .restriction {
    width: 320px;
    height: 310px;
    display: inline-block;
    vertical-align: top;
    margin-left: 10px;
    background-color: #fff;
    @include wrapShow;
    >b {
      font-size: 14px;
      padding: 40px 40px 0 40px;
      display: block;
    }
    > h6 {
      font-size: 16px;
      width: 100%;
      height: 48px;
      line-height: 48px;
      background-color: $pink;
      color: #fff;
      border-radius: 4px 4px 0 0;
      font-weight: normal;
      text-indent: 1.5em;
    }
    > ul {
      padding: 40px;
      > li {
        margin-bottom: 16px;
        font-size: 14px;
        color: $black;
        cursor: pointer;
        &.restriction__select {
          background: url("./images/select@16x16.png") no-repeat;
          text-indent: 1.5em;
        }
        &.restriction__choose {
          background: url("./images/choose@.png") no-repeat;
          text-indent: 1.5em;
        }
        &.is--active {
          background: url("./images/selected@16x16.png") no-repeat;
          text-indent: 1.5em;
        }
        &.is-choose {
          background: url("../Common/images/choosed.png") no-repeat;
          text-indent: 1.5em;
        }
        > p {
          margin-top: 4px;
          width: 210px;
          margin-left: 1.5em;
          text-align: left;
          text-indent: 0;
          line-height: 20px;
          color: $gray;
          font-size: 12px;
        }
      }
    }
  }
</style>



