<template>
  <div id="app">
    <section class="paintingNew">
      <h6>发布画作</h6>
      <app-new @commit="commit" :type="type"></app-new>
      <app-restriction @setRes='setRes'></app-restriction>
    </section>
  </div>
</template>

<!--组件导入-->
<script>
  import 'common/css/reset.css';
  import appHeader from 'components/Header/Header'
  import appFooter from 'components/Footer/Footer'
  import appList from 'components/Painting/List'
  import appRestriction from 'components/Painting/Restriction'
  import appNew from 'components/Painting/New'

  export default {
    components: {
      appHeader,
      appRestriction,
      appNew,
      appFooter,
    },
    data: () => {
      return {
        type: [{title: '同人', isActive: true}, {title: '非同人', isActive: false}],
        filesList: [],
        newData: null,
        permiss: {permiss: '1', act: ['1', '2']}, //权限初始化数据
        actId: '',  //活动id
        uid: '', //登录用户
      }
    },
    mounted(){
      let id = this.$route.params.id;
      if (id) {
        this.actId = id;
      }

      //用户是否登录
      this.isLogin();
    },
    methods: {
      setRes(value){
        this.permiss = value;
        console.log(this.permiss);
      },
      getFiles(value){ //获取图片信息
        this.filesList.push(value)
      },
      commit(value){
        this.newData = value;
        let _this_ = this;
        let baseUrl = document.querySelector('#drawing').toDataURL("image/png"); //获取base64
        let base64Data = baseUrl.replace(/^data:image\/(png|jpg);base64,/, "");

        this.getToken().then(token => {

          const uploadeBase = () => {
            return new Promise(resolve => {
              this.$http('common/uptoken', {
                params: {
                  access_token: token
                }
              }).then(res => {
                //上传base64
                const token = res.data.data.uptoken;
                _this_.Qiniu_Base64(base64Data, token).then(res => {
                  console.log(res);
                  _this_.newData.imgList.push(res);
                  resolve(res);
                })
              });
            })
          };
          const sendPain = (async () => { //异步提交
            await uploadeBase();
            this.$http('huazuo/addhuazuo', {
              params: {
                access_token: token,
                uid: this.uid,
                title: _this_.newData.title,
                imgs: _this_.newData.imgList,
                introduce: _this_.newData.desc,
                pictype: _this_.newData.pictype,
                author: _this_.newData.author,
                role: _this_.newData.role,
                tagsname: _this_.newData.marks,
                act: _this_.permiss.act,
                permiss: _this_.permiss.permiss,
                activityid: _this_.actId,
              }
            }).then(res => {
              console.log(res);
            }).catch(err => {
              console.log(err);
            })
          })();
        });
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../common/css/reset.css";
  @import "../../common/css/define";

  .paintingNew {
    max-width: 1180px;
    margin: 0px auto;

    > h6 {
      padding: 38px 0 20px 0;
      font-size: 22px;
      color: $black;
    }
  }

  #app {
    background-color: #fafafa;
  }

  .new, .restriction {
    background-color: #fff;
  }
</style>



