<!--主页组件-->
<template>
  <!--内容信息-->
  <section>
    <appSwiper></appSwiper>
    <section class="content">
      <section class="content__custom">
        <h6 class="content__title"><i></i>最新约稿<i></i></h6>
        <app-custom
          :custom='custom'>
        </app-custom>
        <a class="content__more" href="pact.html">查看更多</a>
      </section>
      <section class="content__design">
        <h6 class="content__title"><i></i>设计征稿<i></i></h6>
        <app-design
          :design='design'>
        </app-design>
        <a href="design.html?design" class="content__more">查看更多</a>
      </section>
      <section class="content__draw">
        <h6 class="content__title"><i></i>最赞画作<i></i></h6>
        <app-draw :draw='draw' :link="link"></app-draw>
        <a href="painting.html" class="content__more">查看更多</a>
      </section>
      <section>
        <h6 class="content__title"><i></i>最新画铺<i></i></h6>
        <app-goods :goods="goods" :link="link"></app-goods>
        <a href="pact.html?shop" class="content__more">查看更多</a>
      </section>
      <section class="content__daftSale">
        <h6 class="content__title"><i></i>待出售设计<i></i></h6>
        <app-daft-sale :daftSale='daftSale' :link="designLink"></app-daft-sale>
        <a href="design.html?daftSale" class="content__more">查看更多</a>
      </section>
      <section class="content__daftUsed">
        <h6 class="content__title"><i></i>已商用设计<i></i></h6>
        <app-daft-used :daftUsed='daftUsed' :link="usedDetLink"></app-daft-used>
        <a href="design.html?daftUsed" class="content__more">查看更多</a>
      </section>
      <section class="content__painter">
        <h6 class="content__title"><i></i>推荐画师<i></i></h6>
        <app-painter
          :value="margin"
          :items="items"
          :painter="painter"></app-painter>
        <a href="pact.html?painter" class="content__more">查看更多</a>
      </section>
    </section>
  </section>
</template>

<!--数据交互-->
<script>
  import appCustom from 'components/Index/Custom'  //约稿组件
  import appDesign from 'components/Index/Design'  //设搞组件
  import appDraw from 'components/Index/Draw'      //画作组件
  import appGoods from 'components/Index/Goods'    //画铺组件
  import appDaftSale from "components/Index/DaftSale";    //待出售设计组件
  import appDaftUsed from "components/Index/DaftUsed";    //已商用设计组件
  import appPainter from "components/Index/Painter";    //推荐画师组件
  import appSwiper from "components/Index/Swiper";    //轮播图组件

  export default {
    components: {
      appCustom,
      appDesign,
      appDraw,
      appGoods,
      appDaftSale,
      appDaftUsed,
      appPainter,
      appSwiper,
    },
    data: () => ({
      margin: 30, //painter__pic 行间样式
      items: [1, 3, 4, 6, 7, 8,],
      link: 'painting.html#/painting/', //跳转路径
      designLink: 'design.html#/design/', //待出售跳转路径
      usedDetLink: 'design.html#/design/', //已使用跳转路径
      custom: [],//最新约稿
      design: [],//设计征稿
      goods: [],//最新画铺数据
      draw: [], //最赞画作数据
      daftUsed: [],//已出售设计
      daftSale: [],//待出售设计
      painter: [],//推荐画师
      uid: '500',
      followId: [], //已关注的用户
      callbackUrl: '', //登录成功后回调地址
      code: '', //第三方登录code
      source: '' //第三方登录来源
    }),
    mounted(){
      console.log((this.getUserInfo('id')));
      this.fetchData(); //获取数据
      this.getPraise();//获取赞产品
      let type = location.search.substring(1);
      if (type.indexOf('weibo') !== -1) { //微博登录
        this.code = type.split('&')[1].split('=')[0];
        this.source = 'weibo'
      } else { //qq登录
        this.code = type.split('&')[0].split('=')[1];
        this.source = 'tencent'
      }
      if (this.code) {
        this.toLogin();
      }
    },
    methods: {
      fetchData(){
        let _this_ = this;
        this.getToken().then(token => { //获取公共token

          //最新约稿
          this.$http.get('index/newyuegao', {
            params: {
              access_token: token
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this_.custom = res.data.data.list;

            } else {
              console.log('数据异常');
            }
            console.log(res);
          }).catch(err => {
            console.log(err);
          });

          //获取征稿
          this.$http.get('index/newzhenggao', {
            params: {
              access_token: token
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this_.design = res.data.data.list;
            } else {
              console.log('数据异常', res.data);
            }
          }).catch(err => {
            console.log(err);
          });

          //获取最赞画作
          this.$http.get('index/newhuazuo', {
            params: {
              access_token: token
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this_.draw = res.data.data.list
            }
          }).catch(err => {
            console.log(err);
          });

          //获取最新画铺
          this.$http.get('index/newhuapu', {
            params: {
              access_token: token
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this_.goods = res.data.data.list
            }
          }).catch(err => {
            console.log(err);
          });

          //获取待出售设计
          this.$http.get('index/newsheji', {
            params: {
              busintype: "1",
              access_token: token
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this_.daftSale = res.data.data.list;
              console.log(_this_.daftSale)
            }
          }).catch(err => {
            console.log(err);
          });

          //获取已出售设计
          this.$http.get('index/newsheji', {
            params: {
              busintype: "2",
              access_token: token
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this_.daftUsed = res.data.data.list;
              console.log(_this_.daftUsed)
            }
          }).catch(err => {
            console.log(err);
          });

          //获取推荐画师
          this.$http.get('index/newpainter', {
            params: {
              busintype: "2",
              access_token: token
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              _this_.painter = res.data.data.list;
            }
          }).catch(err => {
            console.log(err);
          });

          //获取已关注的画师
          if (this.uid) { //是否登录
            this.getFollowId(this.uid, token);
          }
        })
      },

      //获取点赞
      getPraise(){
        this.getUsers('1', { //点赞
          uid: 18202,
          name: 'picture_praise'
        }).then(res => {
          let data = res.data.data.list.map(item => {
            return item.moduleid
          });
          this.$store.dispatch('onPraiseUsers', data);
        });
      },

      //登录成功跳转
      toLogin(){
        this.getToken().then(token => {
          this.$http.get('login/thirdlogin', {
            params: {
              access_token: token,
              acode: this.code,
              source: this.source
            }
          }).then(res => {
            if (res.data.stats === '2000') {
              location.href = localStorage.url;
            } else {
              alert(res.data.msg)
            }
          }).catch(err => {
            console.log(err);
          })
        })
      }
    }
  }
</script>

<!--组件样式-->
<style scoped lang="scss">
  @import "../../common/css/define";
  @import '../../common/css/reset.css';

  .content {
    background-color: #fafafa;
    h6 {
      padding: 40px 0 30px 0;
    }
    > section {
      padding-bottom: 30px;
    }
    .content__title {
      font-size: $titleSize;
      color: $black;
      text-align: center;
      > i {
        display: inline-block;
        width: 20px;
        border: 2px solid $pink;
        border-radius: 4px;
        vertical-align: middle;
        margin: 0 4px;
      }
    }
    .content__custom, .content__draw, .content__daftSale, .content__painter {
      background-color: #fff;
    }
    .content__design {

    }
    .content__more {
      display: block;
      text-align: center;
      color: $blue;
      font-size: 12px;
      margin-top: 18px;
    }
  }

</style>



