<!-- 2017/3/22- yyy 瀑布流 公共组件(4列+采用盒模型布局--仿瀑布流)-->
<template>
    <section class="flow">
      <ul class="flow__ul">
        <li class="flow__list">
          <EveryList :Paint="Paint01"></EveryList>
        </li>

        <li class="flow__list">
          <EveryList :Paint="Paint02"></EveryList>
        </li>

        <li class="flow__list">
          <EveryList :Paint="Paint03"></EveryList>
        </li>

        <li class="flow__list">
          <EveryList :Paint="Paint04"></EveryList>
        </li>

      </ul>
      <div class="flow__more" @click="loadMore" v-if="loadMoreShow">{{loadOver}}</div>
    </section>
</template>

<script>
  import EveryList from 'components/Common/EveryList'
  export default {
    props: ['items','loadOver','loadMoreShow'],
    components: {
      EveryList
    },
    data:()=>({
      Paint01:[],
      Paint02:[],
      Paint03:[],
      Paint04:[],
    }),
    mounted(){
      if(this.items.length !== 0){
        this.setList(this.items);
      }
    },
    watch:{
      'items'(val){
        this.setList(val);
      }
    },
    methods:{
      loadMore(){
        this.$emit("loadMore")
      },
      setList(val){
        for (let i = 0; i < val.length; i++) {
          let serial = (i % 4);
          switch (serial) {
            case 0:
              this.Paint01.push(val[i]);
              break;
            case 1:
              this.Paint02.push(val[i]);
              break;
            case 2:
              this.Paint03.push(val[i]);
              break;
            case 3:
              this.Paint04.push(val[i]);
              break;
          }
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../common/css/reset.css";
  @import "../../common/css/define";

  .flow__ul {
    width: 1180px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .flow__more {
    text-align: center;
    margin: 20px auto;
    font-size: 16px;
    font-style: italic;
    color: $paleBlue;
    cursor: pointer;
  }
</style>



