<!--发布征稿组件-->
<template>
  <div class="pact-send">
    <h6 class="pact-send__title">发布征稿</h6>
    <section class="pact-send__content">
      <dl>
        <dd class="pact-send__name">
          <h6>&#12288;&#12288;标题</h6>
          <input type="text"
                 v-model="newData.title"/>
        </dd>
        <dd class="pact-send__price">
          <h6>单张预算</h6>
          <input type="text"
                 v-model="newData.lowcost"/>
          <i>-</i>
          <input type="text"
                 v-model="newData.highcost"/> 元/张
        </dd>
        <dd class="pact-send__need">
          <h6>需求张数</h6>
          <input ref="readonly" class="pact-send__input" type="text"
                 v-model="newData.imgcount"/>
          <label for="need" @click='handleEdit'>
            <input id="need" type="checkbox" v-model="checked">
            不限
          </label>
        </dd>
        <dd class="pact-send__gift">
          <h6>投稿福利</h6>
          <input type="text"
                 v-model="newData.gift"/>
        </dd>
        <dd class="pact-send__time">
          <h6>截稿日期</h6>
          <DatePicker
            @onSubmitEndTime="onSubmitEndTime">
          </DatePicker>
        </dd>
        <dd class="pact-send__tags">
          <h6>&#12288;&#12288;标签</h6>
          <input v-model="paramsTag" type="text"/>
          <dl class="pact-send__tags-content">
            <dt>推荐标签</dt>
            <dd class="pact-send__tags-use" v-for="item in tags">
              <section>
                <i
                  @click="handleGettags(list.tags,list.id)"
                  v-for="list in tags">{{list.tags}}</i>
              </section>
            </dd>
          </dl>
        </dd>
        <dd>
          <h6>约稿详情</h6>
          <section class="editor">
            <EditorView :bodyContent="propsContent" @onContent='onContent'></EditorView>
          </section>
        </dd>
        <dd class="pact-send__replay">
          <h6>反馈期限</h6>
          <input type="text"
                 v-model="newData.replaytime"/>天内回复
        </dd>
        <dd class="pact-send__tags-btn"
            @click="handleSend">发布
        </dd>
      </dl>
    </section>
  </div>
</template>

<!--数据交互-->
<script>
  import DatePicker from 'components/Common/DatePicker' //时间选择组件
  import EditorView from 'components/Common/Editor' //富文本编辑器组件

  export default {
    components: {
      DatePicker,
      EditorView,

    },

    created(){
      this.getMarks();
      //判断用户是否登录
      this.isLogin();
    },
    data: () => ({
      isreadonly: false,
      propsContent: ``,
      tags: [],
      paramsTag: [], //标签属性
      uid: '', //登录用户uid
      //用户输入数据
      newData: {
        title: [],
        lowcost: '',
        highcost: '',
        imgcount: '',
        gift: '',
        tagsid: [],
        replaytime: '',
        content: '',
        endtime: ''
      },
      checked: false,
    }),
    methods: {
      //获取权限
      setRes(val){
        console.log(val);
        this.newData.permiss = val.permiss;
      },
      //获取截稿日期
      onSubmitEndTime(t){
        this.newData.endtime = t;
        console.log(t);
      },
      //获取稿件类型
      onSubmitSelect(val){
        this.newData.notetype = val;
      },

      //获取标签
      getMarks(){
        this.getToken().then(token => {
          this.$http('common/gettags', {
            params: {
              access_token: token,
              tagtype: 1
            }
          }).then(res => {
            let data = res.data.data.list;
            data.forEach(item => {
              if (item.type === '4') {
                this.tags.push(item);
              }
            })
          }).catch(err => {
            console.log(err);
          })
        })
      },

      //获取富文本内容
      onContent(val){
        this.newData.content = val;
      },

      //选择标签
      handleGettags(name, id){
        console.log(name, id);
        this.paramsTag.push(name);
        this.newData.tagsid.push(id);
        //去重
        this.paramsTag = [...new Set(this.paramsTag)];
        this.newData.tagsid = [...new Set(this.newData.tagsid)];
      },
      //发布约稿
      handleSend(){
        let _this_ = this;
        this.getToken().then(token => {
          this.$http('zhenggao/addzhenggao', {
            params: {
              access_token: token,
              uid: this.uid,
              title: _this_.newData.title,
              lowcost: _this_.newData.lowcost,
              highcost: _this_.newData.highcost,
              imgcount: _this_.newData.imgcount,
              gift: _this_.newData.gift,
              tagsid: _this_.newData.tagsid,
              content: _this_.newData.content,
              endtime: _this_.newData.endtime
            }
          }).then(res => {
            console.log(res);
            if (res.data.stats === '2000') {
              alert('发布成功')
            }
          }).catch(err => {
            console.log(err);
          })
        })
      },
      //设置是否限制张数
      handleEdit(){
        if (this.checked) { //限制
          this.$refs.readonly.setAttribute('readonly', 'true');
          this.newData.imgcount = '';
        } else { //不限制
          this.$refs.readonly.removeAttribute('readonly');
        }
      }
    }
  }
</script>

<!--样式控制-->
<style lang="scss" scoped>
  @import "../../common/css/define";

  .pact-send {
    max-width: 1180px;
    margin: 0 auto;
    color: $black;
    .pact-send__title {
      font-size: 22px;
      font-weight: bold;
      margin: 40px 0 20px 0;
    }
    .pact-send__content {
      width: 760px;
      padding: 40px;
      background-color: #fff;
      margin-bottom: 20px;
      display: inline-block;
      @include wrapShow;

      > dl > dd {
        margin-bottom: 40px;
        font-size: 14px;
        > h6 {
          display: inline-block;
          font-size: 14px;
          font-weight: normal;
          margin-right: 18px;
        }
        .date {
          display: inline-block;
        }
      }
      .pact-send__name {
        > input {
          width: 478px;
          height: 36px;
          border: 1px solid #e6e6e6;
          border-radius: 2px;
          text-indent: 1em;
          font-size: 14px;
        }
      }
      .pact-send__need, .pact-send__replay {
        > input {
          width: 190px;
          height: 36px;
          border: 1px solid #e6e6e6;
          text-align: center;
          margin-right: 10px;
        }
      }
      .pact-send__gift {
        > input {
          width: 480px;
          height: 36px;
          border: 1px solid #e6e6e6;
          text-align: center;
          margin-right: 10px;
        }
      }
      .pact-send__price {
        > input {
          width: 94px;
          height: 36px;
          border-radius: 2px;
          border: 1px solid #e6e6e6;
          text-align: center;
        }
        > i {
          color: #e6e6e6;
          margin: 0 6px;
        }
      }
      .pact-send__tags {
        > input {
          width: 478px;
          height: 36px;
          text-indent: 1em;
          border-radius: 2px;
          border: 1px solid #e6e6e6;
        }
        .pact-send__tags-content {
          margin-left: 76px;
          margin-top: 20px;
          > dt {
            margin-bottom: 20px;
            font-size: 14px;
          }
          > dd {
            font-size: 0;
            > h6 {
              display: inline-block;
              font-size: 12px;
              font-weight: normal;
              vertical-align: top;
              line-height: 20px;
            }
            section {
              display: inline-block;
              width: 610px;
              vertical-align: top;
              margin-left: 4px;
              > i {
                display: inline-block;
                vertical-align: top;
                height: 20px;
                padding: 0 12px;
                border: 1px solid #e6e6e6;
                color: #666;
                font-size: 12px;
                border-radius: 2px;
                line-height: 20px;
                text-align: center;
                margin: 0 8px 8px 0;
                cursor: pointer;
              }
            }
          }
        }
      }
      .pact-send__tags-btn {
        width: 124px;
        height: 50px;
        border-radius: 4px;
        color: #fff;
        line-height: 50px;
        text-align: center;
        font-size: 16px;
        cursor: pointer;
        margin: 70px 0 0 76px;
        @include boxShow;
        @include linear;
      }
    }
    .editor {
      display: inline-block;
      width: 600px;
      vertical-align: top;
    }
  }
</style>



