<!--画作列表组件-->
<template>
  <div>
    <app-menu
      :menus="menus"
      @change="change"
      @search="search"></app-menu>
    <!--<Waterfall :line-gap="290" :watch="items">-->
    <!--&lt;!&ndash; each component is wrapped by a waterfall slot &ndash;&gt;-->
    <!--<WaterfallSlot-->
    <!--v-for="(item, index) in items"-->
    <!--:width="item.width"-->
    <!--:height="item.height"-->
    <!--:order="index"-->
    <!--:key="item.id">-->
    <ul class="list"
        ref="list">
      <li v-for="(item, index) in items">
        <router-link :to="/painting/+item.id" class="list__pic">
          <img :src="`${JSON.parse(item.imgs)[0]}`" alt=""/>
          <i class="list__mark">{{JSON.parse(item.imgs).length}}P</i>
        </router-link>
        <section class="list__info">
          <section class="list-header">
            <section class="list-header__img">
              <img :src="item.avatar" alt=""/>
            </section>
            <p :title="item.nickname">{{item.nickname}}</p>
          </section>
          <section class="list__time">
            {{item.createtime | UnixToNormalTime(2)}}
          </section>
          <section class="list__hot">
            <i>{{item.bcount ? item.bcount : 0}}</i>
            <PraiseCommon
              :praise="{moduleid:item.id,count:item.pcount,uid: '18202',authorid: item.uid,name: 'design_praise'}"></PraiseCommon>
          </section>
        </section>
        <ul class="list-comment">
          <li v-for="i in 2">
            <section class="list-comment__header">
              <img src="./images/user_pic_big.png" alt=""/>
            </section>
            <section class="list-comment__user">
              <i class="list-comment__name">天道</i>：
              <i class="list-comment__content">好棒！</i>
            </section>
          </li>
        </ul>
      </li>
    </ul>
    <!--</WaterfallSlot>-->
    <!--</Waterfall>-->
    <!--<div class="loading"-->
    <!--v-loading="loading"-->
    <!--element-loading-text="加载中...">-->
    <!--</div>-->
    <div
      @click="handleLoading"
      v-show="loading"
      class="list-next">点击加载下一页
    </div>
  </div>
</template>

<!--数据交互-->
<script>
  import Waterfall from 'vue-waterfall/lib/waterfall'
  import WaterfallSlot from 'vue-waterfall/lib/waterfall-slot'
  import appMenu from 'components/Common/Bar'
  import PraiseCommon from 'components/Common/PraiseCommon'

  export default {
    components: {
      Waterfall,
      WaterfallSlot,
      appMenu,
      PraiseCommon
    },
    data: () => {
      return {
        key: '',
        menus: [
          {title: '最赞', isActive: true},
          {title: '最新', isActive: false},
        ],
        //列表数据
        items: [],
        scroll: 0,
        page: 10,
        loading: false
      }
    },
    props: ['url'],
    mounted(){
      //todo 点击详情页返回后发起多次请求
      this.fetchData(this.page);
      window.addEventListener('scroll', this.handleScroll, false);
      this.getModuleId();
    },
    methods: {
      handleScroll(){ //计算到底部距离
        if (this.getScroll.getScrollTop() + this.getScroll.getWindowHeight() === this.getScroll.getScrollHeight()) {
          this.loading = true;
        }
      },
      fetchData(page){
        let _this_ = this;
        this.getToken().then(token => {
          this.$http.get(this.url, { //获取画作列表
            params: {
              access_token: token,
              order: 'createtime',
              page: page
            }
          }).then(res => {
            let arr = res.data.data.list;
            this.items = this.items.concat(arr);
          }).catch(err => {
            console.log(err);
          })
        })
      },

      change(index){ //菜单切换
        this.menus.forEach(item => {
          item.isActive = false;
        });
        this.menus[index].isActive = 'true';
      },

      //获取点赞产品id
      getModuleId(){
        this.getUsers('1', { //点赞
          uid: 18202,
          name: 'picture_praise'
        }).then(res => {
          let data = res.data.data.list.map(item => {
            return item.moduleid
          });
          this.$store.dispatch('onPraiseUsers', data);
        });
      },

      //点击加载下一页
      handleLoading(){
        this.page++;
        this.fetchData(this.page);
        this.loading = false;
      },
      search(){

      }
    }
  }
</script>


<!--组件样式-->
<style scoped lang="scss">
  @import "../../common/css/define";

  .list {
    transition: 0.3s;
    column-width: 284px;
    font-size: 0;
    > li {
      width: 280px;
      display: inline-block;
      vertical-align: top;
      border-radius: 4px;
      overflow: hidden;
      margin: 0 20px 20px 0;
      @include wrapShow;
      &:nth-of-type(4n) {
        margin-right: 0;
      }
    }
    .list__pic {
      display: block;
      position: relative;
      width: 100%;
      > img {
        width: 100%;
      }
      > i {
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 30px;
        height: 20px;
        background-color: rgba(0, 0, 0, 0.3);
        border-radius: 6px 0 6px 0;
        color: #fff;
        font-size: 12px;
        text-align: center;
        line-height: 20px;
      }
    }
    .list__info {
      font-size: 12px;
      padding: 12px;
      .list-header {
        display: inline-block;
        vertical-align: middle;
        .list-header__img {
          background-color: darkgoldenrod;
          width: 20px;
          height: 20px;
          border-radius: 50%;
          overflow: hidden;
          display: inline-block;
          vertical-align: middle;
          > img {
            width: 100%;
          }
        }
        > p {
          width: 60px;
          display: inline-block;
          padding: 0 6px;
          vertical-align: middle;
          color: $black;
          @include beyondPass;
        }
      }
      .list__time {
        display: inline-block;
        vertical-align: middle;
        color: #999;
      }
      .list__hot {
        display: inline-block;
        vertical-align: middle;
        color: #999;
        float: right;
        height: 20px;
        line-height: 20px;
        > i {
          display: inline-block;
          width: 30px;
          height: 100%;
          text-align: right;
          &:nth-of-type(1) {
            background: url("../../components/Common/images/person@hot.png") no-repeat 0 center;
            margin-right: 10px;
          }
          &:nth-of-type(2) {
            cursor: pointer;
            background: url("../../components/Common/images/Parise@def.png") no-repeat 0 center;
          }
          &.is--praise {
            background: url("../../components/Common/images/praise@single.png") no-repeat 0 center;
          }
        }
      }
    }
    .list-comment {
      padding: 0 12px 12px 12px;
      > li {
        margin-bottom: 10px;
      }
      .list-comment__header {
        width: 20px;
        height: 20px;
        border-radius: 50%;
        overflow: hidden;
        display: inline-block;
        vertical-align: middle;
        > img {
          width: 100%;
        }
      }
      .list-comment__user {
        font-size: 12px;
        display: inline-block;
        vertical-align: middle;
        padding-left: 6px;
        color: $black;
      }
    }
  }

  .list-next {
    text-align: center;
    font-size: 14px;
    line-height: 30px;
    cursor: pointer;
    padding-top: 10px;
  }
</style>



