<!--约稿详情内容组件-->
<template>
  <dl class="pact-detail__desc-info">
    <!--征稿信息稿费-->
    <slot name="title"></slot>
    <dd class="pact-detail__desc-tips">
      <section v-html="content">
      </section>
    </dd>
    <dd class="pact-detail__desc-btn">
      <i>分享icon</i>
      <router-link
        tag="em"
        :to="link">{{btnName}}
      </router-link>
    </dd>
    <dd class="pact-detail__input"
        v-if="isShow">
      <textarea placeholder="你可在这里评论"
                v-model="text"></textarea>
      <section class="pact-detail__input-btn">
        <i @click="handleComment">发表评论</i>
      </section>
    </dd>
  </dl>
</template>

<!--数据交互-->
<script>
  export default {
    data: () => ({
      text: '',
      isShow: true //控制评论是否展示
    }),
    mounted(){
      let type = this.$route.fullPath;
      if (type.indexOf('collect') !== -1) {  //征稿组件不展示
        this.isShow = false;
      }
    },
    methods: {
      handleComment(){
        this.$emit('onSubmitComment', this.text)
      }
    },
    props: ['menus', 'content', 'btnName', 'link'],
  }
</script>

<!--组件样式-->
<style lang="scss">
  @import "../../common/css/define";

  .pact-detail__desc-info {
    > dd {
      margin-bottom: 26px;
      > h6 {
        font-size: 16px;
        margin-bottom: 26px;
      }
      > section {
        line-height: 22px;
        font-size: 14px;
        color: #666;
      }
    }
    .pact-detail__desc-tips {
      img {
        width: 100%;
      }
      > section {
        overflow: hidden;
        > p {
          line-height: 22px;
          font-size: 14px;
          color: #666;
        }
      }
    }
    .pact-detail__desc-pic {
      > section {
        > img {
          width: 100%;
        }
      }
    }
    .pact-detail__desc-btn {
      text-align: center;
      > i {
        display: block;
        margin: 0 auto;
        width: 48px;
        height: 48px;
        background: url("./images/share@icon.png") no-repeat center;
        cursor: pointer;
        text-indent: -999em;
      }
      > em {
        display: inline-block;
        width: 120px;
        height: 50px;
        background-color: $pink;
        font-size: 14px;
        border-radius: 4px;
        line-height: 50px;
        color: #fff;
        margin-top: 12px;
        cursor: pointer;
        @include linear;
        @include boxShow;
      }
    }
  }

  .pact-detail__input {
    > textarea {
      width: 794px;
      height: 118px;
      border: 1px solid #e6e6e6;
      resize: none;
      border-radius: 2px;
      font-size: 14px;
      display: inline-block;
      margin: 0 auto;
    }
    .pact-detail__input-btn {
      text-align: right;
      > i {
        display: inline-block;
        width: 128px;
        height: 48px;
        text-align: center;
        color: #fff;
        line-height: 48px;
        border-radius: 4px;
        font-size: 14px;
        margin-top: 12px;
        cursor: pointer;
        @include boxShow;
        @include linear;
      }
    }
  }
</style>



