<!--征稿-->
<template>
  <div class="collect-item clearfix">
    <dl v-for="item in ComCollect" class="collect-item__list">
      <dt class="collect-item__img">
        <a :href="'personal.html#/accounts/products/'+item.uid" target="_blank"><img :src="item.avatar" alt=""/></a>
      </dt>
      <dd class="collect-item__num">
        <img src="./images/toptr.png" alt="">
        <em>{{item.id}}</em>
      </dd>
      <dd class="collect-item__name">
        <h6><a :href="'personal.html#/accounts/products/'+item.uid" target="_blank">{{item.nickname}}</a></h6>
        <p>+关注</p>
      </dd>
      <dd class="collect-item__info">
        <h6><a :href="'design.html#/collectDet/'+item.id" target="_blank">{{item.title}}</a></h6>
        <section class="collect-item__marks" v-if="item.tag == null">该征稿暂无标签</section>
        <section class="collect-item__marks" v-else v-for="value in item.tag">{{value.tags}}</section>
        <ul class="collect-item__hot">
          <li>截稿日期：<i>{{item.endtime | UnixToNormalTime(0)}}</i></li>
          <li>单张预算：<i>{{item.lowcost}}~{{item.highcost}}</i></li>
          <li>已投稿件：<i>{{item.personcount}}&#8197;张</i></li>
          <li>反馈时间：<i>{{item.replaytime}}&#8197;天内</i></li>
        </ul>
      </dd>
      <dd class="collect-item__btn">
        <a :href="'design.html#/collect/'+item.id" target="_blank">去投稿</a>
      </dd>
      <dd class="collect-item__welfare">福利:{{item.gift}}</dd>
    </dl>
    <Pagination :pageCount="collectPageCount" :pageSize="collectPageSize" @pageChange="collectPageChange"></Pagination>
  </div>
</template>

<!--数据交互-->
<script>
  import Pagination from 'components/Common/Pagination' //分页组件
  export default {
    props:["ComCollect","collectPageCount","collectPageSize"],
    components:{
      Pagination
    },
    methods:{
      collectPageChange(val){
        this.$emit("collectPageChange",val)
      }
    }
  }
</script>

<!--组件样式-->
<style lang="scss" scoped>
  @import "../../common/css/define";
  .collect-item {
    width: 1180px;
    background-color: #fafafa;

    >dl {
      padding: 30px 20px 30px 85px;
      margin:20px 0 20px 0;
      @include wrapShow;
      position: relative;
    }
  }
  .collect-item__num {
    position: absolute;
    top: 0;
    left: 20px;
    img {
      display: block;
      width: 44px;
      height: 44px;
    }
    em {
      width: 44px;
      height: 40px;
      position: absolute;
      top: 0;
      left: 0;
      text-align: center;
      line-height: 40px;
      color: $white;
    }
  }
  .collect-item__img {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    overflow: hidden;
    display: inline-block;
    vertical-align: top;
    >img {
      width: 100%;
    }
  }
  .collect-item__name {
    display: inline-block;
    vertical-align: top;
    margin: 12px 60px 0 10px;
    >h6 {
      font-size: 14px;
      width: 80px;
      @include beyondPass;
      font-weight: normal;
      a {
        color: $black;
      }
    }
    >p {
      width: 48px;
      height: 22px;
      border-radius: 2px;
      background-color: $green;
      color: #fff;
      line-height: 22px;
      text-align: center;
      font-size: 12px;
      margin-top: 10px;
      cursor: pointer;
    }
  }
  .collect-item__info {
    display: inline-block;
    vertical-align: top;
    >h6 {
      font-size: 16px;
      font-weight: bold;
      a {
        color: $black;
      }
    }
    .collect-item__marks {
      font-size: 12px;
      color: #999;
      margin: 10px 0;
    }
    .collect-item__hot {
      >li {
        display: inline-block;
        font-size: 14px;
        margin-right: 16px;
        line-height: 30px;

        >i {
          color: $pink;
        }
      }
    }
  }
  .collect-item__welfare {
    width: 1140px;
    font-size: 14px;
    margin-top: 30px;
    padding-top: 20px;
    font-weight: bold;
    color: $pink;
    border-top: 1px solid $lightgrey;
    position: relative;
    top:0;
    left: -65px;
  }
  .collect-item__btn {
    display: inline-block;
    vertical-align: top;
    font-size: 14px;
    width: 100px;
    height: 40px;
    line-height: 40px;
    transition: 0.3s;
    text-align: center;
    border-radius: 4px;
    cursor: pointer;
    color: $white;
    margin-top: 20px;
    float: right;
    @include boxShow;
    @include linear;
    &:active {
      box-shadow: none;
    }
    a {
      display: block;
      width: 100%;
      height: 100%;
      color: $white;
    }
  }
</style>



